<template>
  <div :class="baseClass">
    <div
      :style="barStyle"
      :class="barClass"/>
    <div
      v-if="hasDefaultSlot"
      :style="barStyle"
      class="bui-progress-innertext">
      <slot/>
    </div>
  </div>
</template>
<script>
import {CSS_PREFIX} from 'components/base.js';
import {isOneOf} from 'utils/helper.js';

export default {
  name: `${CSS_PREFIX}progress`,

  props: {
    percent: {
      type: Number,
      default: 0,
    },
    status: {
      type: String,
      default: 'default',
    },
    active: {
      type: Boolean,
      default: false,
    },
    striped: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, [
          'md',
          'small',
          'large',
          'xs',
          'sm',
          'lg',
          'xl',
        ]);
      },
    },
  },
  computed: {
    baseClass() {
      const baseClass = `${CSS_PREFIX}progress`;
      const classArr = [baseClass, `${baseClass}-${this.size}`, `${baseClass}-${this.status}`];
      if (this.hasDefaultSlot) {
        classArr.push(`${baseClass}-inner`);
      }
      return classArr;
    },
    barClass() {
      const classArr = ['bui-progress-bar'];
      if (this.status) {
        classArr.push(`progress-bar-${this.status}`);
      }
      if (this.active) {
        classArr.push(`active`);
      }
      if (this.striped) {
        classArr.push(`progress-bar-striped`);
      }
      return classArr;
    },
    barStyle() {
      if (this.percent > 100 || this.percent < 0) {
        console.warn(`${CSS_PREFIX}progress percent 输入不合法`);
      }
      return {width: this.percent + '%'};
    },
    hasDefaultSlot() {
      return this.$slots && this.$slots.default;
    },
  },
};
</script>



