<template>
  <div
    :class="baseClass"
    class="bui-color-option"
    @click="handleClick">
    <div
      :style="bgStyle"
      class="bui-color-option-block">
      <byted-icon
        class="bui-color-option-icon-close"
        name="close"/>
    </div>
    <checkboard/>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';
import Checkboard from './common/Checkboard.vue';
import {isOneOf} from 'utils/helper.js';

export default {
  name: `${CSS_PREFIX}color-option`,
  components: {
    Checkboard,
  },
  mixins: [emitter],
  props: {
    value: {
      type: String,
      required: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: '',
      validator(value) {
        return isOneOf(value, [
          '',
          'md',
          'xs',
          'sm',
          'lg',
          'xl',
        ]);
      },
    },
  },

  data() {
    return {
      // 匹配白色背景色
      whiteColrReg: [/^#([fF]{3}|[fF]{6})$/,
        /(^rgb\(255,\s*255,\s*255\)$)|(^rgba\(255,\s*255,\s*255(,\s*\d+\.*\d*)*\)$)/],
    };
  },
  computed: {
    baseClass: function() {
      const classArr = [`${CSS_PREFIX}color-option`];
      if (!this.value) {
        classArr.push(`bui-color-option-none`);
      }
      if (this.size) {
        classArr.push(`bui-color-option-${this.size}`);
      }
      return classArr;
    },

    bgStyle() {
      const style = {
        backgroundColor: this.value,
      };
      if (!this.whiteColrReg.some((reg) => reg.test(this.value))) {
        style.borderColor = this.value;
      }
      return style;
    },
  },
  methods: {
    handleClick(e) {
      if (this.disabled) {
        return;
      }
      this.$emit('click', this.value);
    },
  },
};
</script>




