<template>
  <ul :class="baseClass"><slot/></ul>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';

export default {
  name: `${CSS_PREFIX}dropdown-menu`,
  computed: {
    baseClass() {
      return [`${CSS_PREFIX}dropdown-menu`];
    },
  },
};
</script>



