<template>
  <span
    :class="basicClass"
    :style="{backgroundColor: color}">
    <slot/>
    <byted-icon
      v-if="closable"
      class="bui-tag-close"
      name="close"
      @click.stop="handleClose"/>
  </span>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import {isOneOf} from 'utils/helper.js';
import BytedIcon from 'components/icon';

export default {
  name: `${CSS_PREFIX}tag`,
  components: {
    BytedIcon,
  },
  props: {
    text: {
      type: String,
      default: '',
    },
    closable: {
      type: Boolean,
      default: false,
    },
    type: {
      type: String,
      default: 'default',
      validator(value) {
        return isOneOf(value, ['default', 'success', 'primary', 'info', 'warning', 'danger']);
      },
    },
    color: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, ['sm', 'md', 'lg']);
      },
    },
    plain: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    basicClass() {
      const classArr = [`${CSS_PREFIX}tag`, `${CSS_PREFIX}tag-${this.type}`, `${CSS_PREFIX}tag-${this.size}`];
      if (this.closable) {
        classArr.push(`${CSS_PREFIX}tag-closable`);
      }
      if (this.disabled) {
        classArr.push(`${CSS_PREFIX}tag-disabled`);
      }
      if (this.plain) {
        classArr.push(`${CSS_PREFIX}tag-plain`);
      }
      return classArr;
    },
  },

  methods: {
    handleClose(event) {
      if (this.disabled) return;
      this.$emit('close', event);
    },
  },
};
</script>



