<template>
  <transition
    name="fade"
    @after-enter="afterEnter"
    @after-leave="afterLeave">
    <div
      v-show="display"
      :class="baseClass">
      <div class="bui-message-content">
        <byted-icon
          v-if="typeObj && typeObj.name !== 'loading'"
          :name="typeObj.name"
          class="bui-message-icon"/>
        <Loading
          v-else-if="typeObj && typeObj.name === 'loading'"
          size="md"
          class="bui-message-icon"/>
        <span
          :title="message"
          class="bui-message-text"
          v-html="message"/>
      </div>
    </div>
  </transition>
</template>

<script>
import {isOneOf} from 'utils/helper.js';
import {CSS_PREFIX} from '../../base.js';
import BytedIcon from 'components/icon';
import Loading from 'components/loading';

export default {
  name: `${CSS_PREFIX}message`,
  components: {
    BytedIcon,
    Loading,
  },

  props: {
    display: {
      type: Boolean,
      default: false,
    },
    message: {
      type: String,
      default: '',
    },
    type: {
      type: String,
      default: 'default',
      validator(value) {
        return isOneOf(value, [
          'default',
          'success',
          'warning',
          'info',
          'error',
          'loading',
        ]);
      },
    },
    duration: {
      type: Number,
      default: 2000,
    },
  },

  data() {
    return {
      timer: null,
      CSS_PREFIX: CSS_PREFIX,
    };
  },

  computed: {
    baseClass: function() {
      return [`${CSS_PREFIX}message`, `${CSS_PREFIX}message-${this.type}`];
    },
    typeObj() {
      let ret = false;
      switch (this.type) {
        case 'success':
          ret = {
            name: 'check-circle',
          };
          break;
        case 'error':
          ret = {
            name: 'close-circle',
          };
          break;
        case 'warning':
          ret = {
            name: 'exclamation-circle',
          };
          break;
        case 'info':
          ret = {
            name: 'info-circle',
          };
          break;
        case 'loading':
          ret = {
            name: 'loading',
          };
          break;
        default:
          break;
      }
      return ret;
    },
  },

  methods: {
    afterEnter() {
      if (this.display && this.duration !== -1) {
        this.timer = setTimeout(() => {
          this.$emit('remove');
        }, this.duration);
      }
    },
    afterLeave() {
      this.$el.parentNode.removeChild(this.$el);
      this.$emit('dismiss');
    },
  },
};
</script>



