<template>
  <div
    :style="styles"
    :class="baseClass">
    <slot/>
  </div>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import {isOneOf} from 'utils/helper.js';
export default {
  name: `${CSS_PREFIX}row`,
  props: {
    // 区块间隔 单位px
    gutter: {
      type: [String, Number],
      default: 0,
    },
    // 垂直对齐方式
    align: {
      type: [String],
      default: 'top',
      validator(value) {
        return isOneOf(value, ['top', 'middle', 'bottom']);
      },
    },
    // 水平排列方式
    justify: {
      type: [String],
      default: 'start',
      validator(value) {
        return isOneOf(value, [
          'start',
          'end',
          'center',
          'space-around',
          'space-between',
        ]);
      },
    },
  },

  data: function() {
    return {
      prefix: CSS_PREFIX,
    };
  },
  computed: {
    styles() {
      let style = {};
      style = {
        marginLeft: this.gutter / -2 + 'px',
        marginRight: this.gutter / -2 + 'px',
      };
      return style;
    },
    baseClass() {
      const baseClass = `${CSS_PREFIX}row`;
      return [
        baseClass,
        `${baseClass}-flex`,
        `${baseClass}-flex-${this.align}`,
        `${baseClass}-flex-${this.justify}`,
      ];
    },
  },
};
</script>



