<template>
  <div class="sidebar">
    <div
      v-for="(list, index) in menus"
      :key="index"
      class="list-folder">
      <!-- 一级菜单title -->
      <div
        :class="{'is-open': list.isOpen,'is-closed': !list.isOpen}"
        class="list-title">
        <div
          v-if="hasChildren(list)"
          class="title-block"
          @click="onListClick(index)">
          {{ list.name }}
          <span class="title-angle pull-right glyphicon glyphicon-menu-down"/>
        </div>
        <template v-else>
          <a
            v-if="!router"
            :class="{current: list.selected}"
            :href="list.url"
            class="title-block">
            {{ list.name }}
          </a>
          <router-link
            v-else
            :class="{current: list.selected}"
            :to="list.url"
            class="title-block">
            {{ list.name }}
          </router-link>
        </template>
      </div>
      <!-- / 一级菜单title -->
      <!-- 二级子菜单 -->
      <bui-collapse-transition>
        <div
          v-show="list.isOpen"
          class="list-panel">
          <ul>
            <li
              v-for="(item, index) in list.children"
              :key="index"
              class="list-item">
              <a
                v-if="!router"
                :href="item.url"
                :class="{current: item.selected}">
                {{ item.name }}
              </a>
              <router-link
                v-else
                :class="{current: item.selected}"
                :to="item.url">
                {{ item.name }}
              </router-link>
            </li>
          </ul>
        </div>
      </bui-collapse-transition>
      <!-- / 二级子菜单 -->
    </div>
  </div>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import addData from './addData.js';
import BuiCollapseTransition from 'utils/collapse-transition.vue';

export default {
  name: `${CSS_PREFIX}sidebar`,
  components: {BuiCollapseTransition},

  props: {
    value: {
      type: Array,
      default: function() {
        return [];
      },
    },
    router: {
      type: Boolean,
      default: false,
    },
    // for xiaoqin only
    expandAll: {
      type: Boolean,
      default: false,
    },
  },
  data: function() {
    return {
      query: location.pathname,
      menus: addData(JSON.parse(JSON.stringify(this.value)), this.expandAll),
    };
  },
  watch: {
    query: function(newVal) {
      this.filterSelected();
    },
    $route: function(newVal) {
      this.filterSelected();
    },
    value: function(newVal) {
      this.menus = addData(JSON.parse(JSON.stringify(newVal)));
    },
  },

  created: function() {
    this.filterSelected();
  },

  methods: {
    onListClick: function(curIndex) {
      this.menus.forEach((item, index) => {
        if (index === curIndex) {
          this.menus[index].isOpen = !this.menus[curIndex].isOpen;
        } else {
          if (!this.expandAll) {
            this.menus[index].isOpen = false;
          }
        }
      });
    },
    filterSelected: function() {
      const vm = this;
      vm.menus = vm.menus.map(function(item) {
        if (!vm.hasChildren(item)) {
          // 如果没有子菜单
          if (vm.isSelected(item.url)) {
            item.selected = true; // 一级菜单选中的情况
          } else {
            item.selected = false; // 一级菜单选中的情况
          }
        } else {
          item.children = item.children.map(function(child) {
            if (vm.isSelected(child.url)) {
              child.selected = true;
            } else {
              child.selected = false;
            }
            return child;
          });
          if (!vm.expandAll) {
            item.isOpen = item.children.some((child) => child.selected);
          }
        }
        return item;
      });
    },
    isSelected(url) {
      if (this.router) {
        return this.$route.path.indexOf(url.path) !== -1;
      }
      return this.query.indexOf(url) !== -1;
    },
    hasChildren: function(item) {
      return !!item.children && item.children.length > 0;
    },
  },
};
</script>



