<template>
  <div
    :class="baseClass">
    <div
      v-if="$slots.header"
      :style="headerStyle"
      class="byted-card-header">
      <slot name="header"/>
    </div>
    <div
      :style="bodyStyle"
      class="byted-card-body">
      <slot/>
    </div>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
export default {
  name: `${CSS_PREFIX}card`,
  props: {
    shadow: {
      type: String,
      default: '',
    },
    bodyStyle: {
      type: [Array, Object],
      default: () => {},
    },
    headerStyle: {
      type: [Array, Object],
      default: () => {},
    },
  },
  computed: {
    baseClass() {
      const baseClassArr = ['byted-card'];
      if (this.shadow === 'hover') {
        baseClassArr.push('byted-card-hover-shadow');
      } else if (this.shadow === 'none') {
        baseClassArr.push('byted-card-none-shadow');
      } else {
        baseClassArr.push('byted-card-shadow');
      }
      return baseClassArr;
    },
  },
};
</script>




