<template>
  <div :class="baseClass">
    <div :class="className+'-header'">
      <template v-if="!$slots['header']">
        <span :class="className+'-header-title'">{{ title }}</span>
        <span
          v-if="showClear"
          :class="className+'-header-clear'"
          @click="onClearClick">{{ _clearText }}</span>
      </template>
      <template v-else>
        <slot name="header"/>
      </template>
    </div>
    <div
      ref="container"
      :class="className+'-container'">
      <slot/>
    </div>
  </div>
</template>

<script>
import {CSS_PREFIX, LOCALE_PREFIX} from '../../base.js';
import locale from 'mixins/locale';

export default {
  name: `${CSS_PREFIX}select-panel-moduler`,
  mixins: [locale],
  props: {
    title: {
      type: String,
      default: '',
    },
    showClear: {
      type: Boolean,
      default: false,
    },
    clearText: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      className: `${CSS_PREFIX}select-panel-moduler`,
    };
  },
  computed: {
    _clearText() {
      return this.clearText || this.t(`${LOCALE_PREFIX}.select-panel.clearText`);
    },
    baseClass() {
      const classArr = [this.className];
      if (this.disabled) {
        classArr.push(`${this.className}-disabled`);
      }
      return classArr;
    },
  },
  methods: {
    onClearClick() {
      if (this.disabled) return false;
      this.$emit('clearclick');
    },
  },
};
</script>



