<template>
  <div :class="className">
    <div
      ref="buiSelectPanelLeft"
      :style="leftStyle"
      :class="leftClassName"
      @scroll.passive="handleLeftPanelScroll">
      <div
        v-for="(tab, lindex) in showList"
        v-if="lindex<level"
        :key="lindex"
        :style="leftListStyle(level)"
        :class="['bui-select-panel-left-moduler', {'bui-select-panel-left-none': !tab.length}]">
        <byted-select-panel-moduler
          v-if="tab"
          ref="selectPanelModuler"
          :title="title[lindex] || getLevelTitle(parseInt(lindex))">
          <template v-if="!tab.length">
            <div class="byted-select-panel-moduler-none">
              <byted-icon name="empty"/>
              {{ _noDataText }}
            </div>
          </template>
          <template v-else>
            <byted-select-panel-item
              v-if="hasCheckAll && (hasCheckbox || (lastCheckbox && isLastLevel(tab)))"
              :value="selectAll[lindex]"
              :indeterminate="checkboxIndeterminate(tab, lindex)"
              :inline="inline"
              :disabled="disabled || checkboxDisabled(tab)"
              :active="selectAll[lindex] || checkboxIndeterminate(tab, lindex)"
              :name="_selectAllText"
              @change="onSelectAll(lindex)"
              @nameclick="onSelectAll(lindex)"/>
          </template>
          <template v-for="option in tab">
            <byted-select-panel-item
              :option="option"
              :key="option.id"
              :value="list[option.id].selected"
              :indeterminate="list[option.id].indeterminate"
              :name="option.name"
              :render-name="option.renderName || null"
              :inline="inline"
              :disabled="disabled || option.disabled"
              :has-checkbox="hasCheckbox || (lastCheckbox && !option.children)"
              :active="list[option.id].active || list[option.id].selected"
              :has-child="!!option.children"
              @nameclick="onLabelClick(option)"
              @change="onCheckboxChange(option, lindex)"/>
          </template>
        </byted-select-panel-moduler>
      </div>
    </div>
    <div
      v-if="isSelectedModuleShow"
      :style="rightListStyle"
      :class="{'bui-select-panel-right-none': !value.length}"
      class="bui-select-panel-right">
      <byted-select-panel-moduler
        :show-clear="!!value.length"
        :disabled="disabled"
        :title="_title"
        @clearclick="onClearAll">
        <template v-if="!value.length">
          <div class="byted-select-panel-moduler-none">
            <slot name="empty">
              <byted-icon name="empty"/>
              {{ _emptyText }}
            </slot>
          </div>
        </template>
        <template v-for="id in value">
          <byted-select-panel-item
            v-if="list[id]"
            :key="id"
            :value="list[id].selected"
            :name="list[id].name"
            :disabled="disabled || list[id].disabled"
            :render-name="list[id].renderName || null"
            :has-remove="true"
            @removeclick="onCheckboxChange(list[id])"/>
        </template>
      </byted-select-panel-moduler>
    </div>
  </div>
</template>

<script>
import {CSS_PREFIX, LOCALE_PREFIX} from '../../base.js';
import {scrollIntoView} from 'utils/dom.js';
import BytedIcon from 'components/icon';
import locale from 'mixins/locale';

export default {
  name: `${CSS_PREFIX}select-panel`,
  components: {BytedIcon},
  mixins: [locale],

  props: {
    title: {
      type: Array,
      default() {
        return [];
      },
    },
    options: {
      type: Array,
      default() {
        return [];
      },
    },
    value: {
      type: Array,
      default() {
        return [];
      },
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    inline: {
      type: Boolean,
      default: false,
    },
    // 是否显示checkbox，不显示的时候，默认不显示已选框
    hasCheckbox: {
      type: Boolean,
      default: true,
    },
    lastCheckbox: {
      // 只显示最后level的checkbox,hasCheckbox=false
      type: Boolean,
      default: false,
    },
    async: { // 层级数据是否需要异步加载
      type: Boolean,
      default: false,
    },
    selectedDisabled: { // selected && disabeld and not dispatch value
      type: Array,
      default: () => [],
    },
    // 是否展示右侧已组件
    showSeletedModuler: {
      type: Boolean,
      default: true,
    },
    listWidth: {
      type: String,
      default: '200px',
    },
    autoScroll: {
      type: Boolean,
      default: true,
    },
    hasCheckAll: {
      type: Boolean,
      default: true,
    },
    emptyText: {
      type: String,
      default: '',
    },
    noDataText: {
      type: String,
      default: '',
    },
  },

  data() {
    return {
      isSelectAll: false,
      className: `${CSS_PREFIX}select-panel`,
      list: {},
      selectedLevelIds: null, // 选中层级的id
      showList: {},
      level: 1,
      selectDetail: {},
      scrollPosition: { // buiSelectPanelLeft是否有左右 border
        left: false,
        right: false,
      },
    };
  },
  computed: {
    _firstLevel() {
      return this.t(`${LOCALE_PREFIX}.select-panel.firstLevel`);
    },
    _emptyText() {
      return this.emptyText || this.t(`${LOCALE_PREFIX}.select-panel.emptyText`);
    },
    _title() {
      return this.t(`${LOCALE_PREFIX}.select-panel.title`);
    },
    _selectAllText() {
      return this.t(`${LOCALE_PREFIX}.select-panel.selectAll`);
    },
    _noDataText() {
      return this.noDataText || this.t(`${LOCALE_PREFIX}.select-panel.noDataText`);
    },
    selectAll() {
      const showList = this.showList;
      const keys = Object.keys(showList);
      const list = this.list;
      const selectAll = {};

      keys.forEach((level) => {
        level = parseInt(level);
        let arr =[];
        if (!level) {
          arr = showList[0];
          const options = arr;
          // const options = arr.filter((item) => {
          //   return !item.disabled;
          // });
          selectAll[level] = options.every((item) => {
            return list[item.id] && list[item.id].selected;
          });
        } else if (showList[level]) {
          if (!(showList[level] && showList[level][0])) {
            selectAll[level] = false;
            return;
          }
          const id = list[showList[level][0].id].parent.id;
          // 过滤disabled的元素
          // const undisableds = list[id].children.filter((item) => !item.disabled);
          const undisableds = list[id].children;
          selectAll[level] = (!list[id].disabled && undisableds.every((item) => {
            return list[item.id] && list[item.id].selected;
          })) || list[id].children.every((item) => list[item.id] && list[item.id].selected);
        }
      });
      return selectAll;
    },
    optionLevel() {
      return this.getLevel(this.options);
    },
    isSelectedModuleShow() {
      return this.showSeletedModuler;
    },
    rightListStyle() {
      return {
        width: this.listWidth,
      };
    },
    leftStyle() {
      const marginRight = this.isSelectedModuleShow ? 16 : '';
      return {
        marginRight: `${marginRight}px`,
        width: this.isSelectedModuleShow ? `calc(100% -  ${parseInt(this.listWidth) + marginRight}px)` : '100%',
      };
    },
    leftClassName() {
      const baseClass = 'bui-select-panel-left';
      const classArr = [baseClass];
      if (this.scrollPosition.left) {
        classArr.push('bui-select-panel-scrolling-left');
      }
      if (this.scrollPosition.right) {
        classArr.push('bui-select-panel-scrolling-right');
      }
      return classArr;
    },
  },
  watch: {
    options: {
      handler(newVal, oldVal) {
        if ([].concat(newVal).sort().join(',') === [].concat(oldVal).sort().join(',')) {
          return;
        }
        this.updateOption(newVal);
      },
      deep: true,
    },
    value(newVal, oldVal) {
      if ([].concat(newVal).sort().join(',') !== [].concat(oldVal).sort().join(',')) {
        this.optionInit(this.options, null);
      }
    },
  },
  mounted() {
    this.optionInit(this.options, null);
    this.$set(this.showList, 0, this.options);
  },
  methods: {
    // 判定是否为最后一层
    isLastLevel(tab) {
      return tab.every((option) => !option.children);
    },
    // 触发options初始化，对外暴露的方法，不要随意改名
    updateOption(options) {
      options = options || this.options;
      this.optionInit(options, null);
      if (!this.async) {
        this.level = 1;
        this.$set(this.showList, 0, options);
        return;
      }
      if (this.level === 1) {
        this.$set(this.showList, 0, options);
      } else {
        if (this.list[this.selectedLevelIds].children) {
          this.$set(this.showList, this.level - 1, this.list[this.selectedLevelIds].children);
        }
      }
    },
    leftListStyle(level) {
      const leftWidth = `${100 / level}%`;
      return {
        width: leftWidth,
        minWidth: this.listWidth,
      };
    },
    onClearAll() {
      const list = this.list || {};
      const selected = [].concat(this.value.map((v) => {
        return v.toString();
      })).filter((id) => {
        return list[id] && list[id].disabled;
      });
      this.$emit('input', selected);
      this.$emit('change', selected);
      this.$emit('clear', selected);
    },
    optionInit(option, parent) {
      const selected = this.value.map((item) => {
        return item.toString();
      });

      const selectedDisabled = this.selectedDisabled.map((item) => {
        return item.toString();
      });

      const noParent = !parent;

      const _this = this;
      if (!option) {
        return;
      }

      parent = noParent ? parent : this.list[parent.id];

      option.forEach((item, index) => {
        const ids = [];
        // 获取子元素的所有可选id
        _this.getIds(item, ids);
        // 选项是否选中，由当前或者父元素决定或所有子元素决定
        // bugfix: ids length 为 0 时，[].every(a => !a) === true
        const isSelected = selected.includes(item.id.toString()) || (ids.length && ids.every((id) => selected.includes(id.toString()))) ||
            selected.indexOf(item.id.toString()) > -1 || (parent && parent.selected);

        // 当前层级判断，初始为1
        const level = noParent ? 1 : parent.level + 1;
        // 选项禁用，父组件继承
        const disabled = noParent ? item.disabled : parent.disabled || item.disabled;

        // 向下判断子元素是否都被选中禁用
        const isSelectedDisabled = selectedDisabled.includes(item.id.toString()) || item.children && !!item.children.length &&
          item.children.every((item) => {
            return selectedDisabled.includes(item.id.toString());
          });
        // 不确定性
        const indeterminate = this.getIndeterminate(selected, item, isSelected||isSelectedDisabled);
        // 向上判断父元素是否禁用
        // const selectedDisabledInclude = _this.selectedDisabledInclude(selectedDisabled, item.id);

        const obj = {};
        // set option item disabled
        item.disabled = item.disabled || disabled || isSelectedDisabled;
        Object.assign(obj, _this.list[item.id] || {
          active: false,
        }, {
          level: level,
          parent: parent,
          selected: Boolean(isSelected || isSelectedDisabled),
          indeterminate: indeterminate,
        }, item);

        // _this.$delete(_this.list, item.id);
        _this.$set(_this.list, item.id, obj);
        // if (isSelected && !noParent && index === option.length - 1) {
        //     _this.reCheckParent(parent);
        // }
        if (item.children && item.children.length > 0) {
          _this.optionInit(item.children, item);
        }
      });
    },
    // get item indeterminate
    getIndeterminate(selectedIds, item, itemSelected) {
      if (itemSelected) {
        return false;
      }
      if (selectedIds.includes(item.id.toString())) {
        return true;
      }
      if (item && item.children) {
        return item.children.some((child) => {
          if (selectedIds.includes(child.id.toString())) {
            return true;
          }
          return this.getIndeterminate(selectedIds, child, false);
        });
      }
      return false;
    },
    // check all indeterminate
    checkboxIndeterminate(tab, lindex) {
      if (this.selectAll[lindex]) {
        return false;
      }
      return tab.some((option) => {
        return this.list[option.id].selected || this.list[option.id].indeterminate;
      });
    },
    // check selectedDisabled include current node or parent node
    selectedDisabledInclude(ids, val) {
      const parent = this.list[val] && this.list[val].parent;
      if (ids.includes(val.toString())) {
        return true;
      } else {
        if (parent) this.selectedDisabledInclude(ids, parent.id);
      }
    },
    reCheckParent(parent) {
      let isSelectAll = true; let isFirst = false; let children = []; let hasChildSelected = false; const _this = this;
      if (parent === null) {
        isFirst = true;
        children = this.options;
      } else {
        parent = this.list[parent.id] || parent || {};
        children = parent.children;
      }
      children.forEach((item, index) => {
        const curItem = _this.list[item.id] || {};


        const hasSelected = curItem.hasOwnProperty('selected');
        if (!hasSelected || (hasSelected && !curItem.selected)) {
          isSelectAll = false;
        }
        if (hasSelected && curItem.selected) {
          hasChildSelected = true;
        }
      });
      if (isFirst) {
        _this.isSelectAll = isSelectAll;
        _this.hasChildSelected = hasChildSelected;
        return;
      }
      parent.hasChildSelected = hasChildSelected;
      parent.selected = isSelectAll;
      this.$set(this.list, parent.id, parent);
    },
    // setSelectedLevelIds() {
    //   const _this = this;
    //   Object.keys(this.selectedLevelIds).forEach((key) => {
    //     this.selectedLevelIds[key] = [];
    //   });
    //   this.value.forEach((item) => {
    //     const curItem = _this.list[item];
    //     if (typeof curItem === 'object') {
    //       const curLevel = _this.selectedLevelIds[curItem.level - 1] || [];
    //       curLevel.push(item);
    //       _this.$set(_this.selectedLevelIds, curItem.level - 1, curLevel);
    //     }
    //   });
    // },
    getLevel(option) {
      if (!option || (option && !option.length)) return 0;
      return 1 + Math.max(...option.map((item) => {
        return this.getLevel(item.children);
      }));
    },
    onSelectAll(levelIndex) {
      levelIndex = parseInt(levelIndex);
      const flag = this.selectAll[levelIndex];
      const list = this.list;
      const option = this.showList[levelIndex];
      const parent = this.list[option[0].id].parent;
      const vm = this;
      let selected = [];
      if (this.lastCheckbox) {
        selected = [].concat(this.value.map((v) => {
          return v.toString();
        }));
        flag ? parent.children.forEach((item) => {
          const pos = selected.indexOf(item.id.toString());
          if (pos > -1 && !item.disabled) selected.splice(pos, 1);
        })
        : parent.children.forEach((item) => {
          if (selected.indexOf(item.id.toString()) < 0 && !item.disabled) {
            selected.push(item.id.toString());
          };
        });
      } else {
        if (!flag) { // select all
          if (!levelIndex) {
            // selected = selected.concat(this.value.map((v) => {
            //   return v.toString();
            // }));
            // this.checkDesSelected(this.options, selected);
            // 需要将第一级 disabled 但是已选中的拿出来
            selected = selected.concat(this.value.filter((v) => {
              return list[v] && list[v].disabled;
            }));
            this.options.forEach((item) => {
              if (!vm.checkChildrenDisabled(item.children) &&
                !item.disabled &&
                selected.indexOf(item.id.toString() === -1)) {
                selected.push(item.id.toString());
              }
            });
          } else {
            selected = selected.concat(this.value.map((v) => {
              return v.toString();
            }));
            const parentExistDisabled = this.checkChildrenDisabled(parent.children);
            selected = this.checkDesSelected(parent.children, selected, parentExistDisabled);
            if (!this.checkChildrenDisabled(parent.children)) {
              selected.push(parent.id.toString());
            }
          }
        } else { // cancel select all
          if (levelIndex) {
            selected = selected.concat(this.value.map((v) => {
              return v.toString();
            }));

            this.cancelParentSelected(parent, selected);
            this.cancelChildSelected(option, selected);

            // set child false
            selected = this.checkParentFalse(selected, parent);
          } else {
            // 需要将第一级 disabled 但是已选中的拿出来
            selected = selected.concat(this.value.filter((v) => {
              return list[v] && list[v].disabled;
            }));
          }
        }
      }

      this.setSelectedDetail(selected);
      this.$emit('detail-change', this.selectDetail);
      this.$emit('change', selected, option);
      this.$emit('input', selected, option);
    },
    // if child is not all true, set parent false
    checkParentFalse(selected, node) {
      if (node.parent === null) {
        return selected;
      }
      const parent = this.list[node.parent.id];
      const parentIndex = selected.indexOf(parent.id.toString());
      if (parent.selected) {
        parent.children.forEach((item) => {
          if (item.id.toString() !== node.id.toString() &&
            selected.indexOf(item.id.toString()) === -1) {
            selected.push(item.id.toString());
          }
        });
        if (parentIndex > -1) {
          selected.splice(parentIndex, 1);
        }
        return this.checkParentFalse(selected, parent);
      }
      return selected;
    },
    // if child is all true, set parent true
    checkParentTrue(selected, node) {
      const list = this.list;
      if (node.parent === null) {
        return selected;
      }
      const parent = list[node.parent.id];
      const hasDisabled = this.checkChildrenDisabled(parent.children);
      const someNoSelected = parent.children.some((item) => {
        return item.id.toString() !== node.id.toString() &&
          (!list[item.id].selected && !item.disabled);
      });
      if (!someNoSelected) {
        if (selected.indexOf(parent.id) < 0) {
          if (!hasDisabled && !this.checkChildrenDisabled(node.children)) {
            selected.push(parent.id.toString());
          }
        }

        parent.children.forEach((item) => {
          const pos = selected.indexOf(item.id.toString());
          if (pos > -1 && !hasDisabled) selected.splice(pos, 1);
        });
        return this.checkParentTrue(selected, parent);
      }
      return selected;
    },
    cancelParentSelected(node, selected) {
      if (node === null) return;
      if (selected.indexOf(node.id.toString()) > -1) {
        selected.splice(selected.indexOf(node.id.toString()), 1);
      } else {
        this.cancelParentSelected(node.parent, selected);
      }
    },
    cancelChildSelected(option, selected) {
      const vm = this;
      if (!option) return;
      option.forEach((item) => {
        if (selected.indexOf(item.id.toString()) > -1) {
          if (!item.disabled) {
            selected.splice(selected.indexOf(item.id.toString()), 1);
          }
        } else {
          if (!item.disabled) {
            vm.cancelChildSelected(item.children, selected);
          } else {
            selected.push(item.id);
          }
        }
      });
    },
    // set child selected
    checkDesSelected(option, selected, hasDisabled=false) {
      const vm = this;
      if (!option) return selected;

      option.forEach((item) => {
        if (selected.indexOf(item.id.toString()) > -1) {
          if (!hasDisabled) {
            selected.splice(selected.indexOf(item.id.toString()), 1);
          }
        } else {
          const hasChildDisabled = vm.checkChildrenDisabled(item.children);
          if (!hasChildDisabled && !item.disabled && hasDisabled) {
            vm.setChildUnselected(item.children, selected);
            selected.push(item.id.toString());
          }
          if (item.children) {
            vm.checkDesSelected(item.children, selected, hasChildDisabled);
          }
        }
      });
      return selected;
    },
    setChildUnselected(option, selected) {
      if (!option) return;
      option.forEach((item) => {
        const pos = selected.indexOf(item.id.toString());
        if (pos > -1) selected.splice(pos, 1);
      });
    },
    getLevelList(level) {
      level = parseInt(level);
      const levelList = [];
      Object.keys(this.list).forEach((key) => {
        if (this.list[key].level === level) {
          levelList.push(this.list[key]);
        }
      });
      return levelList;
    },
    getLevelId(level) {
      const levelList = [];
      level = parseInt(level);
      Object.keys(this.list).forEach((key) => {
        if (this.list[key].level === level) {
          levelList.push(this.list[key].id);
        }
      });
      return levelList;
    },
    onCheckboxChange(option, levelIndex) {
      const list = this.list;
      option = list[option.id];
      const flag = option.selected;
      const parent = option.parent;
      let selected = [].concat(this.value.map((v) => {
        return v.toString();
      }));
      if (this.lastCheckbox) {
        if (flag) {
          selected.splice(selected.indexOf(option.id.toString()), 1);
        } else {
          selected.push(option.id.toString());
        }
      } else {
        if (!flag) { // 选中
          const hasDisabled = this.checkChildrenDisabled(option.children);
          if (option.children && option.children.length) {
            selected = this.checkDesSelected(option.children, selected, hasDisabled);
          }
          if (!hasDisabled && !option.disabled) {
            selected.push(option.id.toString());
          }
          this.checkParentTrue(selected, option);
        } else { // 取消选中
          this.cancelParentSelected(parent, selected);
          this.cancelChildSelected(option.children, selected);
          const pos = selected.indexOf(option.id.toString());
          if (pos > -1) selected.splice(pos, 1);
          this.checkParentFalse(selected, option);
        }
      }
      if (option.children) {
        this.onLabelClick(option);
      }

      this.setSelectedDetail(selected);
      this.$emit('detail-change', this.selectDetail);
      this.$emit('change', selected, option);
      this.$emit('input', selected, option);
    },
    onLabelClick(option, onlyActive = false) {
      option = this.list[option.id];
      this.selectedLevelIds = option.id;
      !onlyActive && this.$emit('label-click', option);
      this.$delete(this.showList, option.level);

      if (!option.children && (this.hasCheckbox || this.lastCheckbox) && !this.disabled) {
        if (!onlyActive) {
          this.onCheckboxChange(option);
          this.level = option.level;
          return;
        }
      }
      // if (this.showList[option.level]) {
      //   const temp = this.list[this.showList[option.level][0].id];
      //   if (typeof temp === 'object' && temp.parent.id === option.id) {
      //     if (option.children && option.children.length) {
      //       this.level = option.level + 1;
      //     }
      //     return;
      //   }
      // }
      const levelIndex = option.level - 1;
      const list = this.list;
      // this.$delete(this.showList, levelIndex);
      if (option.parent === null) {
        this.$set(this.showList, levelIndex, this.options.map((item) => {
          list[item.id].active = item.id === option.id ? true: false;
          return list[item.id];
        }));
      } else {
        const parent = this.list[option.parent.id];
        parent.children = parent.children.map((item) => {
          list[item.id].active = item.id === option.id ? true: false;
          return list[item.id];
        });
        if (parent.children) {
          this.$set(this.showList, levelIndex, parent.children);
        }
      }
      // onlyActive 不展开下级
      if (option.children && !onlyActive) {
        if (option.children.length) {
          this.$set(this.showList, option.level, option.children.map((item) => {
            item.disabled = item.disabled ? item.disabled : option.disabled;
            // 清除子元素 active 态 例如 focusItem [1, 11] -> [1]，11 不active
            // if (onlyActive) {
            //   item.active = false;
            // }
            return item;
          }));
        } else {
          // 展开一个空 list
          this.$set(this.showList, option.level, []);
        }
        this.level = option.level + 1;
      } else {
        this.level = option.level;
        !onlyActive && this.$emit('name-click', option);
      }

      // 多级菜单会自动滚动到最右侧
      if (this.autoScroll) {
        this.$nextTick(() => {
          const scrollDom = this.$refs['buiSelectPanelLeft'];
          if (scrollDom) {
            // scrollDom.scrollLeft = SCROLL_DISTANCE;
            const activeChild = scrollDom.children[levelIndex + 1];
            if (activeChild) {
              scrollIntoView(scrollDom, activeChild, 'horizontal');
            }
          }
        });
      }
      this.$nextTick(() => {
        this.handleLeftPanelScroll();
      });
      // if (option.children && option.children.length) {
      //   this.$delete(this.showList, option.level);
      //   if (option.children) {
      //     this.$set(this.showList, option.level, option.children);
      //   }
      //   this.level = option.level + 1;
      // } else {
      //   this.$emit('name-click', option);
      // }
    },
    // 对外暴露方法：自动定位到某个选项，不要随意改名
    focusItem(stack) {
      // stack: [id, id, id] 层级ID数组，索引为level
      const len = stack.length;
      for (let i = 0; i < len; i++) {
        const option = this.list[stack[i]];
        if (!option) {
          break;
        }
        this.onLabelClick(option, true);
        this.verticalScrollIntoView(option);
      }
    },
    verticalScrollIntoView(option) {
      // 垂直方向上某个 item 滚动到顶部可见区域
      // 如果是 autoFocus 还需要考虑纵向滚动
      let siblings = [];
      if (option.parent) {
        siblings = option.parent.children || [];
      } else {
        siblings = this.options || [];
      }
      const levelIndex = option.level - 1;
      let position = siblings.findIndex((item) => item.id === option.id);
      if (position <= -1) {
        return;
      }
      if (this.hasCheckbox || (this.lastCheckbox && this.isLastLevel(siblings))) {
        position++;
      }
      this.$nextTick(() => {
        if (this.$refs.selectPanelModuler &&
          this.$refs.selectPanelModuler[levelIndex] &&
          this.$refs.selectPanelModuler[levelIndex].$refs.container) {
          const scrollContainer = this.$refs.selectPanelModuler[levelIndex].$refs.container;
          const activeChild = scrollContainer.children[position];
          if (activeChild) {
            scrollIntoView(scrollContainer, activeChild);
          }
        }
      });
    },
    getLevelTitle(levelIndex, flag) {
      let title = '';
      const levelSelect = this.showList[levelIndex];
      switch (levelIndex) {
        case 0:
          title = this._firstLevel;
          break;
        default:
          title = levelSelect && levelSelect.length && !flag ? this.list[levelSelect[0].id].parent.name : this.t(`${LOCALE_PREFIX}.select-panel.level`, {level: levelIndex + 1});
      }
      return typeof this.title[levelIndex] === 'undefined' ? title : this.title[levelIndex] || title;
    },
    // 判断某一级别的全选是否需要disabled
    checkboxDisabled(data) {
      return data.every((item) => {
        return item.disabled;
      });
    },
    // 判断子级是否包含disable
    checkChildrenDisabled(data) {
      if (!data) return;
      const selected = [].concat(this.value.map((v) => {
        return v.toString();
      }));
      // disabled = true 但是已选中的，父级可以当该项目为正常项
      return data.some((item) => {
        if (item.disabled) {
          // 如果当前项已经 disabled 了，不需要参考 children
          return selected.indexOf(item.id.toString()) <= -1;
        }
        return item.children ? this.checkChildrenDisabled(item.children) : item.disabled;
      });
    },
    getIds(obj, ids=[]) {
      // 只找子元素，因为 disabled 项同样决定父级是否选中
      if (obj.children) {
        obj.children.forEach((child) => {
          ids.push(child.id);
        });
      }
      // const vm = this;
      // if (this.checkChildrenDisabled(obj.children)) {
      //   obj.children.forEach((child) => {
      //     vm.getIds(child, ids);
      //   });
      //   return;
      // }
      // if (!obj.disabled) {
      //   ids.push(obj.id);
      // }
    },
    setSelectedDetail(selected) {
      const vm = this;
      this.clearDetail();
      selected.forEach((id) => {
        if (vm.list[id]) { // bugfix: value 中存在 list 中不包含的数据
          vm.selectDetail[vm.list[id].level].push(id);
        }
      });
    },
    clearDetail() {
      for (let i=0; i<this.optionLevel; i++) {
        this.selectDetail[i+1] = [];
      }
    },
    handleLeftPanelScroll() {
      const scrollDom = this.$refs['buiSelectPanelLeft'];
      if (!scrollDom) {
        return;
      }
      const scrollWidth = scrollDom.scrollWidth;
      const scrollLeft = scrollDom.scrollLeft;
      const offsetWidth = scrollDom.offsetWidth;
      if (offsetWidth >= scrollWidth) {
        this.scrollPosition.left = false;
        this.scrollPosition.right = false;
        return;
      }
      this.scrollPosition.left = scrollLeft > 0;
      // 注意这里的 =，因为 bui-select-panel-left-moduler 有 margin-left -1 的操作，多级的时候会使子元素宽度的和大于 scrollWidth
      this.scrollPosition.right = scrollLeft + offsetWidth <= scrollWidth;
    },
  },
};
</script>



