export function flat(source, parentId) {
  let result = [];
  if (!source || !Array.isArray(source)) {
    return result;
  }
  source.forEach((item, index) => {
    const {children, ...rest} = item;
    result.push({
      parentId,
      sortOrder: index,
      ...rest,
    });
    if (children) {
      result = result.concat(flat(children, item.id));
    }
  });
  return result;
}

export function getLeafIdsMap(obj) {
  const result = {};
  const values = Object.values(obj);

  function getLeafIds(id) {
    let result = [];
    const children = values.filter((item) => item.parentId === id);
    if (children.length === 0) {
      return [id];
    }
    children.forEach((item) => {
      result = result.concat(getLeafIds(item.id));
    });
    return result;
  }

  for (let i = 0; i < values.length; i += 1) {
    const {id} = values[i];
    result[id] = getLeafIds(id);
  }
  return result;
}

export function getParentsIdsMap(obj) {
  const result = {};
  const values = Object.values(obj);

  function getParentsIds(id) {
    let result = [];
    const {parentId} = obj[id];
    if (!parentId) {
      return [];
    }
    result.push(parentId);
    result = result.concat(getParentsIds(parentId));
    return result;
  }

  for (let i = 0; i < values.length; i += 1) {
    const {id} = values[i];
    result[id] = getParentsIds(id);
  }
  return result;
}

export function isEqual(source, target) {
  if (source.length !== target.length) {
    return false;
  }
  if (source.length === 0) {
    return true;
  }
  const set = new Set(source);
  const result = target.every((t) => set.has(t));
  return result;
}



