<template>
  <div :class="baseClass">
    <slot/>
    <div :class="supClass">
      <slot name="count">{{ derivedCount }}</slot>
    </div>
    <div
      v-if="status"
      :class="statusClass"
    />
  </div>
</template>

<script>
import {isOneOf} from 'utils/helper.js';
import {CSS_PREFIX} from '../../base.js';

export default {
  name: `${CSS_PREFIX}badge`,
  props: {
    count: {
      type: [String, Number],
      default: null,
    },
    overflowCount: {
      type: Number,
      default: 99,
    },
    dot: {
      type: Boolean,
      default: false,
    },
    status: {
      type: String,
      default: null,
      validator(value) {
        return isOneOf(value, ['info', 'success', 'warning', 'danger', 'default']);
      },
    },
  },
  computed: {
    baseClass() {
      const classAttr = [`${CSS_PREFIX}badge`];
      if (this.isStandalone) {
        classAttr.push(`${CSS_PREFIX}badge-standalone`);
      }
      return classAttr;
    },
    isStandalone() {
      return Object.keys(this.$slots).length === 0;
    },
    supClass() {
      if (this.dot) {
        return `${CSS_PREFIX}badge-dot`;
      }
      if (this.$slots.count) {
        return `${CSS_PREFIX}badge-count-slot`;
      }
      return `${CSS_PREFIX}badge-count`;
    },
    statusClass() {
      return [
        `${CSS_PREFIX}badge-status-dot`,
        `${CSS_PREFIX}badge-status-${this.status}`,
      ];
    },
    derivedCount() {
      if (isNaN(this.count)) {
        return this.count;
      }
      if (+this.count <= this.overflowCount) {
        return this.count;
      }
      return `${this.overflowCount}+`;
    },
  },
};
</script>



