
<template>
  <div class="bui-datetime-panels">
    <date
      :value="value"
      :hover-value="hoverValue"
      :view-date="viewDate"
      :disabled-date="disabledDate"
      :date-render="dateRender"
      :range="range"/>
    <div class="bui-datetime-panel-divider"/>
    <TimeSpinner
      :value="value[activePickerInputIndex]"
      :view-date="viewDate">
      <template slot="header">
        {{ panelDateTimeForTimeSpinnerHeader }}
      </template>
    </TimeSpinner>
  </div>
</template>
<script>
import Date from './../base/date.vue';
import TimeSpinner from './../base/time-spinner.vue';
import panel from './../mixins/panel';


export default {
  name: 'PanelDateTime',
  components: {
    Date,
    TimeSpinner,
  },
  mixins: [panel],

  data() {
    return {
      panels: 1,
    };
  },

  methods: {

  },
};
</script>



