<script>
import {CSS_PREFIX} from '../../base.js';
import {isOneOf} from 'utils/helper.js';
import emitter from 'mixins/emitter';
import BytedIcon from 'components/icon';
import BytedButton from 'components/button';
import BytedPopper from 'components/popper';
import clickoutside from 'directives/clickoutside';

const DEFAULT_TYPE = 'default';
const PRIMARY_TYPE = 'primary';
const SUCCESS_TYPE = 'success';
const DANGER_TYPE = 'danger';
const WARNING_TYPE = 'warning';

const HOVER_TRIGGER = 'hover';
const CLICK_TRIGGER = 'click';

const MIDDLE_SIZE = 'md';
const OLD_MIDDLE_SIZE = 'middle';
const OLD_SMALL_SIZE = 'small';
const SMALL_SIZE = 'sm';

export default {
  name: `${ CSS_PREFIX }dropdown`,
  components: {BytedIcon, BytedButton, BytedPopper},
  directives: {clickoutside},
  mixins: [emitter],
  props: {
    type: {
      type: String,
      validator(value) {
        return isOneOf(value, [DEFAULT_TYPE, PRIMARY_TYPE, SUCCESS_TYPE, DANGER_TYPE, WARNING_TYPE]);
      },
      default: DEFAULT_TYPE,
    },
    trigger: {
      type: String,
      validator(value) {
        return isOneOf(value, [HOVER_TRIGGER, CLICK_TRIGGER]);
      },
      default: HOVER_TRIGGER,
    },
    size: {
      type: String,
      validator(value) {
        return isOneOf(value, [MIDDLE_SIZE, OLD_MIDDLE_SIZE, SMALL_SIZE, OLD_SMALL_SIZE]);
      },
      default: SMALL_SIZE,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    splitButton: {
      type: Boolean,
      default: false,
    },
    onClickMainBtn: {
      type: Function,
      default() {
        return (()=>{});
      },
    },
    transfer: {
      type: Boolean,
      default: false,
    },
    hideOnClick: {
      type: Boolean,
      default: true,
    },
    showDelay: {
      type: Number,
      default: 0,
      validator(value) {
        return value >=0;
      },
    },
    hideDelay: {
      type: Number,
      default: 0,
      validator(value) {
        return value >=0;
      },
    },
    offset: {
      type: [String, Number],
      default: 0,
    },
    placement: {
      type: String,
      default: 'bottom-end',
    },
    popperOptions: {
      type: Object,
      default() {
        return {
          modifiers: {
            gpuAcceleration: false,
            flip: {
              behavior: 'flip',
              boundariesElement: 'viewport',
            },
            preventOverflow: {
              enable: false,
              boundariesElement: 'viewport',
            },
          },
        };
      },
    },
    popperClass: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      showDropdownList: false,
      showDropdownListTpl: false, // 模板使用的显示变量
      popperRef: null,
      showTimer: null,
      hideTimer: null,
    };
  },
  computed: {
    baseClass() {
      const baseClass = `${CSS_PREFIX}dropdown`;
      const baseClassArr = [baseClass];

      if (this.size === MIDDLE_SIZE || this.size === OLD_MIDDLE_SIZE) {
        baseClassArr.push(`${baseClass}-${MIDDLE_SIZE}`);
      }
      if (this.size === SMALL_SIZE || this.size === OLD_SMALL_SIZE) {
        baseClassArr.push(`${baseClass}-${SMALL_SIZE}`);
      }

      if (this.splitButton) {
        baseClassArr.push(`${baseClass}-split-button`);
      }

      return baseClassArr;
    },
    menuWrapperClass() {
      const baseClass = `${CSS_PREFIX}dropdown-menu-wrapper`;
      const menuAlignClassArr = [baseClass];
      if (this.size === SMALL_SIZE || this.size === OLD_SMALL_SIZE) {
        menuAlignClassArr.push(`${baseClass}-${SMALL_SIZE}`);
      }
      if (this.popperClass) {
        menuAlignClassArr.push(this.popperClass);
      }

      return menuAlignClassArr;
    },
    popperMergeOptions() {
      // 兼容offset
      const modifiers = Object.assign({}, this.popperOptions.modifiers, {
        offset: {
          offset: this.offset,
        },
      });
      // 兼容placement
      const placement = this.placement;
      return Object.assign({}, this.popperOptions, {placement, modifiers});
    },
  },
  watch: {
    showDropdownList(newVal) {
      const popperName = `${CSS_PREFIX}popper`;
      this.$emit('visible-change', newVal);
      if (this.showTimer) clearTimeout(this.showTimer);
      if (this.hideTimer) clearTimeout(this.hideTimer);
      if (newVal) { // 显示
        this.showTimer = setTimeout(()=>{
          this.showDropdownListTpl = newVal;
          this.broadcast(popperName, 'bui.popper.update');
        }, this.showDelay);
      } else {
        this.hideTimer = setTimeout(()=>{
          this.showDropdownListTpl = newVal;
          this.broadcast(popperName, 'bui.popper.destroy');
        }, this.hideDelay);
      }
    },
  },
  mounted() {
    this.popperRef = this.$refs.popperRef;
    this.$on('click-dropdown-item', (itemName) => {
      this.$emit('on-click', itemName);
    });
  },
  methods: {
    togglePopperShow(value) {
      this.showDropdownListTpl = value;
    },
    onMouseEnter() {
      if (this.disabled) {
        return;
      }

      if (this.trigger === HOVER_TRIGGER) {
        this.showDropdownList = true;
      }
    },
    onMouseLeave() {
      if (this.disabled) {
        return;
      }

      if (this.trigger === HOVER_TRIGGER) {
        this.showDropdownList = false;
      }
    },
    onClickDropdownBtn(evt) {
      if (this.disabled) {
        return;
      }

      if (this.trigger === CLICK_TRIGGER) {
        this.showDropdownList = !this.showDropdownList;
      }
    },
    onClickDropdownList(evt) {
      if (this.hideOnClick) {
        this.showDropdownList = false;
      }
      evt.stopPropagation();
    },
    clickOutsideHandle() {
      this.showDropdownList = false;
    },
  },
  render(h) {
    const {type, baseClass, onMouseEnter, onMouseLeave, onClickDropdownBtn, size,
      onClickDropdownList, disabled, showDropdownListTpl,
      splitButton, onClickMainBtn, menuWrapperClass, popperRef, transfer, popperMergeOptions, placement} = this;
    return splitButton
      ? <div class={baseClass}>
        <div class="btn-group">
          <byted-button type={type}
            plain
            size={size}
            nativeOnClick={onClickMainBtn}
            disabled={disabled}>
            {this.$slots.default}
          </byted-button>
          <byted-button
            type={type}
            plain
            size={size}
            ref="popperRef"
            class='dropdown-arrow-btn'
            disabled={disabled}
            v-clickoutside={this.clickOutsideHandle}
            nativeOnMouseenter={onMouseEnter}
            nativeOnMouseleave={onMouseLeave}
            nativeOnClick={onClickDropdownBtn}>
            <byted-icon name="angle-down"/>
          </byted-button>
          <byted-popper
            v-show={showDropdownListTpl}
            class={menuWrapperClass}
            placement={placement}
            nativeOnMouseenter={onMouseEnter}
            nativeOnMouseleave={onMouseLeave}
            nativeOnClick={onClickDropdownList}
            popperOptions = {popperMergeOptions}
            reference={popperRef}
            appendToBody={transfer}
          >
            {this.$slots.list}
          </byted-popper>
        </div>
      </div>
      : <div class={baseClass}
        ref="popperRef"
        v-clickoutside={this.clickOutsideHandle}
        onMouseenter={onMouseEnter}
        onMouseleave={onMouseLeave}
        onClick={onClickDropdownBtn}>
        {this.$slots.default}
        <byted-popper
          v-show={showDropdownListTpl}
          class={menuWrapperClass}
          placement={placement}
          nativeOnMouseenter={transfer?onMouseEnter : ()=>{}}
          nativeOnMouseleave={transfer?onMouseLeave : ()=>{}}
          nativeOnClick={onClickDropdownList}
          appendToBody={transfer}
          popperOptions = {popperMergeOptions}
          reference={popperRef}
        >
          {this.$slots.list}
        </byted-popper>
      </div>;
  },
};
</script>



