<template>
  <div :class="baseClass">
    <label>
      <div class="bui-checkbox-input-wrapper">
        <input
          :value="val"
          :checked="innerChecked"
          class="bui-checkbox-input"
          type="checkbox"
          @change="handleChange">
        <byted-icon
          name="check"
          width="14px"
          height="14px"/>
      </div><span class="bui-checkbox-input-label"><slot/></span>
    </label>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';
import BytedIcon from 'components/icon';

export default {
  name: `${CSS_PREFIX}checkbox`,

  components: {
    BytedIcon,
  },

  mixins: [emitter],

  props: {
    value: {
      type: [Boolean, Array],
      default() {
        return [];
      },
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    val: {
      type: [Boolean, String, Number],
      default: false,
    },
    indeterminate: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    baseClass() {
      const classArr = [`${CSS_PREFIX}checkbox`];
      if (this.indeterminate) {
        classArr.push('indeterminate');
      }
      if (this.innerChecked) {
        classArr.push('checked');
      }
      if (this.isDisabled) {
        classArr.push('disabled');
      }
      if (Object.keys(this.$slots).length === 0) {
        classArr.push('empty');
      }
      return classArr;
    },
    checkboxGroup() {
      let parent = this.$parent;
      while (parent) {
        if (parent.$options.name === `${CSS_PREFIX}checkbox-group`) {
          return parent;
        }
        parent = parent.$parent;
      }
      return null;
    },
    isGroup() {
      return this.checkboxGroup !== null;
    },
    isDisabled() {
      return (this.isGroup && this.checkboxGroup.disabled) || this.disabled;
    },
    innerChecked() {
      const {isGroup, checkboxGroup, val, value} = this;
      if (isGroup) {
        return checkboxGroup.value.indexOf(val) !== -1;
      }
      if (Array.isArray(value)) {
        return value.indexOf(val) !== -1;
      }
      return !!value;
    },
  },
  watch: {
    value(newValue) {
      if (!this.isGroup) {
        this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [newValue]);
      }
    },
  },
  mounted() {

  },
  methods: {
    handleChange(e) {
      if (this.isDisabled) {
        return false;
      }
      const checked = e.target.checked;
      if (Array.isArray(this.value)) {
        // 多个 checkbox 共一个 v-model
        const valueCopy = this.value.slice(0);
        if (checked) {
          valueCopy.push(this.val);
        } else {
          valueCopy.splice(valueCopy.indexOf(this.val), 1);
        }
        this.$emit('input', valueCopy);
        this.$emit('change', valueCopy);
      } else {
        // 单个checkbox
        this.$emit('input', checked);
        this.$emit('change', checked);
      }
      if (this.isGroup) {
        this.dispatch(`${CSS_PREFIX}checkbox-group`, 'bui.checkbox.change', {
          val: this.val,
          checked,
        });
      }
    },
    isArray(arr) {
      return Object.prototype.toString.call(arr) === '[object Array]';
    },
  },
};
</script>




