<template>
  <div
    :class="baseClass"
    @mouseenter="handleMouseEnter"
    @mouseleave="handleMouseLeave">
    <input
      ref="inputNumber"
      :placeholder="placeholder"
      :disabled="disabled"
      :readonly="readonly"
      :value="displayVal"
      :step="step"
      :min="min"
      :max="max"
      :class="buiInputClass"
      class="bui-input"
      @click="handleClick"
      @input="handleInput"
      @blur="handleBlur"
      @focus="handleFocus"
      @keydown="handleKeydown"
      @keyup="handleKeyup"
      @change="handleInputChange"
      @keydown.up="stepGoUp"
      @keydown.down="stepGoDown"
    >
    <div
      class="bui-input-suffix-default">
      <div
        v-repeat-click="stepGoUp"
        class="bui-control-area">
        <byted-icon
          :class="{'disabled': isSuffixShow.disableStepUpBtn || disabled}"
          class="bui-control-area-icon up"
          name="angle-down"/>
      </div>
      <div
        v-repeat-click="stepGoDown"
        class="bui-control-area">
        <byted-icon
          :class="{'disabled': isSuffixShow.disableStepDownBtn || disabled}"
          class="bui-control-area-icon"
          name="angle-down"/>
      </div>
    </div>
    <div
      v-if="isSuffixShow.show && controlsPositionShow('dispersion')"
      class="bui-input-suffix-dispersion">
      <div
        v-repeat-click="stepGoDown"
        class="bui-control-area bui-control-area-left">
        <byted-icon
          :class="{'disabled': isSuffixShow.disableStepDownBtn || disabled}"
          class="bui-control-area-icon"
          name="minus"/>
      </div>
      <div
        v-repeat-click="stepGoUp"
        class="bui-control-area bui-control-area-right">
        <byted-icon
          :class="{'disabled': isSuffixShow.disableStepUpBtn || disabled}"
          class="bui-control-area-icon"
          name="plus"/>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable vue/require-prop-types */

import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';
import {isOneOf} from 'utils/helper.js';
import RepeatClick from 'directives/repeat-click.js';
import BytedIcon from 'components/icon';

const DefaultMin = -Infinity;
const InitialValue = 0;

export default {
  name: `${CSS_PREFIX}input-number`,
  directives: {
    repeatClick: RepeatClick,
  },
  components: {
    BytedIcon,
  },
  mixins: [emitter],
  props: {
    // eslint-disable-next-line vue/require-default-prop
    value: {}, // 不设默认值，以允许用户绑定空字符串、清空输入，视为 undefined 值。
    placeholder: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, ['md', 'small', 'sm', 'xs']);
      },
    },
    step: {
      type: Number,
      default: 1,
    },
    min: {
      type: Number,
      default: DefaultMin,
    },
    max: {
      type: Number,
      default: Infinity,
    },
    precision: {
      type: Number,
      default: undefined,
      validator(value) {
        return value >= 0;
      },
    },
    controlsPosition: {
      type: String,
      default: 'right',
    },
    // 内部使用，暂时不对外,禁止 dispatch form-item.change
    validateEvent: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      currentValue: InitialValue,
      userInput: null,
      stepUpIntervalPointer: undefined,
      stepDownIntervalPointer: undefined,
      isSuffixShow: {
        disableStepUpBtn: (this.value >= this.max) && !this.readonly,
        disableStepDownBtn: (this.value <= this.min) && !this.readonly,
        disabled: this.disabled,
        show: !(this.controlsPosition === 'right'),
      },
      startStepTime: undefined,
    };
  },
  computed: {
    baseClass() {
      const classArr = [`${CSS_PREFIX}input-number`, `${CSS_PREFIX}input-number-${this.size}`];
      if (this.disabled) {
        classArr.push(`${CSS_PREFIX}input-number-disabled`);
      }
      if (this.controlsPosition === 'dispersion') {
        classArr.push('bui-input-number-dispersion');
      }
      return classArr;
    },
    displayVal() {
      if (this.userInput !== null) {
        return this.userInput;
      }

      let val = this.currentValue;
      if (!isNaN(val) && this.precision !== undefined) {
        const precisionVal = parseFloat(val).toFixed(this.precision);
        if (!isNaN(precisionVal)) {
          val = precisionVal;
        }
      }

      return val;
    },
    buiInputClass() {
      return this.controlsPosition === 'dispersion' ? 'bui-input-dispersion' : 'bui-input-default';
    },
  },
  watch: {
    value: {
      immediate: true,
      handler: function(newVal, oldVal) {
        if (this.precision !== undefined) {
          const precisionVal = parseFloat(newVal).toFixed(this.precision);
          if (!isNaN(precisionVal)) {
            newVal = precisionVal;
          }
        }

        if (newVal >= this.max) newVal = this.max;
        if (newVal <= this.min) newVal = this.min;
        this.currentValue = newVal;
        this.userInput = null;

        this.isSuffixShow = {
          ...this.isSuffixShow,
          disableStepUpBtn: (this.value >= this.max) && !this.readonly,
          disableStepDownBtn: (this.value <= this.min) && !this.readonly,
        };
      },
    },
  },
  methods: {
    controlsPositionShow(type) {
      return this.controlsPosition === type;
    },
    handleClick(e) {
      this.$emit('click', e);
    },
    handleBlur(e) {
      this.isSuffixShow.show = this.isSuffixShowBoolean(false);

      this.$emit('blur', e);
      if (this.validateEvent) {
        this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.blur', [this.value]);
      }
    },
    handleFocus(e) {
      this.isSuffixShow.show = this.isSuffixShowBoolean(true);
      this.$emit('focus', e);
    },
    handleKeydown(e) {
      this.$emit('keydown', e);
    },
    handleKeyup(e) {
      this.$emit('keyup', e);
    },
    setCurrentValue(newVal) {
      const oldVal = this.currentValue;
      if (typeof newVal === 'number' && this.precision !== undefined) {
        newVal = this.toPrecision(newVal, this.precision);
      }
      if (newVal >= this.max) newVal = this.max;
      if (newVal <= this.min) newVal = this.min;
      if (oldVal === newVal) return;
      this.userInput = null;
      this.$emit('input', newVal);
      this.$emit('change', newVal, oldVal);
      if (this.validateEvent) {
        this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [newVal]);
      }
      this.currentValue = newVal;
    },
    handleInputChange(e) {
      this.$emit('input-change', e);

      const value = e.target.value;
      const defaultValue = (this.min !== DefaultMin ? this.min : InitialValue);
      // 特殊处理空输入，格式化为 最小值 || InitialValue（0）。
      const newVal = value === '' ? defaultValue : Number(value);
      if (!isNaN(newVal) || value === '') {
        this.setCurrentValue(newVal);
      }
      this.userInput = null;
    },
    handleInput(e) {
      this.userInput = e.target.value;
    },
    handleMouseEnter(e) {
      this.isSuffixShow.show = this.isSuffixShowBoolean(true);
    },
    handleMouseLeave() {
      this.isSuffixShow.show = this.isSuffixShowBoolean(false);
    },
    isSuffixShowBoolean(bool) {
      // 非默认样式，加减button常驻；默认样式禁用状态隐藏加减button；
      return !this.controlsPositionShow('right') || (!this.isSuffixShow.disabled && bool);
    },
    toPrecision(val, precision) {
      if (precision === undefined) {
        precision = this.getPrecision(this.value);
      }
      return parseFloat(Number(val).toFixed(precision));
    },
    getPrecision(val) {
      const valStr = val.toString();
      const decimalPos = valStr.indexOf('.');
      if (decimalPos === -1) {
        return 0;
      } else {
        return (valStr.length - decimalPos - 1);
      }
    },
    floatCalculate(a, b, type) {
      const maxPrecision = Math.max(this.getPrecision(a), this.getPrecision(b));

      const precision = Math.pow(10, maxPrecision);
      const intA = this.toPrecision(a * precision);
      const intB = this.toPrecision(b * precision);

      let outcome = undefined;
      if (type === 'add') {
        outcome = intA + intB;
      }

      if (type === 'minus') {
        outcome = intA - intB;
      }

      return outcome / precision;
    },
    stepGo(type) {
      let curVal = this.currentValue;
      if (isNaN(curVal)) {
        curVal = 0;
      }

      let directNewVal = parseFloat(curVal) + parseFloat(this.step);
      if (type !== 'up') {
        directNewVal = curVal - this.step;
      }

      let finalNewVal = undefined;
      if (parseInt(directNewVal) === directNewVal) {
        finalNewVal = directNewVal;
      } else {
        finalNewVal = this.floatCalculate(curVal, this.step, type === 'up' ? 'add' : 'minus');
      }

      if (finalNewVal < this.min) {
        finalNewVal = this.min;
      }
      if (finalNewVal > this.max) {
        finalNewVal = this.max;
      }
      this.setCurrentValue(finalNewVal);
      this.userInput = null;
    },
    stepGoUp() {
      if (this.disabled) return;
      this.stepGo('up');
    },
    stepGoDown() {
      if (this.disabled) return;
      this.stepGo('down');
    },
  },
};
</script>



