<template>
  <ul
    :key="forceUpdateKey"
    :class="baseClass"
  >
    <slot/>
  </ul>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';

export default {
  name: `${CSS_PREFIX}menu`,
  mixins: [emitter],
  provide() {
    return {
      rootMenu: this,
    };
  },

  props: {
    defaultActive: {
      type: String,
      default: '',
    },
    defaultOpeneds: {
      type: Array,
      default() {
        return [];
      },
    },
    uniqueOpened: {
      type: Boolean,
      default: false,
    },
    theme: {
      type: String,
      default: 'light',
      validator: (val) => ['light', 'dark'].indexOf(val) !== -1,
    },
    router: {
      type: Boolean,
      default: false,
    },
    paddingLeft: {
      type: Number,
      default: 24,
    },
    collapse: {
      type: Boolean,
      default: false,
    },
    transfer: {
      type: Boolean,
      default: true,
    },
    popperClass: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      activeIndex: this.defaultActive,
      openedMenus: this.defaultOpeneds.length > 0 && !this.collapse ? this.defaultOpeneds.slice(0) : [],
      items: {},
      submenus: {},
      tempOpenedMenus: [],
    };
  },
  computed: {
    baseClass() {
      const classArr = [`${CSS_PREFIX}menu`, 'bui-menu-ul'];
      if (this.theme === 'light') {
        classArr.push('bui-menu-light');
      }
      if (this.theme === 'dark') {
        classArr.push('bui-menu-dark');
      }
      if (this.collapse) {
        classArr.push('bui-menu-collapse');
      }
      return classArr;
    },
    // 更新key值 触发内部collapse强制渲染
    forceUpdateKey() {
      return Number(this.collapse);
    },
  },
  watch: {
    items() {
      this.updateActiveIndex();
    },
    defaultActive(val) {
      this.updateActiveIndex(val);
    },
    defaultOpeneds(value) {
      if (!this.collapse) {
        this.openedMenus = value.slice(0);
      }
    },
    collapse(value) {
      if (value) {
        this.tempOpenedMenus = JSON.parse(JSON.stringify(this.openedMenus));
        this.openedMenus = [];
      } else {
        this.openedMenus = this.tempOpenedMenus;
      }
    },
  },

  mounted() {
    this.initOpenedMenu();
    this.$on('submenu-click', this.handleSubmenuClick);
    this.$on('item-click', this.handleItemClick);
  },
  methods: {
    updateActiveIndex(val) {
      const item = this.items[val] || this.items[this.activeIndex] || this.items[this.defaultActive];
      if (item) {
        this.activeIndex = item.index;
        this.initOpenedMenu();
      } else {
        this.activeIndex = null;
      }
    },
    initOpenedMenu() {
      const item = this.items[this.activeIndex];
      if (item && !this.collapse) {
        item.indexPath.forEach((index) => {
          const submenu = this.submenus[index];
          submenu && this.openMenu(submenu.index, submenu.indexPath);
        });
      }
    },
    addSubmenu(item) {
      this.$set(this.submenus, item.index, item);
    },
    removeSubmenu(item) {
      delete this.submenus[item.index];
    },
    handleSubmenuClick(submenu) {
      const {index, indexPath} = submenu;
      const isOpened = this.openedMenus.indexOf(index) !== -1;

      if (isOpened) {
        this.closeMenu(index);
        this.$emit('close', index, indexPath);
      } else {
        this.openMenu(index, indexPath);
        this.$emit('open', index, indexPath);
      }
    },
    addItem(item) {
      this.$set(this.items, item.index, item);
    },
    removeItem(item) {
      delete this.items[item.index];
    },
    handleItemClick(item) {
      const {index, indexPath} = item;
      const oldActiveIndex = this.activeIndex;
      if (index) {
        this.activeIndex = index;
      }

      this.$emit('select', index, indexPath, item);

      if (this.router && index) {
        this.routeToItem(item, (error) => {
          this.activeIndex = oldActiveIndex;
          if (error) console.error(error);
        });
      }
      if (this.collapse) {
        this.openedMenus = [];
      }
    },
    routeToItem(item, onError) {
      const route = item.route || item.index;
      try {
        this.$router.push(route, () => {}, onError);
      } catch (e) {
        console.log(e);
      }
    },
    openMenu(index, indexPath) {
      if (this.openedMenus.indexOf(index) !== -1) {
        return;
      }
      if (this.uniqueOpened) {
        this.openedMenus = this.openedMenus.filter((submenuIndex) => indexPath.indexOf(submenuIndex) !== -1);
      }
      this.openedMenus.push(index);
    },
    closeMenu(index) {
      const i = this.openedMenus.indexOf(index);
      if (i !== -1 ) {
        this.openedMenus.splice(i, 1);
      }
    },
    open(index) {
      const {indexPath} = this.submenus[index.toString()];
      indexPath && indexPath.forEach((i) => this.openMenu(i, indexPath));
    },
    close(index) {
      this.closeMenu(index);
    },
  },
};
</script>



