<script>
import {CSS_PREFIX} from '../../base.js';
import CommonLogic from './common-logic.js';
import emitter from 'mixins/emitter';
import BuiCollapseTransition from 'utils/collapse-transition.vue';
import BytedIcon from 'components/icon';
import BytedPopper from 'components/popper';
const SHOW_TIMEOUT = 100;
export default {
  name: `${CSS_PREFIX}submenu`,
  components: {BuiCollapseTransition, BytedIcon, BytedPopper},
  mixins: [CommonLogic, emitter],
  props: {
    index: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      disabled: false,
      submenus: {},
      items: {},
      mouseInChild: false,
      popperOptions: {
        type: Object,
        default() {
          return {
            modifiers: {
              flip: {
                enabled: true,
              },
              hide: {
                enabled: false,
              },
              preventOverflow: {
                enabled: true,
                boundariesElement: 'viewport',
              },
            },
          };
        },
      },
      popperEl: null,
    };
  },
  computed: {
    opened() {
      return this.rootMenu.openedMenus.indexOf(this.index) > -1;
    },
    active() {
      let isActive = false;
      const submenus = this.submenus;
      const items = this.items;

      Object.keys(items).forEach((index) => {
        if (items[index].active) {
          isActive = true;
        }
      });

      Object.keys(submenus).forEach((index) => {
        if (submenus[index].active) {
          isActive = true;
        }
      });

      return isActive;
    },
    collapseWrapperClass() {
      const classArr = ['bui-menu-ul-collapse-wrapper'];
      if (this.rootMenu.theme === 'dark') {
        classArr.push('bui-menu-ul-collapse-wrapper-dark');
      }
      return classArr;
    },
    baseClass() {
      const classArr = [`${CSS_PREFIX}submenu`, 'bui-menu-ul'];
      if (this.opened) {
        classArr.push('bui-submenu-opened');
      }
      if (this.disabled) {
        classArr.push('bui-submenu-disabled');
      }
      return classArr;
    },
    submenuTitleClass() {
      const classArr = ['bui-submenu-title'];
      classArr.push(`bui-submenu-${this.rootMenu.theme}`);
      if (this.active) {
        classArr.push('bui-submenu-active');
      }
      return classArr;
    },
  },
  watch: {
    opened(newVal) {
      newVal ?
        this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.update') :
        this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.destroy');
    },
  },
  created() {
    this.$on('mouse-enter-child', () => {
      this.mouseInChild = true;
      clearTimeout(this.timeout);
    });
    this.$on('mouse-leave-child', () => {
      this.mouseInChild = false;
      clearTimeout(this.timeout);
    });
  },
  mounted() {
    this.parentMenu.addSubmenu(this);
    this.rootMenu.addSubmenu(this);
    this.popperEl = this.$refs.popperRef;
  },
  beforeDestroy() {
    this.parentMenu.removeSubmenu(this);
    this.rootMenu.removeSubmenu(this);
  },
  methods: {
    addSubmenu(item) {
      this.$set(this.submenus, item.index, item);
    },
    removeSubmenu(item) {
      delete this.submenus[item.index];
    },
    addItem(item) {
      this.$set(this.items, item.index, item);
    },
    removeItem(item) {
      delete this.items[item.index];
    },
    handleClick() {
      if (this.disabled || this.rootMenu.collapse) {
        return;
      }
      this.dispatch(`${CSS_PREFIX}menu`, 'submenu-click', this);
    },
    handleTitleMouseenter() {
      this.$emit('title-mouseenter', this);
    },
    handleTitleMouseleave() {
      this.$emit('title-mouseleave', this);
    },
    handleMouseenter(showTimeout = SHOW_TIMEOUT) {
      const {rootMenu, disabled} = this;
      if (!rootMenu.collapse || disabled) {
        return;
      }
      this.dispatch(`${CSS_PREFIX}submenu`, 'mouse-enter-child');
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        this.rootMenu.openMenu(this.index, this.indexPath);
      }, showTimeout);
    },
    handleMouseleave(deepDispatch = false) {
      const {rootMenu} = this;
      let currentParent = null;
      if (!rootMenu.collapse) {
        return;
      }
      this.dispatch(`${CSS_PREFIX}submenu`, 'mouse-leave-child');
      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        !this.mouseInChild && this.rootMenu.closeMenu(this.index);
      }, this.hideTimeout);

      if (deepDispatch) {
        if (this.$parent.$options.name === `${CSS_PREFIX}submenu`) {
          this.$parent.handleMouseleave(true);
        }
        if (this.$parent.$options.name === `${CSS_PREFIX}menu-item-group`) {
          currentParent = this.$parent;
          while (currentParent.$options.name !== `${CSS_PREFIX}submenu`) {
            currentParent = currentParent.$parent;
          }
          currentParent.handleMouseleave && currentParent.handleMouseleave(true);
        }
      }
    },
  },
  render(h) {
    const {submenuTitleClass, baseClass, $slots, opened, rootMenu, popperOptions, index, collapseWrapperClass, popperEl} = this;

    const popperMenu = (
      <byted-popper
        v-show={opened}
        reference={popperEl}
        popper-options= {popperOptions}
        append-to-body={rootMenu.transfer}
        class={['bui-submenu-collapse', rootMenu.popperClass]}
        placement="right-start"
      >
        <div
          class={collapseWrapperClass}
          on-mouseenter={() => this.handleMouseenter()}
          on-mouseleave={() => this.handleMouseleave(true)}
        >
          <div class="bui-menu-ul-wrapper">
            {
              rootMenu.$children.map((item) => {
                return item.index;
              }).indexOf(index) > -1 ?
                <div style={this.paddingStyle} class="bui-menu-ul-popper-title">{$slots.title}</div>
                :
                null
            }
            <ul
              class="bui-menu-ul"
            >
              {$slots.default}
            </ul>
          </div>
        </div>
      </byted-popper>
    );

    const inlineMenu = (
      <ul
        v-show={opened}
        class="bui-menu-ul"
      >
        {$slots.default}
      </ul>
    );

    return (
      <li
        ref="popperRef"
        class={baseClass}
        on-mouseenter={() => this.handleMouseenter()}
        on-mouseleave={() => this.handleMouseleave(false)}
      >
        <div
          style={this.paddingStyle}
          class={submenuTitleClass}
          on-click={this.handleClick}
          on-mouseenter={this.handleTitleMouseenter}
          on-mouseleave={this.handleTitleMouseleave}
        >
          {this.$slots.title}
          <byted-icon
            name={rootMenu.collapse ? 'angle-right' : 'angle-down'}
            class="bui-submenu-icon"
          />
        </div>
        {rootMenu.collapse ? popperMenu : <bui-collapse-transition>{inlineMenu}</bui-collapse-transition>}
      </li>
    );
  },
};
</script>



