<template>
  <div
    :class="baseClass"
    :style="style">
    <div
      v-if="!isLast"
      :style="splitStyle"
      class="bui-split-panel">
      <div
        :style="splitColorStyle"
        :class="splitClass"/>
    </div>
    <div
      class="bui-step-head"
      @click="onChange">
      <template v-if="$slots.icon">
        <div
          class="custom-icon">
          <slot name="icon" />
        </div>
      </template>
      <template v-else>
        <div class="bui-step-icon icon-panel">
          <div class="bui-step-icon-inner">
            <template v-if="currentStatus === 'error'">
              <byted-icon
                name="close"
                class="bui-message-icon bui-message-icon-error"
                color="#F45858"/>
            </template>
            <template v-else-if="currentStatus === 'finished'">
              <byted-icon
                name="check"
                class="bui-message-icon bui-message-icon-finished"
              />
            </template>
            <template v-else>
              <div class="bui-step-num">{{ currentIndex }}</div>
            </template>
          </div>
        </div>
      </template>
      <div class="bui-step-content">
        <div
          v-if="$slots.title || title"
          ref="title"
          class="bui-step-title">
          <template v-if="$slots.title">
            <slot name="title"/>
          </template>
          <template v-else>
            {{ title }}
          </template>
        </div>
        <div
          v-if="$slots.description || description"
          ref="description"
          class="bui-step-description">
          <template v-if="$slots.description">
            <slot name="description"/>
          </template>
          <template v-else>
            {{ description }}
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';
import {isOneOf} from 'utils/helper.js';
import BytedIcon from 'components/icon';
const ICON_SIZES = {
  sm: 26,
  md: 30,
  lg: 34,
};
export default {
  name: `${CSS_PREFIX}step-item`,
  components: {
    BytedIcon,
  },
  inject: ['steps'],
  mixins: [emitter],
  props: {
    title: {
      type: String,
      default: '',
    },
    description: {
      type: String,
      default: '',
    },
    status: {
      type: String,
      default: '',
      validator(value) {
        return isOneOf(value, ['error', 'finished', 'waiting', 'inprogress', '']);
      },
    },
    inProgress: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      currentIndex: 0,
      currentStatus: '',
      stepCount: 0,
      lineStyle: '',
    };
  },
  computed: {
    baseClass() {
      const classes = [`${CSS_PREFIX}step-item`, `bui-step-${this.currentStatus}`];
      if (this.steps.current === this.currentIndex) {
        classes.push('bui-step-current');
      }
      return classes;
    },
    splitClass() {
      return [`${this.lineStyle}-line`, 'split-line'];
    },
    isLast() {
      return this.currentIndex === this.stepCount;
    },
    isPreStep() {
      return this.steps.current === this.currentIndex + 1;
    },
    style() {
      const style = {};
      if (this.steps.direction === 'vertical') {
        style.height = `${100 / this.stepCount}%`;
      }
      return style;
    },
    splitStyle() {
      const offset = this.getTitleOrDescriptionWidth() + ICON_SIZES[this.steps.size];
      if (this.steps.direction !== 'vertical') {
        return {
          left: `${offset}px`,
          width: `calc(100% - ${offset}px)`,
        };
      }
    },
    splitColorStyle() {
      if (this.steps.status === 'error' && this.isPreStep) {
        if (this.steps.lineStyle === 'dotted') {
          return {
            borderTopColor: '#F45858',
          };
        } else {
          return {
            background: '#F45858',
          };
        }
      }
    },
  },
  mounted() {
    this.dispatch('byted-steps', 'append');
  },
  destroyed() {
    this.dispatch('byted-steps', 'remove');
  },
  methods: {
    onChange() {
      // waiting状态不可切换
      const status = this.status || this.currentStatus;
      if (status === 'waiting') {
        return;
      }
      if (
        this.steps.onChange &&
        this.steps.onChange instanceof Function
      ) {
        this.steps.onChange(this.currentIndex);
      }
    },
    getTitleOrDescriptionWidth() {
      return this.$refs.title ? this.$refs.title.offsetWidth : this.$refs.description ? this.$refs.description.offsetWidth : 0;
    },
  },
};
</script>



