<template>
  <div
    :class="className"
    @click="onNameClick($event)">
    <byted-checkbox
      v-if="isCheckShow"
      ref="selectPanelItemCheckbox"
      :class="baseClass+'-checkbox'"
      :value="value"
      :indeterminate="indeterminate"
      :disabled="disabled"
      @change="onCheckboxChange"/>
    <template v-if="!renderName">
      <span
        :class="baseClass+'-name'">{{ name }}</span>
    </template>
    <template v-if="renderName">
      <expand
        :class="baseClass+'-name'"
        :name="name"
        :option="option"
        :render="renderName"/>
    </template>
    <span
      v-if="hasChild"
      :class="baseClass+'-toright'"
      @click="onNameClick">
      <byted-icon
        name="angle-right"/>
    </span>
    <span
      v-if="hasRemove && !disabled"
      :class="baseClass+'-closeicon'"
      @click.stop="onRemoveClick">
      <byted-icon name="close"/>
    </span>
  </div>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import {Expand} from './expand.js';
import BytedCheckbox from 'components/checkbox';
import BytedIcon from 'components/icon';

export default {
  name: `${CSS_PREFIX}select-panel-item`,
  components: {Expand, BytedCheckbox, BytedIcon},
  props: {
    inline: {
      type: Boolean,
      default: false,
    },
    value: {
      type: Boolean,
      default: false,
    },
    indeterminate: {
      type: Boolean,
      default: false,
    },
    name: {
      type: String,
      default: '',
    },
    hasChild: {
      type: Boolean,
      default: false,
    },
    active: {
      type: Boolean,
      default: false,
    },
    hasRemove: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    hasCheckbox: {
      type: Boolean,
      default: true,
    },
    renderName: {
      type: Function,
      default: null,
    },
    option: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      baseClass: `${CSS_PREFIX}select-panel-item`,
    };
  },
  computed: {
    className() {
      const classArr = [this.baseClass];
      if (this.active) {
        classArr.push(this.baseClass + '-active');
      }
      if (this.hasRemove) {
        classArr.push(this.baseClass + '-remove');
      } else if (this.hasCheckbox) {
        classArr.push(this.baseClass + '-check');
      }
      if (this.hasChild) {
        classArr.push(this.baseClass + '-has-child');
      }
      if (this.disabled) {
        classArr.push(this.baseClass + '-disabled');
      }
      if (this.inline) {
        classArr.push(this.baseClass + '-inline');
      }
      if (this.value) {
        classArr.push(this.baseClass + '-selected');
      }
      return classArr;
    },
    isCheckShow() {
      if (this.hasRemove) {
        return false;
      }
      return this.hasCheckbox;
    },
    listClass() {
      const classArr = [this.baseClass + '-list'];
      if (this.hasChild) {
        classArr.push(this.baseClass + '-has-icon');
      }
      return classArr;
    },
  },
  methods: {
    onCheckboxChange(...args) {
      if (this.disabled) return false;
      this.$emit('change', args);
    },
    onNameClick(event) {
      const selectPanelItemCheckboxRef = this.$refs['selectPanelItemCheckbox'];
      if (this.disabled || (
        this.isCheckShow && selectPanelItemCheckboxRef && selectPanelItemCheckboxRef.$el.contains(event.target)
      )) {
        return;
      }
      this.$emit('nameclick');
    },
    onRemoveClick() {
      if (this.disabled) return false;
      this.$emit('removeclick');
    },
  },
};
</script>



