import {CSS_PREFIX} from '../../base.js';

export default {
  name: `${CSS_PREFIX}breadcrumb-separator`,
  props: {
    separator: {
      type: [String, Function],
      default: '/',
    },
  },
  render(h) {
    if (typeof this.separator === 'string') {
      return (
        <span class="bui-breadcrumb-separator">{ this.separator }</span>
      );
    } else if (typeof this.separator === 'function') {
      return (
        <span class="bui-breadcrumb-separator bui-breadcrumb-separator-icon">{ this.separator(h) }</span>
      );
    }
  },
};



