<template>
  <ol :class="baseClass">
    <slot/>
  </ol>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';

export default {
  name: `${CSS_PREFIX}breadcrumb`,
  props: {
    separator: {
      type: [String, Function],
      default: '/',
    },
  },
  provide() {
    return {
      breadcrumb: this,
    };
  },
  data() {
    return {

    };
  },
  computed: {
    baseClass() {
      return [`${CSS_PREFIX}breadcrumb`];
    },
  },
};
</script>



