<template>
  <div :class="baseClass">
    <slot name="header">
      <div
        class="byted-schedule-header">
        <div class="byted-schedule-option">
          <slot name="shortcuts">
            <byted-link
              v-for="(option, i) in shortcuts"
              :key="i"
              class="byted-schedule-link"
              @click="changeSelctValue(option.selected)"
            >{{ option.label }}</byted-link>
          </slot>
        </div>
        <div class="byted-schedule-explain">
          <slot name="legend">
            <span class="byted-schedule-header-selected">{{ _selectedText }}</span>
            <span class="byted-schedule-header-selectable">{{ _selectableText }}</span>
          </slot>
        </div>
      </div>
    </slot>
    <div
      ref="calendar"
      class="byted-schedule-calendar">
      <div
        v-if="scheduleShow"
        ref="schedule"
        :style="scheduleStyle"
        :class="scheduleClass"
        class="byted-schedule-rang"
      />
      <div class="tableWrap">
        <table class="byted-schedule-calendar-table">
          <thead>
            <tr class="byted-schedule-calendar-time-all">
              <th
                rowspan="8"
                class="byted-schedule-week-td">{{ _weekText }}</th>
              <th
                class="byted-schedule-calendar-time"
                colspan="24">00:00 - 12:00</th>
              <th
                class="byted-schedule-calendar-time"
                colspan="24">12:00 - 24:00</th>
            </tr>
            <tr class="byted-schedule-calendar-time-item">
              <td
                v-for="(item, index) in dayHour"
                :key="index"
                colspan="2">
                {{ item }}
              </td>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(week, index) in weekName"
              :key="index">
              <td>
                <byted-checkbox
                  v-if="showCheckbox"
                  v-model="weekCheckbox[index]"
                  :indeterminate="weekIndeterminate[index]"
                  @change="handleWeekCheck(index)" />
                {{ week }}
              </td>
              <td
                v-for="(item, i) in dayHalfHour"
                ref="calendarAtomTime"
                :key="i"
                :data-week="index"
                :data-time="item"
                :class="{
                  'byted-schedule-calendar-selected': value[index].indexOf(item) >= 0,
                  'byted-schedule-calendar-disabled': disabledDate(index, item) || false
                }"
                class="byted-schedule-calendar-atom-time"
                @mousemove="setShadow"
                @mousedown="setFirstSource(index, item, $event)"
                @mouseenter="setHoverData($event, { time: item, weekText:week, week: index })"
                @mouseleave="removeHoverData"/>
            </tr>
            <tr v-if="showFooter">
              <td
                class="byted-schedule-table-tip"
                colspan="49">
                <div class="clearfix">
                  <p
                    v-if="!hasSelectedTime"
                    class="byted-schedule-no-selected-time">{{ _noDataText }}</p>
                  <span
                    v-else
                    class="pull-left byted-schedule-tip-text">{{ _titleText }}</span>
                  <byted-link
                    v-if="hasSelectedTime"
                    class="pull-right"
                    @click="onResetAllClick"
                  >{{ _clearText }}</byted-link>
                </div>
                <div
                  v-if="hasSelectedTime"
                  class="byted-schedule-selected-time">
                  <div
                    v-for="(timeArr, index) in value"
                    :key="index">
                    <p v-if="timePeriodStrArr[index] !== ''">
                      <span class="byted-schedule-tip-text">
                        {{ weekName[index] }}
                      </span>
                      <span>{{ timePeriodStrArr[index] }}</span>
                    </p>
                  </div>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <byted-popover
        ref="weektime"
        v-model="isPopover"
        placement="top"
        trigger="manual"
        class="bui-schedule-tooltip"
        width="auto">
        <template slot="body">
          <slot
            :week="hoverTipObj.week"
            :time="hoverTipObj.time"
            name="tooltip">{{ hoverWeek }} {{ hoverTime }}</slot>
        </template>
      </byted-popover>
    </div>
  </div>
</template>

<script>
import {CSS_PREFIX, LOCALE_PREFIX} from '../../base.js';
import locale from 'mixins/locale';
import * as weekData from './data.js';
import {on, off} from 'utils/dom.js';
import BytedLink from 'components/link';
import BytedCheckbox from 'components/checkbox';
import BytedPopover from 'components/popover';
import {isPlainObject} from 'utils/types';

function getContinuousChildArr(arr) {
  if (!(arr instanceof Array)) {
    console.error('ERROR: getContinuousChildArr() Input is not Array.');
    return [];
  }

  const rangeArr = [];

  let rangeStartIndex = 0;
  let rangeEndIndex = 0;
  arr.forEach((item, curIndex, thisArr) => {
    const nextIndex = curIndex + 1;
    const nextItem = thisArr[nextIndex];

    const itemIsContinuous = (nextItem === item + 1);

    if (!itemIsContinuous) {
      if (rangeStartIndex !== rangeEndIndex) {
        rangeArr.push([
          thisArr[rangeStartIndex],
          thisArr[rangeEndIndex],
        ]);
      } else {
        rangeArr.push([item]);
      }

      rangeStartIndex = nextIndex;
      rangeEndIndex = nextIndex;
    } else {
      rangeEndIndex = nextIndex;
    }
  });

  return rangeArr;
}

function getClockString(id, type) {
  if (!Number.isInteger(id)) {
    console.error('ERROR: getClockString() Input id is not integer.');
    return '';
  }
  if (type !== 'start' && type !== 'end') {
    console.error('ERROR: getClockString() Input type is not legal.');
    return '';
  }

  let clock = '';
  let h = Math.floor(id / 2);
  const min = id % 2;
  if (type === 'end' && min === 1) {
    h += 1;
  }

  if (h <= 9) {
    clock += '0' + h;
  } else {
    clock += h;
  }
  if (type === 'start') {
    if (min === 1) {
      clock += ':30';
    } else {
      clock += ':00';
    }
  } else {
    if (min === 0) {
      clock += ':30';
    } else {
      clock += ':00';
    }
  }
  return clock;
}

export default {
  name: `${CSS_PREFIX}schedule`,
  components: {
    BytedLink,
    BytedCheckbox,
    [BytedPopover.name]: BytedPopover,
  },
  mixins: [locale],
  props: {
    value: {
      type: Array,
      default() {
        return [];
      },
    },
    showFooter: {
      type: Boolean,
      default: true,
    },
    type: {
      type: String,
      default: 'normal',
    },
    shortcuts: {
      type: Array,
      default() {
        return [];
      },
    },
    showCheckbox: {
      type: Boolean,
      default: false,
    },
    disabledDate: {
      type: Function,
      default: () => false,
    },
  },
  data() {
    return {
      dayHour: weekData.dayHour,
      // weekName: weekData.weekName,
      dayHalfHour: weekData.dayHalfHour,
      start_td: '',
      end_td: '',
      start_point: {
        x: '',
        y: '',
      },
      isAdd: false,
      hoverWeek: '',
      hoverTime: '',
      hoverTimeoutId: 0,
      isPopover: false,
      popOverCanShow: false, // 控制鼠标离开td时不会继续触发setTimeout延迟事件
      timePeriodStrArr: ['', '', '', '', '', '', ''], // 一周七天对应时间段汉字字符串
      scheduleShow: false,
      scheduleStyle: {},
      hoverTipObj: {},
      scheduleClass: {
        'no-transition': false,
      },
      weekCheckbox: [false, false, false, false, false, false, false],
      weekIndeterminate: [false, false, false, false, false, false, false],
    };
  },
  computed: {
    weekName() {
      let days = this.t(`${LOCALE_PREFIX}.schedule.days`) || [];
      // https://github.com/kazupon/vue-i18n/issues/759
      // the array has become an object
      if (isPlainObject(days)) {
        days = Object.keys(days).map((key)=>days[key]);
      }
      return days;
    },
    hasSelectedTime() {
      return this.value.some((ele) => ele.length >= 1);
    },
    baseClass() {
      const classArr = [`${CSS_PREFIX}schedule`, `${CSS_PREFIX}schedule-${this.type}`];
      return classArr;
    },
    isOptionSelected() {
      const {weekCheckbox} = this;
      return (option = []) => option.every((item) => weekCheckbox[item]);
    },
    _selectedText() {
      return this.t(`${LOCALE_PREFIX}.schedule.selectedText`);
    },
    _selectableText() {
      return this.t(`${LOCALE_PREFIX}.schedule.selectableText`);
    },
    _weekText() {
      return this.t(`${LOCALE_PREFIX}.schedule.weekText`);
    },
    _noDataText() {
      return this.t(`${LOCALE_PREFIX}.schedule.noDataText`);
    },
    _titleText() {
      return this.t(`${LOCALE_PREFIX}.schedule.title`);
    },
    _clearText() {
      return this.t(`${LOCALE_PREFIX}.schedule.clearText`);
    },
  },
  watch: {
    value: {
      immediate: true,
      handler(newVal, oldVal) {
        this.value.map((arr, day) => {
          for (let i = arr.length - 1; i >= 0; i--) {
            const hour = arr[i];
            if (this.disabledDate(day, hour)) {
              arr.splice(i, 1);
            }
          };
        });
        this.$emit('input', this.value);
        this.$emit('change', this.value);
        this.resetTimePeriodStrArr();
        this.initiateTimePeriodStr();
        this.weekCheckbox.map((item, index) => {
          const valLength = this.value[index].length;
          if (valLength === 48) this.weekCheckbox.splice(index, 1, true);
          else this.weekCheckbox.splice(index, 1, false);
          if (valLength < 48 && valLength > 0) this.weekIndeterminate[index] = true;
          else this.weekIndeterminate[index] = false;
        });
      },
    },
  },
  mounted() {
    on(document, 'mouseup', this.scheduleEnd);
    on(document, 'mousewheel', this.scheduleEnd);
  },
  destroyed() {
    off(document, 'mouseup', this.scheduleEnd);
    off(document, 'mousewheel', this.scheduleEnd);
  },
  methods: {
    changeSelctValue(selected) {
      const result = [[], [], [], [], [], [], []];
      selected.forEach((item) => {
        result[item] = [...new Array(48).keys()];
      });
      this.$emit('input', result);
    },
    handleWeekCheck(index) {
      if (this.weekCheckbox[index]) {
        this.value[index] = [...new Array(48).keys()];
      } else {
        this.value[index] = [];
      }
      this.$emit('input', this.value.slice());
    },
    setShadow(e) {
      this.curtd = e.target;
      const curpos = this.getClientPosition(this.curtd, true);
      if (this.start_point) {
        const w = curpos.x - this.start_point.x;
        const h = curpos.y - this.start_point.y;
        const top = h > 0 ? this.start_point.y : curpos.y - 20;
        const left = w > 0 ? this.start_point.x : curpos.x - 10;
        const width = w > 0 ? Math.abs(w) : Math.abs(w) + 22;
        const height = h > 0 ? Math.abs(h) : Math.abs(h) + 42;
        this.scheduleStyle = {
          opacity: 0.6,
          top: top + 'px',
          left: left + 'px',
          width: width + 'px',
          height: height + 'px',
        };
      }
    },
    setHoverData(evt, obj) {
      this.hoverTipObj = obj;
      this.popOverCanShow = true;
      const hour = Math.floor(obj.time / 2) < 10 ? '0' + Math.floor(obj.time / 2) : Math.floor(obj.time / 2);
      const tempHoverTime = (obj.time % 2) ?
          hour + ':30 - ' + ((+hour + 1) < 10 ? '0' + (+hour + 1) :
          (+hour + 1)) + ':00' : hour + ':00 - ' + hour + ':30';
      if (this.hoverTimeoutId) {
        clearTimeout(this.hoverTimeoutId);
      }
      if (this.$refs['weektime']) {
        this.$refs['weektime'].referenceEle = evt.target;
      }
      this.hoverTimeoutId = setTimeout(() => {
        this.hoverWeek = obj.weekText;
        this.hoverTime = tempHoverTime;
        this.isPopover = this.popOverCanShow ? true : false;
        clearTimeout(this.hoverTimeoutId);
      }, 500);
    },
    removeHoverData() {
      this.popOverCanShow = false;
      this.hoverWeek = '';
      this.hoverTime = '';
      this.isPopover = false;
    },
    setFirstSource(week, id, e) {
      const weekData = this.value[week];
      this.isAdd = weekData ?
          weekData.indexOf(id) === -1 : true;

      if (e.which !== 1) {
        return;
      }
      this.start_td = e.target;
      this.start_point = this.getClientPosition(e.target);
      this.scheduleShow = true;
      this.scheduleStyle = {
        left: this.start_point.x + 'px',
        top: this.start_point.y + 'px',
        width: 0,
        height: 0,
        opacity: 0.6,
      };
      this.scheduleClass['no-transition'] = true;
    },
    resetTimePeriodStrArr() {
      this.timePeriodStrArr = ['', '', '', '', '', '', ''];
    },
    onResetAllClick() {
      this.$emit('clear-week-schedule', this.value);
      this.$emit('input', [[], [], [], [], [], [], []]);
      this.resetTimePeriodStrArr();
    },
    getClientPosition(ele, outer) {
      const clientRect = ele.getBoundingClientRect();
      return {
        x: clientRect.left + (outer ? clientRect.width : 0),
        y: clientRect.top + (outer ? clientRect.height : 0),
      };
    },
    scheduleEnd(e) {
      if (this.start_point) {
        this.start_point = null,
        this.end_td = this.curtd;
        this.scheduleClass['no-transition'] = false;
        this.highlightScheduleArea(this.start_td, this.end_td);
      }
    },
    highlightScheduleArea(start, end, isHighlight) {
      if (!start) return;
      const startWeek = parseInt(start.getAttribute('data-week'));
      const startTime = parseInt(start.getAttribute('data-time'));
      const endWeek = parseInt(end.getAttribute('data-week'));
      const endTime = parseInt(end.getAttribute('data-time'));
      const sWeek = Math.min(startWeek, endWeek);
      const eWeek = Math.max(startWeek, endWeek);
      const sTime = Math.min(startTime, endTime);
      const eTime = Math.max(startTime, endTime);
      this.scheduleStyle.opacity = 0;
      this.scheduleShow = false;
      this.getSelectedCollection(sTime, sWeek, eTime, eWeek);
    },
    getSelectedCollection(startTime, startWeek, endTime, endWeek) {
      this.$refs.calendarAtomTime.forEach((item) => {
        const dataTime = parseInt(item.getAttribute('data-time'));
        const dataWeek = parseInt(item.getAttribute('data-week'));
        const curIndex = this.value[dataWeek].indexOf(dataTime);
        if (dataTime >= startTime &&
            dataTime <= endTime &&
            dataWeek >= startWeek &&
            dataWeek <= endWeek) {
          if (typeof this.value[dataWeek] === 'undefined') {
            Vue.set(this.value, dataWeek, []);
            this.value[dataWeek].push(dataTime);
          }
          if (this.value[dataWeek]) {
            if (this.isAdd) {
              if (curIndex === -1) {
                this.value[dataWeek].push(dataTime);
              }
            } else {
              if (curIndex > -1) {
                this.value[dataWeek].splice(curIndex, 1);
              }
            }
          }
        }
      });
      this.$emit('input', this.value);
      // 选中时间改变后，更新时间段字符串
      this.changeTimeStr(startWeek, endWeek);
    },

    changeTimeStr(start, end) {
      for (let i = start; i <= end; i++) {
        this.transformTimeArrToString(this.value[i], i);
      }
    },
    initiateTimePeriodStr() {
      // 依据已选择时间，更新时间段字符串
      for (let i = 0; i < this.value.length; i++) {
        if (this.value[i].length !== 0) {
          this.transformTimeArrToString(this.value[i], i);
        }
      }
    },
    sortArr(arr) {
      return arr.slice().sort((a, b) => a - b);
    },
    transformTimeArrToString(timeArr, targetTimePeriodStrArrIndex) {
      const sortedTimeArr = this.sortArr(timeArr);
      const rangeArr = getContinuousChildArr(sortedTimeArr);
      const resStr = rangeArr.map((range) => {
        let endTimeStr;

        const startTimeStr = getClockString(range[0], 'start');
        if (range.length === 1) {
          endTimeStr = getClockString(range[0] + 1, 'start');
        } else if (range.length === 2) {
          endTimeStr = getClockString(range[1], 'end');
        }

        return [startTimeStr, endTimeStr].join('~');
      }).join('、');
      this.$set(this.timePeriodStrArr, targetTimePeriodStrArrIndex, resStr);
    },
  },
};
</script>



