<template>
  <div
    :class="[contentClass]"
  >
    <byted-infinite-scroll
      ref="scroller"
      :disabled="!infinite"
      :height="maxHeight"
      @loadmore="handleLoadmore">
      <div
        v-if="!loading">
        <div
          v-for="(item, index) in dataSource"
          ref="items"
          :key="index"
          :class="contentItemClass"
          :tabindex="0"
          @keydown.up.prevent="focus('up')"
          @keydown.down.prevent="focus('down')"
          @keydown.enter.prevent="handleSelect(item)"
          @click="handleSelect(item)">
          <expand
            v-if="item.render"
            :value="item.value"
            :label="item.label"
            :render="item.render"
          />
          <slot
            v-if="!item.render"
            :item="item"
            name="dataSource"
          >
            {{ item[defaultProps.itemKey] || item }}
          </slot>
        </div>
      </div>
      <div
        v-if="displayEmpty"
        :class="contentEmptyClass">
        <div>
          <byted-icon name="empty"/>
          <p class="bui-list-item-content">{{ _emptyTip }}</p>
        </div>
      </div>
      <template slot="loading">
        <slot
          v-if="!displayEmpty"
          name="loading">
          <div class="bui-list-loadmore">
            <byted-loading-icon/>
            <span>{{ infiniteLoadingTip }}</span>
          </div>
        </slot>
      </template>
    </byted-infinite-scroll>
    <div
      v-if="loading"
      :class="contentLoadingClass">
      <div>
        <p class="bui-list-item-content">
          <byted-loading-icon/>
          <span>
            {{ _lodingTip }}
          </span>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import emitter from 'mixins/emitter';
import locale from 'mixins/locale';
import clickoutside from 'directives/clickoutside';
import {CSS_PREFIX, LOCALE_PREFIX} from '../../base.js';
import expand from './expand.vue';
import loadingIcon from 'components/loading';
import BytedIcon from 'components/icon';

export default {
  name: `list-item`,
  directives: {clickoutside},
  components: {
    expand,
    BytedIcon,
    [loadingIcon.iconName]: loadingIcon,
  },
  mixins: [emitter, locale],
  props: {
    dataSource: {
      type: Array,
      default: () => [],
    },
    loading: {
      type: Boolean,
      default: false,
    },
    props: {
      type: Object,
      default: () => {
        return null;
      },
    },
    infinite: {
      type: Boolean,
      default: false,
    },
    maxHeight: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      defaultProps: {
        itemKey: 'text',
        valueKey: 'value',
      },
      currentFocusIndex: null,
    };
  },
  computed: {
    _emptyTip() {
      return this.t(`${LOCALE_PREFIX}.auto-complete.emptyTip`);
    },
    _lodingTip() {
      return this.t(`${LOCALE_PREFIX}.auto-complete.loadingTip`);
    },
    infiniteLoadingTip() {
      return this.t(`${LOCALE_PREFIX}.auto-complete.infiniteLoadingTip`);
    },
    baseClass() {
      return `${CSS_PREFIX}auto-complete`;
    },
    contentClass() {
      return `${CSS_PREFIX}list-item`;
    },
    contentItemClass() {
      return `${this.contentClass}__item`;
    },
    contentEmptyClass() {
      return `${this.contentClass}__empty`;
    },
    contentLoadingClass() {
      return `${this.contentClass}__loading`;
    },
    displayEmpty() {
      return this.dataSource.length === 0 && !this.loading;
    },
  },
  watch: {
    props(val) {
      this.defaultProps = Object.assign({}, this.defaultProps, val);
    },
  },
  methods: {
    handleSelect(item) {
      const key = item[this.defaultProps.valueKey] || item;
      this.$emit('input', key);
      this.$emit('select', key, item);
    },
    handleLoadmore() {
      this.$emit('loadmore');
    },
    resetLoading() {
      this.$refs['scroller'] && this.$refs['scroller'].reset();
    },
    focus(direction) {
      if (this.currentFocusIndex === null) {
        this.currentFocusIndex = direction === 'down' ? -1 : 0;
      }

      const len = this.$refs.items.length;
      const index = direction === 'down'
        ? this.currentFocusIndex + 1
        : this.currentFocusIndex - 1;
      this.currentFocusIndex = index < 0 ? index % len + len : index % len;

      this.$refs.items[this.currentFocusIndex].focus();
    },
    resetFocusIndex() {
      this.currentFocusIndex = null;
    },
  },
};
</script>



