
import PopupManager from 'mixins/popup/popup-manager.js';
const Message = function(message, type, duration) {
  const constructorName = Message.opts.constructorName;
  const instance = new this[constructorName]().$mount();
  instance.message = message;
  instance.type = type;
  instance.duration = duration || 2000;
  if (!window.bui_message_container) {
    const div = document.createElement('div');
    div.id = 'bui_message_container';
    document.body.appendChild(div);
  }
  window.bui_message_container.style.zIndex = PopupManager.nextZIndex();
  window.bui_message_container.appendChild(instance.$el);

  instance.$on('remove', () => {
    this.$nextTick(() => {
      instance.display = false;
    });
  });

  this.$nextTick(() => {
    instance.display = true;
  });
  return instance;
};

export default Message;



