<template>
  <div
    v-clickoutside="clickoutsideHandle"
    :class="baseClass">

    <div
      ref="popperLabel"
      :class="{active: optionShow}"
      class="bui-color-picker-wrap"
      @click="labelClick">
      <slot>
        <color-option
          :value="value"
          :size="size"
          :disabled="true" />
        <div class="bui-color-picker-icon-wrap">
          <byted-icon
            :class="{'bui-color-picker-icon-open':optionShow}"
            name="angle-down"
            class="bui-color-picker-icon"/>
        </div>
      </slot>
    </div>

    <byted-popper
      v-show="optionShow"
      :reference="popperRef"
      :append-to-body="transfer"
      :popper-options="popperOptions"
      placement="bottom"
      class="bui-color-picker-popper">
      <div class="bui-saturation-wrap">
        <saturation
          ref="saturation"
          v-model="colors"
          @change="handleCustomColorChange"/>
      </div>
      <div class="bui-color-sliders-wrap">
        <div class="bui-hue-wrap">
          <hue
            v-model="colors"
            @change="handleCustomColorChange"/>
        </div>
        <div
          v-if="showAlpha"
          class="bui-alpha-wrap">
          <alpha
            v-model="colors"
            @change="handleCustomColorChange"/>
        </div>
      </div>
      <div class="bui-color-value-field">
        <div class="bui-color-value-field-hex">
          <byted-input
            v-model="currentValue"
            size="xs"
            class="bui-active-color-input"
            @input="handleActiveInputChange"/>
          <div class="bui-active-color-show">
            <color-option
              :value="currentValue"
              :disabled="true"
              size="md" />
          </div>
        </div>
        <!-- rgba -->
        <div class="bui-color-value-field-rgba">
          <byted-input-number
            v-for="key in currentLabel.split('')"
            :key="key"
            :value="getColorFieldValue(key)"
            :max="labelSize[key].max"
            :min="labelSize[key].min"
            size="xs"
            @input="(value)=>{handleColorFieldChange(value, key)}"/>
          <byted-input-number
            :value="colors.a * 100"
            :max="labelSize.a.max"
            :min="labelSize.a.min"
            :disabled="!showAlpha"
            size="xs"
            @input="handleColorAlphaChange"/>
        </div>
        <div class="bui-color-value-field-label">
          <byted-dropdown
            :transfer="true"
            placement="bottom-start">
            <div class="bui-color-value-field-label-wrap">
              <span
                v-for="key in currentLabel.split('')"
                :key="key"
                class="bui-color-value-field-label-name">
                {{ key.toUpperCase() }}
              </span>
              <div class="bui-color-value-field-label-icon">
                <byted-icon name="sort-asc"/>
              </div>
            </div>
            <byted-dropdown-menu
              slot="list"
              class="bui-color-picker-dropdown-menu">
              <byted-dropdown-item
                v-for="item in labelOptions"
                :key="item"
                @click="handleColorLabelClick(item)">{{ item.toUpperCase() }}</byted-dropdown-item>
            </byted-dropdown-menu>
          </byted-dropdown>
          <span class="bui-color-value-field-label-alpha">A</span>
        </div>

      </div>
      <div
        v-if="predefine.length"
        class="bui-color-option-predefines">
        <div class="bui-color-option-predefines-title">
          {{ _customColorsText }}
        </div>
        <div class="bui-color-options">
          <color-option
            v-for="(option, index) in predefine"
            :key="index"
            :value="option"
            :class="{active: option === currentValue}"
            @click="handleOptionClick"/>
        </div>
      </div>
      <div class="bui-color-picker-confirm-btn">
        <byted-button
          size="xs"
          @click="handleClear">{{ _clearText }}</byted-button>
        <byted-button
          type="primary"
          size="xs"
          @click="handleConfirm">{{ _okText }}</byted-button>
      </div>
    </byted-popper>
  </div>
</template>

<script>

import {CSS_PREFIX, LOCALE_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter.js';
import ColorOption from './color-option.vue';
import EditableInput from './common/EditableInput.vue';
import Saturation from './common/Saturation.vue';
import Hue from './common/Hue.vue';
import Alpha from './common/Alpha.vue';
import Checkboard from './common/Checkboard.vue';
import tinycolor from 'tinycolor2';
import {isOneOf} from 'utils/helper.js';
import {hasClass} from 'utils/dom.js';
import locale from 'mixins/locale';
import BytedIcon from 'components/icon';
import BytedButton from 'components/button';
import BytedPopper from 'components/popper';
import clickoutside from 'directives/clickoutside';
const defaultProps = {
  hex: '',
  hsl: {
    h: '',
    s: '',
    l: '',
    a: 1,
  },
  hsv: {
    h: '',
    s: '',
    v: '',
    a: 1,
  },
  rgb: {
    r: '',
    g: '',
    b: '',
    a: 1,
  },
  a: 1,
};
export default {
  name: `${CSS_PREFIX}color-picker`,
  components: {
    ColorOption,
    Saturation,
    Hue,
    Alpha,
    'ed-in': EditableInput,
    Checkboard,
    BytedIcon,
    BytedButton,
    BytedPopper,
  },
  directives: {clickoutside},
  mixins: [emitter, locale],
  props: {
    value: {
      type: String,
      required: true,
    },
    // 预定义颜色
    predefine: {
      type: Array,
      default() {
        return [];
      },
    },
    showAlpha: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    format: {
      type: String,
      default: 'hex',
      validator(value) {
        return isOneOf(value, [
          'hex',
          'rgba',
        ]);
      },
    },
    transfer: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, [
          'md',
          'xs',
          'sm',
          'lg',
          'xl',
        ]);
      },
    },
    popperOptions: {
      type: Object,
      default() {
        return {
          modifiers: {
            flip: {
              enabled: true,
            },
            hide: {
              enabled: false,
            },
            preventOverflow: {
              enabled: false,
            },
          },
        };
      },
    },
  },
  data() {
    return {
      optionShow: false,
      popperRef: null,
      colors: defaultProps,
      currentValue: '',
      currentLabel: 'rgb',
      labelOptions: ['rgb', 'hsl', 'hsv'],
      labelSize: {
        r: {min: 0, max: 255},
        g: {min: 0, max: 255},
        b: {min: 0, max: 255},
        h: {min: 0, max: 360},
        s: {min: 0, max: 100},
        l: {min: 0, max: 100},
        v: {min: 0, max: 100},
        a: {min: 0, max: 100},
      },
    };
  },
  computed: {
    baseClass: function() {
      // 默认样式 正方形
      const classArr = [`${CSS_PREFIX}color-picker`, `bui-color-picker-default`];
      if (this.disabled) {
        classArr.push(`bui-color-picker-disabled`);
      }
      if (!this.value) {
        classArr.push(`bui-color-picker-none`);
      }
      if (this.$slots && this.$slots.default) {
        classArr.push(`bui-color-picker-custom`);
      } else {
        classArr.push(`bui-color-picker-${this.size}`);
      }
      return classArr;
    },
    _okText() {
      return this.t(`${LOCALE_PREFIX}.color-picker.okText`);
    },
    _clearText() {
      return this.t(`${LOCALE_PREFIX}.color-picker.clearText`);
    },
    _customColorsText() {
      return this.t(`${LOCALE_PREFIX}.color-picker.customColor`);
    },

  },

  watch: {
    value: {
      handler(newVal) {
        if (newVal) {
          this.colors = this.colorChange(newVal);
        } else {
          this.colors = defaultProps;
        }
        this.currentValue = this.value;
      },
      immediate: true,
    },
    optionShow(newVal) {
      newVal ?
        this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.update') :
        this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.destroy');
      if (!newVal) {
        this.value ? this.handleCustomColorChange(this.value) : this.colors = defaultProps;
      }
    },
  },
  mounted() {
    // ref本身是作为渲染结果被创建,需要在mounted进行赋值
    this.popperRef = this.$refs.popperLabel;
  },
  methods: {
    getFormatColor() {
      switch (this.format) {
        case 'hex':
          return this.colors.hex;
          break;
        case 'rgba':
          const rgb = this.colors.rgb;
          if (rgb.r === '' && rgb.g === '' && rgb.b === '') {
            return '';
          }
          return 'rgba(' + [rgb.r, rgb.g, rgb.b, this.colors.a].join(',') + ')';
          break;
        default:
          return '';
          break;
      }
    },
    changeOptionShow(open) {
      this.optionShow = open;
    },
    clickoutsideHandle(event) {
      if (!this.optionShow) {
        return;
      }
      const target = event.target;
      let parent = target.parentNode;

      while (parent && !hasClass(parent, 'bui-color-picker-popper') && !hasClass(target, 'bui-color-picker-popper')) {
        parent = parent.parentNode;
      }
      if (!parent && !hasClass(target, 'bui-color-picker-popper')) {
        this.changeOptionShow(false);
      }
    },
    labelClick() {
      if (this.disabled) {
        return;
      }
      this.changeOptionShow(!this.optionShow);
    },
    handleOptionClick(selectedValue) {
      this.handleCustomColorChange(selectedValue);
    },
    handleCustomColorChange(data) {
      this.colors = this.colorChange(data);
      this.currentValue = this.getFormatColor();
      this.currentLabel = this.currentLabel;
    },
    isValidHex(hex) {
      return tinycolor(hex).isValid();
    },
    handleActiveInputChange(value) {
      if (tinycolor(value).isValid()) {
        this.colors = this.colorChange(value);
      }
    },
    getColorFieldValue(key) {
      const value = this.colors[this.currentLabel][key];
      if (['s', 'l', 'v'].indexOf(key) > -1) {
        return parseInt((value || 0) * 100);
      }
      return parseInt(value || 0);
    },
    handleColorFieldChange(value, key) {
      if (['s', 'l', 'v'].indexOf(key) > -1) {
        value = value / 100;
      }
      const temp = this.colors[this.currentLabel];
      temp[key] = value;
      // 格式化取值，有空值的时候会无效
      Object.keys(temp).forEach((k)=> {
        temp[k] = temp[k] ? temp[k] : 0;
      });
      this.colors = this.colorChange({
        ...temp,
        a: this.colors.a,
        source: this.currentLabel,
      });
      this.currentValue = this.getFormatColor();
    },
    handleColorAlphaChange(value) {
      this.colors.a = value / 100;
    },
    handleColorLabelClick(label) {
      this.currentLabel = label;
    },
    colorChange(data, oldHue) {
      const alpha = data && data.a;
      let color = null;
      if (data && data.hsl) {
        color = tinycolor(data.hsl);
      } else if (data && data.hex && data.hex.length > 0) {
        color = tinycolor(data.hex);
      } else {
        color = tinycolor(data);
      }

      if (color && (color._a === undefined || color._a === null)) {
        color.setAlpha(alpha || 1);
      }

      const hsl = color.toHsl();
      const hsv = color.toHsv();

      if (hsl.s === 0) {
        hsv.h = hsl.h = data.h || (data.hsl && data.hsl.h) || oldHue || 0;
      }
      return {
        hsl: hsl,
        hex: color.toHexString().toUpperCase(),
        rgb: color.toRgb(),
        hsv: hsv,
        oldHue: data.h || oldHue || hsl.h,
        source: data.source,
        a: data.a || color.getAlpha(),
      };
    },
    handleConfirm() {
      const data = this.getFormatColor();
      this.$emit('input', data);
      this.$emit('change', data);
      this.changeOptionShow(false);
    },
    handleClear() {
      this.colors = defaultProps;
      this.currentValue = '';
      this.$emit('input', '');
      this.$emit('change', '');
      this.changeOptionShow(false);
    },
  },
};
</script>



