<template>
  <div class="bui-alpha">
    <div class="bui-alpha-checkboard-wrap">
      <checkboard/>
    </div>
    <div
      :style="{background: gradientColor}"
      class="bui-alpha-gradient"/>
    <div
      ref="container"
      class="bui-alpha-container"
      @mousedown="handleMouseDown"
      @touchmove="handleChange"
      @touchstart="handleChange">
      <div
        :style="{left: colors.a * 100 + '%'}"
        class="bui-alpha-pointer">
        <div class="bui-alpha-picker"/>
      </div>
    </div>
  </div>
</template>

<script>
import checkboard from './Checkboard.vue';

export default {
  name: 'Alpha',
  components: {
    checkboard,
  },
  props: {
    value: {
      type: Object,
      default() {
        return {};
      },
    },
    onChange: {
      type: Function,
      default() {
        return ()=>{};
      },
    },
  },
  computed: {
    colors() {
      return this.value;
    },
    gradientColor() {
      const rgb = this.colors.rgb;
      let rgbStr = [rgb.r, rgb.g, rgb.b].join(',');
      if (rgb.r === '' && rgb.g === '' && rgb.b === '') {
        rgbStr = [0, 0, 0].join(',');
      }

      return 'linear-gradient(to right, rgba(' + rgbStr + ', 0) 0%, rgba(' + rgbStr + ', 1) 100%)';
    },
  },
  methods: {
    handleChange(e, skip) {
      !skip && e.preventDefault();
      const container = this.$refs.container;
      const containerWidth = container.clientWidth;

      const xOffset = container.getBoundingClientRect().left + window.pageXOffset;
      const pageX = e.pageX || (e.touches ? e.touches[0].pageX : 0);
      const left = pageX - xOffset;

      let a;
      if (left < 0) {
        a = 0;
      } else if (left > containerWidth) {
        a = 1;
      } else {
        a = Math.round(left * 100 / containerWidth) / 100;
      }

      if (this.colors.a !== a) {
        this.$emit('change', {
          h: this.colors.hsl.h,
          s: this.colors.hsl.s,
          l: this.colors.hsl.l,
          a: a,
          source: 'rgba',
        });
      }
    },
    handleMouseDown(e) {
      this.handleChange(e, true);
      window.addEventListener('mousemove', this.handleChange);
      window.addEventListener('mouseup', this.handleMouseUp);
    },
    handleMouseUp() {
      this.unbindEventListeners();
    },
    unbindEventListeners() {
      window.removeEventListener('mousemove', this.handleChange);
      window.removeEventListener('mouseup', this.handleMouseUp);
    },
  },
};

</script>




