import imgPreviewConstructor from './src/img-preview.vue';
import imgPreview from './src/img-preview.js';

imgPreview.install = function(Vue) {
  Vue.prototype.$imgPreview = imgPreview;
  Vue.prototype.$imgPreviewConstructor = Vue.extend(imgPreviewConstructor);
};

export default imgPreview;



