<template>
  <div
    :class="baseClass"
    @mouseenter="hovering = true"
    @mouseleave="hovering = false">
    <div
      v-if="hasPrepend"
      ref="prepend"
      :class="inputGroupAddonClass.prepend">
      <slot name="prepend"/>
    </div>
    <input
      ref="input"
      :type="type"
      :placeholder="placeholder"
      :disabled="disabled"
      :readonly="readonly"
      :value="value"
      :minlength="minlength"
      :maxlength="maxlength"
      :class="inputClasses"
      @input="handleInput"
      @click="handleClick"
      @blur="handleBlur"
      @focus="handleFocus"
      @keydown="handleKeydown"
      @keyup="handleKeyup"
      @change="handleInputChange">
    <div/>
    <span
      v-if="showClear || hasSuffix"
      class="bui-input-suffix bui-input-fix-icon">
      <template
        v-if="!showClear && hasSuffix">
        <slot name="suffix">
          <byted-icon
            :name="suffix"
            class="suffix-icon"/>
        </slot>
      </template>
      <byted-icon
        v-if="showClear"
        class="bui-input-clear-icon"
        name="close-circle"
        @click.stop="clearInput"/>
    </span>
    <div
      v-if="hasAppend"
      ref="append"
      :class="inputGroupAddonClass.append">
      <slot name="append"/>
    </div>
    <span
      v-if="hasPrefix"
      class="bui-input-prefix bui-input-fix-icon">
      <slot name="prefix">
        <byted-icon
          :name="prefix"
          class="prefix-icon"/>
      </slot>
    </span>
  </div>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';
import {isOneOf} from 'utils/helper.js';
import {setStyle} from 'utils/dom.js';
import BytedIcon from 'components/icon';

export default {
  name: `${CSS_PREFIX}input`,
  components: {
    BytedIcon,
  },
  mixins: [emitter],
  props: {
    value: {
      type: [String, Number],
      default: '',
    },
    placeholder: {
      type: String,
      default: '',
    },
    type: { // 原生 input 的type
      type: String,
      default: 'text',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    // 输入的最小长度
    minlength: {
      type: Number,
      default: 0,
    },
    // 输入的最大长度
    maxlength: {
      type: Number,
      default: Infinity,
    },
    clearable: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, ['md', 'small', 'xs', 'sm', 'lg', 'xl']);
      },
    },
    status: {
      type: String,
      default: '',
      validator(value) {
        return isOneOf(value, ['', 'error']);
      },
    },
    prefix: {
      type: String,
      default: '',
    },
    suffix: {
      type: String,
      default: '',
    },
    // 内部使用，暂时不对外,禁止 dispatch form-item.change
    validateEvent: {
      type: Boolean,
      default: true,
    },
  },

  data() {
    return {
      slotType: {
        prepend: '',
        append: '',
      },
      focused: false,
      hovering: false,
    };
  },
  computed: {
    baseClass: function() {
      const baseClass = `${CSS_PREFIX}input`;
      const classArr = [baseClass, `${baseClass}-${this.size}`];
      if (this.clearable) {
        classArr.push(`${baseClass}-suffix`);
      }
      if (this.hasPrepend || this.hasAppend) {
        classArr.push(`${baseClass}-group`);
      }
      if (this.hasPrepend) {
        classArr.push(`${baseClass}-prepend`);
      }
      if (this.hasAppend) {
        classArr.push(`${baseClass}-append`);
      }
      if (this.status) {
        classArr.push(`${baseClass}-${this.status}`);
      }
      return classArr;
    },
    inputClasses() {
      const baseClass = 'bui-input';
      const classArr = [baseClass];
      if (this.hasPrefix) {
        classArr.push(`${baseClass}-with-prefix`);
      }
      if (this.hasSuffix) {
        classArr.push(`${baseClass}-with-suffix`);
      }

      return classArr;
    },
    hasPrepend: function() {
      const slot = this._slotContents || this.$slots || {};
      return slot['prepend'] != null;
    },
    hasAppend: function() {
      const slot = this._slotContents || this.$slots || {};
      return slot['append'] != null;
    },
    inputGroupAddonClass: function() {
      return {
        prepend: this.addonClass('prepend'),
        append: this.addonClass('append'),
      };
    },
    showClear() {
      return (this.clearable
              && !this.disabled
              && this.value !== ''
              && (this.focused || this.hovering));
    },
    hasPrefix() {
      return this.prefix !== '' || this.$slots.prefix !== undefined;
    },
    hasSuffix() {
      return this.suffix !== '' || this.$slots.suffix !== undefined;
    },
  },
  mounted() {
    this.checkSlotType();
    this.updateIconOffset();
  },
  updated() {
    this.updateIconOffset();
  },
  methods: {
    checkSlotType() {
      this.slotType = {
        prepend: this.checkTag('prepend'),
        append: this.checkTag('append'),
      };
    },
    checkTag(slotName) {
      const slot = this.$slots[slotName];
      let tag = '';
      if (slot && slot[0] && slot[0].tag) {
        const originTag = slot[0].tag;
        if (originTag.indexOf('select')!==-1) {
          tag = 'select';
        }
        if (originTag.indexOf('button')!==-1) {
          tag = 'button';
        }
      }
      return tag;
    },
    addonClass(name) {
      const classArr = [];
      if (this.slotType[name] === 'button' || this.slotType[name] === 'select' ) {
        classArr.push('bui-input-group-btn');
      } else {
        classArr.push('bui-input-group-addon');
      }
      return classArr;
    },
    handleInput(e) {
      if (e && e.target) {
        this.emitOnInputChange(e.target.value, e);
      }
    },
    handleClick(e) {
      this.$emit('click', e);
    },
    handleBlur(e) {
      this.focused = false;
      this.$emit('blur', e);
      if (this.validateEvent) {
        this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.blur', [this.value]);
      }
    },
    handleFocus(e) {
      this.focused = true;
      this.$emit('focus', e);
    },
    handleKeydown(e) {
      this.$emit('keydown', e);
    },
    handleKeyup(e) {
      this.$emit('keyup', e);
    },
    focus() {
      this.$refs['input'].focus();
    },
    clearInput() {
      this.$emit('clear', '');
      this.emitOnInputChange('');
    },
    emitOnInputChange(newVal, e = null) {
      this.$emit('input', newVal, e);
      this.$emit('change', newVal, e);
      if (this.validateEvent) {
        this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [this.value]);
      }
    },
    handleInputChange(e) {
      // 原生change事件
      this.$emit('input-change', e);
    },
    calcIconOffset(place) {
      const elList = [].slice.call(this.$el.querySelectorAll(`.bui-input-${place}`) || []);
      if (!elList.length) return;
      let el = null;
      let i = 0;
      while (i < elList.length) {
        if (elList[i].parentNode === this.$el) {
          el = elList[i];
          break;
        }
        i++;
      }
      if (!el) return;
      const pendantMap = {
        suffix: 'append',
        prefix: 'prepend',
      };

      const pendant = pendantMap[place];
      if (this.$slots[pendant]) {
        const attr = place === 'suffix' ? 'right' : 'left';
        setStyle(el, attr, `${this.$refs[pendant].offsetWidth + 12}px`);
      } else {
        el.removeAttribute('style');
      }
    },
    updateIconOffset() {
      // 更新 prefix 及 suffix icon位置
      if (this.hasPrefix) {
        this.calcIconOffset('prefix');
      }
      if (this.hasSuffix || this.showClear) {
        this.calcIconOffset('suffix');
      }
    },
  },
};
</script>



