<template>
  <div class="bui-time-panel">
    <div
      v-if="isShowHeader"
      class="bui-time-panel-header">
      <slot name="header"/>
    </div>
    <div class="bui-time-panel-body">
      <time-column
        ref="hour"
        :value="hour"
        :options="hoursOptions"
        @select="handleHourSelect"
        @mouseenter="handleHourMouseEnter"
        @mouseleave="handleMouseLeave"/>
      <time-column
        ref="minute"
        :value="minute"
        :options="minutesOptions"
        @select="handleMinuteSelect"
        @mouseenter="handleMinuteMouseEnter"
        @mouseleave="handleMouseLeave"/>
      <time-column
        v-if="isShowSecond"
        ref="second"
        :value="second"
        :options="secondsOptions"
        @select="handleSecondSelect"
        @mouseenter="handleSecondMouseEnter"
        @mouseleave="handleMouseLeave"/>
      <time-column
        v-if="is12Hours"
        ref="ampm"
        :options="AMPMOptions"
        :value="AMPMMode"
        @select="handleAMPMSelect"
        @mouseenter="handleAMPMMouseEnter"
        @mouseleave="handleMouseLeave"/>
    </div>

  </div>
</template>
<script>
import TimeColumn from './time-column.vue';
import utils from './../mixins/utils';
const getTimeOptions = (start, end, step, disabledOptions) =>{
  const options = [];
  for (let i = start; i<=end; i += step) {
    options.push({
      label: `0${i}`.slice(-2),
      value: i,
      disabled: (disabledOptions || []).includes(i),
    });
  }
  return options;
};

export default {
  name: 'TimeSpinner',
  inject: ['buiPanel'],
  components: {
    TimeColumn,
  },
  mixins: [utils],
  props: {
    value: {
      type: [Object, String],
      default() {
        return null;
      },
    },
    // dayjs 实例
    viewDate: {
      type: [Object],
      default() {
        return null;
      },
    },
  },
  provide() {
    return {
      'buiTime': this,
    };
  },
  data() {
    return {
      AMPMOptions: [{label: 'AM', value: 0}, {label: 'PM', value: 1}],
      AMPMMode: -1,
    };
  },
  computed: {
    isShowHeader() {
      return this.$slots && this.$slots.header;
    },
    isShowSecond() {
      return (this.buiPanel.valueFormat || '').indexOf('ss') > -1;
    },
    is12Hours() {
      return (this.buiPanel.valueFormat || '').indexOf('A') !== -1
      || (this.buiPanel.valueFormat || '').indexOf('a') !== -1;
    },

    disabledHours() {
      return typeof this.buiPanel.disabledTime.disabledHours === 'function' ? this.buiPanel.disabledTime.disabledHours({current: this.value || this.viewDate}) : [];
    },
    disabledMinutes() {
      return typeof this.buiPanel.disabledTime.disabledMinutes === 'function' ? this.buiPanel.disabledTime.disabledMinutes({current: this.value || this.viewDate}) : [];
    },
    disabledSeconds() {
      return typeof this.buiPanel.disabledTime.disabledSeconds === 'function' ? this.buiPanel.disabledTime.disabledSeconds({current: this.value || this.viewDate}) : [];
    },
    step() {
      return 1;
    },
    hoursOptions() {
      const options = getTimeOptions(0, this.is12Hours ? 11 : 23, this.step, this.disabledHours);
      // 12小时制 00显示为12
      if (this.is12Hours) {
        options[0].label = 12;
      }
      return options;
    },
    minutesOptions() {
      return getTimeOptions(0, 59, this.step, this.disabledMinutes);
    },
    secondsOptions() {
      return getTimeOptions(0, 59, this.step, this.disabledSeconds);
    },

    hour() {
      if (!this.value) {
        return '';
      }
      const hour = this.getHour(this.value);
      return this.is12Hours ? hour % 12 : hour;
    },
    minute() {
      return this.value ? this.getMinute(this.value):'';
    },
    second() {
      return this.value ? this.getSecond(this.value):'';
    },
    timeHeaderText() {
      return this.value ? this.getFormat(this.value, this.isShowSecond ? 'HH:mm:ss' : 'HH:mm') : ' ';
    },
  },
  watch: {
    'buiPanel.panelShow'() {
      this.updateTimeColumnScrollTop();
    },
    value() {
      if (this.is12Hours && this.value) {
        const hour = this.getHour(this.value);
        this.AMPMMode = hour >= 12 ? 1 : 0;
      }
    },
  },
  methods: {
    handleHourSelect(value) {
      if (this.is12Hours && this.AMPMMode > 0) {
        value +=12;
      }
      this.buiPanel.handleTimeHourChange({value, _uid: this._uid});
    },
    handleMinuteSelect(value) {
      this.buiPanel.handleTimeMinuteChange({value, _uid: this._uid});
    },
    handleSecondSelect(value) {
      this.buiPanel.handleTimeSecondChange({value, _uid: this._uid});
    },
    handleAMPMSelect(value) {
      if (this.AMPMMode !== value) {
        this.AMPMMode = value;
        this.handleHourSelect(this.hour);
      }
    },
    updateTimeColumnScrollTop() {
      ['hour', 'minute', 'second', 'ampm'].forEach((key)=>{
        if (this.$refs[key]) {
          this.$refs[key].handleValueScrollTop();
        }
      });
    },
    handleHourMouseEnter(value) {
      if (this.is12Hours && this.AMPMMode > 0) {
        value +=12;
      }
      const date = this.value || this.viewDate;
      const currentDate = this.setHour(date, value);
      this.buiPanel.handleDateMouseEnter({currentDate, _uid: this._uid});
    },
    handleMinuteMouseEnter(value) {
      const date = this.value || this.viewDate;
      const currentDate = this.setMinute(date, value);
      this.buiPanel.handleDateMouseEnter({currentDate, _uid: this._uid});
    },
    handleSecondMouseEnter(value) {
      const date = this.value || this.viewDate;
      const currentDate = this.setSecond(date, value);
      this.buiPanel.handleDateMouseEnter({currentDate, _uid: this._uid});
    },
    handleAMPMMouseEnter(value) {
      const hour = value ? this.hour + 12 : this.hour;
      const date = this.value || this.viewDate;
      const currentDate = this.setHour(date, hour);
      this.buiPanel.handleDateMouseEnter({currentDate, _uid: this._uid});
    },
    handleMouseLeave() {
      this.buiPanel.handleDateMouseLeave({currentDate: null, _uid: this._uid});
    },
  },
};
</script>



