<template>
  <div
    v-clickoutside.capture="handleClickOutside"
    ref="popperRef"
    :class="baseClass"

  >
    <div
      :class="inputWrapperClass[0]"
      class="bui-range-picker-input-wrapper"
      @click="handleReferenceClick">
      <input
        ref="startInput"
        :placeholder="_placeholder[0]"
        :value="innerValueFormat[0]"
        :disabled="disabled"
        :readonly="!editable"
        class="bui-range-picker-input"
        type="text"
        @input="handleStartInputChange"
        @click="handleInputClick(0)">
    </div>
    <div
      class="bui-range-picker-seperator"
      @click="handleReferenceClick">{{ rangeSeparator }}</div>
    <div
      :class="inputWrapperClass[1]"
      class="bui-range-picker-input-wrapper"
      @click="handleReferenceClick">
      <input
        ref="endInput"
        :placeholder="_placeholder[1]"
        :value="innerValueFormat[1]"
        :disabled="disabled"
        :readonly="!editable"
        class="bui-range-picker-input"
        type="text"
        @input="handleEndInputChange"
        @click="handleInputClick(1)">
    </div>
    <div
      :class="{
        'bui-range-picker-suffix-has-clear': isClearShow
      }"
      class="bui-range-picker-suffix"
      @click="handleReferenceClick">
      <byted-icon
        class="bui-range-picker-icon"
        name="calendar"
      />
      <byted-icon
        v-if="isClearShow"
        class="bui-range-picker-clear"
        name="close-circle"
        @click.stop="handleClear"/>
    </div>

    <byted-popper
      v-show="panelShow"
      :reference="popperRef"
      :append-to-body="transfer"
      :popper-options="popperOptions"
      class="bui-picker-popper"
      @click.stop.prevent>
      <div class="bui-picker-popper-content">
        <div
          v-if="isShortcutsShow"
          class="bui-picker-shortcuts">
          <slot name="shortcuts">
            <shortcuts
              v-if="shortcuts.length"
              :value="shortcuts"
              @select="handleShortcutSelect"
              @visible-change="handlePickerPanelShow"/>
          </slot>
        </div>
        <component
          :is="panelName"
          :panel-show="panelShow"
          :type="type"
          :format="format"
          :value-format="format"
          :active-picker-input-index="activePickerInputIndex"
          :value="innerValue"
          :view-date="viewDate"
          :hover-value="hoverValue"
          :default-date="defaultDate"
          :disabled-date="disabledDate"
          :default-time="defaultTime"
          :disabled-time="disabledTime"
          :range="isRange"
          :date-render="dateRender"
          :offset="offset"
          @select="handlePickerInnerValueChange"
          @view-date-change="handlePickerViewDateChange"
          @panel-change="handlePanelChange"
          @hover-value-change="handleHoverValueChange"
        />
      </div>
      <div
        v-if="isPanelExtraFooterShow"
        class="bui-picker-popper-footer-extra">
        <slot name="panelExtraFooter"/>
      </div>
      <div
        v-if="isFooterBtnShow && panelName === PANEL_NAME_MAP[type]"
        class="bui-picker-popper-footer">
        <byted-button
          :disabled="isConfirmBtnDisabled"
          type="primary"
          size="xs"
          @click="handleConfirm">{{ _okText }}</byted-button>
      </div>
    </byted-popper>
  </div>

</template>
<script>
import PanelDate from './panels/panel-date.vue';
import PanelDateTime from './panels/panel-date-time.vue';
import PanelYear from './panels/panel-year.vue';
import PanelMonth from './panels/panel-month.vue';
import PanelTime from './panels/panel-time.vue';
import Shortcuts from './base/shortcus.vue';
import {CSS_PREFIX, LOCALE_PREFIX} from '../../base';
import picker from './mixins/picker';
import props from './mixins/props';
import utils from './mixins/utils';
import emitter from 'mixins/emitter';
import locale from 'mixins/locale';
import {PANEL_NAME_MAP} from './const';
import BytedIcon from 'components/icon';
import BytedPopper from 'components/popper';
import {isPlainObject} from 'utils/types';
export default {
  name: `${CSS_PREFIX}range-picker`,
  components: {
    PanelDate,
    PanelDateTime,
    PanelYear,
    PanelMonth,
    Shortcuts,
    BytedIcon,
    BytedPopper,
    PanelTime,
  },
  mixins: [emitter, locale, props, utils, picker],
  props: {
    value: {
      required: true,
      type: Array,
      default() {
        return [];
      },
    },
    placeholder: {
      type: Array,
      default() {
        return null;
      },
    },
    rangeSeparator: {
      type: String,
      default: `～`,
    },
    offset: {
      type: [String, Number],
      default: '',
    },
  },
  data() {
    return {
      hovering: false,
    };
  },
  computed: {
    baseClass() {
      const classArr = [`${CSS_PREFIX}range-picker`];
      if (this.disabled) {
        classArr.push(`bui-range-picker-disabled`);
      }
      if (this.panelShow && !this.disabled) {
        classArr.push(`bui-range-picker-active`);
      }
      classArr.push(`bui-range-picker-${this.size}`);
      return classArr;
    },
    _placeholder() {
      let placeholder = this.placeholder || this.t(`${LOCALE_PREFIX}.range-picker.placeholder`);
      if (isPlainObject(placeholder)) {
        placeholder = Object.keys(placeholder).map((key)=>placeholder[key]);
      }
      placeholder = placeholder.slice();
      if (this.offset && this.hoverValue[0] && this.hoverValue[1]) {
        placeholder[0] = this.getFormat(this.hoverValue[0], this.format);
        placeholder[1] = this.getFormat(this.hoverValue[1], this.format);
      } else if (this.hoverValue[this.activePickerInputIndex]) {
        placeholder[this.activePickerInputIndex] = this.getFormat(this.hoverValue[this.activePickerInputIndex], this.format);
      }
      return placeholder;
    },
    isClearShow() {
      return !this.disabled && this.clearable && !!(this.innerValue[0] || this.innerValue[1]);
    },
    isRange() {
      return this.panelName === PANEL_NAME_MAP[this.type];
    },
    innerValueFormat() {
      const text = this.innerValue.map((date) => date && this.getFormat(date, this.format));
      if (this.offset && this.hoverValue[0] && this.hoverValue[1]) {
        text[0] = text[1] = '';
      } else if (this.hoverValue[this.activePickerInputIndex]) {
        text[this.activePickerInputIndex] = '';
      }
      return text;
    },
    inputWrapperClass() {
      return [{
        'bui-range-picker-input-wrapper-active': !this.offset && this.panelShow && this.activePickerInputIndex === 0,
      }, {
        'bui-range-picker-input-wrapper-active': !this.offset && this.panelShow && this.activePickerInputIndex === 1,
      }];
    },
  },

  methods: {

    handleReferenceClick() {
      if (this.disabled) {
        return;
      }
      if (!this.panelShow) {
        this.setClickFromStart(!this.activePickerInputIndex);
      }
      this.handlePickerPanelShow(true);
    },
    handleInputClick(index) {
      if (this.disabled) {
        return;
      }
      this.setActivePickerInputIndex(index);
      this.setClickFromStart(!index);
    },
    handlePickerInnerValueChange(date, activePickerInputIndex=this.activePickerInputIndex) {
      if (this.isFooterBtnShow) {
        this.handlePickerInnerValueChangeForNeedConfirm(date, activePickerInputIndex);
        return;
      }
      let innerValue = [...this.innerValue];
      // 如果有增加offset，基于当前选中值，得出区间值
      if (this.offset && this.type==='date') {
        innerValue = this.getOffsetRangeDate(date, this);
        this.handleEmitChange(innerValue.map((date)=>this.getFormat(date, this.valueFormat)));
        this.handlePickerPanelShow(false);
        return;
      }

      innerValue[activePickerInputIndex] = date;

      this.checkInnerValueEmitChange(innerValue);
    },


    handlePickerInnerValueChangeForNeedConfirm(date, activePickerInputIndex) {
      if (activePickerInputIndex !== undefined) {
        this.setActivePickerInputIndex(activePickerInputIndex);
      }
      const innerValue = [...this.innerValue];
      // 处理开始日期的选择
      if (this.activePickerInputIndex === 0) {
        innerValue[0] = date;
        this.setPickerInnerValue(innerValue);
        return;
      }
      // 处理结束日期的选择
      if (!this.innerValue.length) {
        // 首次选择结束日期
        this.setPickerInnerValue(['', date]);
        return;
      }
      innerValue[1] = date;
      this.setPickerInnerValue(innerValue);
    },
    handleConfirm() {
      //
      this.checkInnerValueEmitChange(this.innerValue);
    },
    checkInnerValueEmitChange(innerValue) {
      const start = innerValue[0] || '';
      const end = innerValue[1] || '';
      // 如果是time-picker，判断当前startvalue 、endvalue都是有效的、且startvalue小于等于endvalue就emitchange
      if (this.isTimePicker
        && start && this.isValid(start)
        && end && this.isValid(end)
        && !this.isAfter(start, end)) {
        this.handleEmitChange(innerValue.map((date)=>this.getFormat(date, this.valueFormat)));
        this.handlePickerPanelShow(false);
        return;
      }

      // 处理开始日期的选择
      if (this.activePickerInputIndex === 0) {
        // 结束日期 存在,且选择的开始日期 小于 结束日期
        if (end && this.isValid(end)) {
          // 开始和结束时间是有效的
          if (!this.isAfter(start, end)) {
            this.handleEmitChange(innerValue.map((date)=>this.getFormat(date, this.valueFormat)));
            if (!this.clickFromStart) {
              this.handlePickerPanelShow(false);
              // 重置初始值
              this.setClickFromStart(true);
              return;
            }
          } else {
            innerValue = [start, start];
            // 选择的开始日期大于结束日期，把开始和结束日期都置为date，重新选择结束日期
            this.handleEmitChange(innerValue.map((date)=>this.getFormat(date, this.valueFormat)));
            // 重置点击的起点为开始时间
            this.setClickFromStart(true);
          }
        }
        // 切换为结束日期面板选择
        this.setActivePickerInputIndex(1);
        this.$refs.endInput.focus();
        this.setPickerInnerValue(innerValue);
        return;
      }

      if (start && this.isValid(start)) {
        if (!this.isBefore(end, start)) {
          this.clickFromStart && this.handlePickerPanelShow(false);
          this.handleEmitChange(innerValue.map((date)=>this.getFormat(date, this.valueFormat)));
          this.setActivePickerInputIndex(0);
          this.setPickerInnerValue(innerValue);
          return;
        } else {
          // 选择的结束日期小于开始日期，把开始和结束日期都置为date，重新选择结束日期
          innerValue = [end, end];
          this.setClickFromStart(false);
          this.handleEmitChange(innerValue.map((date)=>this.getFormat(date, this.valueFormat)));
        }
      }
      this.setActivePickerInputIndex(0);
      this.setPickerInnerValue(innerValue);
      this.$refs.startInput.focus();
    },
    // 清空
    handleClear() {
      const newValue = [];
      this.setPickerInnerValue(newValue);
      this.handleEmitChange(newValue);
      this.$emit('clear', newValue);
      this.handlePickerPanelShow(false);
    },
    handleMouseEnter() {
      if (this.disabled || !this.clearable) {
        return;
      }
      this.hovering = true;
    },
    handleMouseLeave() {
      if (this.disabled || !this.clearable) {
        return;
      }
      this.hovering = false;
    },
    handleStartInputChange(evt) {
      const target = evt.target || null;
      const value = target.value;
      const newDate = this.parse(value, this.format, true);
      const disabled = this.disabledDate ? this.disabledDate(newDate) : false;
      if (this.isValid(newDate) && !this.isSame(newDate, this.innerValue[0]||null) && !disabled) {
        if (this.offset) {
          const innerValue = this.getOffsetRangeDate(newDate, this);
          this.handleEmitChange(innerValue.map((date)=>this.getFormat(date, this.valueFormat)));
          return;
        }
        const innerValue = [...this.innerValue];
        innerValue[0] = newDate;
        this.setPickerInnerValue(innerValue);
        this.setPickerViewDate(newDate);
        const endValue = innerValue[1];
        // range情况下不显示确认按钮时，两个值都是有效的触发事件
        if (!this.isFooterBtnShow && endValue && this.isValid(endValue) && !this.isAfter(newDate, endValue)) {
          this.handleEmitChange(innerValue.map((date)=>this.getFormat(date, this.valueFormat)));
        }
      }
    },
    handleEndInputChange(evt) {
      const target = evt.target || null;
      const value = target.value;
      const newDate = this.parse(value, this.format, true);
      const disabled = this.disabledDate ? this.disabledDate(newDate) : false;
      if (this.isValid(newDate) && !this.isSame(newDate, this.innerValue[1]||null) && !disabled) {
        if (this.offset) {
          const innerValue = this.getOffsetRangeDate(newDate, this);
          this.handleEmitChange(innerValue.map((date)=>this.getFormat(date, this.valueFormat)));
          return;
        }
        const innerValue = [...this.innerValue];
        innerValue[1] = newDate;
        this.setPickerInnerValue(innerValue);
        const startValue = innerValue[0];
        // range情况下不显示确认按钮时，两个值都是有效的触发事件
        if (!this.isFooterBtnShow && startValue && this.isValid(startValue) && !this.isAfter(startValue, newDate)) {
          this.handleEmitChange(innerValue.map((date)=>this.getFormat(date, this.valueFormat)));
        }
      }
    },

  },
};
</script>



