<template>
  <transition
    name="preview-fade"
    @after-enter="afterEnter"
    @after-leave="afterLeave">
    <div
      v-transfer-dom
      v-if="show"
      :class="baseClass"
      :style="`zIndex:${zIndex}`"
      data-transfer="true"
      class="modal"
      @click="maskClick"
    >
      <div
        class="bui-modal-dialog modal-dialog">
        <div
          ref="imgContainer"
          class="bui-preview-content"
          @mousemove="handleMousemove"
          @mouseleave="handleMouseleave">
          <div
            :style="imgContainerStyle"
            class="bui-preview-img-container"
            @click.stop>
            <img
              v-show="imgShow"
              ref="img"
              :src="curImgUrl"
              :style="imgStyle"
              class="bui-preview-img"
              @mousewheel="imgMousewheel"
              @mousedown="imgMousedown"
              @load="imgLoad">
          </div>
          <!-- 关闭按钮 -->
          <div
            v-show="btnShow.close"
            class="bui-preview-close-btn"
            @click="handleClose">
            <byted-icon
              name="close"
              width="24px"
              color="#fff"
              height="24px"/>
          </div>
          <!-- /关闭按钮 -->
          <!-- 前一个 -->
          <div
            v-if="hasPrevImg"
            v-show="btnShow.prveArrow"
            class="bui-preview-prev-btn"
            @click.stop="changeImg(-1)">
            <byted-icon
              name="angle-down"
              width="24px"
              height="24px"
              color="#fff"/>
          </div>
          <!-- /前一个 -->
          <!-- 后一个 -->
          <div
            v-if="hasNextImg"
            v-show="btnShow.nextArrow"
            class="bui-preview-next-btn"
            @click.stop="changeImg(1)">
            <byted-icon
              name="angle-down"
              width="24px"
              height="24px"
              color="#fff"/>
          </div>
          <!-- /后一个 -->
          <!-- 操作区域 -->
          <div
            v-show="btnShow.tool"
            class="bui-preview-tool-box"
            @mouseenter="cancelTimeout"
            @click.stop
          >
            <byted-icon
              v-for="(item,index) in toolbox"
              :key="index"
              :name="item"
              class="bui-preview-tool"
              width="24px"
              height="24px"
              color="#fff"
              @click="toolClick(item)"/>
          </div>
          <!-- /操作区域 -->
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import PopupManager from 'mixins/popup/popup-manager.js';
import {on, off} from 'utils/dom.js';
import emitter from 'mixins/emitter';
import BytedIcon from 'components/icon';
import TransferDom from 'directives/transfer-dom';
const boxWidth = 820;
const boxHeight = 494;
// 交互参照doc图片预览交互，弹框出现按钮停留几秒
export default {
  name: `${CSS_PREFIX}img-preview`,
  directives: {TransferDom},
  components: {
    BytedIcon,
  },
  mixins: [emitter],
  props: {
    options: {
      type: Object,
      default() {
        return {
          images: [],
        };
      },
    },
  },
  data() {
    return {
      show: false,
      curPosition: 0,
      scale: 1,
      rotate: 0,
      naturalWidth: 0,
      naturalHeight: 0,
      top: 0,
      left: 0,
      imgShow: false,
      dragging: false,
      fullScreenMode: true,
      isStart: true,
      btnShow: {
        close: true,
        tool: true,
        prveArrow: true,
        nextArrow: true,
      },
      mousePos: {
        x: 0,
        y: 0,
      },
      // 相对于容器中心的偏移量
      imgOffsetPos: {
        x: 0,
        y: 0,
      },
      toolbox: [
        'zoom-out',
        'zoom-in',
        'rotate',
        'full-screen',
      ],
      zIndex: 0,
    };
  },
  computed: {
    baseClass() {
      const classArr = [`${CSS_PREFIX}img-preview`];
      if (this.fullScreenMode) {
        classArr.push('bui-preview-fullscreen');
      }
      return classArr;
    },
    curImgUrl() {
      const curImgUrl = this.options.images[this.curPosition];
      return curImgUrl;
    },
    hasPrevImg() {
      return this.options.images[this.curPosition-1] || '';
    },
    hasNextImg() {
      return this.options.images[this.curPosition+1] || '';
    },
    // 是否按高度填充
    isHeigthFill() {
      const ratio = boxWidth/boxHeight;
      let isHeigth = true;
      if (this.naturalWidth/this.naturalHeight > ratio) {
        isHeigth = false;
      }
      return isHeigth;
    },
    imgStyle() {
      return {
        height: this.isHeigthFill ? `${boxHeight}px` : 'auto',
        width: !this.isHeigthFill ? `${boxWidth}px` : 'auto',

      };
    },
    imgContainerStyle() {
      return {
        transform: `rotate(${this.rotate}deg) scale(${this.scale}) translate3d(${this.left / this.scale}px, ${this.top / this.scale}px, 0)`,
      };
    },
  },
  watch: {
    show: function() {
      if (this.show) {
        on(document, 'keyup', this.escKeyup);
      } else {
        off(document, 'keyup', this.escKeyup);
      }
    },
    curPosition: function() {
      this.imgShow = false;
    },
  },

  mounted() {
    this.show = true;
    this.$nextTick(() => {
      this.zIndex = PopupManager.nextZIndex();
    });
    this.curPosition = this.options.startPosition;
    this.timerId = setTimeout(() => {
      this.isStart = false;
      this.btnShow = {
        close: false,
        tool: false,
        prveArrow: false,
        nextArrow: false,
      };
    }, 2000);
  },
  methods: {
    cancelTimeout() {
      if (!this.timerId) return;
      window.clearTimeout(this.timerId);
      this.timerId = null;
    },
    changeImg(step) {
      this.curPosition = this.curPosition + step;
    },
    imgMousewheel(e) {
      e.preventDefault();
      const wheel = e.wheelDelta;
      const delta = Math.max(-1, Math.min(1, wheel) );
      if (delta < 0) {
        this.zoomImg(-0.05);
      } else {
        this.zoomImg(0.05);
      }
    },
    handleMousemove(e) {
      if (this.isStart) return;
      if (this.dragging) {
        this.clearBtnShow();
        return;
      }
      if (!this.$refs['imgContainer']) return;
      const rect = this.$refs['imgContainer'].getBoundingClientRect();
      const curX = e.clientX;
      const curY = e.clientY;

      // close
      if (curY - rect.y < 130) {
        this.btnShow.close = true;
      } else {
        this.btnShow.close = false;
      }
      // prev-arrow
      if (curX - rect.x < 130 && curY - rect.top > 130 && rect.bottom - curY > 130) {
        this.btnShow.prveArrow = true;
      } else {
        this.btnShow.prveArrow = false;
      }
      // next-arrow
      if (rect.right - curX < 130 && curY - rect.top > 130 && rect.bottom - curY > 130) {
        this.btnShow.nextArrow = true;
      } else {
        this.btnShow.nextArrow = false;
      }
      // tool
      if (curY - rect.y > 480) {
        this.cancelTimeout();
        this.btnShow.tool = true;
      } else {
        this.btnShow.tool = false;
      }
    },
    handleMouseleave() {
      if (this.isStart) return;
      this.clearBtnShow();
    },
    clearBtnShow() {
      if (this.isStart) return;
      Object.keys(this.btnShow).forEach((k, i)=>this.btnShow[k]=false);
    },
    setImgPos() {
      this.top = this.imgOffsetPos.y;
      this.left = this.imgOffsetPos.x;
    },
    setMousePos(e) {
      this.mousePos.x = e.clientX;
      this.mousePos.y = e.clientY;
    },
    imgMousedown(e) {
      e.preventDefault();
      if (this.disabled) {
        return false;
      }
      this.onDragStart(e);
      on(window, 'mousemove', this.onDragging);
      on(window, 'mouseup', this.onDragEnd);
    },
    onDragStart(e) {
      this.dragging = true;
      this.setMousePos(e);
    },
    onDragging(e) {
      if (!this.dragging) return;
      this.imgOffsetPos.x = e.clientX - this.mousePos.x + this.imgOffsetPos.x;
      this.imgOffsetPos.y = e.clientY - this.mousePos.y + this.imgOffsetPos.y;
      this.setMousePos(e);
      this.setImgPos();
    },
    onDragEnd(e) {
      this.dragging = false;
      off(window, 'mousemove', this.onDragging);
      off(window, 'mouseup', this.onDragEnd);
    },
    handleClose() {
      this.show = false;
    },
    afterEnter() {
      this.$emit('shown');
    },
    afterLeave() {
      this.$emit('hidden');
    },
    // 图片加载完成后
    imgLoad(e) {
      const target = e.target;
      this.naturalWidth = target.naturalWidth;
      this.naturalHeight = target.naturalHeight;
      this.resetImg();
      this.imgShow = true; // 防抖动,图片加载成功后才显示
    },
    resetImg() {
      this.rotate = 0;
      this.scale = 1;
      this.imgOffsetPos.x = 0;
      this.imgOffsetPos.y = 0;
      this.setImgPos();
    },
    toolClick(toolName) {
      const handleFun = {
        'zoom-out': ()=> this.zoomImg(-0.1),
        'zoom-in': ()=> this.zoomImg(0.1),
        'rotate': ()=> this.rotateImg(90),
        'origin-screen': ()=> this.originScreen(),
        'full-screen': ()=> this.fullScreen(),
        'download': ()=> this.download(),
      };
      handleFun[toolName]();
    },
    zoomImg(step) {
      this.scale = this.scale + step;
      if (this.scale <= 0.1) this.scale = 0.1;
      if (this.scale >= 5) this.scale = 5;
    },
    rotateImg(step) {
      this.rotate = this.rotate + step;
    },
    originScreen() {
      this.toolbox = [
        'zoom-out',
        'zoom-in',
        'rotate',
        'full-screen',
      ];
      this.resetImg();
    },
    fullScreen() {
      this.resetImg();
      const dw = document.documentElement.clientWidth;
      const dh = document.documentElement.clientHeight;
      const screen = this.isHeigthFill ? dh : dw;
      const box = this.isHeigthFill ? boxHeight : boxWidth;
      this.scale = screen/box;
      this.toolbox = [
        'zoom-out',
        'zoom-in',
        'rotate',
        'origin-screen',
      ];
      this.setImgPos();
    },
    download() {
      // 有跨域图片下载的情况
      const src = this.curImgUrl;
      const canvas = document.createElement('canvas');
      const img = document.createElement('img');
      img.onload = function(e) {
        canvas.width = img.width;
        canvas.height = img.height;
        const context = canvas.getContext('2d');
        context.drawImage(img, 0, 0, img.width, img.height);
        canvas.getContext('2d').drawImage(img, 0, 0, img.width, img.height);
        canvas.toBlob((blob)=>{
          const link = document.createElement('a');
          link.href = window.URL.createObjectURL(blob);
          link.download = src.split('/').pop();
          link.click();
        }, 'image/jpeg');
      };
      img.setAttribute('crossOrigin', 'Anonymous');
      img.src = src;
      this.img = null;
      this.canvas = null;
    },
    escKeyup(e) {
      const keyCode = window.event ? e.keyCode : e.which;
      if (keyCode === 27) {
        this.options.escClosable&&this.handleClose();
      }
    },
    maskClick() {
      this.options.maskClosable && this.handleClose();
    },
  },
};
</script>



