<template>
  <div :class="baseClass">
    <slot/>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import {isOneOf} from 'utils/helper.js';
export default {
  name: `${CSS_PREFIX}steps`,
  props: {
    current: {
      type: Number,
      default() {
        return 1;
      },
    },
    status: {
      type: String,
      default: 'inprogress',
      validator(value) {
        return isOneOf(value, ['error', 'finished', 'waiting', 'inprogress']);
      },
    },
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, ['sm', 'md', 'lg']);
      },
    },
    direction: {
      type: String,
      default: 'horizontal',
      validator(value) {
        return isOneOf(value, ['horizontal', 'vertical']);
      },
    },
    lineStyle: {
      type: String,
      default: 'solid',
      validator(value) {
        return isOneOf(value, ['solid', 'dotted']);
      },
    },
    onChange: {
      type: Function,
      default: undefined,
      validator(value) {
        return value instanceof Function;
      },
    },
  },
  provide() {
    return {
      steps: this,
    };
  },
  computed: {
    baseClass: function() {
      return [`${CSS_PREFIX}steps`, `${CSS_PREFIX}steps-${this.size}`, `${CSS_PREFIX}steps-${this.direction}`];
    },
  },
  watch: {
    current(newVal) {
      this.updateChildProps();
    },
    status(newVal) {
      this.updateChildProps(newVal);
    },
    changeable() {
      this.updateChildProps();
    },
  },
  mounted() {
    this.updateChildProps();
    this.$on('append', this.updateChildProps);
    this.$on('remove', this.updateChildProps);
  },
  methods: {
    updateChildProps(status) {
      const vm = this;
      this.$children.forEach((item, index) => {
        item.currentIndex = index + 1;
        item.lineStyle = vm.lineStyle;
        item.stepCount = vm.$children.length;
        if (item.currentIndex === vm.current) {
          item.currentStatus = item.status || status ||
            (item.currentStatus === 'finished' ? 'finished' : 'inprogress');
        } else if (item.currentIndex < vm.current) {
          item.currentStatus = item.status || 'finished';
        } else {
          item.currentStatus = item.status ||
            (item.inProgress ? 'inprogress' : '') ||
            'waiting';
        }
      });
    },
  },
};
</script>



