<template>
  <div :class="baseClass">
    <byted-icon
      v-if="showIcon"
      :name="iconName[type]"
      class="bui-alert-icon"/>
    <div class="content-wrapper">
      <slot/>
    </div>
    <div
      v-if="closable && !plain"
      class="bui-alert-right-close"
      @click="hancleClose">
      <div
        v-if="hasRightCloseSlot"
        class="bui-alert-right-close-slot">
        <slot name="close"/>
      </div>
      <byted-icon
        v-else
        name="close"
        class="bui-alert-close"/>
    </div>
  </div>
</template>
<script>
import {isOneOf} from 'utils/helper.js';
import BytedIcon from 'components/icon';
import {CSS_PREFIX} from '../../base.js';

export default {
  name: `${CSS_PREFIX}alert`,
  components: {
    BytedIcon,
  },
  props: {
    type: {
      type: String,
      default: 'info',
      validator(value) {
        return isOneOf(value, ['info', 'success', 'warning', 'danger']);
      },
    },
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, ['md', 'sm', 'lg']);
      },
    },
    closable: {
      type: Boolean,
      default: false,
    },
    showIcon: {
      type: Boolean,
      default: true,
    },
    banner: {
      type: Boolean,
      default: false,
    },
    plain: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      iconName: {
        info: 'info-circle',
        warning: 'exclamation-circle',
        danger: 'close-circle',
        success: 'check-circle',
      },
    };
  },
  computed: {
    baseClass() {
      const clsArr = [`${CSS_PREFIX}alert`, `${CSS_PREFIX}alert-${this.type}`, `${CSS_PREFIX}alert-${this.size}`];
      if (this.showIcon) {
        clsArr.push(`${CSS_PREFIX}alert-show-icon`);
      }
      if (this.plain) {
        clsArr.push(`${CSS_PREFIX}alert-plain`);
      } else {
        // 无边框模式不支持 关闭 & banner样式
        if (this.closable) {
          clsArr.push(`${CSS_PREFIX}alert-closable`);
        }
        if (this.banner && !this.plain) {
          clsArr.push(`${CSS_PREFIX}alert-banner`);
        }
      }
      return clsArr;
    },
    hasRightCloseSlot() {
      return (this.$slots.close !== undefined);
    },
  },
  methods: {
    hancleClose() {
      this.$emit('close');
    },
  },
};
</script>



