import dayjs from 'dayjs';
// 获取当前语言的星期
import weekday from 'dayjs/plugin/weekday';
// 日期是年中第几周
import weekOfYear from 'dayjs/plugin/weekOfYear';
// 基于当前语言的按周计算的年份(gggg)
import weekYear from 'dayjs/plugin/weekYear';
// 格式扩展
import advancedFormat from 'dayjs/plugin/advancedFormat';
import customParseFormat from 'dayjs/plugin/customParseFormat';

dayjs.extend(weekday);
dayjs.extend(weekOfYear);
dayjs.extend(weekYear);
dayjs.extend(advancedFormat);
dayjs.extend(customParseFormat);

export default {
  methods: {
    getValue(value, index) {
      return value && value.length ? value[index] : null;
    },
    parse(value, format, strict) {
      const innerLocale = 'en';
      return dayjs(value, format, strict).locale(innerLocale); // strict parsing
    },
    // return dayjs obj
    dayjs(value, locale) {
      // 暂时没有使用dayjs的国际化功能
      const innerLocale = 'en';
      return dayjs(value).locale(innerLocale);
    },
    // 格式化
    getFormat(date, format, locale) {
      // 暂时没有使用dayjs的国际化功能
      const innerLocale = 'en';
      return date.locale(innerLocale).format(format);
    },
    getNow: () => dayjs(),
    getWeekDay: (date) => date.weekday(),

    getYear: (date) => date.year(),
    getMonth: (date) => date.month(),
    getDate: (date) => date.date(),
    getHour: (date) => date.hour(),
    getMinute: (date) => date.minute(),
    getSecond: (date) => date.second(),
    // 当月天数
    getDaysInMonth: (date) => date.daysInMonth(),
    getWeek(date, locale) {
      const innerLocale = 'en';
      return date.locale(innerLocale).week();
    },
    // set
    addYear: (date, diff) => date.add(diff, 'year'),
    addMonth: (date, diff) => date.add(diff, 'month'),
    addDate: (date, diff) => date.add(diff, 'day'),
    setYear: (date, year) => date.year(year),
    setMonth: (date, month) => date.month(month),
    setDate: (date, num) => date.date(num),
    setHour: (date, hour) => date.hour(hour),
    setMinute: (date, minute) => date.minute(minute),
    setSecond: (date, second) => date.second(second),
    // Compare
    isValid: (date) => date.isValid(),
    isSame: (date1, date2, unit) => date1.isSame(date2, unit),
    isSameYear(date1, date2) {
      return this.isSame(date1, date2, 'year');
    },
    isSameMonth(date1, date2) {
      return this.isSame(date1, date2, 'year') && this.isSame(date1, date2, 'month');
    },
    isSameDate(date1, date2) {
      return this.isSame(date1, date2, 'year') && this.isSame(date1, date2, 'month') && this.isSame(date1, date2, 'date');
    },
    isBefore: (date1, date2) => date1.isBefore(date2),
    isAfter: (date1, date2) => date1.isAfter(date2),

    isInRange(date, rangeStart, rangeEnd) {
      return !this.isSameDate(date, rangeStart) &&
        !this.isSameDate(date, rangeEnd) &&
        date.isAfter(rangeStart) &&
        date.isBefore(rangeEnd);
    },
    isRangeStart(date, rangeStart) {
      if (!date || !rangeStart || !this.isValid(date) || !this.isValid(rangeStart)) return false;
      return this.isSameDate(date, rangeStart);
    },
    isRangeEnd(date, rangeEnd) {
      if (!date || !rangeEnd || !this.isValid(date) || !this.isValid(rangeEnd)) return false;
      return this.isSameDate(date, rangeEnd);
    },
    // date panel header label format
    getFormatDateLabels(date, format, locale) {
      format = format || '[YYYY年] [M月]';
      const formatRegex = new RegExp(['YYYY', 'MMMM', 'MMM', 'MM', 'M'].join('|'), 'g');
      const componetsRegex = /(\[[^\]]+\])([^\[\]]+)(\[[^\]]+\])/;
      if (!format.match(componetsRegex)) {
        format = '[YYYY] [M]';
      }
      const components = format.match(componetsRegex).slice(1);
      const separator = components[1];
      const labels = [components[0], components[2]].map((component) => {
        const label = component.replace(/\[[^\]]+\]/, (str) => {
          return str.slice(1, -1).replace(formatRegex, (match) => this.getFormat(date, match));
        });
        return {
          label: label,
          type: component.indexOf('YY') != -1 ? 'year' : 'month',
        };
      });
      return {
        separator: separator,
        labels: labels,
      };
    },
    getOffsetRangeDate(date, context) {
      const {value, offset, disabledDate} = context;
      const step = offset < 0 ? -1 : 1;

      let count = 0;
      let start = date;
      let checkedDate = this.addDate(date, step);
      let end = date;
      let hasDisabled = disabledDate ? disabledDate(({current: checkedDate, value: value, type: 'date'})) : false;
      while (!hasDisabled && count < Math.abs(offset)) {
        end = checkedDate;
        checkedDate = this.addDate(checkedDate, step);
        hasDisabled = disabledDate ? disabledDate(({current: checkedDate, value: value, type: 'date'})) : false;
        count++;
      };
      // 按照offset方向获取过程中遇到禁用日期，反方向获取
      if (count < Math.abs(offset)) {
        checkedDate = this.addDate(start, -step);
        hasDisabled = disabledDate ? disabledDate(({current: checkedDate, value: value, type: 'date'})) : false;
        while (!hasDisabled && count < Math.abs(offset)) {
          start = checkedDate;
          checkedDate = this.addDate(checkedDate, -step);
          hasDisabled = disabledDate ? disabledDate(({current: checkedDate, value: value, type: 'date'})) : false;
          count++;
        }
        // 没有获取到合适的范围
        if (count < Math.abs(offset)) {
          return [];
        }
      }
      return offset < 0 ? [end, start] : [start, end];
    },
  },
};



