import props from './props';
import utils from './utils';
import {YEAR_DECADE_COUNT} from '../const';
import {LOCALE_PREFIX} from '../../../base';
import locale from 'mixins/locale';
const NOW = utils.methods.getNow();
export default {
  mixins: [props, utils, locale],
  props: {
    value: {
      type: Array,
      default() {
        return [];
      },
    },
    range: {
      type: Boolean,
      default: false,
    },
    activePickerInputIndex: {
      type: Number,
      default: 0,
    },
    viewDate: {
      type: Object,
      default: NOW,
    },
    panelShow: {
      type: Boolean,
      default: false,
    },
    offset: {
      type: [Number, String],
      default: '',
    },
    // hover 值范围
    hoverValue: {
      type: Array,
      default: [],
    },
  },
  provide() {
    return {
      'buiPanel': this,
    };
  },
  data() {
    return {
    };
  },
  computed: {
    datePanelViewDates(index) {
      const arr = [];
      for (let index = 0; index < this.panels; index++) {
        arr.push(this.addMonth(this.viewDate, index));
      }
      return arr;
    },
    monthPanelViewDates() {
      const arr = [];
      for (let index = 0; index < this.panels; index++) {
        arr.push(this.addYear(this.viewDate, index));
      }
      return arr;
    },
    yearPanelViewDates() {
      const arr = [];
      for (let index = 0; index < this.panels; index ++) {
        arr.push(this.addYear(this.viewDate, index * YEAR_DECADE_COUNT));
      }
      return arr;
    },
    panelDateTimeForTimeSpinnerHeader() {
      const currentValue = this.value[this.activePickerInputIndex];
      const isShowSecond = (this.valueFormat || '').indexOf('ss') > -1;
      return currentValue ? this.getFormat(currentValue, isShowSecond ? 'HH:mm:ss' : 'HH:mm') : ' ';
    },
    panelTimeForTimeSpinnerHeader() {
      return [this.t(`${LOCALE_PREFIX}.date-picker.startTime`), this.t(`${LOCALE_PREFIX}.date-picker.endTime`)];
    },

  },
  methods: {

    // --API--
    // 修改viewDate month
    handleDateHeaderMonthChange(flag) {
      this.handelViewDateEmitChange(this.addMonth(this.viewDate, flag));
    },
    // --API--
    // 修改viewDate year
    handleDateHeaderYearChange(flag) {
      this.handelViewDateEmitChange(this.addYear(this.viewDate, flag));
    },
    getCurrentValue({_uid}) {
      let index;
      // time-panel 存在两个time-spinner，根据_uid判断当前操作的是第一个面板
      if (this.type === 'time' && this.$refs['time-spinner']) {
        index = this.$refs['time-spinner'].findIndex((instance) => {
          return instance._uid === _uid;
        });
      } else {
        // date-time-panel 通过当前操作的activePickerIndex控制
        index = this.activePickerInputIndex;
      }
      return [this.value[index] || this.viewDate, index];
    },
    // --API--
    // time hour 修改
    handleTimeHourChange({value, _uid}) {
      const [currentValue, index]= this.getCurrentValue({_uid});
      const newValue = this.setHour(currentValue, value);

      this.handleEmitSelect(newValue, index);
    },
    // --API--
    // time minute change
    handleTimeMinuteChange({value, _uid}) {
      const [currentValue, index]= this.getCurrentValue({_uid});
      const newValue = this.setMinute(currentValue, value);

      this.handleEmitSelect(newValue, index);
    },
    // --API--
    // time second change
    handleTimeSecondChange({value, _uid}) {
      const [currentValue, index]= this.getCurrentValue({_uid});
      const newValue = this.setSecond(currentValue, value);

      this.handleEmitSelect(newValue, index);
    },
    // --API--
    handelViewDateEmitChange(date) {
      this.$emit('view-date-change', date);
    },
    // 显示年面板
    handlePanelYearShow() {
      this.goToPanel('year');
    },
    // 显示月面板
    handlePanelMonthShow() {
      this.goToPanel('month');
    },
    goToPanel(targetType) {
      // target: year month date/date-time
      this.$emit('panel-change', targetType);
    },

    isHeaderPrevIconShow(index) {
      return index === 0;
    },
    isHeaderNextIconShow(index) {
      return index === this.panels - 1;
    },
    // date 点击日期处理
    handleDateSelect({currentDate}) {
      if (this.value && this.value[this.activePickerInputIndex]) {
        const currentValue = this.value[this.activePickerInputIndex];
        currentDate = this.setHour(currentDate, this.getHour(currentValue));
        currentDate = this.setMinute(currentDate, this.getMinute(currentValue));
        currentDate = this.setSecond(currentDate, this.getSecond(currentValue));
      }
      this.handleEmitSelect(currentDate);
    },
    handleEmitSelect(date, activePickerInputIndex) {
      this.$emit('select', date, activePickerInputIndex);
    },

    // date 移入日期
    handleDateMouseEnter({currentDate, _uid}) {
      const [, index]= this.getCurrentValue({_uid});
      let hoverValue = [];
      // 开始
      if (!this.range) {
        hoverValue = [currentDate];
      } else if (this.offset && this.type === 'date') {
        hoverValue = this.getOffsetRangeDate(currentDate, this);
      } else if (index === 0 && index === this.activePickerInputIndex) {
        const hoverEnd = this.value[1];

        if (hoverEnd && !this.isAfter(currentDate, hoverEnd)) {
          hoverValue = [currentDate, hoverEnd];
        } else if (!hoverEnd) {
          hoverValue = [currentDate, ''];
        }
      } else if (index === 1 && index === this.activePickerInputIndex) {
        // 结束
        const hoverStart = this.value[0];
        if (hoverStart && !this.isBefore(currentDate, hoverStart)) {
          hoverValue = [hoverStart, currentDate];
        } else if (!hoverStart) {
          hoverValue = ['', currentDate];
        }
      }
      this.$emit('hover-value-change', hoverValue);
    },
    // date 移出日期
    handleDateMouseLeave({currentDate, _uid}) {
      //
      this.$emit('hover-value-change', []);
    },

    // --API--
    // 年份头箭头点击
    handleYearHeaderChange(step) {
      this.handelViewDateEmitChange(this.addYear(this.viewDate, step));
    },
    // --API--
    // date 点击日期处理
    handleYearSelect({currentDate}) {
      // 如果type！==year是由其他面板切换到year，此时需要更新viewDate值
      if (this.type !== 'year') {
        this.handelViewDateEmitChange(this.setYear(this.viewDate, this.getYear(currentDate)));
        this.goToPanel(this.type);
        return;
      }

      this.handleEmitSelect(currentDate);
    },
    // ------/yearPanel ------
    // ------ monthPanel ------
    handleMonthSelect({currentDate}) {
      if (this.type !== 'month') {
        this.handelViewDateEmitChange(this.setMonth(this.viewDate, this.getMonth(currentDate)));
        this.goToPanel(this.type);
        return;
      }
      this.handleEmitSelect(currentDate);
    },
    // ------/monthPanel ------

  },
};



