
<template>
  <div class="bui-time-panels">
    <TimeSpinner
      v-for="(panel, index) in panels"
      ref="time-spinner"
      :key="index"
      :value="value[index]"
      :view-date="viewDate">
      <template
        v-if="range"
        slot="header">
        {{ panelTimeForTimeSpinnerHeader[index] }}
      </template>
    </TimeSpinner>
  </div>
</template>
<script>
import TimeSpinner from './../base/time-spinner.vue';
import panel from './../mixins/panel';


export default {
  name: 'PanelTime',
  components: {
    TimeSpinner,
  },
  mixins: [panel],

  data() {
    return {
      panels: this.range ? 2: 1,
    };
  },
  computed: {

  },
  methods: {

  },
};
</script>



