
<template>
  <table
    :style="styles"
    cellspacing="0"
    cellpadding="0"
    border="0"
  >
    <colgroup>
      <template v-for="column in columns">
        <col
          :key="column.id"
          :name="column.id"
          :width="columnsWidth[column._id]?columnsWidth[column._id].width:column.width||''"
          :align="column.align">
      </template>
    </colgroup>
    <tbody>
      <template v-for="(rdata, rindex) in data">
        <tr
          ref="tr"
          :key="rindex"
          :class="getRowClass(rdata)"
          @click="handleRowClick(rdata, rindex)"
          @mouseenter="handleRowMouseEnter(rdata, rindex)"
          @mouseleave="handleRowMouseLeave(rdata, rindex)">
          <template v-for="(column, cindex) in columns">
            <td
              v-if="showWithSpan(rdata, column, rindex, cindex)"
              :key="column._id"
              :class="getColumnClass(column)"
              v-bind="getSpan(rdata, column, rindex, cindex)"
              :style="getTdStyle(column)"
            >
              <ceil
                :row="rdata"
                :column="column"
                :row-index="rindex"
                :fixed="fixed"
              />
            </td>
          </template>
        </tr>
        <tr
          v-if="rdata.isExpand"
          :class="trExpandClass"
          :key="rindex+'expand'">
          <td
            :colspan="columns.length"
            class="bui-table-td-expand">
            <expand
              :row="rdata"
              :render="expandRender"
              :index="rindex"/>
          </td>
        </tr>
      </template>

    </tbody>
  </table>
</template>

<script>
import Expand from './expand.vue';
import Ceil from './ceil.vue';
import {addClass, removeClass} from 'utils/dom';
export default {
  name: 'TableBody',
  components: {
    Expand,
    Ceil,
  },
  props: {
    columns: {
      type: Array,
      default() {
        return [];
      },
    },
    data: {
      type: Array,
      default() {
        return [];
      },
    },
    columnsWidth: {
      type: Object,
      default() {
        return {};
      },
    },
    styles: {
      type: Object,
      default() {
        return {};
      },
    },
    fixed: {
      type: String,
      default: '',
    },
    spanMethod: {
      type: Function,
      default: null,
    },
  },
  data() {
    return {
      spanMethodCache: [],
    };
  },
  computed: {
    expandRender() {
      let render = function() {
        return '';
      };
      for (let i = 0; i < this.columns.length; i++) {
        const column = this.columns[i];
        if (column.type && column.type === 'expand') {
          if (column.renderExpand) render = column.renderExpand;
        }
      }
      return render;
    },
    trExpandClass() {
      return {
        'bui-table-tr-expand-hidden': !!this.fixed,
      };
    },
    hoverIndex() {
      return this.$parent.hoverIndex;
    },
  },
  watch: {
    hoverIndex(newVal, oldVal) {
      if (this.$refs.tr && this.$refs.tr[oldVal]) {
        removeClass(this.$refs.tr[oldVal], 'bui-table-tr-active');
      }
      if (this.$refs.tr && this.$refs.tr[newVal]) {
        addClass(this.$refs.tr[newVal], 'bui-table-tr-active');
      }
    },
  },
  methods: {
    getTdStyle(column) {
      return {
        width: this.columnsWidth[column._id] ? this.columnsWidth[column._id].width + 'px' : '',
        textAlign: column.align,
      };
    },
    handleRowClick(row, index) {
      // event.stopPropagation();
      this.$parent.handleRowClick(row, index);
    },
    handleRowMouseEnter(row, rowIndex) {
      this.$parent.handleRowMouseEnter(row, rowIndex);
    },
    handleRowMouseLeave(row, rowIndex) {
      this.$parent.handleRowMouseLeave(row, rowIndex);
    },
    getRowClass(row) {
      const classArr = [].concat(row.rowClassName);
      if (row.isHover) {
        classArr.push('bui-table-tr-active');
      }
      return classArr;
    },
    getColumnClass(column) {
      return {
        'bui-table-td-hidden': this.fixed && this.fixed !==column.fixed || (!this.fixed && column.fixed),
      };
    },
    getSpan(row, column, rowIndex, columnIndex) {
      const {fixed} =this;
      let result = {};
      const cacheId = `${rowIndex}-${columnIndex}-${fixed || 'center'}`;
      if (this.spanMethodCache[cacheId]) {
        return this.spanMethodCache[cacheId];
      }
      if (this.spanMethod) {
        result = this.spanMethod ? this.spanMethod({row, column, rowIndex, columnIndex, fixed}) : {};
        let rowspan = 1;
        let colspan = 1;
        if (Array.isArray(result)) {
          rowspan = result[0];
          colspan = result[1];
        } else if (typeof result === 'object') {
          rowspan = result.rowspan;
          colspan = result.colspan;
        }
        result = {
          rowspan,
          colspan,
        };
      }
      this.spanMethodCache[cacheId] = result;
      return result;
    },
    showWithSpan(row, column, rowIndex, columnIndex) {
      const result = this.getSpan(row, column, rowIndex, columnIndex);
      return !(('rowspan' in result && result.rowspan === 0) || ('colspan' in result && result.colspan === 0));
    },
  },

};
</script>



