<template>
  <header :class="calclass">
    <!-- logo部分 -->
    <div class="pull-left">
      <div class="l-side">
        <div class="logo">
          <template v-if="!$slots['logo']">
            <byted-icon
              name="logo"
              color="#E16057"
              width="70px"
              height="20px"/>
          </template>
          <template v-else>
            <slot name="logo"/>
          </template>
        </div>
        <div class="title">{{ title }}</div>
      </div>
    </div>
    <!-- /logo部分 -->
    <!-- 左对齐部分 -->
    <div
      v-if="$slots['left']"
      class="pull-left">
      <slot name="left"/>
    </div>
    <!-- /左对齐部分 -->
    <!-- 右对齐部分 -->
    <div class="pull-right">
      <slot name="right"/>
    </div>
    <!-- /右对齐部分 -->
  </header>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import BytedIcon from 'components/icon';

export default {
  name: `${CSS_PREFIX}navheader`,
  components: {
    BytedIcon,
  },

  props: {
    title: {
      type: String,
      default: '',
    },
  },

  computed: {
    calclass: function() {
      return `${CSS_PREFIX}navheader`;
    },
  },
};
</script>



