const imgPreview = function(options) {
  options = handleParam(options);
  const $dom = document.createElement('div');
  // 开始挂载
  this.$el.append($dom);
  const instance = new this.$imgPreviewConstructor();
  instance.options = options;
  instance.$mount($dom);
  return instance;
};

function handleParam({
  images = [],
  startPosition = 0,
  loop = true, // todo
  onClose = ()=>{},
  maskClosable = true,
  escClosable = true,
}) {
  return {
    images,
    startPosition,
    loop,
    onClose,
    maskClosable,
    escClosable,
  };
}
export default imgPreview;



