<template>
  <component
    :is="$props.tag"
    :disabled="disabled"
    :class="baseClass"
    v-bind="typeAttr"
    @click="handleClick">
    <byted-loading-icon
      v-if="loading"
      :size="size"
    />
    <slot/>
  </component>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import {isOneOf} from 'utils/helper.js';
import loadingIcon from 'components/loading';
export default {
  name: `${CSS_PREFIX}button`,
  components: {
    [loadingIcon.iconName]: loadingIcon,
  },
  props: {
    type: {
      type: String,
      validator(value) {
        return isOneOf(value, [
          'text',
          'default',
          'info',
          'success',
          'warning',
          'danger',
          'primary',
        ]);
      },
      default: 'default',
    },

    size: {
      type: String,
      validator(value) {
        return isOneOf(value, ['default', 'small', 'large', 'xl', 'lg', 'md', 'sm', 'xs']);
      },
      default: 'md',
    },

    plain: {
      type: Boolean,
      default: false,
    },

    disabled: {
      type: Boolean,
      default: false,
    },
    loading: {
      type: Boolean,
      default: false,
    },
    tag: {
      type: String,
      default: 'button',
    },
    htmlType: {
      type: String,
      default: 'button',
      validator(value) {
        return isOneOf(value, ['submit', 'reset', 'button', 'menu']);
      },
    },
  },

  computed: {
    baseClass: function() {
      const classArr = ['bui-btn', 'bui-btn-' + this.type, `${CSS_PREFIX}button`];
      const sizeObj = {
        small: 'bui-btn-sm',
        large: 'bui-btn-lg',
        xl: 'bui-btn-xl',
        lg: 'bui-btn-lg',
        md: 'bui-btn-md',
        sm: 'bui-btn-sm',
        xs: 'bui-btn-xs',
        default: 'bui-btn-md',
      };
      if (this.size) {
        classArr.push(sizeObj[this.size]);
      }
      if (this.plain) {
        classArr.push('bui-btn-plain');
      }
      if (this.loading) {
        classArr.push('bui-btn-loading');
      }
      return classArr;
    },
    typeAttr() {
      return this.tag === 'button' ? {type: this.htmlType} : {};
    },
  },

  methods: {
    handleClick: function(evt) {
      if (this.loading) return;
      if (!this.disabled) {
        this.$emit('click', evt);
      }
    },
  },
};
</script>



