<template>
  <div
    ref="container"
    :style="{background: bgColor}"
    role="saturationPanel"
    class="bui-saturation"
    @mousedown="handleMouseDown"
    @touchmove="handleChange"
    @touchstart="handleChange">
    <div class="bui-saturation--white"/>
    <div class="bui-saturation--black"/>
    <div
      :style="{top: pointerTop, left: pointerLeft}"
      role="currentSaturationPointer"
      class="bui-saturation-pointer">
      <div class="bui-saturation-circle"/>
    </div>
  </div>
</template>

<script>
import throttle from 'lodash.throttle';

export default {
  name: 'Saturation',
  props: {
    value: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  computed: {
    colors() {
      return this.value;
    },
    bgColor() {
      return `hsl(${this.colors.hsv.h || 0}, 100%, 50%)`;
    },
    pointerTop() {
      return (-((this.colors.hsv.v || 0) * 100) + 1) + 100 + '%';
    },
    pointerLeft() {
      return (this.colors.hsv.s || 0) * 100 + '%';
    },
  },
  methods: {
    throttle: throttle((fn, data) => {
      fn(data);
    }, 20,
    {
      'leading': true,
      'trailing': false,
    }),
    handleChange(e, skip) {
      !skip && e.preventDefault();
      const container = this.$refs.container;
      const containerWidth = container.clientWidth;
      const containerHeight = container.clientHeight;

      const xOffset = container.getBoundingClientRect().left + window.pageXOffset;
      const yOffset = container.getBoundingClientRect().top + window.pageYOffset;
      const pageX = e.pageX || (e.touches ? e.touches[0].pageX : 0);
      const pageY = e.pageY || (e.touches ? e.touches[0].pageY : 0);
      let left = pageX - xOffset;
      let top = pageY - yOffset;

      if (left < 0) {
        left = 0;
      } else if (left > containerWidth) {
        left = containerWidth;
      } else if (top < 0) {
        top = 0;
      } else if (top > containerHeight) {
        top = containerHeight;
      }

      const saturation = left / containerWidth;
      let bright = -(top / containerHeight) + 1;

      bright = bright > 0 ? bright : 0;
      bright = bright > 1 ? 1 : bright;

      this.throttle(this.onChange, {
        h: this.colors.hsv.h || 0,
        s: saturation,
        v: bright,
        a: this.colors.hsv.a,
        source: 'hsva',
      });
    },
    onChange(param) {
      this.$emit('change', param);
    },
    handleMouseDown(e) {
      window.addEventListener('mousemove', this.handleChange);
      window.addEventListener('mouseup', this.handleChange);
      window.addEventListener('mouseup', this.handleMouseUp);
    },
    handleMouseUp(e) {
      this.unbindEventListeners();
    },
    unbindEventListeners() {
      window.removeEventListener('mousemove', this.handleChange);
      window.removeEventListener('mouseup', this.handleChange);
      window.removeEventListener('mouseup', this.handleMouseUp);
    },
  },
};
</script>




