<template>
  <div class="bui-month-panel">
    <div class="bui-month-header">
      <byted-icon
        v-if="prev"
        class="bui-month-header-btn bui-month-header-prev-year"
        name="double-angle-left"
        @click="handleHeaderCLick(-1)"/>
      <div
        class="bui-month-header-text"
        @click="handleHeaderTextClick">
        {{ monthHeaderLabel }}
      </div>
      <byted-icon
        v-if="next"
        class="bui-month-header-btn bui-month-header-next-year"
        name="double-angle-right"
        @click="handleHeaderCLick(1)"/>
    </div>
    <div class="bui-month-body">
      <table class="bui-month-table">
        <tbody>
          <tr
            v-for="(row,rowIndex) in months.length/colsCount"
            :key="row"
          >
            <td
              v-for="(month,colIndex) in monthsRow[rowIndex]"
              :key="colIndex"
              :class="getCellClass(month, rowIndex, colIndex)"
              class="bui-month-cell"
              @click="handleSelect(month)"
              @mouseenter="handleMouseEnter(month, rowIndex, colIndex)"
              @mouseleave="handleMouseLeave(month, rowIndex, colIndex)"
            >
              <span class="bui-month-cell-inner">{{ months[rowIndex * colsCount +colIndex] }}</span>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
import utils from './../mixins/utils';
import locale from 'mixins/locale';
import {LOCALE_PREFIX} from '../../../base';
import BytedIcon from 'components/icon';
import {isPlainObject} from 'utils/types';
const NOW = utils.methods.getNow();
export default {
  name: 'Month',
  components: {BytedIcon},
  mixins: [utils, locale],
  inject: ['buiPanel'],
  props: {
    viewDate: {
      type: Object,
      default() {
        return NOW;
      },
    },
    prev: {
      type: Boolean,
      default: true,
    },
    next: {
      type: Boolean,
      default: true,
    },
    // 当前选中值
    value: {
      type: [String, Array, Date],
      default: () => [],
    },
    // 日期禁用
    disabledDate: {
      type: Function,
      default: ()=>{
        return null;
      },
    },
    // hover范围
    hoverValue: {
      type: Array,
      default() {
        return [];
      },
    },
    range: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    colsCount() {
      // 3 列排
      return 3;
    },

    months() {
      let months = this.t(`${LOCALE_PREFIX}.date-picker.months`) || [];
      // https://github.com/kazupon/vue-i18n/issues/759
      // the array has become an object
      if (isPlainObject(months)) {
        months = Object.keys(months).map((key)=>months[key]);
      }
      return months;
    },
    monthsRow() {
      const arr = [];
      const cur = this.isValid(this.viewDate) ? this.viewDate : this.getNow();
      this.months.forEach((item, index) => {
        const date = this.setMonth(cur, index);
        if (index%this.colsCount===0) {
          arr.push([]);
        }
        arr[arr.length-1].push(date);
      });
      return arr;
    },
    monthHeaderLabel() {
      const locale = this.t(`${LOCALE_PREFIX}.locale`);
      const datePanelLabel = this.t(`${LOCALE_PREFIX}.date-picker.datePanelLabel`);
      const {labels} = this.getFormatDateLabels(this.viewDate, datePanelLabel, locale);
      const findIndex =labels.findIndex((item)=>item.type === 'year');
      return labels[findIndex].label;
    },
  },
  created() {},
  methods: {
    // 增加 class 的前缀
    getCellClassName(string = '', cellPrefixCls = `bui-month-cell-`) {
      return `${cellPrefixCls}${string}`;
    },
    getCellClass(currentDate, rowIndex, colIndex) {
      if (!currentDate || !this.isValid(currentDate)) {
        return;
      }
      const disabled = this.disabledDate && this.disabledDate({current: this.getDateInMonth(currentDate), value: this.value, type: 'month'});
      const rangeStart = this.range ? this.getValue(this.value, 0) : '';
      const rangeEnd = this.range ? this.getValue(this.value, 1) : '';

      const hoverStart = this.range ? this.getValue(this.hoverValue, 0) : '';
      const hoverEnd = this.range ? this.getValue(this.hoverValue, 1): '';
      // 只要start end存在 range start要小于 range end 才可以处于 inrange
      const isStartBeforEnd = rangeStart && rangeEnd && this.isBefore(rangeStart, rangeEnd) || !rangeStart || !rangeEnd;
      const isSameStartEnd = rangeStart && rangeEnd && this.isSameDate(rangeStart, rangeEnd);
      const isRangeStart = isStartBeforEnd && rangeStart && this.isRangeStart(currentDate, rangeStart);
      const isRangeStartSingle = isRangeStart && !rangeEnd;
      const isRangeEnd = isStartBeforEnd && rangeEnd && this.isRangeEnd(currentDate, rangeEnd);
      const isRangeEndSingle = isRangeEnd && !rangeStart;

      const isInRange = rangeStart && rangeEnd && this.isInRange(currentDate, rangeStart, rangeEnd) && !isSameStartEnd && isStartBeforEnd;
      const isRangeDate = isInRange || isRangeStart || isRangeEnd;
      const isSameHoverStartEnd = hoverStart && hoverEnd && this.isSameDate(hoverStart, hoverEnd);
      const isHoverStart = hoverStart && hoverEnd && this.isSameDate(currentDate, hoverStart) && !isSameHoverStartEnd;
      const isHoverEnd = hoverStart && hoverEnd && this.isSameDate(currentDate, hoverEnd) && !isSameHoverStartEnd;
      const isInHover = hoverStart && hoverEnd && this.isInRange(currentDate, hoverStart, hoverEnd) && !isSameHoverStartEnd;
      const isHoverDate = isInHover || isHoverStart || isHoverEnd;

      const isEdgeStart = this.getMonth(currentDate) === 0;
      const isEdgeEnd = this.getMonth(currentDate) === 11;
      return {
        [this.getCellClassName(`in-view`)]: true,
        [this.getCellClassName(`disabled`)]: disabled,
        [this.getCellClassName(`now`)]: this.isSameMonth(currentDate, this.getNow()),
        [this.getCellClassName(`selected`)]: this.isSameCell(currentDate, this.value),
        // 行首
        [this.getCellClassName(`row-start`)]: colIndex === 0,
        // 行尾
        [this.getCellClassName(`row-end`)]: colIndex === this.colsCount - 1,

        [this.getCellClassName(`in-range`)]: isInRange,
        [this.getCellClassName(`range-start`)]: isRangeStart,
        [this.getCellClassName(`range-end`)]: isRangeEnd,
        [this.getCellClassName(`range-start-single`)]: isRangeStartSingle,
        [this.getCellClassName(`range-end-single`)]: isRangeEndSingle,
        [this.getCellClassName(`range-edge-start`)]: !isRangeStartSingle && !isRangeEndSingle && isRangeDate &&isEdgeStart,

        [this.getCellClassName(`range-edge-end`)]: !isRangeStartSingle && !isRangeEndSingle && isRangeDate && isEdgeEnd,
        // Range Hover
        [this.getCellClassName(`range-hover`)]: isInHover,
        [this.getCellClassName(`range-hover-start`)]: isHoverStart,
        [this.getCellClassName(`range-hover-end`)]: isHoverEnd,
        [this.getCellClassName(`range-hover-edge-start`)]: !isRangeStartSingle && !isRangeEndSingle && isHoverDate && isEdgeStart,
        [this.getCellClassName(`range-hover-edge-end`)]: !isRangeStartSingle && !isRangeEndSingle && isHoverDate && isEdgeEnd,
        // In Range & Hover
        [this.getCellClassName(`in-range-hovering`)]: isStartBeforEnd && !isRangeStartSingle && !isRangeEndSingle && !isSameStartEnd && isRangeDate && isHoverDate,
        [this.getCellClassName(`range-start-hovering`)]: !isRangeStartSingle && !isRangeEndSingle && !isSameStartEnd && isRangeStart && isHoverDate,
        [this.getCellClassName(`range-end-hovering`)]: !isRangeStartSingle && !isRangeEndSingle && !isSameStartEnd && isRangeEnd && isHoverDate,
      };
    },
    // 将传入disabledDate function 内的日期置为初始值
    getDateInMonth(currentDate) {
      return this.setDate(currentDate, 1);
    },
    isSameCell(currentDate, value) {
      if (!Array.isArray(value)) {
        return this.isSameMonth(currentDate, value);
      }
      return value.some((item) => this.isSameMonth(currentDate, item));
    },
    handleHeaderCLick(step) {
      const yearStep = step;
      this.buiPanel.handleYearHeaderChange(yearStep);
    },
    handleSelect(currentDate) {
      const disabled = this.disabledDate && this.disabledDate({current: this.getDateInMonth(currentDate), value: this.value, type: 'month'});
      if (disabled) {
        return;
      }
      this.buiPanel.handleMonthSelect({
        currentDate,
        _uid: this._uid,
      });
    },
    handleMouseEnter(currentDate, rowIndex, colIndex) {
      const disabled = this.disabledDate && this.disabledDate({current: this.getDateInMonth(currentDate), value: this.value, type: 'month'});
      if (disabled) {
        return;
      }
      if (this.buiPanel.handleDateMouseEnter) {
        this.buiPanel.handleDateMouseEnter({
          currentDate,
          _uid: this._uid,
        });
      }
    },
    handleMouseLeave(currentDate, rowIndex, colIndex) {
      const disabled = this.disabledDate && this.disabledDate({current: this.getDateInMonth(currentDate), value: this.value, type: 'month'});
      if (disabled) {
        return;
      }
      if (this.buiPanel.handleDateMouseLeave) {
        this.buiPanel.handleDateMouseLeave({
          currentDate,
          _uid: this._uid,
        });
      }
    },
    handleHeaderTextClick() {
      this.buiPanel.handlePanelYearShow();
    },
  },
};
</script>



