<template>
  <div
    ref="popperRef"
    :class="className"
    :style="styles"
    class="bui-popper">
    <slot/>
  </div>
</template>
<script>
import {getStyle} from 'utils/dom.js';
import {isOneOf} from 'utils/helper.js';
const Popper = require('popper.js').default;
import {CSS_PREFIX} from '../../base.js';
import PopupManager from 'mixins/popup/popup-manager.js';
import ResizeObserver from 'resize-observer-polyfill';
export default {
  name: `${CSS_PREFIX}popper`,
  props: {
    reference: {
      type: [Object, HTMLElement], // 可以直接传原生对象，也可以传 VNode
      default() {
        return {};
      },
    },
    placement: {
      type: String,
      default: 'bottom-start',
    },
    // 增加popper options后下掉modifier
    modifiers: {
      type: Object,
      default: function() {
        return null;
      },
    },
    // popper options 配置
    popperOptions: {
      type: Object,
      default() {
        return null;
      },
    },
    appendToBody: {
      type: [Boolean, HTMLElement],
      default: false,
    },
    className: {
      type: String,
      default: '',
    },
    fitPopperResize: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      popper: null,
      width: '',
      zIndex: '',
      defaultOptions: {
        modifiers: {
          flip: {
            enabled: false,
          },
          hide: {
            enabled: false,
          },
          preventOverflow: {
            enabled: false,
          },
        },
      },
    };
  },
  computed: {
    styles() {
      const style = {};
      if (this.width) style.width = `${this.width}px`;
      if (this.appendToBody) {
        style.zIndex = this.zIndex;
      }
      return style;
    },
    referenceEle() {
      if (this.reference instanceof HTMLElement) {
        return this.reference;
      } else if (this.reference) {
        return this.reference.$el;
      }
      return null;
    },
  },
  mounted() {
    this.$on('bui.popper.update', this.update);
    this.$on('bui.popper.destroy', this.destroy);
    this.$on('bui.popper.scheduleUpdate', this.scheduleUpdate);
    this.addPopperEventListener();
  },
  created() {},
  beforeDestroy() {
    if (this.popperRefRo) {
      this.popperRefRo.unobserve(this.$refs.popperRef);
      this.popperRefRo = null;
    }
    if (this.popper) {
      this.popper.destroy();
    }
    const parentElement = this.$el.parentNode;
    if (parentElement && this.$el) {
      parentElement.removeChild(this.$el);
    }
  },
  methods: {
    // popper事件监听
    addPopperEventListener() {
      // 实时调整popper框的位置
      if (this.fitPopperResize) {
        this.$nextTick(() => {
          if (!this.$refs.popperRef) {
            return;
          }
          this.popperRefRo = new ResizeObserver(() => {
            if (this.popper) {
              this.popper.update();
            }
          });
          this.popperRefRo.observe(this.$refs.popperRef);
        });
      }
    },
    handleEle() {
      let dom;
      // boolean值为true将popper append到body上  传入值为dom对象时则在指定对象添加
      if (typeof this.appendToBody === 'boolean' && this.appendToBody) {
        dom = document.body;
      } else if (this.appendToBody instanceof HTMLElement) {
        dom = this.appendToBody;
      }
      if (dom && this.$el.parentNode && this.$el.parentNode === dom) {
        return;
      }
      dom && dom.appendChild(this.$el);
    },
    getPopperOptions() {
      let modifiers = this.defaultOptions.modifiers;
      let currentOptions = this.defaultOptions;
      currentOptions.placement = this.placement;
      if (this.popperOptions) {
        modifiers = Object.assign({}, modifiers, this.popperOptions.modifiers);
        currentOptions = Object.assign({}, currentOptions, this.popperOptions);
      }
      if (this.modifiers) {
        modifiers = Object.assign({}, modifiers, this.modifiers);
      }
      if (!currentOptions.onCreate) {
        currentOptions.onCreate = (data) => {
          this.$nextTick(data.instance.update());
        };
      }
      return Object.assign(currentOptions, {modifiers});
    },
    update() {
      this.handleEle();
      if (this.popper) {
        this.popper.destroy();
      }
      if (!this.referenceEle) {
        return;
      }
      const options = this.getPopperOptions();

      this.popper = new Popper(this.referenceEle, this.$el, options);
      // 修改当前popperzIndex
      this.zIndex = this.appendToBody ? PopupManager.nextZIndex() : '';
      // 特殊处理，当用于select时，宽度与被挂载的select保持一致
      if (isOneOf(this.$parent.$options.name, [`${CSS_PREFIX}select`])) {
        this.width = parseInt(getStyle(this.$parent.$el, 'width'));
      }
    },
    scheduleUpdate() {
      this.popper && this.popper.scheduleUpdate();
    },
    destroy() {
      if (this.popper) {
        this.popper.destroy();
        this.popper = null;
      }
    },
  },
};
</script>



