import locale from 'mixins/locale';
import {LOCALE_PREFIX} from '../../../base';
import {isPlainObject} from 'utils/types';

export default {
  name: 'panelMonth',
  mixins: [locale],
  props: {
    value: null,
    calendarYear: {
      default: new Date().getFullYear(),
    },
    calendarMonth: {
      default: new Date().getMonth(),
    },
    disabledMonth: Function,
  },
  methods: {
    isDisabled(month) {
      if (typeof this.disabledMonth === 'function' && this.disabledMonth(month)) {
        return true;
      }
      return false;
    },
    selectMonth(month) {
      if (this.isDisabled(month)) {
        return;
      }
      this.$emit('select', month);
    },
  },
  render(h) {
    let months = this.t(`${LOCALE_PREFIX}.data-picker-new.months`);
    // const currentYear = this.value && new Date(this.value).getFullYear();
    // const currentMonth = this.value && new Date(this.value).getMonth();
    if (isPlainObject(months)) {
      months = Object.keys(months).map((key)=>months[key]);
    }
    months = months.map((v, i) => {
      return <span
        class={{
          'cell': true,
          'actived': this.calendarMonth === i,
          'disabled': this.isDisabled(i),
        }}
        onClick={this.selectMonth.bind(this, i)}>
        {v}
      </span>;
    });
    return <div class="mx-panel mx-panel-month">{months}</div>;
  },
};



