<template>
  <div
    :class="baseClass"
    @mouseenter="handleMouseEnter"
    @mouseleave="handleMouseLeave">
    <div
      :class="containerClass"
      :style="containerStyle"
    >
      <slot/>
    </div>
    <!-- 左键头 -->
    <div
      v-if="arrow !== 'never' && type !== 'card'"
      v-show="arrow === 'always' || hover"
      class="bui-carousel-arrow-btn bui-carousel-arrow-left"
      @click="leftBtnClick">
      <byted-icon
        color="#E4E9ED"
        name="angle-left"
        width="16px"
        height="16px"/>
    </div>
    <!-- /左箭头 -->
    <!-- 右箭头 -->
    <div
      v-if="arrow !== 'never' && type !== 'card'"
      v-show="arrow === 'always' || hover"
      class="bui-carousel-arrow-btn bui-carousel-arrow-right"
      @click="rightBtnClick">
      <byted-icon
        color="#E4E9ED"
        name="angle-right"
        width="16px"
        height="16px"/>
    </div>
    <!-- /右箭头 -->
    <!-- 指示器 -->
    <ul :class="dotsClasses">
      <li
        v-for="(item, index) in items"
        :key="index"
        :class="{'bui-carousel-is-active': index === currentIndex}"
        @click="dotsClick(index)">
        <div
          :class="{'bui-carousel-btn-dotted': dotsType === 'dotted'}"
          :style="dotsComputedStyle"
          class="bui-carousel-btn"/>
      </li>
    </ul>
    <!-- /指示器 -->
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import {isOneOf} from 'utils/helper.js';
import {doubleRaf} from './raf.js';
export function range(num, min, max) {
  return Math.min(Math.max(num, min), max);
}
import BytedIcon from 'components/icon';

export default {
  name: CSS_PREFIX + 'carousel',
  components: {
    BytedIcon,
  },
  props: {
    type: {
      type: String,
      default: 'plane',
      validator(val) {
        return isOneOf(val, ['plane', 'card']);
      },
    },
    autoplay: {
      type: Boolean,
      default: true,
    },
    interval: {
      type: Number,
      default: 3000,
    },
    loop: {
      type: Boolean,
      default: true,
    },
    value: {
      type: Number,
      default: 0,
    },
    dotsStyle: {
      type: Object,
      default() {
        return {
          align: 'left',
          outside: false,
          dotsWidth: '',
        };
      },
    },
    dotsType: {
      type: String,
      default: 'solid',
      validator(val) {
        return isOneOf(val, ['solid', 'dotted', 'none']);
      },
    },
    arrow: {
      type: String,
      default: 'hover',
      validator(val) {
        return isOneOf(val, ['hover', 'always', 'never']);
      },
    },
  },
  provide() {
    return {
      carousel: this,
    };
  },
  data() {
    return {
      hover: false,
      items: [],
      timer: null,
      currentIndex: -1,
      showIndex: -2,
      transitionFlag: false, // 是否开启 transition 动画
      computedWidth: 0,
      computedHeight: 0,
      offset: 0, // 容器的偏移量
    };
  },
  computed: {
    baseClass() {
      return [`${CSS_PREFIX}carousel`];
    },
    containerClass() {
      const classes = [`${CSS_PREFIX}carousel-container`];
      if (this.dotsStyle.outside || (this.type === 'card' && this.dotsType !== 'none')) {
        classes.push('carousel-outside-container');
      }
      return classes;
    },
    dotsClasses() {
      const classes = [`${CSS_PREFIX}carousel-dots`];
      if (this.dotsType === 'none') {
        classes.push('bui-carousel-dots-none');
      }
      if (this.dotsStyle.outside || this.type === 'card') {
        classes.push('bui-carousel-outside-dots');
      }
      if (this.dotsStyle.align) {
        classes.push(`carousel-${this.dotsStyle.align}`);
      }
      return classes;
    },
    isCard() {
      return this.type === 'card';
    },
    // 单个的宽度
    itemWidth() {
      if (this.isCard) {
        return this.computedWidth * 0.5;
      } else {
        // 常规模式下,单个宽度等于组件宽度
        return this.computedWidth;
      }
    },
    itemsCount() {
      return this.items.length;
    },
    minOffset() {
      const rect = this.$el.getBoundingClientRect();
      return (
        rect.width - this.itemWidth * this.itemsCount
      );
    },
    containerSize() {
      if (this.isCard) {
        return this.itemWidth * this.itemsCount;
      } else {
        return this.itemWidth * this.itemsCount;
      }
    },
    containerStyle() {
      const width = this.containerSize;
      return {
        width: `${width}px`,
        transitionDuration: `${this.transitionFlag? '400':0}ms`,
        transform: `translateX(${this.offset}px)`,
      };
    },
    dotsComputedStyle() {
      const style = {};
      if (this.dotsStyle.dotsWidth) {
        style.width = this.dotsStyle.dotsWidth + 'px';
        if (this.dotsType === 'dotted') {
          style.height = this.dotsStyle.dotsWidth + 'px';
          style.borderRadius = this.dotsStyle.dotsWidth / 2 + 'px';
        }
      }
      return style;
    },
  },
  watch: {
    autoplay: {
      immediate: true,
      handler: function(val) {
        val ? this.startTimer() : this.pauseTimer();
      },
    },
  },
  created() {
    this.updateItems();
  },
  mounted() {
    this.initOffset();
  },
  methods: {
    updateItems() {
      this.items = this.$children.filter((child) => child.$options.name === `${CSS_PREFIX}carousel-item`);
      this.updateChildProps();
    },
    startTimer() {
      if (!this.autoplay) return;
      this.timer = setInterval(this.playSlides, this.interval);
    },
    pauseTimer() {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
    },
    handleMouseEnter() {
      this.hover = true;
      this.pauseTimer();
    },
    handleMouseLeave() {
      this.hover = false;
      this.startTimer();
    },
    updateChildProps() {
      this.items.forEach((item, index) => {
        item.index = index;
      });
    },
    // 自动播放
    playSlides() {
      this.fixPosition();
      doubleRaf(()=>{
        this.transitionFlag = true;
        this.move(1);
      });
    },
    leftBtnClick() {
      this.prev();
    },
    rightBtnClick() {
      this.next();
    },
    getShowIndex(step) {
      const {showIndex, itemsCount} = this;
      if (step) {
        let min = 0;
        let max = itemsCount - 1;
        if (this.loop) {
          min = -1;
          max = itemsCount;
          if (this.isCard) {
            min = -2;
            max = itemsCount + 1;
          }
        }
        return range(showIndex+step, min, max);
      }
      return showIndex;
    },
    getTargetOffset(targetIndex) {
      if (this.isCard) {
        const offset = -(targetIndex-1) * this.itemWidth; // 每次移动一张卡片的距离
        return offset;
      } else {
        const offset = -targetIndex * this.itemWidth;
        return offset;
      }
    },
    initOffset() {
      this.showIndex = this.value;
      this.syncCurrentIndex();
      if (this.$el) {
        const rect = this.$el.getBoundingClientRect();
        this.computedWidth = +this.width || rect.width;
        this.computedHeight = +this.height || rect.height;
      }
      this.transitionFlag = false;
      if (this.type==='card') {
        this.setCardZindex(this.currentIndex, 0);
      }
      this.move(0);
    },
    // 修正位置
    fixPosition() {
      this.transitionFlag = false;
      if (this.showIndex <= -1) {
        this.move(this.itemsCount);
      }
      if (this.showIndex >= this.itemsCount) {
        this.move(-this.itemsCount);
      }
    },
    // 将 showIndex 同步到 currentIndex
    syncCurrentIndex() {
      this.currentIndex = (this.showIndex + this.itemsCount) % this.itemsCount;
    },
    updateItemsStyle() {
      this.items.forEach((item)=>item.updateItemStyle());
    },
    // 移动
    move(step) {
      const {minOffset, containerSize, itemsCount} = this;
      if (!this.isCard) {
        if (itemsCount <= 1) return; // 只有一张图片,不做处理
        const showIndex = this.getShowIndex(step);
        const targetOffset = this.getTargetOffset(showIndex);
        if (this.loop) {
          if (this.items[0]) {
            this.items[0].silentTrans = targetOffset < minOffset;
            this.items[0].offset = targetOffset < minOffset ? containerSize:0;
          }
          if (this.items[itemsCount-1]) {
            this.items[itemsCount-1].silentTrans = targetOffset > 0;
            this.items[itemsCount-1].offset = targetOffset > 0 ? -containerSize:0;
          }
        }
        this.showIndex = showIndex;
        this.syncCurrentIndex();
        this.offset = targetOffset;
      } else {
        if (itemsCount <=1 ) return; // 只有一张图片,不做处理
        const showIndex = this.getShowIndex(step);
        const targetOffset = this.getTargetOffset(showIndex);

        this.items.forEach((item, index)=>{
          let itemOffset = 0;
          if (index === showIndex) {
            itemOffset = -this.itemWidth/2;
          } else if (index > showIndex) {
            itemOffset = -this.itemWidth;
          } else {
            itemOffset = 0;
          }
          item.offset = itemOffset;
        });
        if (this.loop) {

        }
        this.showIndex = showIndex;
        this.syncCurrentIndex();
        this.offset = targetOffset;
      }
      this.updateItemsStyle();
      this.$emit('change', this.currentIndex);
    },
    // 指示器点击
    dotsClick(targetIndex) {
      this.transitionFlag = true;
      const step = targetIndex - this.showIndex;
      if (this.type==='card') {
        this.setCardZindex(targetIndex, step);
      }
      this.move(step);
    },
    //  暴露给外部手动切换方法
    setActiveItem(targetIndex) {
      this.dotsClick(targetIndex);
    },
    prev() {
      this.fixPosition();
      doubleRaf(()=>{
        this.transitionFlag = true;
        this.move(-1);
      });
    },
    next() {
      this.fixPosition();
      doubleRaf(()=>{
        this.transitionFlag = true;
        this.move(1);
      });
    },
    // 设置 Card 模式下的 z-index
    setCardZindex(targetIndex, step) {
      const nextIndex = targetIndex + step;
      const prevIndex = this.currentIndex;
      this.items.forEach((item, index)=>{
        if (index === prevIndex) {
          item.zIndex = 2;
        } else if (index===nextIndex) {
          item.zIndex = 3;
        } else if (index===targetIndex) {
          item.zIndex = 4;
        } else {
          item.zIndex = 1;
        }
      });
      this.updateItemsStyle();
    },
    // 卡片点击
    cardItemClick(targetIndex) {
      this.transitionFlag = false;
      this.fixPosition();
      this.updateItemsStyle();
      const step = targetIndex - this.showIndex;
      this.setCardZindex(targetIndex, step);
      doubleRaf(()=>{
        this.transitionFlag = true;
        this.move(step);
      });
    },
  },
};
</script>



