export const Expand = {
  name: 'Expand',
  props: {
    render: Function,
    name: {
      type: String,
      default: '',
    },
    option: {
      type: Object,
      default: () => { },
    },
  },
  methods: {
    onNameClick() {
      this.$emit('click');
    },
  },
  render() {
    return (
      <span on-click={() => this.onNameClick()}>
        {this.render(this.name, this.option)}
      </span>
    );
  },
};



