<template>
  <byted-popover
    ref="popover"
    :placement="placement"
    :transfer="transfer"
    :width="width"
    :offset="offset"
    :enterable="false"
    :disabled="disabled"
    :show-delay="openDelay || showDelay"
    :hide-delay="hideDelay"
    :popper-options="popperOptions"
    :fit-popper-resize="fitPopperResize"
    :popper-class="tooltipPopperClass"
    trigger="hover">
    <template slot="body">
      <slot/>
    </template>
    <template slot="reference">
      <!-- 参照物对象 -->
      <slot name="reference"/>
    </template>
  </byted-popover>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import {addClass} from 'utils/dom';
// import BytedPopper from 'components/popper';
import BytedPopover from 'components/popover';
import {isOneOf} from 'utils/helper.js';

export default {
  name: `${CSS_PREFIX}tooltip`,
  components: {
    BytedPopover,
  },
  props: {
    placement: {
      type: String,
      default: 'top',
    },
    offset: {
      type: [String, Number],
      default: 0,
    },
    width: {
      type: [String, Number],
      default: '246px',
    },
    transfer: {
      type: [Boolean, HTMLElement],
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    openDelay: {
      type: Number,
      default: 0,
    },
    showDelay: {
      type: Number,
      default: 0,
    },
    hideDelay: {
      type: Number,
      default: 200,
    },
    popperOptions: {
      type: Object,
      default() {
        return {
          modifiers: {
            flip: {
              enabled: false,
            },
            hide: {
              enabled: false,
            },
            preventOverflow: {
              enabled: false,
            },
          },
        };
      },
    },
    // dark || light
    theme: {
      type: String,
      default: 'light',
      validator: (val) => isOneOf(val, ['light', 'dark']),
    },
    // 是否监听popper内容变化自动更新弹框位置
    fitPopperResize: {
      type: Boolean,
      default: false,
    },
    popperClass: {
      type: [String, Array],
      default: '',
    },
  },
  computed: {
    tooltipPopperClass() {
      let classArr = [this.theme === 'dark' ? 'bui-popover-dark' : ''];
      if (this.popperClass) {
        if (typeof this.popperClass === 'string') {
          classArr.push(this.popperClass);
        }
        if (Array.isArray(this.popperClass)) {
          classArr = classArr.concat(this.popperClass);
        }
      }
      return classArr;
    },
  },
  mounted() {
    this.$nextTick(() => {
      this.initPopperClass();
    });
  },
  methods: {
    initPopperClass() {
      if (this.$refs.popover && this.$refs.popover.popper) {
        const popEle = this.$refs.popover.popper.$el;
        addClass(popEle, this.$options.name);
      }
    },
  },
};
</script>



