<template>
  <div :class="baseClass">
    <template v-if="hasCustomNav">
      <slot name="header"/>
      <slot name="breadcrumb"/>
      <div class="layout-body">
        <div
          v-if="hasNav"
          class="layout-nav">
          <slot name="nav"/>
        </div>
        <!-- <div class="layout-content" v-bui-scrollbar = "contentScrollbar"> -->
        <div
          class="layout-content"
          @scroll="domScroll">
          <slot/>
        </div>
      </div>
    </template>
    <template v-else>
      <slot/>
    </template>
  </div>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';

export default {
  name: `${CSS_PREFIX}layout`,

  props: {
    hasSider: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      contentScrollbar: null,
    };
  },
  computed: {
    baseClass() {
      const classArr = [`${CSS_PREFIX}layout`];
      if (this.hasSider) {
        classArr.push(`${CSS_PREFIX}layout-has-sider`);
      }
      return classArr;
    },
    hasCustomNav() {
      const slot = this._slotContents || this.$slots;
      return slot['nav'] != null || slot['header'] != null || slot['breadcrumb'] != null;
    },
    hasNav() {
      const slot = this._slotContents || this.$slots;
      return slot['nav'] != null;
    },
  },
  methods: {
    domScroll(e) {
      e.preventDefault();
      e.stopPropagation();
      const scTop = document.getElementsByClassName('layout-content')[0].scrollTop;
      this.$root.$emit('getScrollValue', scTop);
    },
  },
};
</script>



