<template>
  <div :class="[CSS_PREFIX+'upload']">
    <div @click="handleClick">
      <input
        ref="input"
        :accept="accept"
        :disabled="disabled"
        :multiple="multiple"
        type="file"
        name="Filedata"
        class="_file-input"
        @change="handleChange">
      <slot/>
    </div>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import uploadAjax from './upload.js';
export default {

  name: `${CSS_PREFIX}upload`,
  props: {
    // 上传地址
    action: {
      type: String,
      default: '',
      reqiured: true,
    },
    // 上传的文件字段名
    name: {
      type: String,
      default: 'Filedata',
    },
    // 额外参数
    data: {
      type: Object,
      default() {
        return {};
      },
    },
    headers: {
      type: Object,
      default() {
        return {};
      },
    },
    // 支持文件类型
    accept: {
      type: String,
      default: '',
    },
    // 选择文件后，上传之前（返回false，终止上传操作）
    beforeUpload: {
      type: Function,
      default() {
        return (()=>{});
      },
    },
    // 上传请求成功回调
    onSuccess: {
      type: Function,
      default() {
        return {};
      },
    },
    // 上传请求失败回调
    onError: {
      type: Function,
      default() {
        return {};
      },
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    multiple: {
      type: Boolean,
      default: false,
    },
    withCredentials: {
      // 是否携带cookie信息
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      CSS_PREFIX: CSS_PREFIX,
      uploadAjax: uploadAjax,
    };
  },
  computed: {

  },
  methods: {
    handleClick() {
      if (this.disabled) {
        return false;
      }
      this.$refs.input.click();
    },
    handleChange(e) {
      const files = e.target.files;
      let postFiles = Array.prototype.slice.call(files);
      this.$refs.input.value = null; // 清空
      if (!files || !postFiles.length) {
        return false;
      }
      if (!this.multiple) {
        postFiles = postFiles.slice(0, 1);
      }
      const fileLength = postFiles.length;
      postFiles.forEach((file, index) => {
        file.index = index;
        file.total_length = fileLength;
        this.uploadFile(file);
      });
    },
    uploadFile(file) {
      const options = {
        headers: this.headers,
        withCredentials: this.withCredentials, // 默认不发送
        file: file,
        data: this.data,
        filename: this.name, // 表单的name
        action: this.action,
        onProgress: (e) => { },
        onSuccess: (res) => {
          this.onSuccess(res, file, this);
        },
        onError: (err) => {
          this.onError(err, file, this);
        },
      };
      if (!this.beforeUpload) {
        this.uploadAjax(options);
        return;
      }
      const beforeReturn = this.beforeUpload(file, this);
      if (beforeReturn && beforeReturn.then) { // 返回的是promise对象
        beforeReturn.then((processedFile) => { // 传进来处理后的文件
          if (Object.prototype.toString.call(processedFile) === '[object File]') {
            options['file'] = processedFile;
            this.uploadAjax(options);
          } else {
            this.uploadAjax(options);
          }
        }, () => { });
      } else if (beforeReturn !== false) {
        this.uploadAjax(options);
      } else {
        // 返回false，不进行上传
      }
    },
  },

};
</script>




