
const DEFAULT_CANVAS =
  typeof document !== 'undefined' ? document.createElement('canvas') : null;
const DEFAULT_FONT_WEIGHT = 400;
const DEFAULT_FONT_STYLE = 'normal';
const DEFAULT_FONT_FAMILY = 'PingFang SC';
const DEFAUTL_FONT_SIZE = '14px';


export function getMeasureTextWidth(text = '', config = {}) {
  if (!DEFAULT_CANVAS) return 0;

  const {
    fontWeight = DEFAULT_FONT_WEIGHT,
    fontSize = DEFAUTL_FONT_SIZE,
    fontStyle = DEFAULT_FONT_STYLE,
    fontFamily = DEFAULT_FONT_FAMILY,
    canvas = DEFAULT_CANVAS,
  } = config;
  const context = canvas.getContext('2d');
  context.font = `${fontWeight} ${fontStyle} ${fontSize} ${fontFamily}`;
  const width = context.measureText(text).width;
  return width;
};



