import Modal from './src/confirm';
import BytedIcon from 'components/icon';

let modalInstance;

Modal.install = function(Vue) {
/**
 * @return {Object}
 */
  function getModalInstance() {
    modalInstance = modalInstance || Modal.newInstance(Vue);
    return modalInstance;
  }
  /**
   * @param {Object} options 弹窗参数
   */
  function confirm(options) {
    const instance = getModalInstance();

    options.onRemove = function() {
      modalInstance = null;
    };

    instance.show(options);
  }

  /**
   * @param {Object} options 弹窗参数
   * @param {String} type    类型参数，仅处理info、success、warning、error类型的状态
   */
  function handleType(options, type) {
    const originTitle = options.title;
    const originContent = options.content;
    const getIconNameByType = {
      info: 'info-circle',
      warning: 'exclamation-circle',
      error: 'close-circle',
      success: 'check-circle',
    };

    if (typeof options.title !== 'function') {
      options.title = (h) => (
        <div class={`bui-modal-dialog-${type}-title`}>
          <byted-icon
            class={`bui-modal-dialog-type-title-icon`}
            width="18px" height="18px"
            name={getIconNameByType[type]}
          ></byted-icon><span class="bui-modal-title">{originTitle}</span>
        </div>
      );
    }

    if (typeof options.content !== 'function') {
      options.content = (h) => (
        <div class="bui-modal-dialog-type-content">{originContent}</div>
      );
    }
  }

  Modal.info = function(props = {}) {
    handleType(props, 'info');
    return confirm(props);
  };

  Modal.success = function(props = {}) {
    handleType(props, 'success');
    return confirm(props);
  };

  Modal.warning = function(props = {}) {
    handleType(props, 'warning');
    return confirm(props);
  };

  Modal.error = function(props = {}) {
    handleType(props, 'error');
    return confirm(props);
  };

  Modal.confirm = function(props = {}) {
    props.showCancel = true;
    return confirm(props);
  };

  Modal.remove = function() {
    if (!modalInstance) {
      return false;
    }

    const instance = getModalInstance();

    instance.remove();
  };

  Vue.component(Modal.name, Modal);
  Vue.use(BytedIcon);
  Vue.prototype.$modal = Modal;
};

export default Modal;



