<template>
  <div
    v-clickoutside="clickoutsideHandle"
    :class="baseClass">
    <div
      @click="handleInputClick">
      <byted-input
        ref="popperRef"
        :placeholder="placeholder"
        :value="formatValue(value)"
        :disabled="disabled"
        :clearable="clearable"
        :validate-event="false"
        readonly
        @clear="handleClearClick">
        <template slot="suffix">
          <byted-icon
            name="clock-circle"
            color="#999"/>
        </template>
      </byted-input>
    </div>
    <byted-popper
      v-show="panelShow"
      :reference="popperRef"
      :append-to-body="transfer"
      :popper-options="popperOptions">
      <div class="bui-timepicker-popper">
        <div class="bui-timepicker-select">
          <div
            ref="col-h"
            class="bui-timepicker-h">
            <div
              v-for="(cell,index) in hArr"
              :class="{selected:cell.selected}"
              :key="cell.text"
              class="bui-timepicker-cell"
              @click="handleCellClick('h',cell,index)"
            >{{ cell.text }}</div>
          </div>
          <div
            ref="col-m"
            class="bui-timepicker-m">
            <div
              v-for="(cell,index) in mArr"
              :class="{selected:cell.selected}"
              :key="cell.text"
              class="bui-timepicker-cell"
              @click="handleCellClick('m',cell,index)"
            >{{ cell.text }}</div>
          </div>
          <div
            v-if="showSeconds"
            ref="col-s"
            class="bui-timepicker-s">
            <div
              v-for="(cell,index) in sArr"
              :class="{selected:cell.selected}"
              :key="cell.text"
              class="bui-timepicker-cell"
              @click="handleCellClick('s',cell,index)"
            >{{ cell.text }}</div>
          </div>
        </div>
        <div class="bui-timepicker-operation">
          <byted-button
            type="primary"
            size="xs"
            @click="confirmClick">{{ _okText }}</byted-button>
        </div>
      </div>
    </byted-popper>
  </div>
</template>
<script>
import {CSS_PREFIX, LOCALE_PREFIX} from '../../base.js';
import {hasClass} from 'utils/dom.js';
import dateUtil from 'utils/fecha.js';
import {addZero} from 'utils/utils.js';
import emitter from 'mixins/emitter';
import clickoutside from 'directives/clickoutside';
import locale from 'mixins/locale';
import BytedButton from 'components/button';
import BytedIcon from 'components/icon';
import BytedInput from 'components/input';
import BytedPopper from 'components/popper';

const initVal = '00:00:00';
const defaultFormat = 'HH:mm:ss';

export default {
  name: `${CSS_PREFIX}time-picker`,
  directives: {clickoutside},
  components: {
    BytedButton,
    BytedIcon,
    BytedInput,
    BytedPopper,
  },
  mixins: [emitter, locale],

  props: {
    value: {
      type: String,
      default: '',
      required: true,
    },
    format: {
      type: String,
      default: defaultFormat,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    clearable: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: '',
    },
    transfer: {
      type: Boolean,
      default: false,
    },
    popperOptions: {
      type: Object,
      default() {
        return {
          modifiers: {
            flip: {
              enabled: true,
            },
            hide: {
              enabled: false,
            },
            preventOverflow: {
              enabled: false,
            },
          },
        };
      },
    },
  },
  data() {
    return {
      panelShow: false,
      popperRef: null,
      innerValue: this.value || initVal,
    };
  },
  computed: {
    _okText() {
      return this.t(`${LOCALE_PREFIX}.time-picker.okText`);
    },
    baseClass() {
      const classArr = [`${CSS_PREFIX}time-picker`];
      if (this.disabled) {
        classArr.push(`disabled`);
      }
      return classArr;
    },
    hArr() {
      const arr = [];
      let i = 0;
      const max = 24;
      while (i < max) {
        const text = ('0' + i).substr(-2, 2);
        arr.push({
          text: text,
          selected: this.str2TimeObj(this.innerValue).h === text,
        });
        i++; // todo 用 step 来递增
      }
      return arr;
    },
    mArr() {
      const arr = [];
      let i = 0;
      const max = 60;
      while (i < max) {
        const text = ('0' + i).substr(-2, 2);
        arr.push({
          text: text,
          selected: this.str2TimeObj(this.innerValue).m === text,
        });
        i++;
      }
      return arr;
    },
    sArr() {
      const arr = [];
      let i = 0;
      const max = 60;
      while (i < max) {
        const text = ('0' + i).substr(-2, 2);
        arr.push({
          text: text,
          selected: this.str2TimeObj(this.innerValue).s === text,
        });
        i++;
      }
      return arr;
    },
    showSeconds() {
      return !(this.format || '').match(/mm$/);
    },
  },
  watch: {
    panelShow(newVal) {
      if (newVal) {
        this.innerValue = this.value || initVal;
        this.$nextTick(() => {
          this.scrollCell();
        });
      }
      newVal
        ? this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.update')
        : this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.destroy');
      this.$emit('visible-change', newVal);
    },
    value(newVal) {
      this.innerValue = newVal || initVal;
    },
    innerValue(newVal) {
      this.scrollCell();
    },
  },
  mounted() {
    this.popperRef = this.$refs.popperRef; // f**k vue
  },
  methods: {
    togglePopperShow(value) {
      this.pickerPanelShow(value);
    },
    formatValue(val) {
      if (!val) return val;
      const date = dateUtil.parse(val, this.format);
      if (!date) return '';
      return dateUtil.format(date, this.format);
    },
    str2TimeObj(str) {
      const date = dateUtil.parse(str, defaultFormat);
      return {
        h: addZero(date.getHours()),
        m: addZero(date.getMinutes()),
        s: addZero(date.getSeconds()),
      };
    },
    timeObj2Str(obj) {
      return obj['h'] + ':' + obj['m'] + ':' + obj['s'];
    },
    clickoutsideHandle(e) {
      if (!this.panelShow) {
        return;
      }
      const target = e.target;
      let parent = target.parentNode;
      while (parent && !hasClass(parent, 'bui-timepicker-popper')) {
        parent = parent.parentNode;
      }
      if (!parent) {
        this.pickerPanelShow(false);
      }
    },
    pickerPanelShow(open) {
      this.panelShow = open;
    },
    handleInputClick() {
      if (this.disabled) {
        return;
      }
      this.pickerPanelShow(!this.panelShow);
    },
    handleCellClick(colLabel, cellData, cellIndex) {
      const obj = this.str2TimeObj(this.innerValue);
      obj[colLabel] = cellData.text;
      this.innerValue = this.timeObj2Str(obj);
    },
    scrollToTop(el, scrollDuration) {},
    scrollCell() {
      // todo 动画待优化
      const obj = this.str2TimeObj(this.innerValue);
      for (const label in obj) {
        if (Object.hasOwnProperty.call(obj, label)) {
          const v = obj[label];
          this[label + 'Arr'].forEach((item, index) => {
            if (item.text === v && this.$refs['col-' + label]) {
              this.$refs['col-' + label].scrollTop = index * 34;
            }
          });
        }
      }
    },
    confirmClick() {
      this.$emit('input', this.innerValue);
      this.$emit('change', this.innerValue);
      this.pickerPanelShow(false);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [this.innerValue]);
    },
    clearTime() {
      const data = '';
      this.$emit('input', data);
      this.$emit('change', data);
      this.$emit('clear', data);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [data]);
    },
    handleClearClick() {
      this.clearTime();
      this.pickerPanelShow(false);
    },
  },
};
</script>



