<template>
  <form
    :class="baseClass"
    @submit.prevent>
    <slot/>
  </form>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import {isOneOf} from 'utils/helper.js';
export default {

  name: `${CSS_PREFIX}form`,
  props: {
    labelWidth: {
      type: [Number, String],
      default: 4,
    },
    labelAlign: {
      type: String,
      default: 'right',
      validator(value) {
        return isOneOf(value, ['right', 'left']);
      },
    },
    layoutType: {
      type: String,
      default: 'horizontal',
      validator(value) {
        return isOneOf(value, ['horizontal', 'vertical']);
      },
    },
    model: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      // form下属的form-item集合
      formItems: [],
    };
  },
  computed: {
    baseClass: function() {
      if (this.type === 'basic') {
        return;
      }
      // layoutType  inline/horizontal/""
      return [`${CSS_PREFIX}form`, `${CSS_PREFIX}form-${this.layoutType}`];
    },
  },
  created() {
    // 监听 formitem 的增加
    this.$on('bui.formItem.addItem', (item) => {
      if (item) this.formItems.push(item);
      return false;
    });
    // 监听 formitem 的减少
    this.$on('bui.formItem.removeItem', (item) => {
      if (item.prop) this.formItems.splice(this.formItems.indexOf(item), 1);
      return false;
    });
  },
  methods: {
    reset() {
      this.formItems.forEach((item) => {
        item.reset();
      });
    },
    // 表单验证
    validate(cb) {
      let valid = true;
      let count = 0;
      const errorArr = [];
      const propArr = [];
      this.formItems.forEach((item, index) => {
        item.validate('', (itemValid, errorObj, prop) => { // 调用form-item的validate方法校验
          if (!itemValid) {
            errorArr.push(errorObj);
            propArr.push(prop);
            valid = false;
          }
          if (typeof cb === 'function' && ++count === this.formItems.length) {
            cb(valid, errorArr, propArr); // 返回整体的表单校验结果，以及单个item的校验错误的数组和错误字段名
          }
          // this.$emit('validate', [itemValid, errorObj, prop]);
        });
      });
    },
    // 清除校验结果
    resetValidation() {
      this.formItems.forEach((item) =>{
        item.resetValidation();
      });
    },
    validateField(prop, cb) {
      if (!prop) return;
      this.formItems.find((item)=>{
        return item.prop === prop;
      }).validate('', (itemValid, errorObj, prop)=>{
        cb(itemValid, errorObj, prop);
      });
    },
  },
};
</script>



