function install(Vue, opts = {}) {
  if (Vue.version < '2.2.0') {
    console.error('Vue version < 2.2.0, 请升级VUE版本');
  }
  if (opts.i18n) locale.i18n(opts.i18n);
  components.map(function(component) {
    try {
      component.install(Vue);
    } catch (e) {
      console.error(e);
      return;
    }
  });
  if (Vue.prototype.$BUI) {
    console.error('Vue.prototype.$BUI为bui内部使用变量，请勿覆盖使用。');
  }
  Vue.prototype.$BUI = {
    pagination: Object.assign({
      gotoText: '',
      pagesizeText: '',
      totalText: '',
    }, opts.pagination || {}),
  };
  window.BUI = require('./utils/utils');
  window.BUI.version = buiVersion;
  window.BUI.Locale = require('./locale');
  window.BUI.popupManager = popupManager;
}

if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
};

export const Locale = locale;

