import Modal from './modal.vue';
import Button from '../../button/src/button.vue';
import {LOCALE_PREFIX} from '../../base';
import locale from 'mixins/locale';

Modal.newInstance = (Vue) => {
  const Instance = new Vue({
    mixins: [locale],
    data: {
      size: 'small',
      title: undefined,
      okText: '',
      cancelText: '',
      content: undefined,
      showCancel: false,
      visible: false,
      render: undefined,
      forceRender: false,
    },
    computed: {
      _okText() {
        return this.okText || this.t(`${LOCALE_PREFIX}.modal.okText`);
      },
      _cancelText() {
        return this.cancelText || this.t(`${LOCALE_PREFIX}.modal.cancelText`);
      },
    },
    methods: {
      ok() {
        this.onOk();
        this.close();
      },
      cancel() {
        this.onCancel();
        this.close();
      },
      close() {
        this.visible = false;
        this.remove();
      },
      remove() {
        setTimeout(() => {
          this.destroy();
        }, 300);
      },
      destroy() {
        this.$destroy();
        document.body.removeChild(this.$el);
        this.onRemove();
      },
      onOk() {
      },
      onCancel() {
      },
      onRemove() {
      },
    },
    render(h) {
      let contentDom;
      let titleDom;
      if (typeof this.content === 'function') {
        contentDom = h('div', {}, [this.content(h)]);
      } else if (this.render) {
        console.warn('The function render is deprecated and will be removed in the future: use content instead');
        contentDom = h('div', {}, [this.render(h)]);
      } else {
        contentDom = h('div', {
          domProps: {
            innerText: this.content,
          },
        });
      }
      if (typeof this.title === 'function') {
        titleDom = h('div', {}, [this.title(h)]);
      }

      return (
        <Modal
          forceRender={this.forceRender}
          title={this.title}
          size={this.size}
          value={this.visible}
          onInput={this.cancel}
          maskClosable={false}
        >
          {
            titleDom ?
              <div slot="header">
                {titleDom}
              </div>
              :
              ''
          }
          <div slot="body">
            {contentDom}
          </div>
          <div slot="footer">
            {
              this.showCancel ?
                (<Button onClick={this.cancel}>{this._cancelText}</Button> ) : ''
            }
            <Button type="primary" onClick={this.ok}>{this._okText}</Button>
          </div>
        </Modal>
      );
    },
  });
  const modal = Instance.$mount();
  document.body.appendChild(modal.$el);

  return {
    show(props) {
      if ('size' in props) {
        modal.size = props.size;
      }
      if ('title' in props) {
        modal.title = props.title;
      }
      if ('content' in props) {
        modal.content = props.content;
      }
      if ('okText' in props) {
        modal.okText = props.okText;
      }
      if ('cancelText' in props) {
        modal.cancelText = props.cancelText;
      }
      if ('onOk' in props) {
        modal.onOk = props.onOk;
      }
      if ('onCancel' in props) {
        modal.onCancel = props.onCancel;
      }
      if ('render' in props) {
        modal.render = props.render;
      }
      modal.showCancel = props.showCancel;
      modal.onRemove = props.onRemove;
      modal.visible = true;
    },
    remove() {
      modal.visible = false;
      modal.remove();
    },
    component: modal,
  };
};

export default Modal;



