<script>
import {CSS_PREFIX} from '../../base.js';
import {isOneOf} from 'utils/helper.js';
import BuiCollapseTransition from 'utils/collapse-transition.vue';
import BytedIcon from 'components/icon';

export default {
  name: `${CSS_PREFIX}collapse`,
  components: {
    BuiCollapseTransition,
    BytedIcon,
  },

  props: {
    title: {
      type: String,
      default: '',
    },
    collapseTitle: {
      type: String,
      default: '',
    },
    expandTitle: {
      type: String,
      default: '',
    },
    value: {
      type: Boolean,
      default: false,
    },
    direction: {
      type: String,
      default: 'top',
      validator(value) {
        return isOneOf(value, ['top', 'bottom']);
      },
    },
  },
  data() {
    return {
      innerShow: this.value || false,
    };
  },
  computed: {
    baseClass() {
      const classArr = [`${CSS_PREFIX}collapse`];
      if (this.innerShow) {
        classArr.push('is-expand');
      } else {
        classArr.push('is-collapse');
      }
      if (this.isCustomHeader) {
        classArr.push('is-custom');
      }
      return classArr;
    },
    isCustomHeader() {
      if (this.title || this.expandTitle || this.collapseTitle) {
        return false;
      } else {
        return true;
      }
    },
    isTitleInTop() {
      return this.direction === 'bottom';
    },
  },
  watch: {
    value: function(newVal) {
      this.innerShow = newVal;
    },
  },
  created() {},
  methods: {
    handleClick() {
      this.innerShow = !this.innerShow;
      this.$emit('input', this.innerShow);
      this.$emit('change', this.innerShow);
    },
    renderHeader(h) {
      const slots = [];
      if (this.$slots['collapse-header'] && !this.innerShow) {
        slots.push(this.$slots['collapse-header']);
      }
      if (this.$slots['expand-header'] && this.innerShow) {
        slots.push(this.$slots['expand-header']);
      }
      if (!this.$slots['collapse-header'] && !this.$slots['expand-header'] && this.$slots.header) {
        slots.push(this.$slots.header);
      }

      const content = !this.isCustomHeader ? (
        [
          h(BytedIcon, {
            class: 'arrow-icon',
            props: {name: 'caret-up'},
            on: {click: this.handleClick},
          }),
          h('div', {
            class: 'title',
            on: {
              click: this.handleClick,
            },
          }, [this.innerShow ? (this.expandTitle || this.title) : (this.collapseTitle || this.title)]),
          h('div', {class: 'line'}),
        ]
      ) : (
        [h('div', {class: 'custom-header-inner', on: {click: this.handleClick}}, slots)]
      );
      return h('div', {class: 'collapse-header'}, content);
    },
  },
  render(h) {
    return h('div', {class: this.baseClass}, [
      this.isTitleInTop && this.renderHeader(h),
      h(BuiCollapseTransition, [
        h('div', {
          class: 'collapse-content',
          directives: [
            {
              name: 'show',
              value: this.innerShow,
            },
          ],
        }, this.$slots.default),
      ]),
      !this.isTitleInTop && this.renderHeader(h),
    ]);
  },
};
</script>



