<template>
  <div
    :class="baseClass"
    :style="baseStyle"
    @click="handleItemClick">
    <slot/>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';

const CARD_SCALE = 0.88;

export default {
  name: CSS_PREFIX + 'carousel-item',
  inject: ['carousel'],
  data() {
    return {
      index: 0,
      offset: 0,
      zIndex: 1,
      silentTrans: false, // 无延时的变化标志
      itemStyle: {},
    };
  },
  computed: {
    baseClass() {
      const classList = [`${CSS_PREFIX}carousel-item`];
      if (this.isCard) {
        classList.push('bui-carousel-item-card');
      }
      if (this.isCurrent) {
        classList.push('bui-active-item');
      }
      return classList;
    },
    isCard() {
      return this.carousel.type === 'card';
    },
    isCurrent() {
      return this.carousel.currentIndex === this.index;
    },
    scale() {
      if (this.isCard) {
        return this.isCurrent ? 1 : CARD_SCALE;
      }
      return 1;
    },
    baseStyle() {
      return {
        ...this.itemStyle,
        width: `${this.carousel.itemWidth}px`,
      };
    },
  },
  created() {
    this.carousel && this.carousel.updateItems();
    this.updateItemStyle();
  },
  beforeDestroy() {
    this.$nextTick(()=> {
      this.carousel && this.carousel.updateItems();
      this.carousel.initOffset();
    });
  },
  methods: {
    handleItemClick() {
      if (this.carousel && this.isCard) {
        const index = this.carousel.items.indexOf(this);
        this.carousel.cardItemClick(index);
      }
    },
    updateItemStyle() {
      const transitionFlag = !this.silentTrans && this.carousel.transitionFlag;
      this.itemStyle = {
        zIndex: `${this.zIndex}`,
        width: `${this.carousel.itemWidth}px`,
        transitionDuration: `${transitionFlag? '400':0}ms`,
        transform: `translateX(${ this.offset }px) scale(${ this.scale })`,
      };
      this.silentTrans = false;
    },
  },
};
</script>



