<script>
import Vue from 'vue';
import {getStyle, getComponentFromProp, createChainedFunction, getTransitionProps} from './util.js';
import StateMixin from './stateMixin';
import NotificationItem from './notification-item.vue';
import {CSS_PREFIX} from '../../base.js';
import PopupManager from 'mixins/popup/popup-manager.js';

function noop() {}

let seed = 0;
const now = Date.now();

const PREFIX_CLS = `${CSS_PREFIX}-notification`;

function getUuid() {
  return `${PREFIX_CLS}_${now}_${seed++}`;
}

const Notification = {
  mixins: [StateMixin],
  props: {
    prefixCls: {
      type: String,
      default: PREFIX_CLS,
    },
    transitionName: {
      type: String,
      default: '',
    },
    animation: {
      type: [String, Object],
      default: 'fade',
    },
    maxCount: {
      type: Number,
      default: null,
    },
    closeIcon: {
      type: null,
    },
  },
  data() {
    return {
      notices: [],
    };
  },
  methods: {
    getTransitionName() {
      const props = this.$props;
      let transitionName = props.transitionName;
      if (!transitionName && props.animation) {
        transitionName = `${props.prefixCls}-${props.animation}`;
      }
      return transitionName;
    },

    add(notice) {
      const key = (notice.key = notice.key || getUuid());
      const {maxCount} = this.$props;
      this.setState((previousState) => {
        const notices = previousState.notices;
        const noticeIndex = notices.map((v) => v.key).indexOf(key);
        const updatedNotices = notices.concat();
        if (noticeIndex !== -1) {
          updatedNotices.splice(noticeIndex, 1, notice);
        } else {
          if (maxCount && notices.length >= maxCount) {
            notice.updateKey = updatedNotices[0].updateKey || updatedNotices[0].key;
            updatedNotices.shift();
          }
          updatedNotices.push(notice);
        }
        return {
          notices: updatedNotices,
        };
      });
    },

    remove(key) {
      this.setState((previousState) => {
        return {
          notices: previousState.notices.filter((notice) => notice.key !== key),
        };
      });
    },
  },

  render(h) {
    const {prefixCls, notices, remove, getTransitionName} = this;
    const transitionProps = getTransitionProps(getTransitionName());
    const noticeNodes = notices.map((notice, index) => {
      const update = Boolean(index === notices.length - 1 && notice.updateKey);
      const key = notice.updateKey ? notice.updateKey : notice.key;

      const {content, duration, closable, onClose, style, class: className} = notice;
      const close = createChainedFunction(remove.bind(this, notice.key), onClose);
      const noticeProps = {
        props: {
          prefixCls,
          duration,
          closable,
          update,
          closeIcon: getComponentFromProp(this, 'closeIcon'),
        },
        on: {
          close,
          click: notice.onClick || noop,
        },
        style: Object.assign({}, style),
        class: className,
        key,
      };
      return (
        <NotificationItem {...noticeProps}>{typeof content === 'function' ? content(h) : content}</NotificationItem>
      );
    });
    const className = {
      [prefixCls]: 1,
    };
    const style = getStyle(this);
    return (
      <div
        class={className}
        style={
          style || {
            top: '65px',
            left: '50%',
          }
        }
      >
        <transition-group {...transitionProps}>{noticeNodes}</transition-group>
      </div>
    );
  },
};

Notification.newInstance = function newNotificationInstance(properties, callback) {
  const {getContainer, style, class: className, ...props} = properties || {};
  const div = document.createElement('div');
  if (getContainer) {
    const root = getContainer();
    root.appendChild(div);
  } else {
    document.body.appendChild(div);
  }
  new Vue({
    el: div,
    name: `${CSS_PREFIX}notification`,
    data() {
      return {
        zIndex: '',
      };
    },
    computed: {
      style() {
        return {...style, zIndex: this.zIndex};
      },
    },
    mounted() {
      const self = this;
      this.$nextTick(() => {
        callback({
          notice(noticeProps) {
            self.$refs.notification.add(noticeProps);
            self.zIndex = PopupManager.nextZIndex();
          },
          removeNotice(key) {
            self.$refs.notification.remove(key);
          },
          component: self,
          destroy() {
            self.$destroy();
            self.$el.parentNode.removeChild(self.$el);
            self.zIndex = '';
          },
        });
      });
    },
    render() {
      const p = {
        props,
        ref: 'notification',
        style: this.style,
        class: className,
      };
      return <Notification {...p} />;
    },
  });
};

export default Notification;
</script>



