<template>
  <div class="bui-editable-input">
    <input
      ref="input"
      :aria-label="label"
      v-model="val"
      :maxlength="maxLength"
      class="bui-editable-input-input"
      @keydown="handleKeyDown"
      @input="update">
    <span class="bui-editable-input-label">{{ label }}</span>
  </div>
</template>

<script>
export default {
  name: 'EditableInput',
  props: {
    label: {
      type: String,
      default: '',
    },
    value: {
      type: [String, Number],
      default: '',
    },
    maxLength: {
      type: Number,
      default: 0,
    },
    max: {
      type: Number,
      default: 0,
    },
    min: {
      type: Number,
      default: 0,
    },
    arrowOffset: {
      type: Number,
      default: 1,
    },
  },
  computed: {
    val: {
      get() {
        return this.value;
      },
      set(v) {
        // TODO: min
        if (!(this.max === undefined) && +v > this.max) {
          this.$refs.input.value = this.max;
        } else {
          return v;
        }
      },
    },
  },
  methods: {
    update(e) {
      this.handleChange(e.target.value);
    },
    handleChange(newVal) {
      const data = {};
      data[this.label] = newVal;
      if (data.hex === undefined && data['#'] === undefined) {
        this.$emit('change', data);
      } else if (newVal.length > 5 || newVal === '') {
        this.$emit('change', data);
      }
    },
    handleKeyDown(e) {
      let val = this.val;
      const number = Number(val);
      if (number) {
        const amount = this.arrowOffset || 1;

        // Up
        if (e.keyCode === 38) {
          val = number + amount;
          this.handleChange(val);
          e.preventDefault();
        }

        // Down
        if (e.keyCode === 40) {
          val = number - amount;
          this.handleChange(val);
          e.preventDefault();
        }
      }
    },
  },
};
</script>




