<template>
  <byted-button
    :class="baseClass"
    :disabled="isDisabled"
    :tag="tag"
    plain
    type="primary"
    @click="onClick">
    <slot/>
  </byted-button>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';
import BytedButton from 'components/button';

export default {
  name: `${CSS_PREFIX}checkbox-button`,

  components: {
    BytedButton,
  },

  mixins: [emitter],
  props: {
    val: {
      type: [String, Boolean, Number],
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    single: {
      type: Boolean,
      default: false,
    },
    tag: {
      type: String,
      default: 'button',
    },
  },
  computed: {
    baseClass: function() {
      const classArr = [`${CSS_PREFIX}checkbox-button`];
      if (this.isActive) {
        classArr.push('active');
      }
      if (this.single) {
        classArr.push('single');
      }
      return classArr;
    },
    checkboxGroup() {
      const parent = this.$parent;
      return parent;
    },
    isGroup() {
      return this.checkboxGroup !== null;
    },
    isDisabled() {
      return (this.isGroup && this.checkboxGroup.disabled) || this.disabled;
    },
    isActive() {
      return this.innerChecked;
    },
    innerChecked() {
      const {isGroup, checkboxGroup, val, value} = this;
      if (isGroup) {
        return checkboxGroup.value.indexOf(val) !== -1;
      }
      if (this.isArray(value)) {
        return value.indexOf(val) !== -1;
      }
      return !!value;
    },
  },
  mounted() {},
  methods: {
    onClick(e) {
      if (this.isDisabled) {
        return false;
      }
      const checked = !this.innerChecked;
      const valueCopy = this.checkboxGroup.value.slice(0);
      if (checked) {
        valueCopy.push(this.val);
      } else {
        valueCopy.splice(valueCopy.indexOf(this.val), 1);
      }
      this.$emit('change', checked);
      this.$emit('click', e);
      if (this.isGroup) {
        this.dispatch(`${CSS_PREFIX}checkbox-group`, 'bui.checkbox.change', {
          val: this.val,
          checked,
        });
      }
    },
  },
};
</script>



