<script>
export default {
  name: 'Expand',
  props: {
    value: {
      type: Array,
      default: () => {
        return [];
      },
    },
    render: {
      type: Function,
      default: null,
    },
    label: {
      type: String,
      default: '',
    },
  },
  render(h) {
    if (this.label !== '') {
      return (<span>{this.label}</span>);
    }
    if (this.render === null) return null;
    const renderNode = this.render(h);
    return (<span>{renderNode}</span>);
  },
};
</script>



