<template>
  <li
    :class="baseClass"
    @click="onClick"><slot/></li>
</template>

<script>
import emitter from 'mixins/emitter';
import {CSS_PREFIX} from '../../base.js';

export default {
  name: `${CSS_PREFIX}dropdown-item`,
  mixins: [emitter],
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    name: {
      type: String,
      default: '',
    },
  },
  computed: {
    baseClass() {
      const classes = [`${CSS_PREFIX}dropdown-item`];
      if (this.disabled) {
        classes.push('disabled');
      }

      return classes;
    },
  },
  methods: {
    onClick(evt) {
      if (this.disabled) {
        return;
      }
      this.dispatch('byted-dropdown', 'click-dropdown-item', this.name);
      this.$emit('click', evt);
    },
  },
};
</script>



