<template>
  <div :class="baseClass">
    <slot/>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';

export default {
  name: `${CSS_PREFIX}layout-sider`,
  computed: {
    baseClass() {
      return `${CSS_PREFIX}layout-sider`;
    },
  },
};
</script>



