<template>
  <div
    v-clickoutside="closePopup"
    :class="{
      'mx-datepicker-range': range,
      'disabled': disabled
    }"
    :style="{
      'width': computedWidth
    }"
    class="mx-datepicker">
    <div
      @click="showPopup">
      <byted-input
        ref="input"
        :class="inputClass"
        :name="inputName"
        :disabled="disabled"
        :readonly="!editable"
        :value="text"
        :placeholder="innerPlaceholder"
        :size="size"
        :clearable="clearable"
        :validate-event="false"
        type="text"
        autocomplete="off"
        @clear="handleClearClick"
        @input="handleInput"
        @input-change="handleChange">
        <template slot="suffix">
          <byted-icon
            name="calendar"
            color="#999"/>
        </template>
      </byted-input>
    </div>
    <div
      v-show="popupVisible"
      ref="calendar"
      :style="innerPopupStyle"
      class="mx-datepicker-popup"
      @click.stop.prevent>
      <slot name="header">
        <div
          v-if="range && innerShortcuts.length"
          class="mx-shortcuts-wrapper">
          <!-- <div class="bui-shortcuts-header">
            预设时间范围
          </div> -->
          <div class="bui-shortcuts-list">
            <a
              v-for="(curShortcut, index) in innerShortcuts"
              :key="index"
              type="button"
              class="mx-shortcuts"
              @click="shortcutClick(curShortcut)">{{ curShortcut.text }}</a>
          </div>
        </div>
      </slot>
      <calendar-panel
        v-if="!range"
        ref="calendarPanel"
        v-bind="$attrs"
        :type="innerType"
        :date-format="innerDateFormat"
        :value="currentValue"
        :visible="popupVisible"
        @select-date="selectDate"
        @select-time="selectTime"/>
      <div
        v-else
        class="mx-range-wrapper">
        <calendar-panel
          ref="calendarPanelRange0"
          v-bind="$attrs"
          :type="innerType"
          :date-format="innerDateFormat"
          :start-at="currentValue[0]"
          :end-at="currentValue[1]"
          :visible="popupVisible"
          :next-arrow="false"
          @select-date="selectDateRange"
          @select-time="selectStartTime"/>
        <calendar-panel
          ref="calendarPanelRange1"
          v-bind="$attrs"
          :type="innerType"
          :date-format="innerDateFormat"
          :start-at="currentValue[0]"
          :end-at="currentValue[1]"
          :visible="popupVisible"
          :prev-arrow="false"
          @select-date="selectDateRange"
          @select-time="selectEndTime"/>
      </div>
      <slot
        :confirm="confirmDate"
        name="footer">
        <div
          v-if="confirm"
          class="mx-datepicker-footer">
          <button
            type="button"
            class="mx-datepicker-btn mx-datepicker-btn-confirm"
            @click="confirmDate">{{ confirmText }}</button>
        </div>
      </slot>
    </div>
  </div>
</template>

<script>
import {CSS_PREFIX} from 'components/base.js';
import fecha from 'utils/fecha.js';
import clickoutside from './directives/clickoutside';
import {isValidDate, isValidRange, isDateObejct, formatDate, parseDate, throttle} from './utils/index';
import CalendarPanel from './calendar.vue';
import locale from 'mixins/locale';
import Languages from './locale/languages';
import emitter from 'mixins/emitter';
import {isOneOf} from 'utils/helper.js';
import {LOCALE_PREFIX} from '../../base';
import BytedIcon from 'components/icon';
import BytedInput from 'components/input';

export default {
  fecha,
  name: `${CSS_PREFIX}date-picker-new`,
  components: {CalendarPanel, BytedIcon, BytedInput},
  directives: {
    clickoutside,
  },
  mixins: [locale, emitter],
  props: {
    value: {
      type: null,
      default: null,
    },
    placeholder: {
      type: String,
      default: null,
    },
    lang: {
      type: [String, Object],
      default: 'zh',
    },
    format: {
      type: String,
      default: 'YYYY-MM-DD',
    },
    valueFormat: {
      type: String,
      default: 'YYYY-MM-DD',
    },
    dateFormat: {
      type: String, // format the time header and date tooltip
      default: '',
    },
    type: {
      type: String,
      default: 'date', // ['date', 'datetime'] zendy added 'month', 'year', mxie added "time"
    },
    range: {
      type: Boolean,
      default: false,
    },
    rangeSeparator: {
      type: String,
      default: '~',
    },
    width: {
      type: [String, Number],
      default: null,
    },
    confirmText: {
      type: String,
      default: '确认',
    },
    confirm: {
      type: Boolean,
      default: false,
    },
    editable: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    clearable: {
      type: Boolean,
      default: false,
    },
    shortcuts: {
      type: [Array],
      default() {
        return [];
      },
    },
    inputName: {
      type: String,
      default: 'date',
    },
    inputClass: {
      type: [String, Array],
      default: '',
    },
    appendToBody: {
      type: Boolean,
      default: false,
    },
    popupStyle: {
      type: Object,
      default() {
        return {};
      },
    },
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, ['md', 'xs', 'sm', 'lg', 'xl']);
      },
    },
  },
  data() {
    return {
      currentValue: this.range ? [null, null] : null,
      userInput: null,
      popupVisible: false,
      position: {},
      selectDateRangeCount: 0, // 范围选择的点击次数
    };
  },
  computed: {
    language() {
      // if (isPlainObject(this.lang)) {
      //   return {...Languages.en, ...this.lang};
      // }
      // return Languages[this.lang] || Languages.en;
      return Languages.zh;
    },
    innerPlaceholder() {
      if (typeof this.placeholder === 'string') {
        return this.placeholder;
      }
      return this.range ? this.t(`${LOCALE_PREFIX}.data-picker-new.placeholder.dateRange`) : this.t(`${LOCALE_PREFIX}.data-picker-new.placeholder.date`);
    },
    text() {
      if (this.userInput !== null) {
        return this.userInput;
      }
      if (!this.range) {
        return isValidDate(this.value) ? this.stringify(this.value) : '';
      }
      return isValidRange(this.value)
        ? `${this.stringify(this.value[0])} ${this.rangeSeparator} ${this.stringify(this.value[1])}`
        : '';
    },
    computedWidth() {
      if (typeof this.width === 'number' || (typeof this.width === 'string' && /^\d+$/.test(this.width))) {
        return this.width + 'px';
      }
      return this.width;
    },
    showClearIcon() {
      return !this.disabled && this.clearable && (this.range ? isValidRange(this.value) : isValidDate(this.value));
    },
    innerType() {
      return String(this.type).toLowerCase();
    },
    innerShortcuts() {
      if (Array.isArray(this.shortcuts)) {
        return this.shortcuts;
      }
      if (this.shortcuts === false) {
        return [];
      }
      const arr = [];
      return arr;
    },
    innerDateFormat() {
      if (this.dateFormat) {
        return this.dateFormat;
      }
      if (this.innerType === 'date') {
        return this.format;
      }
      return this.format.replace(/[Hh]+.*[msSaAZ]|\[.*?\]/g, '').trim() || 'YYYY-MM-DD';
    },
    innerPopupStyle() {
      return {...this.position, ...this.popupStyle};
    },
  },
  watch: {
    value: {
      immediate: true,
      handler: 'handleValueChange',
    },
    popupVisible(val) {
      if (val) {
        this.initCalendar();
      } else {
        this.userInput = null;
      }
    },
  },
  mounted() {
    if (this.appendToBody) {
      this.popupElm = this.$refs.calendar;
      document.body.appendChild(this.popupElm);
    }
    this._displayPopup = throttle(() => {
      if (this.popupVisible) {
        this.displayPopup();
      }
    }, 200);
    window.addEventListener('resize', this._displayPopup);
    window.addEventListener('scroll', this._displayPopup);
  },
  beforeDestroy() {
    if (this.popupElm && this.popupElm.parentNode === document.body) {
      document.body.removeChild(this.popupElm);
    }
    window.removeEventListener('resize', this._displayPopup);
    window.removeEventListener('scroll', this._displayPopup);
  },
  methods: {
    initCalendar() {
      if (this.range) {
        this.selectDateRangeCount = 0;
        this.setCalendarPanelRange();
        this.bindHoverEvt();
      }
      this.handleValueChange(this.value);
      this.displayPopup();
    },
    bindHoverEvt() {
      this.$on('panelDate.triggerHover', (date)=>{
        if (this.currentValue[0]&&!this.currentValue[1]) {
          this.broadcast('panelDate', 'panelDate.setHover', date);
        }
      });
    },
    // 设置日期范围的 calendar 的月份显示
    setCalendarPanelRange() {
      let panelDate0 = new Date();
      let panelYear0 = panelDate0.getFullYear();
      let panelMonth0 = panelDate0.getMonth();
      let panelDate1 = new Date(panelYear0, panelMonth0+1);
      let panelYear1 = panelDate1.getFullYear();
      let panelMonth1 = panelDate1.getMonth();
      // 如果有默认回显
      if (this.currentValue[0]&&this.currentValue[1]) {
        panelDate0 = this.currentValue[0];
        panelYear0 = panelDate0.getFullYear();
        panelMonth0 = panelDate0.getMonth();
        panelDate1 = this.currentValue[1];
        panelYear1 = panelDate1.getFullYear();
        panelMonth1 = panelDate1.getMonth();
        if (panelMonth0 === panelMonth1) {
          panelDate1 = new Date(panelYear0, panelMonth0+1);
          panelYear1 = panelDate1.getFullYear();
          panelMonth1 = panelDate1.getMonth();
        }
      }
      this.$refs['calendarPanelRange0'].calendarYear = panelYear0;
      this.$refs['calendarPanelRange0'].calendarMonth = panelMonth0;
      this.$refs['calendarPanelRange1'].calendarYear = panelYear1;
      this.$refs['calendarPanelRange1'].calendarMonth = panelMonth1;
    },
    stringify(date, format) {
      return formatDate(date, format || this.format);
    },
    parseDate(value, format) {
      return parseDate(value, format || this.format);
    },
    dateEqual(a, b) {
      return isDateObejct(a) && isDateObejct(b) && a.getTime() === b.getTime();
    },
    rangeEqual(a, b) {
      return Array.isArray(a) && Array.isArray(b) && a.length === b.length && a.every((item, index) => this.dateEqual(item, b[index]));
    },
    shortcutClick(curShortcut) {
      if (Array.isArray(curShortcut.value)) {
        this.currentValue = [parseDate(curShortcut.value[0], this.valueFormat), parseDate(curShortcut.value[1], this.valueFormat)];
      } else {
        this.currentValue = [parseDate(curShortcut.value, this.valueFormat), parseDate(curShortcut.value, this.valueFormat)];
      }
      // if (typeof curShortcut.onClick === 'function') {
      //   return curShortcut.onClick(this);
      // }
      this.updateDate(true);
      this.setCalendarPanelRange();
      this.closePopup();
    },
    handleClearClick() {
      this.clearDate();
      this.closePopup();
    },
    clearDate() {
      const date = this.range ? ['', ''] : '';
      this.currentValue = date;
      this.updateDate(true);
      this.$emit('clear', date);
    },
    confirmDate() {
      const valid = this.range ? isValidRange(this.currentValue) : isValidDate(this.currentValue);
      if (valid) {
        this.updateDate(true);
      }
      this.$emit('confirm', this.currentValue);
      this.closePopup();
    },
    updateDate(confirm = false) {
      if ((this.confirm && !confirm) || this.disabled) {
        return false;
      }
      const equal = this.range ? this.rangeEqual(this.value, this.currentValue) : this.dateEqual(this.value, this.currentValue);
      if (equal) {
        return false;
      }
      const tempValue = !this.range ?
      formatDate(this.currentValue, this.valueFormat)
      :[formatDate( this.currentValue[0], this.valueFormat), formatDate( this.currentValue[1], this.valueFormat)];
      this.$emit('input', tempValue);
      this.$emit('change', tempValue);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [tempValue]);
      return true;
    },
    handleValueChange(value) {
      if (!this.range) {
        const tempValue = parseDate(value, this.valueFormat);
        this.currentValue = tempValue&&isValidDate(tempValue) ? new Date(tempValue) : null; // hack yl 20190729
      } else {
        const tempValue = [parseDate(value[0], this.valueFormat), parseDate(value[1], this.valueFormat)];
        this.currentValue = isValidRange(value) ? [new Date(tempValue[0]), new Date(tempValue[1])] : [null, null];
      }
    },
    selectDate(date) {
      this.currentValue = date;
      this.updateDate() && this.closePopup();
    },
    selectDateRange(date) {
      if (this.selectDateRangeCount === 0) {
        this.$set(this.currentValue, 0, date);
        this.$set(this.currentValue, 1, null);
      }
      if (this.selectDateRangeCount === 1) { // 如果是第二次点击(即选择了结束日期),则更新日期
        if (date.getTime() > this.currentValue[0].getTime()) {
          this.$set(this.currentValue, 1, date);
        } else {
          // 如果出现逆向选择周期
          this.$set(this.currentValue, 1, this.currentValue[0]);
          this.$set(this.currentValue, 0, date);
        }
        this.updateDate() && this.closePopup();
      }
      this.selectDateRangeCount++;
    },
    selectTime(time, close) {
      this.currentValue = time;
      this.updateDate() && close && this.closePopup();
    },
    selectStartTime(time) {
      this.selectStartDate(time);
    },
    selectEndTime(time) {
      this.selectEndDate(time);
    },
    showPopup() {
      if (this.disabled) {
        return;
      }
      this.popupVisible = true;
    },
    closePopup() {
      this.popupVisible = false;
    },
    getPopupSize(element) {
      const originalDisplay = element.style.display;
      const originalVisibility = element.style.visibility;
      element.style.display = 'block';
      element.style.visibility = 'hidden';
      const styles = window.getComputedStyle(element);
      const width = element.offsetWidth + parseInt(styles.marginLeft) + parseInt(styles.marginRight);
      const height = element.offsetHeight + parseInt(styles.marginTop) + parseInt(styles.marginBottom);
      const result = {width, height};
      element.style.display = originalDisplay;
      element.style.visibility = originalVisibility;
      return result;
    },
    displayPopup() {
      const dw = document.documentElement.clientWidth;
      const dh = document.documentElement.clientHeight;
      const InputRect = this.$el.getBoundingClientRect();
      const PopupRect = this._popupRect || (this._popupRect = this.getPopupSize(this.$refs.calendar));
      const position = {};
      let offsetRelativeToInputX = 0;
      let offsetRelativeToInputY = 0;
      if (this.appendToBody) {
        offsetRelativeToInputX = window.pageXOffset + InputRect.left;
        offsetRelativeToInputY = window.pageYOffset + InputRect.top;
      }
      if (
        dw - InputRect.left < PopupRect.width &&
        InputRect.right < PopupRect.width
      ) {
        position.left = offsetRelativeToInputX - InputRect.left + 1 + 'px';
      } else if (InputRect.left + InputRect.width / 2 <= dw / 2) {
        position.left = offsetRelativeToInputX + 'px';
      } else {
        position.left = offsetRelativeToInputX + InputRect.width - PopupRect.width + 'px';
      }
      if (
        InputRect.top <= PopupRect.height &&
        dh - InputRect.bottom <= PopupRect.height
      ) {
        position.top = offsetRelativeToInputY + dh - InputRect.top - PopupRect.height + 'px';
      } else if (InputRect.top + InputRect.height / 2 <= dh / 2) {
        position.top = offsetRelativeToInputY + InputRect.height + 'px';
      } else {
        position.top = offsetRelativeToInputY - PopupRect.height + 'px';
      }
      if (position.top !== this.position.top || position.left !== this.position.left) {
        this.position = position;
      }
    },
    handleInput(value, event) {
      this.userInput = value;
    },
    handleChange(event) {
      const value = event.target.value;
      if (this.editable && this.userInput !== null) {
        const calendar = this.range ? this.$refs['calendarPanelRange0'] : this.$refs['calendarPanel'];
        const checkDate = calendar.isDisabledTime;
        if (!value) {
          this.clearDate();
          return;
        }
        if (this.range) {
          const range = value.split(` ${this.rangeSeparator} `);
          if (range.length === 2) {
            const start = this.parseDate(range[0], this.format);
            const end = this.parseDate(range[1], this.format);
            if (start && end && !checkDate(start, null, end) && !checkDate(end, start, null)) {
              this.currentValue = [start, end];
              this.updateDate(true);
              this.closePopup();
              return;
            }
          }
        } else {
          const date = this.parseDate(value, this.format);
          if (date && !checkDate(date, null, null)) {
            this.currentValue = date;
            this.updateDate(true);
            this.closePopup();
            return;
          }
        }
        this.$emit('input-error', value);
      }
    },
  },
};
</script>



