<template>
  <byted-popover
    v-if="showPopover"
    v-model="dragging"
    :width="popperWidth"
    trigger="manual"
    placement="top">
    <span>{{ value }}</span>
    <div
      slot="reference"
      ref="bui-slider-button"
      :class="{'bui-slider-button-active': dragging}"
      :style="wrapperStyle"
      class="bui-slider-button"
      @mousedown="mouseDown"
    />
  </byted-popover>
  <div
    v-else
    ref="bui-slider-button"
    :class="{'bui-slider-button-active': dragging}"
    :style="wrapperStyle"
    class="bui-slider-button"
    @mousedown="mouseDown"
  />
</template>

<script>
import {on, off} from 'utils/dom.js';
import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';
import BytedPopper from 'components/popper';

export default {
  components: {
    BytedPopper,
  },
  mixins: [emitter],
  props: {
    value: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      // auto adaption
      popperWidth: '',
      dragging: false,
      isClick: false,
      currentValue: this.value,
    };
  },
  computed: {
    disabled() {
      return this.$parent.disabled;
    },
    sliderRect() {
      return this.$parent.sliderRect;
    },
    sliderSize() {
      return this.$parent.sliderSize;
    },
    max() {
      return this.$parent.max;
    },
    min() {
      return this.$parent.min;
    },
    step() {
      return this.$parent.step;
    },
    marks() {
      return this.$parent.marks;
    },
    curPosition() {
      return (this.currentValue - this.min) / (this.max - this.min) * 100;
    },
    wrapperStyle() {
      return {
        'left': this.curPosition + '%',
      };
    },
    showPopover() {
      return this.$parent.showPopover;
    },
  },
  watch: {
    value(newVal) {
      this.setValue();
    },
    dragging(val) {
      this.$parent.dragging = val;
    },
  },
  methods: {
    /**
     * 点击滑块，触发拖拽
     */
    mouseDown(evt) {
      evt.preventDefault();
      if (this.disabled) {
        return false;
      }
      this.onDragStart(evt);
      on(window, 'mousemove', this.onDragging);
      on(window, 'mouseup', this.onDragEnd);
    },

    /**
     * 拖拽行为开始
     */
    onDragStart(evt) {
      this.dragging = true;
      this.isClick = true;
    },

    /**
     * 拖拽中，更新滑块按钮的位置
     */
    onDragging(evt) {
      if (this.showPopover) {
        this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.update');
      }

      if (!this.dragging) return;
      this.isClick = false;
      this.$parent.resetSize();
      const currentX = evt.clientX;
      const sliderOffsetLeft = this.sliderRect.left;
      const curPos = currentX - sliderOffsetLeft;
      this.setPosition(curPos);
      this.$emit('input', this.currentValue);
    },

    /**
     * 拖拽行为结束，改变value值
     */
    onDragEnd(evt) {
      this.dragging = false;
      if (this.isClick) return;
      const currentX = evt.clientX;
      const sliderOffsetLeft = this.sliderRect.left;
      const curPos = currentX - sliderOffsetLeft;
      this.setPosition(curPos);
      this.$emit('input', this.currentValue);
      this.$parent.emitChange(this.currentValue);
      off(window, 'mousemove', this.onDragging);
      off(window, 'mouseup', this.onDragEnd);
    },
    /**
     * 设置按钮当前值
     * @param {number} position - 滑块按钮的位置
     */
    setPosition(position) {
      position =
          position < 0
            ? 0
            : position > this.sliderSize ? this.sliderSize : position;
      const diffValue = position / this.sliderSize * (this.max - this.min);
      if (this.step !== null) {
        this.currentValue = (Math.round(diffValue / this.step) * this.step + this.min).toFixed(2) - 0;
      } else {
        const curVal = diffValue + this.min;
        const points = Object.keys(this.marks).map(parseFloat);
        const diffs = points.map((point) => Math.abs(curVal - point));
        const closestPoint = points[diffs.indexOf(Math.min(...diffs))];
        this.currentValue = closestPoint;
      }
    },
    /**
     * 判断当前value值是否在[min,max]范围
     */
    setValue() {
      if (this.value < this.min) {
        this.value = this.min;
      } else if (this.value > this.max) {
        this.value = this.max;
      } else {
        this.currentValue = this.value;
      }
    },
  },
};
</script>



