<template>
  <div
    v-clickoutside="handleClickoutside"
    :class="baseClass">
    <byted-input
      ref="reference"
      :size="size"
      :placeholder="_placeholder"
      :value="value"
      v-bind="$attrs"
      @input="handleInput"
      @focus="handleFocus"
      @blur="handleBlur"
      @keydown.up.prevent="handleKeydown('up')"
      @keydown.down.prevent="handleKeydown('down')"
    >
      <template slot="prefix">
        <slot name="prefix" />
      </template>
      <template slot="suffix">
        <slot name="suffix" />
      </template>
      <template slot="append">
        <slot name="append" />
      </template>
      <template slot="prepend">
        <slot name="prepend" />
      </template>
    </byted-input>
    <byted-popper
      v-show="popperVisible"
      :append-to-body="true"
      :style="popperStyle"
      :reference="reference"
    >
      <list-item
        ref="listItem"
        :data-source="dataSource"
        :infinite="infinite"
        :max-height="maxHeight"
        @loadmore="$emit('loadmore')"
        @select="handleSelect"
      >
        <span
          slot="dataSource"
          slot-scope="{item}">
          <slot
            :item="item"
            name="dataSource">
            {{ item[textKey] || item }}
          </slot>
        </span>
        <template slot="loading">
          <slot name="loading" />
        </template>
      </list-item>
    </byted-popper>
  </div>
</template>

<script>
import emitter from 'mixins/emitter';
import locale from 'mixins/locale';
import {hasClass} from 'utils/dom.js';
import BytedIcon from 'components/icon';
import BytedInput from 'components/input';
import BytedPopper from 'components/popper';
import clickoutside from 'directives/clickoutside';
import {isOneOf} from 'utils/helper.js';
import {CSS_PREFIX, LOCALE_PREFIX} from '../../base.js';
import ListItem from './list-item.vue';
import InfiniteScroll from '../../infinite-scroll';

export default {
  name: `${CSS_PREFIX}auto-complete`,
  directives: {clickoutside},
  components: {
    ListItem,
    BytedIcon,
    BytedInput,
    BytedPopper,
    InfiniteScroll,
  },
  mixins: [emitter, locale],
  props: {
    placeholder: {
      type: String,
      default: '',
    },
    value: {
      type: String,
      default: '',
    },
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, ['md', 'small', 'xs', 'sm', 'lg', 'xl']);
      },
    },
    dataSource: {
      type: Array,
      default: () => [],
    },
    open: {
      type: Boolean,
      default: false,
    },
    infinite: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      // 如果 dataSource 传入为 [object], object 需要是 { value, text } 形状
      valueKey: 'value',
      textKey: 'text',
      reference: null,
      popperStyle: {
        width: 0,
      },
      popperVisible: this.open,
      maxHeight: 192,
    };
  },
  computed: {
    _placeholder() {
      return this.placeholder || this.t(`${LOCALE_PREFIX}.auto-complete.placeholder`);
    },
    _emptyTip() {
      return this.t(`${LOCALE_PREFIX}.auto-complete.emptyTip`);
    },
    baseClass() {
      return `${CSS_PREFIX}auto-complete`;
    },
  },
  watch: {
    popperVisible(val) {
      if (this.$listeners['update:open']) {
        this.$emit('update:open', val);
      }
      val ?
        this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.update') :
        this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.destroy');
      this.$emit('visible-change', val);
    },
    open(val) {
      this.togglePopperShow(val);
    },
  },
  mounted() {
    this.reference = this.$refs.reference;
    this.$nextTick(() => {
      this.setPopperStyle();
    });
  },
  methods: {
    setPopperStyle() {
      if (this.reference.$el) {
        const offsetWidth = this.reference.$el.offsetWidth;
        this.popperStyle.width = offsetWidth ? offsetWidth + 'px' : '';
      }
    },
    togglePopperShow(value) {
      this.popperVisible = value;
      if (this.popperVisible && !this.popperStyle.width) {
        this.setPopperStyle();
      }
    },
    handleInput(val) {
      this.$emit('input', val);
      this.$emit('search', val);
      this.togglePopperShow(true);
      this.$refs.listItem.resetFocusIndex();
    },
    handleSelect(value, item) {
      this.togglePopperShow(false);
      value = item[this.valueKey] || value;
      this.$emit('input', value);
      this.$emit('select', value, item);
    },
    handleClickoutside(e) {
      if (!this.popperVisible) {
        return;
      }
      const target = e.target;
      let parent = target.parentNode;
      while (parent && !hasClass(parent, 'byted-auto-complete-content')) {
        parent = parent.parentNode;
      }
      if (!parent) {
        this.togglePopperShow(false);
      }
    },
    handleFocus(evt) {
      this.$emit('focus', evt);
    },
    handleBlur(evt) {
      this.$emit('blur', evt);
    },
    resetLoading() {
      this.$refs.listItem && this.$refs.listItem.resetLoading();
    },
    handleKeydown(direction) {
      if (!this.popperVisible) return;
      this.$refs.listItem.focus(direction);
    },
  },
};
</script>



