import clickoutside from 'directives/clickoutside';
import {hasClass} from 'utils/dom.js';
import {DEFAULT_TIME, PANEL_NAME_MAP, DEFAULT_FORMAT} from './../const';
import {LOCALE_PREFIX, CSS_PREFIX} from '../../../base';

export default {
  directives: {clickoutside},
  data() {
    return {
      panelShow: false,
      popperRef: null,
      innerValue: [],
      hoverValue: [],
      viewDate: this.getNow(),
      // 当前激活的input索引
      activePickerInputIndex: 0,
      panelName: null,
      PANEL_NAME_MAP,
      // 从开始输入开启
      clickFromStart: true,
    };
  },
  computed: {
    baseClass() {
      const classArr = [`${CSS_PREFIX}date-picker`];
      if (this.disabled) {
        classArr.push(`bui-date-picker-disabled`);
      }
      classArr.push(`bui-date-picker-${this.size}`);
      return classArr;
    },
    // datepicker placeholder
    _placeholder() {
      let placeholder = this.placeholder || this.t(`${LOCALE_PREFIX}.date-picker.placeholder`);
      if (this.hoverValue.length) {
        const hover = this.getFormat(this.hoverValue[0], this.format);
        placeholder = this.multiple ? this.innerValue.map((date) => date && this.getFormat(date, this.format)).concat(hover).join(',') : hover;
      }
      return placeholder;
    },
    isPanelExtraFooterShow() {
      return this.$slots && !!this.$slots.panelExtraFooter;
    },
    // 是否为多选
    isMultiple() {
      return this.multiple && this.type === 'date';
    },
    _clearText() {
      return this.t(`${LOCALE_PREFIX}.date-picker.clearText`);
    },
    _okText() {
      return this.t(`${LOCALE_PREFIX}.date-picker.okText`);
    },
    range() {
      return this.$options.name.indexOf('range') > 0;
    },
    // 是否显示快捷选项
    isShortcutsShow() {
      return (this.shortcuts && this.shortcuts.length) || this.$slots && this.$slots.shortcuts;
    },
    // time picker
    isTimePicker() {
      return this.type === 'time';
    },

    // 面板内的确认和清空按钮是否显示
    isFooterBtnShow() {
      return (this.multiple && this.type === 'date')
        || ['datetime', 'time'].indexOf(this.type) > -1;
    },
    isConfirmBtnDisabled() {
      // 日期多选
      if (this.multiple) {
        return !this.innerValue || this.innerValue && !this.innerValue.length;
      }
      // time picker range 需要两个值都为合法值,f否则禁用确认
      if (this.isTimePicker && this.range) {
        return !(this.innerValue.length >1 && this.innerValue.every((item) => item && this.isValid(item)));
      }
      const currentValue = this.innerValue[this.activePickerInputIndex];
      return !currentValue || currentValue && !this.isValid(currentValue);
    },
    innerValueFormat() {
      return this.hoverValue.length ? '' : this.innerValue.map((date) => {
        return date ? this.getFormat(date, this.format) : '';
      }).join(this.multiple ? ',' : '');
    },
  },
  watch: {
    panelShow(newVal) {
      newVal
        ? this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.update')
        : this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.destroy');
      this.$emit('visible-change', newVal);
    },
    value() {
      this.handleValueChange();
    },
  },
  created() {
    this.panelName = PANEL_NAME_MAP[this.type] || PANEL_NAME_MAP.default;
    this.handleValueChange();
  },

  mounted() {
    this.popperRef = this.$refs.popperRef;
  },
  methods: {
    // -----expose api----
    togglePopperShow(value) {
      this.handlePickerPanelShow(value);
    },
    handleShortcutSelect(value) {
      let innerValue = [];
      if (Array.isArray(value)) {
        innerValue = value.map((date)=>this.getParseValue(date));
      } else {
        innerValue = [this.getParseValue(value)];
      }
      this.handleEmitChange(innerValue.map((date) => {
        return this.getFormat(date, this.valueFormat);
      }));
      this.handlePickerPanelShow(false);
    },
    handlePickerPanelShow(open) {
      this.panelShow = open;
    },
    handleClickOutside(evt) {
      if (!this.panelShow) {
        return;
      }
      const target = evt.target;
      // 排除popper点击，待优化
      let parent = target.parentNode;
      while (parent && !hasClass(parent, 'bui-picker-popper')) {
        parent = parent.parentNode;
      }
      if (!parent) {
        this.handleValueChange();
        this.handlePickerPanelShow(false);
        this.setActivePickerInputIndex(0);
        this.setClickFromStart(true);
      }
    },
    setActivePickerInputIndex(index) {
      this.activePickerInputIndex = index;
    },
    setClickFromStart(flag) {
      this.clickFromStart = flag;
    },
    getParseValue(value) {
      const parseValue = this.parse(value, this.valueFormat, true);
      if (this.isValid(parseValue)) {
        return parseValue;
      }
      if (this.isTimePicker && value) {
        const defaultDate = this.dayjs(this.defaultDate);
        let date = this.isValid(defaultDate) ? this.getFormat(defaultDate, DEFAULT_FORMAT) : this.getFormat(this.getNow(), DEFAULT_FORMAT);
        date = this.dayjs(`${date} ${value}`);
        if (this.isValid(date)) {
          return date;
        }
      }
      if (value) {
        console.error('[BUI error] value parse return inValid value, please check value and valueFormat');
      }
      return '';
    },
    // value变化时同步内部用的数据
    handleValueChange() {
      // range or multiple
      if ((this.range || this.multiple) && !Array.isArray(this.value)) {
        console.error('[BUI error] value should be array');
      }
      // setInnerValue
      if (Array.isArray(this.value)) {
        this.innerValue = this.value.map((date) => {
          date = this.getParseValue(date);
          return date;
        });
      } else {
        // 单选
        this.innerValue = this.value ? [this.getParseValue(this.value)] : [];
      }
      // set viewDate
      if (!this.range) {
        const last = this.innerValue.length ? this.innerValue[this.innerValue.length - 1] : '';
        const viewDate = last || this.getInitialDate();
        this.setPickerViewDate(viewDate);
      } else {
        const first = this.innerValue[0];
        const viewDate = first ? first : this.getInitialDate();
        this.setPickerViewDate(viewDate);
      }
      this.handleHoverValueChange([]);
    },
    // 获取初始化 viewDate
    getInitialDate() {
      const defaultDate = this.dayjs(this.defaultDate);
      let date = this.isValid(defaultDate) ? defaultDate : this.getNow();
      const defaultTimeArr = DEFAULT_TIME.split(':');
      const time = this.defaultTime ? this.defaultTime.split(':') : defaultTimeArr;
      date = this.setHour(date, time[0] || defaultTimeArr[0]);
      date = this.setMinute(date, time[1] || defaultTimeArr[1]);
      date = this.setSecond(date, time[2] || defaultTimeArr[2]);
      return date;
    },
    handleEmitChange(value) {
      this.$emit('input', value);
      this.$emit('change', value);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [value]);
    },
    handlePanelChange(targetType) {
      this.panelName = PANEL_NAME_MAP[targetType];
    },
    handlePickerInnerValueChange(date) {
      this.handleHoverValueChange([]);
      if (!this.isFooterBtnShow) {
        // 关闭弹出框
        this.handlePickerPanelShow(false);
        this.handleEmitChange(this.getFormat(date, this.valueFormat));
        return;
      }
      this.setPickerInnerValue(date);
    },
    setPickerInnerValue(date) {
      this.handleHoverValueChange([]);
      if (Array.isArray(date)) {
        this.innerValue = date;
        return;
      }
      if (!date) {
        this.innerValue = [];
        return;
      }
      if (this.isMultiple) {
        const findIndex = this.innerValue.findIndex((item) => this.isSame(item, date));
        if (findIndex > -1) {
          this.innerValue.splice(findIndex, 1);
        } else {
          this.innerValue.push(date);
        }
        return;
      } else {
        this.innerValue = [date];
      }
    },
    handlePickerViewDateChange(date) {
      this.setPickerViewDate(date);
    },
    setPickerViewDate(date) {
      this.viewDate = date;
    },
    // 清空
    handleClear() {
      const newValue = this.isMultiple ? [] : '';
      this.setPickerInnerValue(newValue);
      this.handleEmitChange(newValue);
      this.$emit('clear', newValue);
      this.handlePickerPanelShow(false);
    },
    handleConfirm() {
      const newValue = this.innerValue.map((date) => date && this.getFormat(date, this.valueFormat));
      this.handleEmitChange(this.range || this.isMultiple ? newValue : newValue[0]);
      this.handlePickerPanelShow(false);
    },
    handleHoverValueChange(value) {
      this.hoverValue = value;
    },
  },
};



