<template>
  <div
    :class="{active:active}"
    class="bui-tab-pane">
    <slot/>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';

export default {
  name: `${CSS_PREFIX}tab-pane`,
  props: {
    label: {
      type: String,
      default: '',
      require: true,
    },
    name: {
      type: [String, Number],
      default: '',
      require: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      active: false,
    };
  },
  watch: {
    name: function(newVal) {
      this.active = this.$parent.value === newVal;
    },
  },
  mounted() {
    this.$parent.addPanes(this);
  },
  destroyed() {
    this.$parent.removePanes(this);
  },
  methods: {},
};
</script>




