<template>
  <div
    :class="baseClass">
    <label
      v-if="hasLabel"
      :class="labelWidthClass"
      :style="labelStyle"
      class="bui-form-item-label">
      <slot name="label">
        <span class="bui-form-item-label-text">{{ label }}</span>
      </slot>
    </label>
    <template v-if="isHor">
      <div
        :class="inputClass"
        :style="inputStyle">
        <slot/>
      </div>
    </template>
    <template v-else>
      <div
        :class="inputClass">
        <slot/>
      </div>
    </template>
    <span
      v-if="showMessage && validateMessage.length!==0"
      :class="messageClass"
      :style="inputStyle"
      class="bui-form-item-help"
    >
      {{ validateMessage }}
    </span>
  </div>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';
import AsyncValidator from 'async-validator';

export default {

  name: `${CSS_PREFIX}form-item`,

  mixins: [emitter],
  props: {
    // 标签文本
    label: {
      type: String,
      default: '',
    },
    // 表单的 model 中的字段，校验与重置实现使用
    prop: {
      type: String,
      default: '',
    },
    // 此表单项的校验规则
    rules: {
      type: [Object, Array],
      default() {
        return [];
      },
    },
    // 是否显示校验错误提示
    showMessage: {
      type: Boolean,
      default: true,
    },
    required: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      initialValue: null,
      validateState: '', // doing,success,error
      validateMessage: '',
      validateDisabled: false,
    };
  },

  computed: {
    baseClass: function() {
      const classArr = [`${CSS_PREFIX}form-item`];
      if (this.validateMessage.length!==0) {
        classArr.push(`${CSS_PREFIX}form-item-error`);
      }
      if (this.required) {
        classArr.push(`${CSS_PREFIX}form-item-required`);
      }
      if (this.tooltip) {
        classArr.push(`${CSS_PREFIX}form-item-tooltip`);
      }
      return classArr;
    },
    hasLabel() {
      return this.label || this.$slots.label;
    },
    hasLabelCol() {
      return this.form.labelWidth && Number(this.form.labelWidth) > 0 && Number(this.form.labelWidth) < 24;
    },
    parentLabelWidth() {
      return this.form.labelWidth;
    },
    isHor() {
      return this.form.layoutType === 'horizontal';
    },
    labelWidthClass() {
      const classArr = [];
      if (this.isHor && this.hasLabelCol) {
        classArr.push(`${CSS_PREFIX}col-span-${this.parentLabelWidth}`);
      }
      if (this.form.labelAlign) {
        classArr.push(`bui-form-item-label-${this.form.labelAlign}`);
      }
      return classArr;
    },
    inputClass() {
      const classArr = [];
      if (this.hasLabelCol && this.isHor) {
        this.addLabelColClass(classArr);
      }
      if (this.validateMessage.length !== 0) {
        classArr.push(`${CSS_PREFIX}form-item-error-input`);
      }
      return classArr;
    },
    messageClass() {
      const classArr = [];
      if (this.hasLabelCol) {
        this.addLabelColClass(classArr);
      }
      if (this.hasLabelCol) {
        classArr.push(`${CSS_PREFIX}col-offset-${this.parentLabelWidth}`);
      }
      return classArr;
    },
    labelStyle() {
      if (!this.hasLabelCol) {
        return {
          width: this.form.labelWidth,
        };
      }
      return {};
    },
    inputStyle() {
      if (!this.hasLabelCol) {
        return {
          marginLeft: this.form.labelWidth,
        };
      }
      return {};
    },
    // 表单项的值
    itemValue: {
      cache: false,
      get() {
        const model = this.form.model;
        if (!model || !this.prop) {
          return;
        }

        const path = this.prop;
        const pathArr = path.split('.');
        let obj = model;
        for (let i = 0, len = pathArr.length; i < len; i++) {
          obj = obj[pathArr[i]];
        }
        return obj;
      },
    },
    // 表单父组件
    form() {
      let parent = this.$parent;
      while (parent.$options.name !== `${CSS_PREFIX}form`) {
        parent = parent.$parent;
      }
      return parent;
    },
  },
  mounted() {
    if (this.prop) {
      this.dispatch(`${CSS_PREFIX}form`, 'bui.formItem.addItem', [this]);
      this.initialValue = this.itemValue;
      const rules = this.getRules();
      if (rules.length !== 0) {
        // 接受从表单组件传来的事件
        this.$on('bui.form.blur', this.onItemBlur);
        this.$on('bui.form.change', this.onItemChange);
      }
    }
  },
  beforeDestroy() {
    if (this.prop) {
      this.dispatch(`${CSS_PREFIX}form`, 'bui.formItem.removeItem', [this]);
    }
  },
  methods: {
    addLabelColClass(classArr) {
      const width = 24 - this.parentLabelWidth;
      classArr.push(`${CSS_PREFIX}col-span-${width}`);
    },
    onItemBlur() {
      this.validate('blur');
    },
    onItemChange() {
      this.form.$emit('change', this.prop, this.itemValue);
      if (this.validateDisabled) {
        this.validateDisabled = false;
        return;
      }
      this.validate('change');
    },
    validate(trigger, cb) { // trigger 只有 blur change
      cb = cb || function() { };
      const rules = this.filterRuleByTrigger(trigger); // 依据触发情况，筛选触发条件
      if (!rules || rules.length === 0) {
        cb(true);
        return true;
      }
      this.validateState = 'doing';
      const ruleDesc = {};
      ruleDesc[this.prop] = rules;
      const validator = new AsyncValidator(ruleDesc);
      const model = {};
      model[this.prop] = this.itemValue;
      validator.validate(model, {firstFields: true}, (errors, item) => {
        const valid = !errors;
        this.validateState = !errors ? 'success' : 'error';
        this.validateMessage = errors ? errors[0].message : '';
        cb(valid, this.validateMessage, this.prop); // 给回调函数返回三个参数 （是否通过,报错信息,字段名）
      });
    },
    getRules() {
      // 深拷贝一份rules
      return [].concat(this.rules || []);
    },
    // 依据触发条件，筛选校验规则
    filterRuleByTrigger(trigger) {
      const rules = this.getRules();
      return rules.filter((rule) => {
        return !rule.trigger || rule.trigger.indexOf(trigger) !== -1;
      });
    },
    // 清除校验结果
    resetValidation() {
      this.validateState = '';
      this.validateMessage = '';
      this.validateDisabled = false;
    },
    reset() {
      this.resetValidation();

      const model = this.form.model;
      const value = this.itemValue;

      if (Array.isArray(value) && value.length > 0) {
        model[this.prop] = [].concat(this.initialValue); // 对象就深拷贝
      } else if (typeof value !== 'undefined') {
        model[this.prop] = this.initialValue;
      }
    },
  },
};
</script>



