<template>
  <div :class="className">
    <div
      ref="slider"
      class="bui-slider-container">
      <slider-button
        ref="button1"
        v-model="firstValue"
      />
      <slider-button
        v-if="range"
        ref="button2"
        v-model="secondValue"
      />
      <div
        :style="sliderActiveBarStyle"
        class="bui-slider-active"
        refs="slider-active"
      />
      <template v-if="showStops">
        <div
          v-for="(item, key) in stops"
          :key="key"
          :style="{ left: item + '%' }"
          :class="['bui-slider-stop', {'bui-slider-stop-active': judgeStopIsActive(item)}]"
        />
      </template>
      <template v-if="markList.length">
        <div
          v-for="item in markList"
          :key="item.key"
          :style="{ left: item.position + '%' }"
          :class="['bui-slider-stop', {'bui-slider-stop-active': judgeStopIsActive(item.position)}]"
        />
        <div class="bui-slider-mark">
          <div
            v-for="item in markList"
            :key="item.key"
            :style="{ left: item.position + '%' }"
            class="bui-slider-mark-text">
            {{ item.value }}
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import SliderButton from './button.vue';
import {CSS_PREFIX} from '../../base.js';
import {on, off} from 'utils/dom.js';

export default {
  name: `${CSS_PREFIX}slider`,
  components: {
    SliderButton,
  },
  props: {
    value: {
      type: [Number, Array],
      default: 0,
    },
    min: {
      type: Number,
      default: 0,
    },
    max: {
      type: Number,
      default: 100,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    step: {
      type: [Number, null],
      default: 1,
      validator(value) {
        if (value === 0) {
          console.error('[BUI Error][Slider]step should not be 0.');
          return false;
        }
        return true;
      },
    },
    showStops: {
      type: Boolean,
      default: false,
    },
    range: {
      type: Boolean,
      default: false,
    },
    showPopover: {
      type: Boolean,
      default: true,
    },
    marks: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      dragging: false,
      sliderSize: 1,
      firstValue: null,
      secondValue: null,
    };
  },
  computed: {
    className() {
      const className = [`${CSS_PREFIX}slider`];
      if (this.disabled) {
        className.push(`${CSS_PREFIX}slider-disabled`);
      }
      return className;
    },
    sliderActiveBarLeft() {
      return this.range
        ? (this.minValue - this.min) / (this.max - this.min) * 100
        : 0;
    },
    sliderActiveBarWidth() {
      return this.range
        ? (this.maxValue - this.minValue) / (this.max - this.min) * 100
        : (this.firstValue - this.min) / (this.max - this.min) * 100;
    },
    sliderActiveBarStyle() {
      return {
        'left': this.sliderActiveBarLeft + '%',
        'width': this.sliderActiveBarWidth + '%',
      };
    },
    sliderRect() {
      const sliderRef = this.$refs.slider || {};
      return sliderRef.getBoundingClientRect();
    },
    stops() {
      if (!this.showStops || this.min > this.max || this.step === null) return [];
      const stopsCount = (this.max - this.min) / this.step;
      const stepWidth = this.step / (this.max - this.min) * 100;
      const result = [];
      for (let i=0; i<=stopsCount; i++) {
        result.push(stepWidth * i);
      }
      return result;
    },
    minValue() {
      return Math.min(this.firstValue, this.secondValue);
    },
    maxValue() {
      return Math.max(this.firstValue, this.secondValue);
    },
    markList() {
      const marksKeys = Object.keys(this.marks);
      if (!marksKeys.length) {
        return [];
      }
      if (marksKeys.some((key) => isNaN(key))) {
        console.warn('[BUI Error][Slider]the key of marks should be a number.');
        return [];
      }
      return marksKeys
          .sort((a, b) => a-b)
          .filter((key) => key >= this.min && key <= this.max)
          .map((key) => ({
            key,
            position: (key - this.min) / (this.max - this.min) * 100,
            value: this.marks[key],
          }));
    },
  },
  watch: {
    value(newVal) {
      if (this.dragging) return;
      this.setValue();
    },
    // 拖动结束后重置value
    dragging(newVal) {
      if (!newVal) {
        this.setValue();
      }
    },
    firstValue(newVal) {
      if (this.range) {
        this.$emit('input', [this.minValue, this.maxValue]);
      } else {
        this.$emit('input', newVal);
      }
    },
    secondValue(newVal) {
      if (this.range) {
        this.$emit('input', [this.minValue, this.maxValue]);
      }
    },
  },
  mounted() {
    if (this.range) {
      if (Array.isArray(this.value)) {
        this.firstValue = Math.max(this.min, this.value[0]);
        this.secondValue = Math.min(this.max, this.value[1]);
      } else {
        this.firstValue = this.min;
        this.secondValue = this.max;
      }
    } else {
      this.setValue();
    }
    this.resetSize();
    on(window, 'resize', this.resetSize);
  },
  beforeDestroy() {
    off(window, 'resize', this.resetSize);
  },
  methods: {
    /**
     * 判断当前stops时候active
     */
    judgeStopIsActive(item) {
      return item >= this.sliderActiveBarLeft && item <= this.sliderActiveBarLeft + this.sliderActiveBarWidth;
    },
    /**
     * 当窗口变化时重新设置滑块尺寸
     */
    resetSize() {
      if (this.$refs.slider) {
        this.sliderSize = this.$refs.slider['clientWidth'];
      }
    },
    /**
     * 判断当前value值是否在[min,max]范围
     */
    setValue() {
      if (this.range && Array.isArray(this.value)) {
        if (this.value[1] < this.min) {
          this.$emit('input', [this.min, this.min]);
        } else if (this.value[0] > this.max) {
          this.$emit('input', [this.max, this.max]);
        } else if (this.value[0] < this.min) {
          this.$emit('input', [this.min, this.value[1]]);
        } else if (this.value[1] > this.max) {
          this.$emit('input', [this.value[0], this.max]);
        } else {
          this.firstValue = this.value[0];
          this.secondValue = this.value[1];
        }
      } else if (!this.range && !isNaN(this.value)) {
        if (this.value < this.min) {
          this.$emit('input', this.min);
        } else if (this.value > this.max) {
          this.$emit('input', this.max);
        } else {
          this.firstValue = this.value;
        }
      }
    },
    /**
     * 向父组件传递change后的值
     */
    emitChange(currentValue) {
      this.$emit('change', this.range ? [this.minValue, this.maxValue] : currentValue);
    },
  },
};
</script>



