<template>
  <header :class="baseClass">
    <slot/>
  </header>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';

export default {
  name: `${CSS_PREFIX}layout-header`,
  computed: {
    baseClass() {
      return `${CSS_PREFIX}layout-header`;
    },
  },
};
</script>



