<template>
  <div :class="baseClass">
    <slot/>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';

export default {
  name: `${CSS_PREFIX}checkbox-group`,

  mixins: [emitter],
  props: {
    value: {
      type: Array,
      default() {
        return [];
      },
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isCheckboxButton: false,
    };
  },
  computed: {
    baseClass: function() {
      const classArr = [`${CSS_PREFIX}checkbox-group`];
      if (this.isCheckboxButton) {
        classArr.push('btn-group');
      }
      return classArr;
    },
  },
  mounted() {
    if (this.$children[0] && this.$children[0].$options.name === `${CSS_PREFIX}checkbox-button`) {
      this.isCheckboxButton = true;
    }
  },

  created() {
    this.$on('bui.checkbox.change', this.checkboxChange);
  },
  methods: {
    checkboxChange({val, checked}) {
      const arr = this.value.slice();
      checked ? arr.push(val) : arr.splice(arr.indexOf(val), 1);
      this.$emit('input', arr);
      this.$emit('change', arr);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [arr]);
    },
  },
};
</script>



