<template>
  <div class="bui-shortcuts-panel">
    <div
      v-for="(item, index) in value"
      :key="index"
      class="bui-shortcut-item"
      @click="handleShortcutClick(item)">
      {{ item.text }}
    </div>
  </div>
</template>
<script>
export default {
  name: 'Shortcuts',
  props: {
    value: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  methods: {
    handleShortcutClick(item) {
      if (item.value) this.$emit('select', item.value);
      if (item.onClick) {
        item.onClick(this);
        this.$emit('visible-change', false);
      }
    },
  },
};
</script>



