<template>
  <div :class="getCellClass(column)">
    <template v-if="extra.render">
      <expand
        :row="column"
        :render="extra.render"
        :column="column"
        :index="columnIndex"
      />
    </template>
    <template v-else>
      {{ extra.title }}
    </template>
  </div>
</template>
<script>
import Expand from './expand.vue';
export default {
  name: 'HeadExtraCell',
  components: {Expand},
  props: {
    data: {
      type: Array,
      default() {
        return [];
      },
    },
    extra: {
      type: Object,
      default() {
        return {};
      },
    },
    columnIndex: {
      type: Number,
      default: -1,
    },
  },
  computed: {
    column() {
      return this.extra.column;
    },
  },
  methods: {
    getCellClass(column) {
      const classArr = ['bui-table-ceil', 'bui-table-summary-ceil'];
      if (column && column.align === 'right') {
        classArr.push('bui-table-ceil-right');
      }
      if (column && column.align === 'center') {
        classArr.push('bui-table-ceil-center');
      }
      return classArr.join(' ');
    },
  },
};
</script>



