<template>
  <div
    :disabled="disabled"
    :class="baseClass"
    role="link"
    @click="handleClick"><slot/></div>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import {isOneOf} from 'utils/helper.js';
export default {
  name: `${CSS_PREFIX}link`,

  props: {
    size: {
      type: String,
      validator(value) {
        return isOneOf(value, ['default', 'small', 'large', 'lg', 'md', 'sm']);
      },
      default: 'md',
    },

    disabled: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    baseClass: function() {
      const classArr = [`${CSS_PREFIX}link`];
      const sizeObj = {
        small: 'byted-link-sm',
        large: 'byted-link-lg',
        lg: 'byted-link-lg',
        md: 'byted-link-md',
        sm: 'byted-link-sm',
        default: 'byted-link-md',
      };
      if (this.size) {
        classArr.push(sizeObj[this.size]);
      }
      return classArr;
    },
  },

  methods: {
    handleClick: function(evt) {
      if (!this.disabled) {
        this.$emit('click', evt);
      }
    },
  },
};
</script>



