<script>
import {CSS_PREFIX} from '../../base.js';
import CommonLogic from './common-logic';
import emitter from 'mixins/emitter';
import BytedPopper from 'components/popper';

export default {
  name: `${CSS_PREFIX}menu-item`,
  components: {[BytedPopper.name]: BytedPopper},
  mixins: [CommonLogic, emitter],
  props: {
    index: {
      default: null,
      validator: (val) => typeof val === 'string' || val === null,
    },
    route: {
      type: [String, Object],
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      popperVisible: false,
      popperEl: null,
    };
  },
  computed: {
    active() {
      return this.index === this.rootMenu.activeIndex;
    },
    baseClass() {
      const classArr = [`${CSS_PREFIX}menu-item`, 'bui-menu-ul'];
      if (this.rootMenu.theme === 'light') {
        classArr.push('bui-menu-item-light');
      }
      if (this.rootMenu.theme === 'dark') {
        classArr.push('bui-menu-item-dark');
      }
      if (this.active) {
        classArr.push('bui-menu-item-active');
      }
      if (this.disabled) {
        classArr.push('bui-menu-item-disabled');
      }
      return classArr;
    },
  },
  watch: {
    popperVisible(newVal) {
      newVal ?
        this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.update') :
        this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.destroy');
    },
  },
  mounted() {
    this.parentMenu.addItem(this);
    this.rootMenu.addItem(this);
    this.popperEl = this.$refs.popperRef;
  },
  beforeDestroy() {
    this.parentMenu.removeItem(this);
    this.rootMenu.removeItem(this);
  },
  methods: {
    onMouseEnter() {
      this.popperVisible = true;
      this.$emit('item-mouseenter', this);
    },
    onMouseLeave() {
      this.popperVisible = false;
      this.$emit('item-mouseleave', this);
    },
    handleClick() {
      if (!this.disabled) {
        this.dispatch(`${CSS_PREFIX}menu`, 'item-click', this);
        this.$emit('click', this);
      }
    },
  },
  render(h) {
    const {paddingStyle, rootMenu, handleClick, onMouseEnter, onMouseLeave, $slots, baseClass, parentMenu, popperVisible, popperEl} = this;
    return (
      <li
        ref="popperRef"
        style={paddingStyle}
        class={baseClass}
        on-click={handleClick}
        on-mouseenter={onMouseEnter}
        on-mouseleave={onMouseLeave}
      >
        {
          parentMenu.$options.name === `${CSS_PREFIX}menu` && rootMenu.collapse && $slots.title ?
            [
              <div class="bui-menu-item-icon">{$slots.default}</div>,
              <byted-popper
                v-show={popperVisible}
                reference={popperEl}
                append-to-body={rootMenu.transfer}
                placement="right-middle"
                class={['bui-menu-item-popper', `bui-menu-item-popper-${rootMenu.theme}`]}
              >
                {$slots.title}
              </byted-popper>,
            ]
            :
            [$slots.default, $slots.title]
        }
      </li>
    );
  },
};
</script>



