<template>
  <byted-button
    :class="baseClass"
    :disabled="isDisabled"
    :tag="tag"
    plain
    type="primary"
    @click="onClick">
    <slot/>
  </byted-button>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';
import BytedButton from 'components/button';

export default {
  name: `${CSS_PREFIX}radio-button`,

  components: {
    BytedButton,
  },

  mixins: [emitter],

  props: {
    val: {
      type: [Boolean, String, Number],
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    tag: {
      type: String,
      default: 'button',
    },
  },
  computed: {
    baseClass: function() {
      const classArr = [`${CSS_PREFIX}radio-button`];
      if (this.isActive) {
        classArr.push('active');
      }
      return classArr;
    },
    radioGroup() {
      const parent = this.$parent;
      return parent;
    },
    isGroup() {
      return this.radioGroup !== null;
    },
    isDisabled() {
      if (this.radioGroup.disabled || this.disabled) {
        return true;
      } else {
        return false;
      }
    },
    isActive() {
      if (this.radioGroup.value == this.val) {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    onClick(e) {
      if (this.isDisabled) {
        return false;
      }
      this.$emit('click', e);
      if (this.isActive) {
        return;
      }
      this.$emit('change', this.val);
      if (this.isGroup) {
        this.dispatch(`${CSS_PREFIX}radio-group`, 'bui.radio.change', {
          val: this.val,
          checked: true,
        });
      }
    },
  },
};
</script>



