<template>
  <div :class="baseClass">
    <div class="bui-rate-wrapper">
      <span
        v-for="(item, index) in max"
        :key="index"
        class="bui-rate-item"
        @mousemove="handleMouseMove(index)"
        @mouseleave="handleMouseLeave(index)"
        @click="handleClick(index)">
        <byted-icon
          :name="Number(item) <= currentValue ? filledIconName : emptyIconName"
          :class="{
            'bui-rate-icon-fill': item <= currentValue,
            'bui-rate-icon-default': item > currentValue,
        }"/>
        <span
          class="bui-rate-item-half"
          @mousemove.stop="handleMouseMove(index, true)"
          @click.stop="handleClick(index, true)">
          <byted-icon
            :name="item - 0.5 <= currentValue ? filledIconName : emptyIconName"
            :class="{
              'bui-rate-icon-fill': item - 0.5 <= currentValue,
              'bui-rate-icon-default': item - 0.5 > currentValue,
          }"/>
        </span>
      </span>
    </div>
    <!--<span-->
    <!--v-if="value && showText"-->
    <!--:style="{color: rateColor.fill}"-->
    <!--class="bui-rate-value">{{ value }}</span>-->
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import {isOneOf} from 'utils/helper.js';
import emitter from 'mixins/emitter';
import BytedIcon from 'components/icon';

export default {
  name: `${ CSS_PREFIX }rate`,
  name: `${ CSS_PREFIX }rate`,
  components: {
    BytedIcon,
  },
  mixins: [emitter],
  props: {
    max: {
      // 最大值
      type: Number,
      default: 5,
    },
    value: {
      type: Number,
      default: 0,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    // showText: {
    //   // 是否显示值
    //   type: Boolean,
    //   default: false,
    // },
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, ['xl', 'lg', 'md', 'sm']);
      },
    },
  },

  data() {
    return {
      emptyIconName: 'star-empty',
      filledIconName: 'star-filled',
      rateColor: {
        default: '#e9e9e9',
        fill: '#f5a623',
      },
      currentValue: this.value,
    };
  },

  computed: {
    baseClass() {
      const buiClass = 'bui-rate';
      const classes = [`${ CSS_PREFIX }rate`, buiClass];
      if (this.size) {
        classes.push(`${buiClass}-${this.size}`);
      }
      if (this.disabled) {
        classes.push(`${buiClass}-disabled`);
      }
      return classes;
    },
  },
  watch: {
    value() {
      this.currentValue = this.value;
    },
  },

  methods: {
    handleMouseMove(index, isHalf) {
      if (this.disabled) return;
      if (isHalf) {
        this.currentValue = index + 0.5;
      } else {
        this.currentValue = index + 1;
      }
    },
    handleMouseLeave(index) {
      if (this.disabled) return;
      this.currentValue = this.value;
    },
    handleClick(index, isHalf) {
      if (this.disabled) return;
      const value = isHalf ? index + 0.5 : index + 1;
      this.currentValue = value;
      this.$emit('input', value);
      this.$emit('change', value);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [value]);
    },
  },
};
</script>




