<template>
  <div
    :class="baseClass"
    :style="{ fill: color, width: width,height:height }"
    @click="handleClick"
    v-html="content"/>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import BuiBasicIcon from './../bui-icon-addon.js';

export default {
  name: `${CSS_PREFIX}icon`,

  props: {
    name: {
      type: String,
      default: '',
    },
    color: {
      type: String,
      default: '',
    },
    width: {
      type: String,
      default: '',
    },
    height: {
      type: String,
      default: '',
    },
  },

  data: function() {
    return {
      CSS_PREFIX: CSS_PREFIX,
    };
  },

  computed: {
    baseClass() {
      const classArr = [`${CSS_PREFIX}icon`];
      classArr.push(`bui-icon-${this.name}`);
      return classArr;
    },
    content: function() {
      let iconStr = '';
      if (window.BUI && window.BUI.iconAddon && window.BUI.iconAddon[this.name]) {
        iconStr = window.BUI.iconAddon[this.name];
      } else {
        iconStr = BuiBasicIcon[this.name]; // || require(`!html-loader!./../svg/${this.name}.svg`);
      }
      return iconStr;
    },
  },

  methods: {
    handleClick(e) {
      this.$emit('click', e);
    },
  },
};
</script>



