<template>
  <div class="bui-date-panel">
    <div class="bui-date-header">
      <byted-icon
        :class="prevClass"
        class="bui-date-header-prev-year"
        name="double-angle-left"
        @click="handleIconYear(-1)"/>
      <byted-icon
        :class="prevClass"
        class="bui-date-header-prev-month"
        name="angle-left"
        @click="handleIconMonth(-1)"/>
      <div class="bui-date-header-text">
        <span
          class="bui-date-header-text-btn"
          @click="handleHeaderBtnClick(dateHeaderLabel.labels[0].type)">{{ dateHeaderLabel.labels[0].label }}</span>
        <span
          v-if="dateHeaderLabel.separator && dateHeaderLabel.separator.trim()"
          class="bui-date-header-text-btn bui-date-header-year">{{ dateHeaderLabel.separator.trim() }}</span>
        <span
          class="bui-date-header-text-btn"
          @click="handleHeaderBtnClick(dateHeaderLabel.labels[1].type)">{{ dateHeaderLabel.labels[1].label }}</span>
      </div>
      <byted-icon
        :class="nextClass"
        class="bui-date-header-next-month"
        name="angle-right"
        @click="handleIconMonth(1)"/>
      <byted-icon
        :class="nextClass"
        class="bui-date-header-next-year"
        name="double-angle-right"
        @click="handleIconYear(1)"/>
    </div>
    <div class="bui-date-body">
      <div
        v-if="week"
        class="bui-date-week">
        <div class="bui-date-week-header"/>
        <div
          v-for="(row, rowIndex) in rowCount"
          :key="row"
          :class="getWeekCellClass(rowIndex)"
          class="bui-date-week-cell">
          <span class="bui-date-week-cell-inner">{{ getWeek(getRowDates(rowIndex)[0]) }}</span>
        </div>
      </div>
      <table class="bui-date-table">
        <thead>
          <tr>
            <th
              v-for="day in days"
              :key="day"
              class="bui-date-day">{{ day }}</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(row, rowIndex) in dates.length / days.length"
            :key="row">
            <td
              v-for="(date, colIndex) in getRowDates(rowIndex)"
              :class="getCellClass(date, rowIndex, colIndex)"
              :key="colIndex"
              :title="getCellTitle(date)"
              class="bui-date-cell"
              @click="handleSelect(date)"
              @mouseenter="handleMouseEnter(date, rowIndex, colIndex)"
              @mouseleave="handleMouseLeave(date, rowIndex, colIndex)">
              <template v-if="dateRender">
                <date-expand
                  :render="dateRender"
                  :date="date"
                  :view-date="viewDate"
                />
              </template>
              <template v-else>
                <span class="bui-date-cell-inner">{{ getDate(date) }}</span>
              </template>

            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>

</template>

<script>
import locale from 'mixins/locale';
import {LOCALE_PREFIX} from '../../../base';
import utils from './../mixins/utils';
import {DATE_COL_COUNT, DATE_ROW_COUNT, DEFAULT_FORMAT} from './../const';
import DateExpand from './date-expand.vue';
import BytedIcon from 'components/icon';
import {isPlainObject} from 'utils/types';
const NOW = utils.methods.getNow();
export default {
  name: 'Date',
  components: {
    DateExpand,
    BytedIcon,
  },
  mixins: [locale, utils],
  inject: ['buiPanel'],
  props: {
    // 周起始日
    weekStart: {
      type: Number,
      default: DATE_COL_COUNT,
      validator(val) {
        return val >= 1 && val <= DATE_COL_COUNT;
      },
    },
    // 日期禁用
    disabledDate: {
      type: Function,
      default: ()=>{
        return null;
      },
    },
    // dayjs 实例
    viewDate: {
      type: [Object],
      default() {
        return NOW;
      },
    },
    // 当前选中值
    value: {
      type: [String, Array, Date],
      default: () => [],
    },
    // hover范围
    hoverValue: {
      type: Array,
      default() {
        return [];
      },
    },
    range: {
      type: Boolean,
      default: false,
    },
    prev: {
      type: Boolean,
      default: true,
    },
    next: {
      type: Boolean,
      default: true,
    },
    week: {
      type: Boolean,
      default: false,
    },
    // 日期自定义渲染
    dateRender: {
      type: Function,
      default: null,
    },
  },
  data() {
    return {
      rowCount: DATE_ROW_COUNT,
      hoveringRowIndex: -1,
    };
  },
  computed: {
    nextClass() {
      return {
        'bui-date-header-btn-hidden': !this.next,
      };
    },
    prevClass() {
      return {
        'bui-date-header-btn-hidden': !this.prev,
      };
    },
    // 星期数
    days() {
      let days = this.t(`${LOCALE_PREFIX}.date-picker.days`) || [];
      // https://github.com/kazupon/vue-i18n/issues/759
      // the array has become an object
      if (isPlainObject(days)) {
        days = Object.keys(days).map((key)=>days[key]);
      }
      const firstday = parseInt(this.weekStart, 10);
      return days.concat(days).slice(firstday, firstday + days.length);
    },
    // 面板的日期数组
    dates() {
      const arr = [];
      const viewDate= this.viewDate || this.getNow();

      // 当前月的天数
      const curMonthLength = this.getDaysInMonth(viewDate);
      let curMonthFirstDate = this.setDate(viewDate, 1);
      const lastMonthLength = (this.getWeekDay(curMonthFirstDate) + this.days.length - this.weekStart) % this.days.length; // time.getDay() 0是星期天, 1是星期一 ...
      const nextMonthLength = this.days.length * DATE_ROW_COUNT - (lastMonthLength + curMonthLength);
      let lastMonthLastDate = curMonthFirstDate;
      for (let i = 0; i < lastMonthLength; i++) {
        lastMonthLastDate = this.addDate(lastMonthLastDate, -1);
        arr.unshift(lastMonthLastDate);
      }
      for (let i = 1; i <= curMonthLength; i++) {
        curMonthFirstDate = this.setDate(curMonthFirstDate, i);
        arr.push(curMonthFirstDate);
      }

      for (let i = 1; i <= nextMonthLength; i++) {
        const nextMonthDate = this.addDate(curMonthFirstDate, i);
        arr.push(nextMonthDate);
      }
      return arr;
    },
    dateHeaderLabel() {
      const locale = this.t(`${LOCALE_PREFIX}.locale`);
      const datePanelLabel = this.t(`${LOCALE_PREFIX}.date-picker.datePanelLabel`);
      const {labels, separator} = this.getFormatDateLabels(this.viewDate, datePanelLabel, locale);
      return {
        separator: separator,
        labels: labels,
      };
    },

  },
  methods: {
    getRowDates(rowIndex) {
      return this.dates.slice(DATE_COL_COUNT * rowIndex, DATE_COL_COUNT*(rowIndex+1));
    },
    handleIconMonth(flag) {
      this.buiPanel.handleDateHeaderMonthChange(flag);
    },
    handleIconYear(flag) {
      this.buiPanel.handleDateHeaderYearChange(flag);
    },
    handleHeaderBtnClick(type) {
      if (type==='year') {
        this.buiPanel.handlePanelYearShow();
      } else {
        this.buiPanel.handlePanelMonthShow();
      }
    },
    getCellTitle(date) {
      return this.getFormat(date, DEFAULT_FORMAT);
    },

    getCellClassConfig(
        date,
        colIndex,
        type= 'date'
    ) {
      const {hoverValue, value, range, isSameCell, isSameDate, getNow, isAfter, isBefore, getDate, getMonth, disabledDate, addDate} = this;

      const selected = isSameCell(date, value);
      const now = isSameDate(date, getNow());
      const hasRange =
      range && value.length > 1 && value[1] && value[0] && isBefore(value[0], value[1]);
      const hasHover = hoverValue.length > 1 && hoverValue[0] && hoverValue[1] && isBefore(hoverValue[0], hoverValue[1]);
      // const isValidRange = hasRange && isBefore(value[0], value[1]);
      const inRange =
      hasRange && isBefore(value[0], date) && isAfter(value[1], date);
      const inHover =
      hasHover && isBefore(hoverValue[0], date) && isAfter(hoverValue[1], date);

      const isRangeStart = hasRange && isSameDate(value[0], date);
      const isRangeEnd = hasRange && isSameDate(value[1], date);
      const isHoverStart = hasHover && isSameDate(hoverValue[0], date);
      const isHoverEnd = hasHover && isSameDate(hoverValue[1], date);
      // month panel start or year panel start or date panel start
      let isEdgeStart = false;
      // month panel end or year panel end or date panel end
      let isEdgeEnd = false;
      // col end
      let isColEnd = false;
      let disabled = false;
      switch (type) {
        case 'date':
          isEdgeStart = getDate(date) === 1;
          isEdgeEnd = getMonth(date) !== getMonth(addDate(date, 1));
          isColEnd = colIndex === DATE_COL_COUNT - 1;
          disabled = disabledDate && disabledDate({current: date, value: value, type: 'date'}); ;
          break;
      }

      const config = {
        disabled,
        selected,
        now,
      };

      if (
        hasHover &&
      (inHover || isHoverStart || isHoverEnd) &&
      hasRange &&
      (inRange || isRangeStart || isRangeEnd)
      ) {
        config['range-hover'] = true;
        // range hover boundary add hover border
        if (
          (isRangeStart && inHover) ||
        (isRangeEnd && inHover) ||
        (isRangeStart && isHoverEnd) ||
        (isRangeEnd && isHoverStart)
        ) {
          config['hover'] = true;
        }
      } else if (
        (inRange || isRangeStart || isRangeEnd) &&
      hasRange
      ) {
        config['range'] = true;
      } else if (inHover || isHoverStart || isHoverEnd) {
        config['hover'] = true;
      }
      /**
       * paddingLeft
       * row start
       * month start
       * !hasRange && hoverStart
       * rangeStart <= hoverStart or rangeStart > hoverEnd
       * hoverStart <= rangeStart or hoverStart > rangerEnd
       */

      if (
        colIndex === 0 ||
      isEdgeStart ||
      (!hasRange && isHoverStart) ||
      (isRangeStart &&
        (!isAfter(date, hoverValue[0]) || isAfter(date, hoverValue[1]))) ||
      (isHoverStart &&
        (!isAfter(date, value[0]) || isAfter(date, value[1])))
      ) {
        config.left = true;
      }
      //  hoverStart < rangeStart <= hover
      if (
        isRangeStart &&
      isAfter(date, hoverValue[0]) &&
      !isAfter(date, hoverValue[1])
      ) {
        config['before-left-after-right'] = true;
      }
      // hoverStart <= RangeEnd <hoverEnd
      if (
        isRangeEnd &&
      !isBefore(date, hoverValue[0]) &&
      isBefore(date, hoverValue[1])
      ) {
        config['before-right-after-left'] = true;
      }
      /**
       * paddingRight
       * row end
       * month end
       * !hasRange && hoverEnd
       * rangeEnd < hoverStart || rangeEnd >= hoverEnd
       * hoverEnd < rangeStart || hoverEnd >= rangeEnd
       */

      if (
        isColEnd ||
      isEdgeEnd ||
      (!hasRange && isHoverEnd) ||
      (isRangeEnd &&
        (isBefore(date, hoverValue[0]) || !isBefore(date, hoverValue[1]))) ||
      (isHoverEnd &&
        (isBefore(date, value[0]) || !isBefore(date, value[1])))
      ) {
        config.right = true;
      }
      return config;
    },
    getCellClass(currentDate, rowIndex, colIndex) {
      if (!currentDate || !this.isValid(currentDate)) {
        return;
      }
      // 周选择
      if (this.week) {
        return this.getCellClassForWeek(currentDate, rowIndex, colIndex);
      }
      const month = this.getMonth(currentDate);
      const config= this.getCellClassConfig(currentDate, colIndex);
      if (!this.isInView(month)) {
        return [config.disabled ? this.getCellClassName('disabled') : ''];
      }
      config['in-view'] = true;
      return Object.keys(config).map((key)=>{
        return config[key] ? this.getCellClassName(key) : '';
      });
    },
    getCellClassForWeek(currentDate, rowIndex, colIndex) {
      const month = this.getMonth(currentDate);
      const disabled = this.disabledDate && this.disabledDate({current: currentDate, value: this.value, type: 'date'});
      // const weekday = this.getWeekDay(currentDate);
      return {
        [this.getCellClassName(`disabled`)]: disabled,
        // 当前月
        [this.getCellClassName(`in-view`)]: this.isInView(month),
        [this.getCellClassName(`week-hover`)]: this.hoveringRowIndex === rowIndex,
        // 当天
        [this.getCellClassName(`now`)]: this.isSameDate(currentDate, this.getNow()),
        // 选中项
        [this.getCellClassName(`week-selected`)]: this.isInWeek(currentDate, this.value),
        // 行首
        [this.getCellClassName(`row-start`)]: colIndex === 0,
        // 行尾
        [this.getCellClassName(`row-end`)]: colIndex === this.days.length - 1,
      };
    },
    getWeekCellClass(rowIndex) {
      const cellPrefixCls = 'bui-date-week-cell-';
      return {
        [this.getCellClassName('hover', cellPrefixCls)]: this.week && this.hoveringRowIndex === rowIndex,
        [this.getCellClassName('selected', cellPrefixCls)]: this.week && this.isInWeek(this.getRowDates(rowIndex)[0], this.value),
      };
    },
    // 当月
    isInView(month) {
      return month === this.getMonth(this.viewDate);
    },
    isSameCell(currentDate, value) {
      if (!Array.isArray(value)) {
        return this.isSameDate(currentDate, value);
      }
      return value.some((item) => this.isSameDate(currentDate, item));
    },
    // 属于同一周
    isInWeek(currentDate, value) {
      const currentWeek = this.getWeek(currentDate);
      if (!Array.isArray(value)) {
        return value ? this.getWeek(value) ===currentWeek : false;
      }
      return value.some((date)=> date ? this.getWeek(date) ===currentWeek : false );
    },
    getCellClassName(string = '', cellPrefixCls = `bui-date-cell-`) {
      return `${cellPrefixCls}${string}`;
    },

    handleSelect(currentDate) {
      const disabled = this.disabledDate && this.disabledDate({current: currentDate, value: this.value, type: 'date'});
      if (disabled) {
        return;
      }
      this.buiPanel.handleDateSelect({
        currentDate,
        _uid: this._uid,
      });
    },
    handleMouseEnter(currentDate, rowIndex, colIndex) {
      const disabled = this.disabledDate && this.disabledDate({current: currentDate, value: this.value, type: 'date'});
      if (disabled) {
        return;
      }
      this.hoveringRowIndex = rowIndex;
      if (this.buiPanel.handleDateMouseEnter) {
        this.buiPanel.handleDateMouseEnter({
          currentDate,
          _uid: this._uid,
        });
      }
    },
    handleMouseLeave(currentDate, rowIndex, colIndex) {
      const disabled = this.disabledDate && this.disabledDate({current: currentDate, value: this.value, type: 'date'});
      if (disabled) {
        return;
      }
      this.hoveringRowIndex = -1;
      if (this.buiPanel.handleDateMouseLeave) {
        this.buiPanel.handleDateMouseLeave({
          currentDate,
          _uid: this._uid,
        });
      }
    },
  },
};
</script>



