

<script>
import utils from './../mixins/utils';
const NOW = utils.methods.getNow();

export default {
  name: 'DateExpand',
  props: {
    render: {
      type: Function,
      default: null,
    },
    date: {
      type: Object,
      default() {
        return NOW;
      },
    },
    viewDate: {
      type: Object,
      default() {
        return NOW;
      },
    },
  },
  render(h) {
    return (
      <div>
        {this.render(h, {current: this.date, viewDate: this.viewDate})}
      </div>
    );
  },
};
</script>



