<template>
  <div>
    <slot>
      <ul :class="baseClass">
        <TreeControlItem
          v-for="item in children"
          :key="item.id"
          v-bind="item"
        >
          <template
            slot="node"
            slot-scope="{item}">
            <slot
              :item="item"
              name="node"><span v-html="highlightedLabel(item)"/></slot>
          </template>
        </TreeControlItem>
      </ul>
    </slot>
  </div>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import TreeControlItem from './tree-item.vue';
import {getParentsIdsMap} from './util';

export default {
  name: `${CSS_PREFIX}tree-search`,
  components: {TreeControlItem},
  inject: [
    '$expandedKeys',
    '$keyword',
    '$childrenMap',
    '$handleToggleExpand',
  ],
  props: {
    baseClass: {
      type: String,
      default: '',
    },
    treeNodesMap: {
      type: Object,
      default: () => ({}),
    },
    filterMethod: {
      type: Function,
      default: () => {},
    },
  },
  provide() {
    return {
      $expandedKeys: () => Boolean(this.keyword) ? this.expandedKeysForSearch : this.$expandedKeys(),
      $childrenMap: () => Boolean(this.keyword) ? this.childrenMap : this.$childrenMap(),
      $handleToggleExpand: (id) => Boolean(this.keyword) ? this.handleToggleExpand(id) : this.$handleToggleExpand(id),
    };
  },
  data() {
    return {
      expandedKeysForSearch: [],
      treeNodesMapForSearch: null,
    };
  },
  computed: {
    keyword() {
      return this.$keyword();
    },
    childrenMap() {
      if (!this.treeNodesMapForSearch) {
        return {};
      }
      const result = {};
      const values = Object.values(this.treeNodesMapForSearch);
      for (let i = 0; i < values.length; i += 1) {
        const {id} = values[i];
        result[id] = values
            .filter((item) => item.parentId === id)
            .sort((prev, next) => prev.sortOrder - next.sortOrder);
      }
      return result;
    },
    children() {
      if (!this.treeNodesMapForSearch) {
        return [];
      }
      return Object.values(this.treeNodesMapForSearch)
          .filter((item) => item.parentId === undefined)
          .sort((prev, next) => prev.sortOrder - next.sortOrder);
    },
  },
  watch: {
    keyword(val) {
      if (val) {
        this.$set(this, 'treeNodesMapForSearch', {});
      } else {
        this.$set(this, 'treeNodesMapForSearch', null);
        return;
      }
      const hits = [];
      const misses = [];
      Object.values(this.treeNodesMap).forEach((node) => {
        if (this.filterMethod(val, node)) {
          hits.push(node.id);
        } else {
          misses.push(node.id);
        }
      });
      misses.forEach((id) => {
        this.treeNodesMapForSearch[id] = {...this.treeNodesMap[id], visible: false};
      });
      const parentsIdsMap = getParentsIdsMap(this.treeNodesMap);
      const hitsParents = hits.map((hit) => parentsIdsMap[hit]).flat();
      const visibleNodes = Array.from(new Set(hits.concat(hitsParents)));
      visibleNodes.forEach((id) => {
        this.treeNodesMapForSearch[id] = {...this.treeNodesMap[id], visible: true};
      });
      this.expandedKeysForSearch = visibleNodes;
    },
  },
  methods: {
    getParentsIdsMap(nodesMap) {
      const result = getParentsIdsMap(nodesMap);
      return result;
    },
    highlightedLabel({label, keyword}) {
      if (!keyword) {
        return label;
      };
      const regex = RegExp(keyword, 'gi');
      return label.replace(regex, '<em>$&</em>');
    },
    handleToggleExpand(id) {
      if (!this.expandedKeysForSearch.includes(id)) {
        this.expandedKeysForSearch = this.expandedKeysForSearch.concat(id);
      } else {
        this.expandedKeysForSearch = this.expandedKeysForSearch.filter((key) => key !== id);
      }
    },
  },
};
</script>



