<template>
  <ul
    ref="timeColumn"
    class="bui-time-column">
    <li
      v-for="(option, index) in options"
      :key="option.value"
      :class="getTimeUntiClass(option)"
      class="bui-time-option"
      @mouseenter="handleMouseEnter(option, index)"
      @mouseleave="handleMouseLeave(option, index)"
      @click="handleSelect(option,index)">
      <div class="bui-time-option-inner">{{ option.label }}</div>
    </li>
  </ul>
</template>
<script>
import {scrollTop} from 'utils/dom.js';
export default {
  name: 'TimeColumn',

  props: {
    value: {
      type: [Number, String],
      default: -1,
    },
    options: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  watch: {
    value() {
      this.handleValueScrollTop();
    },
  },
  mounted() {
    this.handleValueScrollTop();
  },
  methods: {
    getTimeUntiClass(option) {
      return {
        'bui-time-option-disabled': option.disabled,
        'bui-time-option-selected': option.value === this.value,
      };
    },
    handleSelect(option) {
      if (!option.disabled) {
        this.$emit('select', option.value);
      }
    },
    handleValueScrollTop() {
      const index = this.options.findIndex((option)=>option.value ===this.value);
      const itemHeight = 30;
      const margin = 0;

      if (index > -1 ) {
        this.$nextTick(()=>{
          // +1考虑borderTop，第一项没有borderTop
          if (this.$refs.timeColumn) {
            const top = index * (itemHeight + margin) + margin;
            scrollTop(this.$refs.timeColumn, this.$refs.timeColumn.scrollTop, top, 500);
          }
        });
      }
    },
    handleMouseEnter(option, index) {
      if (option.disabled) return;
      this.$emit('mouseenter', option.value);
    },
    handleMouseLeave(option, index) {
      if (option.disabled) return;
      this.$emit('mouseleave', option.value);
    },
  },
};
</script>



