<template>
  <div
    :class="baseClass"
    class="radio-inline">
    <label>
      <div class="bui-radio-input-wrapper">
        <input
          :checked="innerChecked"
          :value="val"
          class="bui-radio-input"
          type="radio"
          @change="handleChange">
      </div>
      <span class="bui-radio-input-label"><slot/></span>
    </label>
  </div>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';

export default {
  name: `${CSS_PREFIX}radio`,

  mixins: [emitter],

  props: {
    value: {
      type: [Boolean, String, Number],
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    val: {
      type: [Boolean, String, Number],
      default: '',
    },
  },
  data: function() {
    return {
      prefix: CSS_PREFIX,
    };
  },
  computed: {
    baseClass() {
      const classArr = [`${CSS_PREFIX}radio`];
      if (this.innerChecked) {
        classArr.push('checked');
      }
      if (this.isDisabled) {
        classArr.push('disabled');
      }
      if (Object.keys(this.$slots).length === 0) {
        classArr.push('empty');
      }
      return classArr;
    },
    radioGroup() {
      let parent = this.$parent;
      while (parent) {
        if (parent.$options.name === `${CSS_PREFIX}radio-group`) {
          return parent;
        }
        parent = parent.$parent;
      }
      return null;
    },
    isGroup() {
      return this.radioGroup !== null;
    },
    isDisabled() {
      return (this.isGroup && this.radioGroup.disabled) || this.disabled;
    },
    innerChecked() {
      const {isGroup, radioGroup, val, value} = this;
      if (isGroup) {
        return radioGroup.value === val;
      }
      if (val) {
        // 多个互斥
        return value === val;
      }
      return !!value;
    },
  },
  watch: {},
  methods: {
    handleChange(e) {
      const {isGroup, isDisabled, val} = this;
      if (isDisabled) {
        return false;
      }
      const checked = e.target.checked;
      if (val) {
        // 多个互斥
        this.$emit('input', val);
        this.$emit('change', val);
      } else {
        // 单个checkbox
        this.$emit('input', checked);
        this.$emit('change', checked);
      }
      if (isGroup) {
        this.dispatch(`${CSS_PREFIX}radio-group`, 'bui.radio.change', {
          val: this.val,
          checked,
        });
      } else {
        this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [checked]);
      }
    },
  },
  ready() {},
};
</script>



