<template>
  <div
    v-byted-loading="loading"
    ref="originImageContain"
    :class="baseClass"
    :style="baseStyle"/>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import Cropper from 'cropperjs';

export default {
  name: `${CSS_PREFIX}cropper`,
  props: {
    originImg: {
      type: [String, File],
      default: '',
    },
    // 裁剪图片的宽高比
    aspectRatio: {
      type: [Number, String],
      default: 'auto',
      validator(value) {
        return value === 'auto' || !isNaN(value);
      },
    },
    // 容器宽度
    width: {
      type: [Number, String],
      default: 0,
      validator(value) {
        return !isNaN(value);
      },
    },
    // 容器高度
    height: {
      type: [Number, String],
      default: 0,
      validator(value) {
        return !isNaN(value);
      },
    },
    minImgWidth: {
      type: [Number, String],
      default: 0,
      validator(value) {
        return !isNaN(value);
      },
    },
    minImgHeight: {
      type: [Number, String],
      default: 0,
      validator(value) {
        return !isNaN(value);
      },
    },
  },
  data() {
    return {
      originImgID: `originImage_${this._uid}`,
      cropper: null,

      // 原始图片地址
      originImgSrc: '',
      // 原始图片宽度
      originImgWidth: 0,
      // 原始图片高度
      originImgHeight: 0,
      loading: false,
    };
  },
  computed: {
    baseClass() {
      return `${CSS_PREFIX}cropper`;
    },
    baseStyle() {
      const style = {};
      if (this.width) {
        style.width = `${this.width}px`;
      }
      if (this.height) {
        style.height = `${this.height}px`;
      }
      return style;
    },
    minCropBox() {
      if (!this.originImgWidth || !this.originImgHeight || !this.width || !this.height || !this.aspectRatio) {
        return {
          width: 0,
          height: 0,
        };
      }

      let minImgHeight = 0;
      let minImgWidth = 0;
      // 判断传入的最小宽高和宽高比进行匹配，向上满足宽高比
      if (this.minImgHeight && this.minImgWidth) {
        minImgHeight = this.minImgHeight;
        minImgWidth = this.minImgWidth;
        if (this.minImgHeight * this.aspectRatio > this.minImgWidth) {
          minImgWidth = this.minImgHeight * this.aspectRatio;
        } else if (this.minImgHeight * this.aspectRatio < this.minImgWidth) {
          minImgHeight = this.minImgWidth / this.aspectRatio;
        }
      } else if (this.minImgHeight) {
        minImgWidth = this.minImgHeight * this.aspectRatio;
      } else if (this.minImgWidth) {
        minImgHeight = this.minImgWidth / this.aspectRatio;
      }
      if (minImgWidth > this.originImgWidth || minImgHeight > this.originImgHeight) {
        return {
          width: 0,
          height: 0,
        };
      }
      const width = Number(this.width) || 1;
      const height = Number(this.height) || 1;
      // 计算最大的缩放比
      const widthScale = this.originImgWidth / width;
      const heightScale = this.originImgHeight / height;
      const scale = Math.max(widthScale, heightScale);
      // 考虑width和height满足>minImgWidth&&>minImgHeight
      const boxWidth = minImgWidth / scale;
      const boxHeight = boxWidth / this.aspectRatio;
      return {
        width: boxWidth,
        height: boxHeight,
      };
    },
    // 裁剪初始化参数
    cropperOptions() {
      return {
        originImg: this.originImg,
        aspectRatio: this.aspectRatio === 'auto' ? '' : Number(this.aspectRatio),
        // restrict the minimum canvas size to fit within the container
        viewMode: 2,
        // show the grid background of the container
        background: false,
        // 禁用zoom
        zoomOnWheel: false,
        zoomable: false,
        rotatable: false,
        scalable: false,
        // 裁剪框改变触发函数
        crop: null,
        minContainerWidth: this.width,
        minContainerHeight: this.height,
        minCropBoxWidth: this.minCropBox.width || this.minImgWidth,
        minCropBoxHeight: this.minCropBox.height || this.minImgHeight,
      };
    },
  },
  watch: {
    cropperOptions() {
      this.handleDestroy();
      this.handleOriginImg();
    },
  },
  mounted() {
    if (this.originImg) {
      // 初始化渲染原始图片
      this.handleOriginImg();
    }
  },
  destroyed() {
    this.handleDestroy();
  },
  methods: {
    // 格式化originImage
    handleOriginImg() {
      const vm = this;
      this.loading = true;
      this.originImgSrc = '';
      switch (typeof this.originImg) {
        case 'object':
          const reader = new FileReader();
          reader.onload = function() {
            vm.originImgSrc = this.result;
            vm.handleoriginImgSrc();
          };
          reader.readAsDataURL(this.originImg);
          break;
        default:
          if (!this.originImg) {
            this.loading = false;
          } else {
            this.originImgSrc = this.originImg;
            this.handleoriginImgSrc();
          }
          break;
      }
    },
    // 生成图片对象
    handleoriginImgSrc() {
      const vm = this;
      let image = null;
      if (window[this.originImgID]) {
        image = window[this.originImgID];
      } else {
        image = new Image();
        image.id = this.originImgID;
        image.setAttribute('crossOrigin', 'Anonymous');
      }
      image.src = this.originImgSrc;
      image.onload = () => {
        vm.originImgWidth = image.naturalWidth;
        vm.originImgHeight = image.naturalHeight;
        vm.handleOriginImgLoaded.call(vm, image);
      };
    },
    // 图片对象加载完成后初始化裁剪框
    handleOriginImgLoaded(image) {
      if (!window[this.originImgID]) {
        this.$refs.originImageContain.appendChild(image);
      }
      // 裁剪框初始化
      this.cropper = new Cropper(window[this.originImgID],
          Object.assign(this.cropperOptions, {
          // 裁剪框拖动结束后触发
            cropend: this.handleCrop,

            // ready后触发裁剪框函数，记录初始位置
            ready: () => {
              this.loading = false;
              this.handleCrop();
            },
          })
      );
    },
    // 裁剪触发函数
    handleCrop(e) {
      const data = this.cropper.getData();
      const imgData = this.cropper.getImageData();
      // 输出数据
      const exportData = {
        x: data.x,
        y: data.y,
        width: data.width,
        height: data.height,
      };
      // 图片原始尺寸
      const exportImgData = {
        naturalWidth: imgData.naturalWidth,
        naturalHeight: imgData.naturalHeight,
      };
      this.$emit('crop', exportData, exportImgData);
    },
    // 销毁函数
    handleDestroy() {
      if (this.cropper) {
        this.cropper.destroy();
        this.cropper = null;
      }
      if (this.$refs.originImageContain && this.$refs.originImageContain.removeChild && window[this.originImgID]) {
        this.$refs.originImageContain.removeChild(window[this.originImgID]);
      }
    },
  },
};
</script>



