<template>
  <div
    v-clickoutside="clickoutsideHandle"
    :class="baseClass">
    <div
      ref="popperRef"
      :class="wrapperClass"
      class="bui-select-wrapper"
      @click="handleSelectClick">
      <slot name="input">
        <div
          v-show="isMultipleWrapperShow"
          ref="multiple"
          :class="multipleClass"
          class="bui-select-wrapper-multiple">
          <byted-tag
            v-for="(item, index) in value"
            :key="index"
            :disabled="disabled"
            :plain="disabled"
            :size="size"
            class="bui-select-tag"
            type="default"
            closable
            @close="handleTagRemove(index)">
            {{ getSelectedLabel(item) }}
          </byted-tag>
        </div>
        <div
          v-if="isMultipeCollapseTagsShow"
          :data-id="selectMultipleCollapseWrapperId"
          class="bui-select-wrapper-multiple bui-select-wrapper-multiple-collapse">
          <byted-tag
            v-for="(list, index) in collapseTagsList"
            :key="index"
            :disabled="disabled"
            :plain="disabled"
            :size="size"
            class="bui-select-tag"
            closable
            @close="handleTagRemove(index)">
            {{ getSelectedLabel(list) }}
          </byted-tag>

          <span class="bui-selected-label">
            {{ _hasSelect }}
          </span>
        </div>
        <input
          v-if="isInputShow"
          ref="input"
          :placeholder="placeholder"
          v-model="currentLabels"
          :disabled="disabled"
          readonly
          type="text"
          class="bui-input text-overflow"
          @blur="handleInputBlur"
          @focus="handleInputFocus">
      </slot>
    </div>
    <div
      :class="{'bui-select-icon-wrapper': showClear}"
    >
      <byted-icon
        :class="{open:optionShow}"
        name="angle-down"
        class="bui-cascader-icon bui-select-arrow-icon"
        @click="handleIconClick"/>
      <byted-icon
        v-if="showClear"
        name="close-circle"
        class="bui-select-icon bui-select-clear-icon"
        @click.stop="handleClearClick"/>
    </div>
    <byted-popper
      v-show="optionShow"
      ref="popper"
      :reference="popperRef"
      :popper-options= "popperOptions"
      :append-to-body = "transfer"
      :class="innerPopperClass"
      class="bui-cascader-menus"
      placement="bottom-start"
    >
      <byted-input
        v-if="filterable"
        :clearable="true"
        v-model="query"
        :placeholder="_filterPlaceholder"
        @input="handleQueryInput"
      >
        <template slot="prefix">
          <byted-icon name="search"/>
        </template>
      </byted-input>
      <list-item
        v-if="filterable && query !== ''"
        :loading="loading"
        :data-source="querySelections"
        :props="{itemKey: 'label'}"
        :list-width="listWidth"
        class="bui-cascader-queryselections-item"
        @select="handleSelectItem"
      >
        <template
          slot="dataSource"
          slot-scope="{item}">
          <byted-checkbox
            v-if="multiple"
            :val="item.value.join('-')"
            :value="multipleSelectedIds"
            class="bui-cascader-queryselections-checkbox"
            @click.native="($event) => $event.preventDefault()" />
          <span
            :class="dropdownListClass(item)"
            :style="listItemStyle"
            v-html="item.render ? item.display : item.display || item.label"/>
        </template>
      </list-item>
      <cascader-menus
        v-show="optionsMenuVisibile"
        ref="menu"
        v-model="currentValue"
        :options="rebuildOptions"
        :original-options="options"
        :visible="optionShow"
        :multiple="multiple"
        :props="props"
        class="bui-cascader-menus-wrapper"
        @active-item-change="handleActiveItemChange"
        @change="handleMenuChange">
        <template
          slot="infinite-loading"
          slot-scope="scope">
          <slot
            v-bind="scope"
            name="infinite-loading" />
        </template>
      </cascader-menus>
      <slot name="footer" />
    </byted-popper>
  </div>
</template>

<script>
import {CSS_PREFIX, LOCALE_PREFIX} from '../../base.js';
import Menu from './menu.vue';
import clickoutside from 'directives/clickoutside';
import emitter from 'mixins/emitter';
import locale from 'mixins/locale';
import {hasClass} from 'utils/dom.js';

import {isOneOf} from 'utils/helper.js';
import {copyArray} from './util.js';
import ResizeObserver from 'resize-observer-polyfill';

import ListItem from '../../auto-complete/src/list-item.vue';

const _renderFormat = function(value, context) {
  if (context.multiple === true) {
    return value[value.length - 1];
  }
  return value.join('/');
};
import BytedTag from 'components/tag';
import BytedIcon from 'components/icon';
import BytedPopper from 'components/popper';
import BytedInput from 'components/input';
import BytedCheckbox from 'components/checkbox';
export default {
  name: `${ CSS_PREFIX }cascader`,

  components: {
    [Menu.name]: Menu,
    ListItem,
    BytedTag,
    BytedIcon,
    BytedPopper,
    BytedInput,
    BytedCheckbox,
  },

  directives: {clickoutside},

  mixins: [emitter, locale],

  provide() {
    return {cascader: this};
  },

  props: {
    options: {
      type: Array,
      default() {
        return [];
      },
    },
    value: {
      type: Array,
      default() {
        return [];
      },
    },
    placeholder: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    // 选择后展示的函数，用于自定义显示格式
    renderFormat: {
      type: Function,
      default: function(value) {
        return _renderFormat(value, this);
      },
    },
    // 当此项为 true 时，点选每级菜单选项值都会发生变化
    changeOnSelect: {
      type: Boolean,
      default: false,
    },
    // popper appendtoBody
    transfer: {
      type: Boolean,
      default: false,
    },
    // 是否多选
    multiple: {
      type: Boolean,
      default: false,
    },
    props: {
      type: Object,
      default: () => {
        return {};
      },
    },
    // 选中项是否要折叠
    collapseTags: {
      type: Boolean,
      default: false,
    },
    status: {
      type: String,
      default: '',
      validator(value) {
        return isOneOf(value, [
          '',
          'error',
        ]);
      },
    },
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, [
          'md',
          'sm',
        ]);
      },
    },
    filterMethod: {
      type: Function,
      default: null,
    },
    filterable: {
      type: Boolean,
      default: false,
    },
    clearable: {
      type: Boolean,
      default: false,
    },
    popperOptions: {
      type: Object,
      default() {
        return {
          modifiers: {
            flip: {
              enabled: true,
            },
            hide: {
              enabled: false,
            },
            preventOverflow: {
              enabled: true,
              boundariesElement: 'viewport',
            },
          },
        };
      },
    },
    noDataText: {
      type: String,
      default: '',
    },
    infinite: {
      type: [Boolean, Array],
      default: () => false,
    },
    independent: {
      type: Boolean,
      default: false,
    },
    popperClass: {
      type: String,
      default: '',
    },
    filterPopperWidth: {
      type: Number,
      default: 300,
    },
  },

  data() {
    return {
      optionShow: false,
      currentValue: this.value || [],
      popperRef: null,
      // 记录popperRef宽度
      popperRefWidth: 0,

      selectMultipleCollapseWrapperId: `bui-select-wrapper-multiple-collapse_${this._uid}`,
      query: '',
      loading: false,
      customDataSource: null,
    };
  },

  computed: {
    _hasSelect() {
      if (this.value.length > 99) {
        return '99+';
      }
      return this.value.length;
    },
    _filterPlaceholder() {
      return this.t(`${LOCALE_PREFIX}.cascader.filterPlaceholder`);
    },
    baseClass() {
      const baseClass = `${ CSS_PREFIX }cascader`;
      const classArr = [baseClass];
      if (this.disabled) {
        classArr.push(`${baseClass}-disabled`);
      }
      if (this.multiple) {
        classArr.push(`${baseClass}-multiple`);
      }
      const slot = this._slotContents || this.$slots || {};
      const hasCustomInput = slot['input'] != null;
      if (hasCustomInput) {
        classArr.push(`${baseClass}-custom`);
      }
      classArr.push(`${baseClass}-${this.size}`);
      return classArr;
    },
    wrapperClass() {
      const classArr = [];
      if (this.optionShow) {
        classArr.push('active');
      }
      return classArr;
    },
    innerPopperClass() {
      const classArr = [`bui-cascader-menus-${this.size}`, this.popperClass];
      if (this.filterable) {
        classArr.push(`bui-cascader-menus-filterable`);
      }
      return classArr;
    },
    currentLabels() {
      let options = this.options;
      const labels = [];
      this.value.forEach((value) => {
        const targetOption = (options &&
          options.filter((option) => option.value === value)[0]);
        if (targetOption) {
          labels.push(targetOption.label);
          options = targetOption.children;
        }
      });
      if (this.renderFormat instanceof Function) {
        return this.renderFormat(labels);
      } else {
        return _renderFormat(labels, this);
      }
    },
    isInputShow() {
      if (this.multiple && Array.isArray(this.value) && this.value.length) {
        return false;
      }
      return true;
    },
    multipleClass() {
      return {
        'bui-select-wrapper-absolute': this.multiple && this.optionShow && this.collapseTags,
      };
    },
    isMultipleWrapperShow() {
      return this.multiple && this.value.length && (!this.collapseTags || this.optionShow);
    },
    isMultipeCollapseTagsShow() {
      return this.multiple && this.collapseTags && this.value.length && !this.optionShow;
    },
    // options selected Array
    rebuildOptions() {
      const data = copyArray(this.options, this.props, this.value, this.multiple, [], this.independent);
      return data;
    },
    // 计算collapseTags下tag可以显示的tag
    collapseTagsList() {
      if (!this.multiple) {
        return [];
      }
      if (!this.popperRefWidth) {
        return this.value.length ? [this.value[0]] : [];
      }
      // 容器总宽度 - paddingLeft - paddingRight - border
      const wrapperWidth = this.popperRefWidth - 8 - 32 - 2 - 58;
      let currentWidth = 0;
      const list = [];
      let i = 0;
      const fontSize = this.size === 'sm' ? 12 : 14;
      const tagPaddingLeft = this.size === 'sm' ? 8 : 8;
      const tagPaddingRight = this.size === 'sm' ? 25 : 25;
      while (currentWidth < wrapperWidth && i < this.value.length) {
        // border + marginLeft
        const width = 2 + 4 + tagPaddingLeft + this.getSelectedLabel(this.value[i]).length * fontSize + tagPaddingRight;
        if (currentWidth + width < wrapperWidth) {
          list.push(this.value[i]);
          currentWidth += width;
        } else {
          break;
        }
        i++;
      }
      return list.length ? list : [this.value[0]];
    },
    showClear() {
      const hasValue = this.currentValue.length > 0;
      return (this.clearable
              && !this.disabled
              && hasValue);
    },
    querySelections() {
      if (this.customDataSource) return this.customDataSource;
      let selections = [];
      const saveSelection = (item) => {
        selections.push({
          label: item._label,
          value: item._value,
          display: item._label,
          item: item,
          children: item.children,
          disabled: !!item.disabled,
          isLeaf: item.children && item.children.length > 0 ? false : true,
        });
      };
      const getSelections = (arr, label, value) => {
        for (let i = 0; i < arr.length; i++) {
          const item = arr[i];
          item._label = label ? label + ' / ' + item.label : item.label;
          item._value = value ? value.concat([item.value]) : [item.value];
          if (item.children && item.children.length) {
            getSelections(item.children, item._label, item._value);
            delete item._label;
            delete item._value;
          } else {
            saveSelection(item);
          }
        }
      };
      getSelections(this.options);
      selections = selections.filter((item) => {
        return item.label ? item.label.indexOf(this.query) > -1 : false;
      }).map((item) => {
        item.display = item.display.replace(new RegExp(this.query, 'g'), `<em>${this.query}</em>`);
        return item;
      });
      return selections;
    },
    optionsMenuVisibile() {
      return !this.filterable || (this.filterable && this.query === '');
    },
    multipleSelectedIds() {
      if (!this.multiple) return [];
      return this.value.map((arr) => arr.join('-'));
    },
    listWidth() {
      if (this.filterPopperWidth > 0) {
        return this.filterPopperWidth - 24 - 24;
      }
      return this.popperRefWidth - 24 - 24;
    },
    listItemStyle() {
      const style = {};
      if (this.listWidth) style.maxWidth = `${this.listWidth}px`;
      return style;
    },
  },
  watch: {
    value(newVal) {
      this.currentValue = newVal;
      if (this.multiple) {
        // value change 改变 poperRef
        this.changePoperRef();
      }
    },
    optionShow(newVal) {
      newVal ?
        this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.update') :
        this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.destroy');
      this.$emit('visible-change', newVal);
    },
  },
  mounted() {
    this.$nextTick(() => {
      if (!this.$refs.popperRef) {
        return;
      }
      this.changePoperRef();
      // TODO 监听DOM宽度对的动态变化
      this.popperRefRo = new ResizeObserver(() => {
        this.popperRefWidth = this.$refs.popperRef && this.$refs.popperRef.offsetWidth || 0;
      });
      this.popperRefRo.observe(this.$refs.popperRef);
      // 监测DOM变化，更新popper
      this.bindEvent();
    });
  },
  updated() {
    this.setPopperWidth();
  },
  beforeDestroy() {
    try {
      if (this.popperRefRo) {
        this.popperRefRo.unobserve(this.$refs.popperRef);
        this.popperRefRo = null;
      }
      if (this.RO) {
        this.RO.unobserve(this.$refs.multiple);
        this.RO = null;
      }
    } catch (error) {
      this.popperRefRo = null;
    }
    if (this.RO && this.$refs.multiple) {
      this.RO.unobserve(this.$refs.multiple);
      this.RO = null;
    }
  },
  methods: {
    setPopperWidth() {
      if (!this.$refs.popper || !this.$refs.popper.$el) return;
      if (this.filterable && this.query.length > 0) {
        this.$refs.popper.$el.style.width = `${this.filterPopperWidth > 0 ? this.filterPopperWidth : this.popperRefWidth}px`;
      } else {
        this.$refs.popper.$el.style.width = 'auto';
      }
    },
    getValues(checkStrategy = 'child') {
      const isParent = checkStrategy === 'parent';
      const isChild = checkStrategy === 'child';
      const isAll = checkStrategy === 'all';

      const roots = [];
      function getRoots(options) {
        options.forEach((item) => {
          if (item.checked && !item.indeterminate) {
            roots.push(item.parent.concat(item.value));
          } else if (item.indeterminate) {
            getRoots(item.children);
          }
        });
      }

      if (isChild) return this.value;

      if (isParent || isAll) {
        getRoots(this.rebuildOptions);
      }

      if (isParent) return roots;

      const allValues = [].concat(this.value);
      const stringifiedValue = this.value.map(JSON.stringify);
      for (let i = 0; i < roots.length; i++) {
        if (stringifiedValue.indexOf(JSON.stringify(roots[i])) < 0) {
          allValues.push(roots[i]);
        }
      }

      return allValues;
    },
    togglePopperShow(value) {
      this.changeOptionShow(value);
    },
    dropdownListClass(item) {
      const classArr = ['bui-dropdown-list-item'];
      if (item.disabled) {
        classArr.push('bui-dropdown-list-item-disabled');
      }
      return classArr;
    },
    handleQueryInput(value) {
      if (this.filterMethod) {
        this.loading = true;
        this.filterMethod(value, (customDataSource) => {
          this.loading = false;
          if (customDataSource) {
            this.customDataSource = customDataSource;
          }
        });
      }
    },
    handleSelectItem(value, item) {
      if (!this.multiple) {
        this.query = '';
        this.changeOptionShow(false);
      }
      if (this.customDataSource) {
        this.$emit('custom-item-click', value, item);
        return;
      }
      if (item.item.disabled) return false;
      if (this.multiple) {
        this.handleMultipleSelectItem(item);
        return;
      }
      const diffArr = value.filter((item, i) => {
        if (item === this.currentValue[i]) return item;
      });
      if (diffArr.length === value.length) return;
      this.currentValue = value;
      this.$emit('input', this.currentValue );
      this.$emit('change', this.currentValue );
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [this.currentValue]);
    },
    handleMultipleSelectItem(item) {
      const arrayHasEle = (array, element) => {
        for (let j = 0; j < array.length; j++) {
          const el = array[j];
          if (el.length === element.length) {
            for (let i = 0; i < el.length; i++) {
              if (el[i] !== element[i]) {
                break;
              }
              if (i == (el.length - 1)) { // 到最后一个元素都没有出现不相等，就说明这两个数组相等。
                return j;
              }
            }
          }
        }
        return -1;
      };
      const findSubItem = (item, result) => {
        if (item.children && item.children.length) {
          item.children.forEach((item) => {
            findSubItem(item, result);
          });
        } else {
          const valueArr = item._value;
          if (result.length === 0) {
            result.push(valueArr);
          } else {
            if (arrayHasEle(result, valueArr) === -1) {
              result.push(valueArr);
            }
          }
        }
      };
      if (!item.isLeaf) {
        findSubItem(item, this.currentValue);
        return;
      }

      const valueArr = item.value;
      if (this.currentValue.length > 0) {
        const index = arrayHasEle(this.currentValue, valueArr);
        if (index === -1) {
          this.currentValue.push(valueArr);
        } else {
          // delete
          this.currentValue.splice(index, 1);
        }
      } else {
        this.currentValue.push(valueArr);
      }
      this.$emit('input', this.currentValue );
      this.$emit('change', this.currentValue );
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [this.currentValue]);
    },
    bindEvent() {
      if (this.multiple && this.$refs.multiple) {
        this.RO = new ResizeObserver(this.handleResize);
        this.RO.observe(this.$refs.multiple);
      }
    },
    // this.$refs.multiple resize,更新popper
    handleResize() {
      if (this.optionShow) {
        this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.update');
      }
    },
    // remove tag
    handleTagRemove(curIndex) {
      const value = this.value.slice();
      value.splice(curIndex, 1);
      this.$emit('input', value);
      this.$emit('change', value);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [value]);
    },
    // 修改popper reference,在collapseTags为true时，this.$refs.multiple为绝对定位
    changePoperRef() {
      if (this.multiple && this.collapseTags && this.value.length && this.$refs.multiple) {
        this.popperRef = this.$refs.multiple;
      } else {
        this.popperRef = this.$refs.popperRef;
      }
      this.handlePopperPositionUpdate();
    },
    handlePopperPositionUpdate() {
      if (!this.optionShow) {
        return;
      }
      if (this.isMultipleWrapperShow) {
        setTimeout(()=>{
          this.handleResize();
        }, 0);
      } else {
        this.$nextTick(()=>{
          this.handleResize();
        });
      }
    },
    clickoutsideHandle(e) {
      if (!this.optionShow) {
        return;
      }
      const target = e.target;
      let parent = target.parentNode;
      const targetId = target.getAttribute('data-id');
      let parentId = parent && parent.getAttribute ? parent.getAttribute('data-id') : '';
      // 此处select-wrapper-multiple display:none
      if (targetId === this.selectMultipleCollapseWrapperId ) {
        return;
      }
      while (parent && !hasClass(parent, 'bui-cascader-menus') && parentId !== this.selectMultipleCollapseWrapperId) {
        parent = parent.parentNode;
        parentId = parent && parent.getAttribute ? parent.getAttribute('data-id') : '';
      }
      if (!parent) {
        this.changeOptionShow(false);
      }
    },
    handleSelectClick() {
      if (this.disabled) {
        return false;
      }
      this.changeOptionShow(!this.optionShow);
    },
    // 点击箭头关闭弹框
    handleIconClick() {
      if (this.disabled) return false;
      this.changeOptionShow(!this.optionShow);
    },
    // 点击清空按钮
    handleClearClick() {
      const value = [];
      this.currentValue = value;
      this.$emit('input', value);
      this.$emit('change', value);
      this.$emit('clear', value);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [value]);
      this.changeOptionShow(false);
    },
    handleInputBlur(e) {
      this.$emit('blur', e);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.blur', [this.currentValue]);
    },
    handleInputFocus(e) {
      this.$emit('focus', e);
    },
    handleMenuChange(value, close = true) {
      this.currentValue = value;
      this.$emit('input', value);
      this.$emit('change', value);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [value]);
      if (close) {
        this.changeOptionShow(false);
      }
    },
    changeOptionShow(flag) {
      this.optionShow = flag;
      this.changePoperRef();
    },
    handleActiveItemChange(value, close) {
      this.$emit('active-item-change', value);
      this.handleResize();
    },
    getSelectedLabel(item) {
      if (!this.multiple) return '';
      let option = {};
      const labels = [];
      this.rebuildOptions.some((opt) => {
        if (opt.value === item[0]) {
          option = opt;
          return true;
        }
      });
      const curPath = item.slice();
      curPath.forEach((key) => {
        if (option.value === key) {
          labels.push(option.label);
        }
        if (option && option.children && option.children.length) {
          option.children.some((opt) => {
            if (opt.value === key) {
              labels.push(opt.label);
              option = opt;
              return true;
            }
          });
        }
      });
      if (this.renderFormat instanceof Function) {
        return this.renderFormat(labels);
      } else {
        return _renderFormat(labels, this);
      }
    },
  },
};
</script>



