<template>
  <div class="bui-month-panels">
    <month
      v-for="(panel,index) in panels"
      :key="index"
      :prev="isHeaderPrevIconShow(index)"
      :next="isHeaderNextIconShow(index)"
      :value="value"
      :hover-value="hoverValue"
      :disabled-date="disabledDate"
      :view-date="monthPanelViewDates[index]"
      :range="range"
    />
  </div>
</template>
<script>
import Month from './../base/month.vue';
import panel from './../mixins/panel';

export default {
  name: 'PanelMonth',
  components: {
    Month,
  },
  mixins: [panel],

  data() {
    return {
      panels: this.range ? 2: 1,
    };
  },

  methods: {

  },
};
</script>



