<template>
  <div
    v-transfer-dom
    v-show="innerValue"
    :class="baseClass"
    :style="drawerStyle"
    :data-transfer="true"
    :data-show="value"
    @click.self="onMaskClick">
    <!-- <transition name="fade">
      <div
        v-if="value&&maskable"
        class="bui-drawer-backdrop"
        @click="onMaskClick"/>
    </transition> -->
    <transition
      :name="'move-'+placement"
      @after-enter="afterEnter"
      @after-leave="afterLeave">
      <div
        v-show="value"
        :style="drawerWrapperStyle"
        class="bui-drawer-wrapper">
        <byted-icon
          v-if="closable"
          class="bui-drawer-icon-close"
          name="close"
          @click="onModalClose"/>
        <div
          v-if="hasHeader"
          class="bui-drawer-header">
          <slot
            v-if="$slots.header"
            name="header"/>
          <span
            v-else
            class="bui-drawer-header-title">{{ title }}</span>
        </div>
        <div
          class="bui-drawer-body">
          <slot
            v-if="$slots.body"
            name="body"/>
          <slot v-else/>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import TransferDom from 'directives/transfer-dom';
import {isOneOf} from 'utils/helper.js';
// import {setScrollBar, resetScrollBar} from 'components/modal/src/setScrollEffect.js';
import Popup from 'mixins/popup/index.js';
import BytedIcon from 'components/icon';

export default {
  name: `${ CSS_PREFIX }drawer`,
  components: {BytedIcon},

  directives: {TransferDom},

  mixins: [Popup],

  props: {
    // 是否可见
    value: {
      type: Boolean,
      default: false,
    },
    // 左滑入or右滑入
    placement: {
      type: String,
      validator(value) {
        return isOneOf(value, ['left', 'right']);
      },
      default: 'right',
    },
    // 面板宽度
    width: {
      type: [Number, String],
      default: 256,
    },
    // 是否显示关闭按钮
    closable: {
      type: Boolean,
      default: true,
    },
    title: {
      type: String,
      default: '',
    },
    // 抽屉样式
    drawerStyle: {
      type: Object,
      default: () => {
        return {};
      },
    },
  },

  data() {
    return {
      innerValue: this.value,
    };
  },

  computed: {
    hasHeader() {
      return this.$slots.header || this.title !== '';
    },
    baseClass() {
      const clsArr = [`${ CSS_PREFIX }drawer`, `${ CSS_PREFIX }drawer-${ this.placement }`];
      if (!this.hasHeader) {
        clsArr.push(`${ CSS_PREFIX }drawer-no-header`);
      }
      if (!this.maskable) {
        clsArr.push(`${ CSS_PREFIX }drawer-no-mask`);
      }
      return clsArr;
    },
    drawerWrapperStyle() {
      const wrapperStyle = {
        width: `${ this.width }px`,
      };
      return {...this.drawerStyle, ...wrapperStyle};
    },
  },

  watch: {
    value(newVal) {
      if (newVal) {
        this.$emit('show');
        this.innerValue = newVal;
      } else {
        this.$emit('hide');
        // 动画的消失需要250ms，等动画完成再关闭组件
        setTimeout(() => {
          this.innerValue = newVal;
        }, 250);
      }
    },
    innerValue(newVal) {
      if (newVal) {
        // this.addBackdrop();
      } else {
        // this.$nextTick(()=>{
        //   this.resetBodyScrollBar();
        // });
      }
    },
  },
  destroyed() {
    this.close();
  },
  methods: {
    onModalClose() {
      this.$emit('input', false);
    },
    onMaskClick() {
      if (this.maskClosable) {
        this.$emit('input', false);
      }
    },
    // checkScrollBar() {
    //   const masks = document.getElementsByClassName(`${ CSS_PREFIX }drawer`) || [];
    //   return Array.from(masks).some((item) => item.dataset['show'] === 'true');
    // },
    // setBodyScrollBar() {
    //   setScrollBar();
    //   document.body.style.overflow = 'hidden';
    // },
    // resetBodyScrollBar() {
    //   if (this.checkScrollBar()) return false;
    //   resetScrollBar();
    //   document.body.style.overflow = '';
    // },
    afterEnter() {
      this.$emit('shown');
    },
    afterLeave() {
      this.$emit('hidden');
    },
  },
};
</script>




