<script>
import {CSS_PREFIX} from '../../base.js';
import {isOneOf} from 'utils/helper.js';
import emitter from 'mixins/emitter';

export default {
  name: `${CSS_PREFIX}tabs`,

  mixins: [emitter],

  props: {
    value: {
      type: [String, Number],
      default: '',
      require: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    // 大小
    size: {
      type: String,
      validator(value) {
        return isOneOf(value, ['lg', 'md', 'sm']);
      },
      default: 'sm',
    },
    // 标签的基本样式
    type: {
      type: String,
      validator(value) {
        return isOneOf(value, ['line', 'line-high', 'card', 'editable-card', 'border-card']);
      },
      default: 'line',
    },
    // 标签是否可关闭
    closable: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      panes: [],
      // 左侧箭头展示
      leftShow: false,
      // 右侧箭头展示
      rightShow: true,
      // 滚动内容偏移量
      navOffset: 0,
      // 滚动内容宽度
      containerSize: 0,
      // 窗口宽度
      navSize: 0,
      navs: null,
      tabs: null,
      operate: '',
    };
  },
  computed: {
    baseClass() {
      const classArr = [`${CSS_PREFIX}tabs`];
      const sizeObj = {
        lg: 'byted-tabs-lg',
        md: 'byted-tabs-md',
        sm: 'byted-tabs-sm',
      };
      if (this.size) {
        classArr.push(sizeObj[this.size]);
      }
      const typeObj = {
        'line': 'byted-tabs-line',
        'line-high': 'byted-tabs-line-high',
        'border-card': 'byted-tabs-border-card',
      };
      if (this.type) {
        classArr.push(typeObj[this.type]);
      }
      if (this.disabled) {
        classArr.push(`${CSS_PREFIX}tabs-disabled`);
      }
      return classArr;
    },
    // 当内容与展示窗框宽度相同时，则无需点击滚动
    scrollable() {
      return this.navSize < this.containerSize;
    },
    prevable() {
      return this.scrollable && this.leftShow;
    },
    nextable() {
      return this.scrollable && this.rightShow;
    },
  },
  watch: {
    value: {
      handler(value) {
        this.selectedTab();
        this.broadcast(`${CSS_PREFIX}table`, 'on-visible-change', true);
      },
      immediate: true,
    },
    panes() {
      if (!this.operate) {
        this.selectedTab();
        this.operate = '';
      } else {
        this.moveAnimate(0);
        this.selectedTab();
      }
    },
  },
  mounted() {
    this.navs = this.$refs.navs;
    this.tabs = this.$refs.tabs;
    this.updateSize();
  },
  updated() {
    if (this.operate !== 'select') {
      this.updateSize();
    }
    this.operate = '';
  },
  methods: {
    // 更新视口及tab内容宽度
    updateSize() {
      this.navSize = this.navs ? this.navs.offsetWidth : 0;
      this.containerSize = this.tabs ? this.tabs.offsetWidth : 0;
    },
    // value值选中状态
    selectedTab() {
      this.panes.forEach((item, index)=>{
        item.active = item.name === this.value;
        this.$nextTick(() => {
          if (item.active && this.scrollable) {
            this.activeMove(this.tabs.children[index]);
          }
        });
      });
    },
    // 左侧按钮滚动
    scrollPrev() {
      const navSize = this.navSize;
      let navOffset = this.navOffset;

      if (navOffset <= navSize) {
        this.leftShow = false;
      }
      this.rightShow = true;

      this.$nextTick(() => {
        if (!this.leftShow) {
          navOffset = 0;
        } else {
          navOffset = navOffset - navSize;
        }
        this.moveAnimate(navOffset);
      });
    },
    // 右侧按钮滚动
    scrollNext() {
      const {containerSize, navSize} = this;

      let navOffset = this.navOffset;
      if (containerSize - navOffset <= navSize * 2) {
        this.rightShow = false;
      }
      this.leftShow = true;

      this.$nextTick(() => {
        if (!this.rightShow) {
          navOffset = this.containerSize - this.navSize;
        } else {
          navOffset = this.navOffset + this.navSize;
        }
        this.moveAnimate(navOffset);
      });
    },
    // 移动过渡函数
    moveAnimate(distance) {
      this.tabs.style.transform = `translate3d(${ -distance }px, 0, 0)`;
      this.navOffset = distance;
    },
    handleClosePane(pane, event) {
      if (pane.disabled) return;
      event && event.stopPropagation();
      this.removePanes(pane, event);
    },
    // 数据层移除tab
    removePanes(pane, event) {
      const panes = this.panes;
      const index = panes.indexOf(pane);

      if (event) {
        this.$emit('remove', pane.name);
        this.removeScroll(this.tabs.children[index]);
      } else if (index > -1) {
        panes.splice(index, 1);
      }
      this.operate = 'remove';
    },
    // 删除tab,自动左侧滚动
    removeScroll(delTab) {
      // 删除元素
      if (!delTab || !this.scrollable) {
        return;
      }
      const {containerSize, navSize} = this;
      const targetFromParent = delTab.offsetLeft + delTab.offsetWidth;
      // 当无右侧点击滚动按钮，不触发
      if (containerSize - targetFromParent < navSize) {
        this.scrollNext();
      }
    },
    // 展示窗口边界元素被点击时，需调整滚动距离
    activeMove(target) {
      if (!target) return;
      // 元素距父级距离
      const targetLeft = target.offsetLeft;
      // 元素本身宽度
      const targetWidth = target.offsetWidth;
      // 本身与父级距离
      const targetFromParent = targetLeft + targetWidth;
      // 判断启始极限 = 移动距离
      let navOffset = this.navOffset;
      // 判断终点极限
      const endDistance = navOffset + this.navs.offsetWidth;
      // 当选择区域在视口内，则触发激活滚动
      if (targetLeft > navOffset && targetFromParent < endDistance) {
        return;
      }
      // 选中tab非第一个, 则展示左箭头
      this.leftShow = targetLeft !== 0;
      // 选中tab非最后一个， 则展示右箭头
      this.rightShow = targetFromParent !== this.tabs.offsetWidth;
      this.$nextTick(()=>{
        // 选中元素在视口内 启始移动临近位置
        if (endDistance > targetLeft && endDistance < targetFromParent) {
          navOffset = targetFromParent - this.navs.offsetWidth;
        } else if (navOffset > targetLeft && navOffset < targetFromParent) {
          navOffset = targetLeft;
        }

        // 选中元素不在视口内 选择项移动展示
        if (endDistance <= targetLeft) {
          navOffset = targetFromParent - this.navs.offsetWidth;
        }
        this.moveAnimate(navOffset);
      });
    },
    // 选中元素触发激活状态
    activePanes(pane) {
      if (pane) {
        this.$emit('input', pane.name);
        this.$emit('change', pane.name);
      }
    },
    // 初始化添加pane
    addPanes(paneItem) {
      this.panes.push(paneItem);
    },
    // 触发点击回调\]
    handleClick(item) {
      if (this.disabled || item.disabled) return;
      this.operate = 'select';
      this.activePanes(item);
    },
  },
  render() {
    const {baseClass, panes, handleClick, closable, scrollNext, scrollPrev, nextable, prevable, handleClosePane} = this;
    return (<div class={ baseClass }>
      <div class="bui-tabs-header">
        <div class="bui-tabs-wrapper">
          <byted-icon name="angle-left" class={['tabs-angle-left', prevable ? '' : 'is-disabled']} onClick={ scrollPrev }/>
          <div class="bui-tabs-scroll" ref="navs">
            <ul class="bui-tabs-nav" ref="tabs">
              {
                panes.map((pane) =>
                  (<li key={pane._uid} class={{'active': pane.active, 'disabled': pane.disabled, 'bui-tabs-nav-item': true}} onClick={ handleClick.bind(this, pane)}>
                    <a class="bui-tab-nav">
                      { pane.$slots.label ? pane.$slots.label : pane.label }
                      { closable && <byted-icon class="bui-nav-close-icon" name="close" onClick={ handleClosePane.bind(this, pane)}></byted-icon> }
                    </a>
                  </li>))
              }
            </ul>
          </div>
          <byted-icon name="angle-right" class={['tabs-angle-right', nextable ? '' : 'is-disabled']} onClick={ scrollNext }/>
          { this.$slots.append && <div class="add-tab">{ this.$slots.append }</div> }
        </div>
      </div>
      <div class="bui-tabs-content">
        { this.$slots.default }
      </div>
    </div>);
  },
};
</script>



