<template>
  <div class="bui-year-panels">
    <year
      v-for="(panel,index) in panels"
      :key="index"
      :prev="isHeaderPrevIconShow(index)"
      :next="isHeaderNextIconShow(index)"
      :value="value"
      :hover-value="hoverValue"
      :disabled-date="disabledDate"
      :view-date="yearPanelViewDates[index]"
      :range="range"
    />
  </div>
</template>
<script>
import Year from './../base/year.vue';
import panel from './../mixins/panel';

export default {
  name: 'PanelYear',
  components: {
    Year,
  },
  mixins: [panel],

  data() {
    return {
      panels: this.range ? 2: 1,
    };
  },

  methods: {

  },
};
</script>



