import {CSS_PREFIX} from '../../base.js';

export default {
  inject: ['rootMenu'],
  props: {
    paddingLeft: {
      type: Number,
      default() {
        return this.$options.name === `${CSS_PREFIX}menu-item-group` ? 8 : 16;
      },
    },
  },
  computed: {
    paddingStyle() {
      let paddingLeft = this.rootMenu.paddingLeft;
      let parent = this.$parent;
      let flag = this;
      if (!this.rootMenu.collapse) {
        while (parent && parent.$options.name !== `${CSS_PREFIX}menu`) {
          if ([`${CSS_PREFIX}submenu`, `${CSS_PREFIX}menu-item-group`].indexOf(parent.$options.name) !== -1) {
            paddingLeft += flag.paddingLeft;
            flag = parent;
          }
          parent = parent.$parent;
        }
      } else {
        paddingLeft = 16;
      }

      return {paddingLeft: paddingLeft + 'px'};
    },
    indexPath() {
      const paths = [this.index];
      let parent = this.$parent;
      while (parent && parent.$options.name !== `${CSS_PREFIX}menu`) {
        if (parent.index) {
          paths.unshift(parent.index);
        }
        parent = parent.$parent;
      }
      return paths;
    },
    parentMenu() {
      let parent = this.$parent;
      while (parent && [`${CSS_PREFIX}menu`, `${CSS_PREFIX}submenu`].indexOf(parent.$options.name) === -1) {
        parent = parent.$parent;
      }
      return parent;
    },
  },
};



