<template>
  <TreeSearch
    :base-class="baseClass"
    :tree-nodes-map="treeNodesMap"
    :filter-method="filterMethod">
    <ul
      v-if="!Boolean(keyword)"
      :class="baseClass">
      <TreeItem
        v-for="item in children"
        :key="item.id"
        v-bind="item"
      >
        <template
          slot="node"
          slot-scope="{item}">
          <slot
            :item="item"
            name="node">{{ item.label }}</slot>
        </template>
      </TreeItem>
    </ul>
  </TreeSearch>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import TreeItem from './tree-item.vue';
import TreeSearch from './tree-search.vue';
import {flat, getLeafIdsMap, isEqual} from './util';

export default {
  name: `${CSS_PREFIX}tree`,
  components: {TreeItem, TreeSearch},
  provide() {
    return {
      // 展开的节点
      $expandedKeys: () => this.expandedKeys,
      // 选中的节点，单选则只包含一个id
      $checkedKeys: () => this.checkedKeys,
      $childrenMap: () => this.childrenMap,
      $leafIdsMap: () => this.leafIdsMap,
      $indent: () => this.indent,
      $showCheckbox: () => this.showCheckbox,
      // 搜索关键词
      $keyword: () => this.keyword,
      $showLine: () => this.showLine,
      $checkStrictly: () => this.checkStrictly,
      $handleNodeClick: this.handleNodeClick,
      $handleToggleExpand: this.handleToggleExpand,
      $handleToggleChecked: this.handleToggleChecked,
      loadingNodesMap: () => this.loadingNodesMap,
    };
  },
  props: {
    source: {
      type: Array,
      default: () => [],
    },
    value: {
      type: Array,
      default: () => [],
    },
    indent: {
      type: Number,
      default: 16,
    },
    lazy: {
      type: Boolean,
      default: false,
    },
    load: {
      type: Function,
      default: () => {},
    },
    showCheckbox: {
      type: Boolean,
      default: false,
    },
    defaultExpandedKeys: {
      type: Array,
      default: () => [],
    },
    accordion: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    keyword: {
      type: String,
      default: '',
    },
    filterMethod: {
      type: Function,
      default: () => {},
    },
    showLine: {
      type: Boolean,
      default: false,
    },
    checkStrictly: {
      type: Boolean,
      default: false,
    },
    spread: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      expandedKeys: this.defaultExpandedKeys,
      checkedKeys: [],
      treeNodesMap: {},
      loadingNodesMap: {},
    };
  },
  computed: {
    baseClass() {
      return `${CSS_PREFIX}tree-control`;
    },
    childrenMap() {
      const result = {};
      const values = Object.values(this.treeNodesMap);
      for (let i = 0; i < values.length; i += 1) {
        const {id} = values[i];
        result[id] = values
            .filter((item) => item.parentId === id)
            .sort((prev, next) => prev.sortOrder - next.sortOrder);
      }
      return result;
    },
    leafIdsMap() {
      const result = getLeafIdsMap(this.treeNodesMap);
      return result;
    },
    children() {
      const result = this.source.map((item) => this.treeNodesMap[item.id]).filter(Boolean);
      return result;
    },
  },
  watch: {
    source: {
      handler(val, oldVal) {
        const flatted = flat(this.source);
        this.setTreeNodesMap(flatted, false);
      },
      immediate: true,
    },
    value: {
      handler(val) {
        this.checkedKeys = this.valueToChecked(val);
      },
      immediate: true,
    },
    checkedKeys(val, oldVal) {
      if (!isEqual(val, oldVal)) {
        this.$emit('input', this.checkedToValue(val));
      }
    },
  },
  methods: {

    setTreeNodesMap(treeNodes, isSubtree = false) {
      let newTreeNodesMap = {};
      if (isSubtree) {
        newTreeNodesMap = {...this.treeNodesMap};
        for (let i = 0; i < treeNodes.length; i += 1) {
          newTreeNodesMap[treeNodes[i].id] = {disabled: this.disabled, ...treeNodes[i], loading: false};
        }
        this.treeNodesMap = newTreeNodesMap;
        return;
      }
      for (let i = 0; i < treeNodes.length; i += 1) {
        newTreeNodesMap[treeNodes[i].id] = {disabled: this.disabled, ...treeNodes[i], loading: false};
      }
      this.treeNodesMap = Object.seal(newTreeNodesMap);
    },
    handleNodeClick(id) {
      this.$emit('node-click', this.treeNodesMap[id]);
      if (!this.showCheckbox) {
        this.checkedKeys = [id];
        this.$emit('selected-change', this.treeNodesMap[id]);
      }
    },
    isNeedLoadChildren(treeNode) {
      const hasChildrenLoaded =
        Object.values(this.treeNodesMap)
            .filter((node) => node.parentId === treeNode.id)
            .length > 0;
      return treeNode.hasChildren && !hasChildrenLoaded;
    },
    handleToggleExpand(id) {
      const treeNode = this.treeNodesMap[id];
      if (this.lazy && this.isNeedLoadChildren(treeNode)) {
        this.$set(
            this.loadingNodesMap,
            id,
            true
        );
        treeNode.loading = true;
        new Promise((resolve) => this.load(treeNode, resolve)).then((subtree) => {
          if (this.loadingNodesMap[id]) {
            this.loadingNodesMap[id] = false;
          }
          treeNode.loading = false;
          const flatted = flat(subtree, id);
          this.setTreeNodesMap(flatted, true);
          this.checkedKeys = this.valueToChecked(this.checkedKeys);
        });
      }
      if (!this.expandedKeys.includes(id)) {
        this.$emit('node-expand', treeNode);
        this.expandedKeys = this.expandedKeys.concat(id);
        if (this.accordion) {
          const siblings = Object.values(this.treeNodesMap)
              .filter((node) => node.parentId === treeNode.parentId && node.id !== id)
              .map((node) => node.id);
          this.expandedKeys = this.expandedKeys.filter((key) => !siblings.includes(key));
        }
      } else {
        this.$emit('node-collapse', treeNode);
        this.expandedKeys = this.expandedKeys.filter((key) => key !== id);
      }
    },
    // 是否叶子结点
    isLeaf(id) {
      const leafIds = this.leafIdsMap[id];
      return leafIds[0] === id && leafIds.length === 1;
    },
    handleToggleChecked(id) {
      const treeNode = this.treeNodesMap[id];
      this.$emit('check', treeNode);
      if (this.checkStrictly) {
        if (!this.checkedKeys.includes(id)) {
          this.checkedKeys = this.checkedKeys.concat(id);
        } else {
          this.checkedKeys = this.checkedKeys.filter((key) => key !== id);
        }
        return;
      }
      const leafIds = this.leafIdsMap[id];
      if (this.isLeaf(id)) {
        if (!this.checkedKeys.includes(id)) {
          this.checkedKeys = this.checkedKeys.concat(id);
        } else {
          this.checkedKeys = this.checkedKeys.filter((key) => key !== id);
        }
      } else {
        const isAllLeafsSelected =
          this.checkedKeys.length && leafIds.every((leaf) => this.checkedKeys.includes(leaf));
        if (isAllLeafsSelected) {
          this.checkedKeys = this.checkedKeys.filter((s) => !leafIds.includes(s));
        } else {
          this.checkedKeys = Array.from(new Set(this.checkedKeys.concat(leafIds)));
        }
      }
    },
    valueToChecked(val) {
      if (this.checkStrictly) {
        return val;
      }
      if (!this.showCheckbox) {
        return val;
      }
      const leafs = val.map((v) => this.leafIdsMap[v]).flat();
      return Array.from(new Set(leafs));
    },
    checkedToValue(checked) {
      if (this.checkStrictly) {
        return checked;
      }
      if (this.spread) {
        return checked;
      }
      if (!this.showCheckbox) {
        return checked;
      }
      let orphans = checked;
      const result = [];
      const treeNodes = Object.values(this.treeNodesMap).sort((prev, next) =>
        this.leafIdsMap[next.id].length - this.leafIdsMap[prev.id].length
      );
      for (let i = 0; i < treeNodes.length; i += 1) {
        if (orphans.length === 0) {
          break;
        }
        const {id} = this.treeNodesMap[treeNodes[i].id];
        const leafIds = this.leafIdsMap[id];
        if (this.isLeaf(id)) {
          if (orphans.includes(id)) {
            result.push(id);
            orphans = orphans.filter((s) => s !== id);
          }
        } else {
          if (leafIds.every((leaf) => orphans.includes(leaf))) {
            result.push(id);
            orphans = orphans.filter((s) => !leafIds.includes(s));
          }
        }
      }
      return result;
    },
  },
};
</script>



