<template>
  <li
    v-show="!Boolean(keyword) || visible"
    :class="{
      [baseClass]: true,
      [showLineClass]: showLine && hasExpectedChildren
  }">
    <div :class="itemClass">
      <loading-icon
        v-if="isLoading"/>
      <byted-icon
        v-else
        :class="{
          [switcherClass]: true,
          [switcherHiddenClass]: !hasExpectedChildren,
          [switcherOpenClass]: expand
        }"
        :name="showLine ? (expand ? 'minus' : 'plus') : 'sort-asc'"
        @click="handleToggleExpand"
      /><span
        :class="{
          [highlightClass]: true,
          [selectedClass]: isSelected,
          [disabledClass]: disabled
        }"
        @click="handleNodeClick"
      ><byted-checkbox
        v-if="$showCheckbox()"
        :disabled="disabled"
        :class="checkboxClass"
        :value="checked"
        :indeterminate="indeterminate"
        @input="handleToggleChange"
      /><span
        :class="contentClass"
      ><slot
        :item="{id, label, disabled, hasChildren, children, keyword, ...$attrs}"
        name="node"
      /></span
      ></span>
    </div>
    <bui-collapse-transition>
      <ul
        v-if="children"
        v-show="expand"
        :class="childrenClass"
        :style="{'padding-left': $indent() + 'px'}"
      >
        <byted-tree-item
          v-for="item in children"
          :key="item.id"
          v-bind="item"
        >
          <template
            slot="node"
            slot-scope="{item}">
            <slot
              :item="item"
              name="node" />
          </template>
        </byted-tree-item>
      </ul>
    </bui-collapse-transition>
  </li>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import BuiCollapseTransition from 'utils/collapse-transition.vue';
import TreeItem from './tree-item.vue';
import BytedCheckbox from 'components/checkbox/index.js';
import LoadingIcon from 'components/icon/loading-icon.js';
import BytedIcon from 'components/icon/index.js';

export default {
  name: `${CSS_PREFIX}tree-item`,
  components: {
    BuiCollapseTransition,
    TreeItem,
    BytedCheckbox,
    LoadingIcon,
    BytedIcon,
  },
  inheritAttrs: false,
  inject: [
    '$expandedKeys',
    '$checkedKeys',
    '$childrenMap',
    '$leafIdsMap',
    '$indent',
    '$showCheckbox',
    '$keyword',
    '$showLine',
    '$checkStrictly',
    '$handleToggleExpand',
    '$handleToggleChecked',
    '$handleNodeClick',
    'loadingNodesMap',
  ],
  props: {
    id: {
      type: String,
      default: '',
    },
    label: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    hasChildren: {
      type: Boolean,
      default: false,
    },
    loading: {
      type: Boolean,
      default: false,
    },
    visible: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    isLoading() {
      return this.loadingNodesMap()[this.id];
    },
    baseClass() {
      return `${CSS_PREFIX}tree-control-item`;
    },
    showLineClass() {
      return `${this.baseClass}--show-line`;
    },
    itemClass() {
      return `${this.baseClass}__item`;
    },
    switcherClass() {
      return `${this.baseClass}__switcher`;
    },
    switcherOpenClass() {
      return `${this.switcherClass}--open`;
    },
    switcherHiddenClass() {
      return `${this.switcherClass}--hidden`;
    },
    highlightClass() {
      return `${this.baseClass}__highlight`;
    },
    checkboxClass() {
      return `${this.baseClass}__checkbox`;
    },
    contentClass() {
      return `${this.baseClass}__content`;
    },
    childrenClass() {
      return `${this.baseClass}__children`;
    },
    disabledClass() {
      return `${this.highlightClass}--disabled`;
    },
    selectedClass() {
      return `${this.highlightClass}--selected`;
    },
    isSelected() {
      return !this.$showCheckbox() && this.checkedKeys.includes(this.id);
    },
    childrenMap() {
      return this.$childrenMap();
    },
    children() {
      return this.childrenMap[this.id];
    },
    leafIdsMap() {
      return this.$leafIdsMap();
    },
    leafIds() {
      return this.leafIdsMap[this.id];
    },
    isLeaf() {
      return this.leafIds[0] === this.id && this.leafIds.length === 1;
    },
    checkedKeys() {
      return this.$checkedKeys();
    },
    checked() {
      if (this.checkStrictly) {
        return this.checkedKeys.includes(this.id);
      }
      if (this.isLeaf) {
        return this.checkedKeys.includes(this.id);
      }
      return this.leafIds.every((leaf) => this.checkedKeys.includes(leaf));
    },
    indeterminate() {
      if (this.isLeaf) {
        return false;
      }
      if (this.checkStrictly) {
        return false;
      }
      return !this.checked && this.leafIds.some((leaf) => this.checkedKeys.includes(leaf));
    },
    expandedKeys() {
      return this.$expandedKeys();
    },
    expand() {
      return this.expandedKeys.includes(this.id);
    },
    keyword() {
      return this.$keyword();
    },
    hasExpectedChildren() {
      return this.hasChildren || (this.children && this.children.length > 0);
    },
    showLine() {
      return this.$showLine();
    },
    checkStrictly() {
      return this.$checkStrictly();
    },
  },

  methods: {
    handleToggleExpand() {
      this.$handleToggleExpand(this.id);
    },
    handleToggleChange() {
      this.$handleToggleChecked(this.id);
    },
    handleNodeClick(e) {
      e.preventDefault();
      let node = e.target;
      const currentTarget = e.currentTarget;
      while (true) {
        if (node.classList.value === 'bui-checkbox-input-wrapper' && !this.disabled) {
          this.handleToggleChange();
          break;
        }
        if (node === currentTarget || node === document.body) {
          e.preventDefault();
          break;
        }
        node = node.parentNode;
      }
      if (!this.disabled) {
        this.$handleNodeClick(this.id);
      }
    },
  },
};
</script>



