<template>
  <div class="bui-date-panels">
    <date
      v-for="(panel, index) in panels"
      :key="index"
      :prev="isHeaderPrevIconShow(index)"
      :next="isHeaderNextIconShow(index)"
      :value="value"
      :hover-value="hoverValue"
      :view-date="datePanelViewDates[index]"
      :disabled-date="disabledDate"
      :week="type==='week'"
      :date-render="dateRender"
      :range="range"/>
  </div>
</template>
<script>
import Date from './../base/date.vue';
import panel from './../mixins/panel';

export default {
  name: 'PanelDate',
  components: {
    Date,
  },
  mixins: [panel],

  data() {
    return {
      panels: this.range ? 2: 1,
    };
  },

  methods: {

  },
};
</script>



