<template>
  <transition
    :name="`${CSS_PREFIX}loading-fade`"
    @enter="showLoading"
    @leave-cancelled="leaveCancelled"
    @after-leave="hideLoading">
    <div
      v-show="show"
      :class="baseClass">
      <div :class="baseIconClass">
        <loading-icon/>
      </div>
    </div>
  </transition>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import {getStyle, setStyle} from 'utils/dom.js';
import LoadingIcon from '../../icon/loading-icon';
import {isOneOf} from 'utils/helper.js';
export default {
  name: `${CSS_PREFIX}loading`,
  iconName: `${CSS_PREFIX}loading-icon`,
  components: {
    LoadingIcon,
  },
  props: {
    show: {
      type: Boolean,
      default: true,
    },
    parent: {
      type: Object,
      default() {
        return null;
      },
    },
    size: {
      type: String,
      default: 'normal', // normal, small
      validator(value) {
        return isOneOf(value, ['normal', 'default', 'small', 'xl', 'lg', 'md', 'sm', 'xs']);
      },
    },
    color: {
      type: String,
      default: '#598FE6',
    },
  },
  data() {
    return {
      parentStyle: {
        overflow: '',
        position: '',
      },
      inDirective: false,
      CSS_PREFIX,
    };
  },
  computed: {
    baseClass() {
      return {
        [`${CSS_PREFIX}loading`]: true,
        [`${CSS_PREFIX}loading-v`]: this.inDirective,
      };
    },
    baseIconClass() {
      const baseIconClass = 'bui-loading-icon';
      const baseIconArr = [baseIconClass];
      baseIconArr.push(`${baseIconClass}-${this.size}`);
      return baseIconArr;
    },
  },
  methods: {
    showLoading() {
      this.$nextTick(() => {
        if (!this.inDirective || !this.parent) return;
        this.parentStyle.position = this.parent.style.position;
        this.parentStyle.overflow = this.parent.style.overflow;

        const parentPosition = getStyle(this.parent, 'position');
        if (parentPosition === 'static') {
          setStyle(this.parent, 'position', 'relative');
        }
        setStyle(this.parent, 'overflow', 'hidden');
      });
    },
    leaveCancelled() {
      this.hideLoading();
    },
    hideLoading() {
      setStyle(this.parent, 'position', this.parentStyle.position);
      setStyle(this.parent, 'overflow', this.parentStyle.overflow);
    },
  },
};
</script>




