<template>
  <li
    :class="extendCLass">
    <div
      :style="paddingStyle"
      class="bui-menu-item-group-title"
    >
      <template v-if="!$slots.title">{{ title }}</template>
      <slot
        v-else
        name="title"/>
    </div>
    <ul class="bui-menu-ul">
      <slot/>
    </ul>
  </li>
</template>

<script>
import {CSS_PREFIX} from '../../base.js';
import CommonLogic from './common-logic.js';

export default {
  name: `${CSS_PREFIX}menu-item-group`,

  mixins: [CommonLogic],

  props: {
    title: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
    };
  },
  computed: {
    extendCLass() {
      return [].concat([`${CSS_PREFIX}menu-item-group`, 'bui-menu-ul'], this.rootMenu.theme === 'light' ? 'bui-menu-item-group-light': 'bui-menu-item-group-dark');
    },
  },
};
</script>



