<template>
  <div
    v-clickoutside="clickoutsideHandle"
    :class="baseClass">
    <div
      ref="popperRef"
      :class="wrapperClass"
      class="bui-select-wrapper"
      @click="handleSelectClick"
      @mouseenter="handleWrapMouseEnter"
      @mouseleave="handleWrapMouseLeave">
      <slot name="input">
        <div
          v-show="isMultipleWrapperShow"
          ref="multiple"
          :class="multipleClass"
          class="bui-select-wrapper-multiple">
          <byted-tag
            v-for="item in selected"
            :key="item.key"
            :disabled="disabled"
            :plain="disabled"
            :size="multipleSize"
            class="bui-select-tag"
            type="default"
            closable
            @close="handleTagRemove(item.value)">
            {{ getSelectedLabel(item) }}
          </byted-tag>

        </div>
        <div
          v-if="isMultipeCollapseTagsShow"
          :data-id="selectMultipleCollapseWrapperId"
          class="bui-select-wrapper-multiple bui-select-wrapper-multiple-collapse">
          <byted-tag
            v-for="(tag, index) in collapseTagsList"
            :key="index"
            :disabled="disabled"
            :plain="disabled"
            :size="multipleSize"
            class="bui-select-tag"
            closable
            @close="handleTagRemove(tag.value)">
            {{ getSelectedLabel(tag) }}
          </byted-tag>
          <span class="bui-selected-label">
            {{ _hasSelect }}
          </span>
        </div>
        <input
          v-if="isInputShow"
          ref="input"
          :placeholder="_placeholder"
          :value="getSelectedLabel(selected[0])"
          :disabled="disabled"
          :style="inputStyle"
          readonly
          class="bui-input"
          @blur="handleInputBlur"
          @focus="handleInputFocus">
        <div
          v-if="plain"
          :style="inputStyle"
          class="bui-select-wrapper-plain">
          <span
            class="bui-input">{{ getSelectedLabel(selected[0]) || _placeholder }}</span>
          <byted-icon
            :class="{open:optionShow}"
            name="angle-down"
            class="bui-select-icon-plain"
            @click.stop="handleIconClick"/>
        </div>
      </slot>
      <byted-icon
        v-show="!plain && !showClear"
        :class="{open:optionShow}"
        name="angle-down"
        class="bui-select-icon"
        @click.stop="handleIconClick"/>
      <byted-icon
        v-if="!plain && showClear"
        name="close-circle"
        class="bui-select-icon bui-select-clear-icon"
        @click.stop="handleClearClick"/>
    </div>
    <byted-popper
      v-show="optionShow"
      ref="popper"
      :reference="popperRef"
      :append-to-body="transfer"
      :popper-options="popperOptions"
      :class="popperClass"
      placement="bottom-start"
      class="bui-select-popper">
      <div class="bui-select-option">
        <div
          v-if="filterable"
          class="bui-option-filter">
          <byted-input
            ref="filter"
            :size="size"
            v-model="filterLabel"
            :validate-event="false"
            :placeholder="_filterPlaceholder"
            prefix="search"
            clearable
            @input="handlefilterInput"
            @clear="handleFilterClear">
            <template slot="append">
              <slot name="filter-append"/>
            </template>
          </byted-input>
        </div>
        <div class="bui-select-option-wrapper">
          <slot/>
        </div>
        <div
          v-if="optionsArr.length && filteredOptionsCount <= 0"
          class="bui-option-match-none">
          <byted-icon name="empty"/>
          {{ _noMatchText }}
        </div>
        <template v-if="!optionsArr.length">
          <slot
            name="empty">
            <div
              class="bui-option-match-none">
              <byted-icon name="empty"/>
              {{ _noDataText }}
            </div>
          </slot>
        </template>

        <slot name="footer" />
      </div>
    </byted-popper>
  </div>
</template>
<script>
import {CSS_PREFIX, LOCALE_PREFIX} from '../../base.js';
import {isOneOf} from 'utils/helper.js';
import emitter from 'mixins/emitter';
import BytedTag from 'components/tag';
import BytedIcon from 'components/icon';
import BytedInput from 'components/input';
import BytedPopper from 'components/popper';
import locale from 'mixins/locale';
import clickoutside from 'directives/clickoutside';
import {hasClass} from 'utils/dom.js';
import ResizeObserver from 'resize-observer-polyfill';
import Util from 'utils/utils.js';
import {getMeasureTextWidth} from 'utils/measure-text';

export default {
  name: `${CSS_PREFIX}select`,
  directives: {clickoutside},

  provide() {
    return {
      select: this,
      optgroup: null,
    };
  },

  components: {
    BytedTag,
    BytedIcon,
    BytedInput,
    BytedPopper,
  },

  directives: {clickoutside},

  mixins: [emitter, locale],

  props: {
    // 是否多选
    multiple: {
      type: Boolean,
      default: false,
    },
    // 选中值
    value: {
      type: [String, Number, Array],
      required: true,
    },
    // 默认填充文案
    placeholder: {
      type: String,
      default: '',
    },
    // 是否禁用
    disabled: {
      type: Boolean,
      default: false,
    },
    clearable: {
      type: Boolean,
      default: false,
    },
    transfer: {
      type: Boolean,
      default: false,
    },
    // 选中项是否要折叠
    collapseTags: {
      type: Boolean,
      default: false,
    },
    // 是否要过滤选项
    filterable: {
      type: Boolean,
      default: false,
    },
    filterPlaceholder: {
      type: String,
      default: '',
    },
    noDataText: {
      type: String,
      default: '',
    },
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, [
          'md',
          'small',
          'large',
          'xs',
          'sm',
          'lg',
          'xl',
        ]);
      },
    },
    // 搜索条件无匹配时显示的文字
    noMatchText: {
      type: [String, Function],
      default: '',
    },
    plain: {
      type: Boolean,
      default: false,
    },
    status: {
      type: String,
      default: '',
      validator(value) {
        return isOneOf(value, [
          '',
          'error',
        ]);
      },
    },
    align: {
      type: String,
      default: 'left',
      validator(value) {
        return isOneOf(value, [
          'left',
          'center',
          'right',
        ]);
      },
    },
    popperOptions: {
      type: Object,
      default() {
        return {
          modifiers: {
            flip: {
              enabled: true,
            },
            hide: {
              enabled: false,
            },
            preventOverflow: {
              enabled: false,
            },
          },
        };
      },
    },
    popperClass: {
      type: String,
      default: '',
    },
    filterMethod: {
      type: Function,
      default: null,
    },


  },

  data() {
    return {
      // popper show
      optionShow: false,
      // options array
      optionsArr: [],
      // popper reference
      popperRef: null,
      popperRefWidth: 0,
      // filter word
      filterLabel: '',
      selectMultipleCollapseWrapperId: `bui-select-wrapper-multiple-collapse_${this._uid}`,
      hovering: false,
      selected: [],
      // 该变量提供给业务方二次开发使用，用于渲染显示label标签，类型为function
      renderLabel: null,
    };
  },

  computed: {
    _placeholder() {
      return this.placeholder || this.t(`${LOCALE_PREFIX}.select.placeholder`);
    },
    _filterPlaceholder() {
      return this.filterPlaceholder || this.t(`${LOCALE_PREFIX}.select.filterPlaceholder`);
    },
    _noDataText() {
      return this.noDataText || this.t(`${LOCALE_PREFIX}.select.noDataText`);
    },
    _noMatchText() {
      return this.noMatchText || this.t(`${LOCALE_PREFIX}.select.noMatchText`);
    },
    _hasSelect() {
      if (this.selected.length > 99) {
        return '99+';
      }
      return this.selected.length;
    },
    baseClass() {
      const baseClass = `${CSS_PREFIX}select`;
      const classArr = [baseClass];
      if (this.multiple) {
        classArr.push(`${baseClass}-multiple`);
      }
      if (this.disabled) {
        classArr.push(`${baseClass}-disabled`);
      }
      const slot = this._slotContents || this.$slots || {};
      const hasCustomInput = slot['input'] != null;
      if (hasCustomInput) {
        classArr.push(`${baseClass}-custom`);
      }
      if (!this.multiple) {
        classArr.push(`${baseClass}-${this.size}`);
      } else {
        classArr.push(`${baseClass}-${this.multipleSize}`);
      }
      if (this.plain) {
        classArr.push(`${baseClass}-plain`);
      }
      if (this.status) {
        classArr.push(`${baseClass}-${this.status}`);
      }
      return classArr;
    },
    multipleSize() {
      if (this.size === 'sm' || this.size === 'md') {
        return this.size;
      }
      return 'md';
    },

    wrapperClass() {
      return {
        'active': this.optionShow && !this.collapseTags,
      };
    },
    isInputShow() {
      if (this.multiple && Array.isArray(this.value) && this.value.length) {
        return false;
      }
      // 无边框 不显示input
      if (this.plain) {
        return false;
      }
      return true;
    },
    inputStyle() {
      return {
        textAlign: this.align,
      };
    },
    multipleClass() {
      return {
        'bui-select-wrapper-absolute': this.multiple && this.optionShow && this.collapseTags,
      };
    },
    isMultipleWrapperShow() {
      return this.multiple && this.selected.length && (!this.collapseTags || this.optionShow);
    },
    isMultipeCollapseTagsShow() {
      return this.multiple && this.collapseTags && this.selected.length && !this.optionShow;
    },


    // 计算collapseTags下tag可以显示的tag
    collapseTagsList() {
      if (!this.multiple) return [];
      if (!this.popperRefWidth) {
        return this.selected.length ? [this.selected[0]] : [];
      }
      // label length width
      const labelWidth = Math.max(26, (getMeasureTextWidth(this.selected.length) + 12));
      // 容器总宽度 - paddingLeft - paddingRight - border-label
      const wrapperWidth = this.popperRefWidth - 8 - 32 - 2 - labelWidth;
      let currentWidth = 0;
      const list = [];
      let i = 0;
      const fontSize = this.size === 'sm' ? 12 : 14;
      const tagPaddingLeft = this.size === 'sm' ? 8 : 8;
      const tagPaddingRight = this.size === 'sm' ? 25 : 25;
      while (currentWidth < wrapperWidth && i < this.selected.length) {
        // border + marginLeft
        const width = 2 + 4 + tagPaddingLeft + getMeasureTextWidth(this.getSelectedLabel(this.selected[i]), {fontSize}) + tagPaddingRight;
        // const width = 2 + 4 + tagPaddingLeft + this.getSelectedLabel(this.selected[i]).length * fontSize + tagPaddingRight;
        if (currentWidth + width < wrapperWidth) {
          list.push(this.selected[i]);
          currentWidth += width;
        } else {
          break;
        }
        i++;
      }
      return list.length ? list : [this.selected[0]];
    },
    showClear() {
      let hasValue;
      if (this.multiple) {
        hasValue = Array.isArray(this.value) && this.value.length > 0;
      } else {
        hasValue = this.value !== undefined && this.value !== null && this.value !== '';
      }
      return (this.clearable
              && !this.disabled
              && hasValue
              && this.hovering);
    },
    // 记录过滤项的个数
    filteredOptionsCount() {
      return this.optionsArr.filter((option) => {
        return option.visible;
      }).length;
    },
  },

  watch: {
    value(newVal) {
      this.setSelected();
    },
    optionShow(newVal) {
      newVal
        ? this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.update')
        : this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.destroy');
      this.$emit('visible-change', newVal);
    },
    optionsArr() {
      this.setSelected();
    },
    filterLabel(val) {
      this.$emit('change:filter', val);
    },
  },
  created() {
    if (this.multiple && !Array.isArray(this.value)) {
      console.error('SELECT ERROR: 请设置value值为数组');
    }
  },
  mounted() {
    this.$nextTick(() => {
      if (!this.$refs.popperRef) {
        return;
      }
      this.popperRef = this.$refs.popperRef;
      this.popperRefRo = new ResizeObserver(() => {
        this.popperRefWidth = this.$refs.popperRef && this.$refs.popperRef.clientWidth || 0;
      });
      this.popperRefRo.observe(this.$refs.popperRef);
    });
    this.setSelected();
    // 监测DOM变化，更新popper
    this.bindEvent();
  },
  beforeDestroy() {
    try {
      if (this.RO) {
        this.RO.unobserve(this.$refs.multiple);
        this.RO = null;
      }
      if (this.popperRefRo) {
        this.popperRefRo.unobserve(this.$refs.popperRef);
        this.popperRefRo = null;
      }
    } catch (e) {
      this.RO = null;
      this.popperRefRo = null;
    }
  },

  methods: {
    togglePopperShow(value) {
      this.changeOptionShow(value);
    },
    bindEvent() {
      if (this.multiple) {
        this.$nextTick(() => {
          if (!this.$refs.multiple) {
            return;
          }
          this.RO = new ResizeObserver(this.handleResize);
          this.RO.observe(this.$refs.multiple);
        });
      }
    },
    // 修改popper reference,在collapseTags为true时，this.$refs.multiple为绝对定位
    changePoperRef() {
      if (this.multiple && this.collapseTags && this.selected.length && this.$refs.multiple) {
        this.popperRef = this.$refs.multiple;
      } else {
        this.popperRef = this.$refs.popperRef;
      }
    },
    // 获取选中项的label
    getSelectedLabel(option = null) {
      if (option) {
        return option.currentLabel;
      }
      return '';
    },
    handleWrapMouseEnter() {
      this.hovering = true;
    },
    handleWrapMouseLeave() {
      this.hovering = false;
    },
    // 点击箭头关闭弹框
    handleIconClick() {
      if (this.disabled) return false;
      this.changeOptionShow(!this.optionShow);
    },
    // 点击清空按钮
    handleClearClick() {
      const value = this.multiple ? [] : '';
      this.$emit('input', value);
      this.emitChange(value);
      this.$emit('clear', value);
      this.changeOptionShow(false);
    },
    changeOptionShow(open) {
      this.optionShow = open;
      this.changePoperRef();
      this.handlePopperPositionUpdate();
    },
    handlePopperPositionUpdate() {
      if (!this.optionShow) {
        return;
      }
      if (this.isMultipleWrapperShow) {
        setTimeout(()=>{
          this.handleResize();
        }, 0);
      } else {
        this.handleResize();
      }
    },
    clickoutsideHandle(e) {
      if (!this.optionShow) {
        return;
      }
      if (!this.multiple && !this.filterable) {
        this.changeOptionShow(false);
        return;
      }
      const target = e.target;
      let parent = target.parentNode;
      const targetId = target.getAttribute('data-id');
      let parentId = parent && parent.getAttribute ? parent.getAttribute('data-id') : '';
      // 此处select-wrapper-multiple display:none
      if (targetId === this.selectMultipleCollapseWrapperId ) {
        return;
      }
      while (parent && !hasClass(parent, 'bui-select-popper') && parentId !== this.selectMultipleCollapseWrapperId) {
        parent = parent.parentNode;
        parentId = parent && parent.getAttribute ? parent.getAttribute('data-id') : '';
      }
      if (!parent) {
        this.changeOptionShow(false);
        this.filterLabel = '';
        this.handleQueryChange('');
      }
    },
    // select wrapper click
    handleSelectClick() {
      if (this.disabled) return;
      this.changeOptionShow(!this.optionShow);
    },
    setFilterFocus() {
      this.filterable && this.$refs.filter && this.$refs.filter.focus();
    },
    // 销毁option
    handleOptionDestory(option) {
      // option销毁后，修改option的数组
      const index = this.optionsArr.indexOf(option);
      if (index > -1) {
        this.optionsArr.splice(index, 1);
      }
    },
    // 处理option选中
    handleOptionClick(option) {
      let value;
      if (this.multiple) {
        value = this.value.slice();
        const optionIndex = this.value.indexOf(option.value);
        optionIndex > -1 ? value.splice(optionIndex, 1) : value.push(option.value);
        this.$emit('input', value);
        this.emitChange(value);
      } else {
        value = [option.value];
        this.$emit('input', option.value);
        this.emitChange(option.value);
        this.changeOptionShow(false);
      }
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', value);
    },
    emitChange(val) {
      if (!this.valueEquals(this.value, val)) {
        this.$emit('change', val);
      }
    },
    // 比较值是否完全相等
    valueEquals(a, b) {
      if (a === b) return true;
      if (!(a instanceof Array)) return false;
      if (!(b instanceof Array)) return false;
      if (a.length !== b.length) return false;
      for (let i = 0; i !== a.length; i++) {
        if (a[i] !== b[i]) return false;
      }
      return true;
    },
    // 设置option的选中项
    setSelected() {
      const oldSelected = this.selected;
      let selected = [];
      const curValue = this.multiple ? this.value || []: [this.value];

      curValue.forEach((value, index) => {
        const option = this.optionsArr.filter((item) => item.value === value);
        const oldOption = oldSelected.filter((item) => item.value === value);
        // 外部返回label值，暂时支持基本类型，option > old option, 兜底label显示value
        if (this.renderLabel && typeof this.renderLabel === 'function') {
          selected.push({value, currentLabel: this.renderLabel(value)});
        } else if (option.length) {
          selected = selected.concat(option);
        } else if (oldOption.length) {
          selected = selected.concat(oldOption);
        } else if (value !== '') {
          selected.push({value, currentLabel: value});
        }
        if (selected[index]) selected[index]['key'] = Util.getRandomStr(6);
      });
      this.selected = selected;

      this.changePoperRef();
    },
    // remove tag
    handleTagRemove(curVal) {
      const value = this.value.slice();
      value.splice(value.indexOf(curVal), 1);
      this.$emit('input', value);
      this.emitChange(value);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', value);
    },
    // 词搜索
    handleQueryChange(query) {
      if (!this.filterable) {
        return;
      }
      if (this.filterMethod && typeof(this.filterMethod) === 'function') {
        this.filterMethod(query);
      } else {
        // 初始化过滤选项值
        this.broadcast(`${CSS_PREFIX}option`, 'queryChange', query.trim());
      }
    },
    // 过滤input事件
    handlefilterInput(value) {
      this.handleQueryChange(value);
    },
    handleFilterClear(value) {
      this.handleQueryChange(value);
    },
    // input blur
    handleInputBlur(e) {
      this.$emit('blur', e);
    },
    // input focus
    handleInputFocus(e) {
      this.$emit('focus', e);
    },
    // this.$refs.multiple resize,更新popper
    handleResize() {
      if (this.optionShow) {
        this.broadcast(`${CSS_PREFIX}popper`, 'bui.popper.update');
      }
    },
  },
};
</script>



