
<template>
  <div
    :style="fixed&&rowHeight ? {} : styles"
  >
    <template v-for="(rdata, rindex) in data">
      <div
        ref="tr"
        :key="rindex"
        :class="getRowClass(rdata)"
        :style="{
          position: 'relative',
          height: rowHeight,
        }"
        class="bui-table-tr"
        @click="handleRowClick(rdata, rindex)"
        @mouseenter="handleRowMouseEnter(rdata, rindex)"
        @mouseleave="handleRowMouseLeave(rdata, rindex)"
      >
        <template v-for="(column, cindex) in columns">
          <div
            v-if="showWithSpan(rdata, column, rindex, cindex)"
            :key="column._id"
            :class="getColumnClass(column)"
            :style="getColumnStyle(rdata, column, rindex, cindex)"
            class="bui-table-column"
          >
            <template v-if="isCeilShow(column)">
              <ceil
                :row="rdata"
                :column="column"
                :row-index="rindex"
                :fixed="fixed"
              />
            </template>
          </div>

        </template>
      </div>
      <div
        v-if="rdata.isExpand"
        :key="rindex+'expand'"
        :class="trExpandClass"
      >
        <div class="bui-table-column bui-table-td-expand">
          <expand
            :row="rdata"
            :render="expandRender"
            :index="rindex"
          />
        </div>

      </div>
    </template>
  </div>
</template>

<script>
import Expand from './expand.vue';
import Ceil from './ceil.vue';
import {addClass, removeClass} from 'utils/dom';

export default {
  name: 'TableBodyV',
  components: {
    Expand,
    Ceil,
  },
  props: {
    columns: {
      type: Array,
      default() {
        return [];
      },
    },
    data: {
      type: Array,
      default() {
        return [];
      },
    },
    columnsWidth: {
      type: Object,
      default() {
        return {};
      },
    },
    styles: {
      type: Object,
      default() {
        return {};
      },
    },
    fixed: {
      type: String,
      default: '',
    },
    rowHeight: {
      type: String,
      default: '',
    },
    spanMethod: {
      type: Function,
      default: null,
    },
  },
  data() {
    return {
      spanMethodCache: {},
      posLeftCache: {},
    };
  },
  computed: {
    expandRender() {
      let render = function() {
        return '';
      };
      for (let i = 0; i < this.columns.length; i++) {
        const column = this.columns[i];
        if (column.type && column.type === 'expand') {
          if (column.renderExpand) render = column.renderExpand;
        }
      }
      return render;
    },
    trExpandClass() {
      return {
        'bui-table-tr-expand-hidden': !!this.fixed,
      };
    },
    renderColumns() {
      return this.columns.slice(this.$parent.renderColumns.startIndex, this.$parent.renderColumns.endIndex);
    },
    hoverIndex() {
      return this.$parent.hoverIndex;
    },
  },
  watch: {
    hoverIndex(newVal, oldVal) {
      // rdata.isHover的方式会导致当前行重新渲染，滚动过程中hover会出现卡顿
      if (this.$refs.tr && this.$refs.tr[oldVal]) {
        removeClass(this.$refs.tr[oldVal], 'bui-table-tr-active');
      }
      if (this.$refs.tr && this.$refs.tr[newVal]) {
        addClass(this.$refs.tr[newVal], 'bui-table-tr-active');
      }
    },
  },
  methods: {
    isCeilShow(column) {
      if (!this.rowHeight) {
        return true;
      }
      return (this.fixed && column.fixed=== this.fixed) || (!this.fixed && !column.fixed);
    },

    handleRowClick(row, index) {
      // event.stopPropagation();
      this.$parent.handleRowClick(row, index);
    },
    handleRowMouseEnter(row, rowIndex) {
      this.$parent.handleRowMouseEnter(row, rowIndex );
    },
    handleRowMouseLeave(row, rowIndex) {
      this.$parent.handleRowMouseLeave(row, rowIndex);
    },
    getRowClass(row) {
      const classArr = [].concat(row.rowClassName);
      // if (row.isHover) {
      //   classArr.push('bui-table-tr-active');
      // }
      return classArr;
    },
    getColumnClass(column) {
      return {
        'bui-table-td-hidden': this.fixed && this.fixed !==column.fixed || (!this.fixed && column.fixed),
      };
    },
    getColumnPosLeft(column, columnIndex, colspan, width) {
      if (this.posLeftCache[columnIndex] && width) {
        this.posLeftCache[columnIndex+colspan] = this.posLeftCache[columnIndex] + parseInt(width);
        return this.posLeftCache[columnIndex];
      }
      this.posLeftCache[columnIndex+colspan] = this.columnsWidth[column._id] ?this.columnsWidth[column._id].width : '';
      return 0;
    },
    getColumnStyle(row, column, rowIndex, columnIndex) {
      let width = this.columnsWidth[column._id] ? `${parseInt(this.columnsWidth[column._id].width)}px` : '';
      let height = this.rowHeight;
      if (!width) return {};
      if (this.spanMethod) {
        const result = this.getSpan(row, column, rowIndex, columnIndex);
        width= result.colspan ? this.getSpanColumnWidth(result.colspan, this.columns, columnIndex): width;
        height = result.rowspan ? `${parseInt(this.rowHeight) * result.rowspan}px` : height;
        const left = this.getColumnPosLeft(column, columnIndex, result.colspan === undefined ? 1 : result.colspan, width);
        return {
          width,
          height,
          textAlign: column.align,
          position: 'absolute',
          top: 0,
          left: `${left}px`,
          zIndex: 1,
        };
      }
      const style = {
        width,
        height,
        textAlign: column.align,
      };
      return style;
    },

    getSpanColumnWidth(colspan, columns, columnIndex) {
      let step = 0;
      let width = 0;

      if (!this.columnsWidth) return '';
      while (step < (colspan || 1)) {
        const id = columns[columnIndex + step]._id;
        width += this.columnsWidth[id] ? this.columnsWidth[id].width : '';
        step ++;
      }
      return `${width}px`;
    },
    getSpan(row, column, rowIndex, columnIndex) {
      const {fixed} =this;
      const isColumnHidden = this.fixed && this.fixed !==column.fixed || (!this.fixed && column.fixed);
      let result = {};
      const cacheId = `${rowIndex}-${columnIndex}-${fixed || 'center'}`;
      if (this.spanMethodCache[cacheId]) {
        return this.spanMethodCache[cacheId];
      }
      if (this.spanMethod && !isColumnHidden) {
        result = this.spanMethod ? this.spanMethod({row, column, rowIndex, columnIndex, fixed}) : {};
        let rowspan = 1;
        let colspan = 1;
        if (Array.isArray(result)) {
          rowspan = result[0];
          colspan = result[1];
        } else if (typeof result === 'object') {
          rowspan = result.rowspan;
          colspan = result.colspan;
        }
        result = {
          rowspan,
          colspan,
        };
      }
      this.spanMethodCache[cacheId] = result;
      return result;
    },
    showWithSpan(row, column, rowIndex, columnIndex) {
      const result = this.getSpan(row, column, rowIndex, columnIndex);
      return !(('rowspan' in result && result.rowspan === 0) || ('colspan' in result && result.colspan === 0));
    },
  },

};
</script>



