<template>
  <div
    ref="scrollBox"
    :style="scrollBoxStyle"
    :class="baseClass">
    <div
      ref="scroller"
      :class="scrollerCls"
      class="bui-infinite-scroller-box">
      <slot/>
      <slot
        v-if="!disabled && displayLoading"
        name="loading">
        <div :class="loadingClass">
          <byted-loading-icon/>
          <span>{{ _loadingText }}</span>
        </div>
      </slot>
    </div>
  </div>
</template>
<script>
import {isOneOf} from 'utils/helper.js';
import throttle from 'lodash.throttle';
import {CSS_PREFIX, LOCALE_PREFIX} from '../../base.js';
import loadingIcon from 'components/loading';
import locale from 'mixins/locale';

export default {
  name: `${CSS_PREFIX}infinite-scroll`,
  components: {
    [loadingIcon.iconName]: loadingIcon,
  },
  mixins: [locale],
  props: {
    height: {
      type: Number,
      default: 0,
    },
    width: {
      type: Number,
      default: 0,
    },
    throttle: {
      type: Number,
      default: 200,
    },
    threshold: {
      type: Number,
      default: 50,
    },
    direction: {
      type: String,
      default: 'vertical',
      validator(value) {
        return isOneOf(value, ['vertical', 'horizontal']);
      },
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    loadingText: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      // size
      scrollerHeight: 0,
      scrollerWidth: 0,
      boxHeight: 0,
      boxWidth: 0,

      // states
      loading: false,
      prePosition: 0,
      displayLoading: true,
    };
  },
  computed: {
    baseClass() {
      const clsArr = [`${CSS_PREFIX}infinite-scroll`];
      if (this.disabled) {
        clsArr.push(`${CSS_PREFIX}infinite-scroll--disabled`);
      }
      return clsArr;
    },
    loadingClass() {
      return ['bui-list-loadmore', this.isY ? '' : 'bui-list-loadmore-vertical-rl'];
    },
    scrollerCls() {
      const clsArr = [];
      if (!this.isY) {
        clsArr.push(`${CSS_PREFIX}infinite-scroll--flex`);
      }
      return clsArr;
    },
    scrollBoxStyle() {
      const obj = {};
      if (this.disabled) return obj;

      if (this.direction === 'vertical' && this.height) {
        obj.height = this.height ? `${this.height}px` : '';
      } else if (this.direction === 'horizontal' && this.width) {
        obj.width = this.width ? `${this.width}px` : '';
      }
      return obj;
    },
    isY() {
      return this.direction === 'vertical';
    },
    _loadingText() {
      return this.loadingText || this.t(`${LOCALE_PREFIX}.infinite-scroll.loadingTip`);
    },
  },
  created() {
    if (this.disabled) return;
    this.handleScroll = throttle(this.handleScroll, this.throttle);
  },
  mounted() {
    if (this.disabled) return;
    this.scroller = this.$refs['scroller'];
    this.scrollBox = this.$refs['scrollBox'];

    this.scrollBox.addEventListener('scroll', this.handleScroll);
    this.calcDomSize();
  },
  beforeDestroy() {
    if (this.disabled) return;
    this.scrollBox.removeEventListener('scroll', this.handleScroll);
  },
  updated() {
    if (this.disabled) return;
    this.calcDomSize();
  },
  methods: {
    // we need to calculate/re-calculate the dom size on `mounted` and `updated`
    calcDomSize() {
      if (this.disabled) return;
      this.scrollerHeight = this.scroller.offsetHeight;
      this.scrollerWidth = this.scroller.offsetWidth;
      if (!this.scrollerHeight || !this.scrollerWidth) return;
      this.boxHeight = this.height || this.scrollBox.offsetHeight;
      this.boxWidth = this.width || this.scrollBox.offsetWidth;
      this.prePosition = this.isY ? this.scrollBox.scrollTop : this.scrollBox.scrollLeft;

      if (
        (this.isY && this.boxHeight >= this.scrollerHeight) ||
        (!this.isY && this.boxWidth >= this.scrollerWidth)
      ) {
        // cannot scroll, no need to show loading
        this.displayLoading = false;
      } else {
        this.displayLoading = true;
      }
    },
    handleScroll(ev) {
      if (this.disabled) return;
      const {scrollBox, isY, scrollerHeight, scrollerWidth, boxHeight, boxWidth, threshold, loading, prePosition} = this;
      const scrollDistance = isY ? scrollBox.scrollTop : scrollBox.scrollLeft;
      const scrollerSize = isY ? scrollerHeight : scrollerWidth;
      const boxSize = isY ? boxHeight : boxWidth;

      // when to trigger the loading action:
      // 1. scrolled to the boundary.
      // 2. scroll to the expected direction, e.g:
      //    when scrolling vertically, we need to ensure that the scrolling direction is `up`.
      const shouldTriggerLoading =
        (scrollDistance + boxSize > scrollerSize - threshold) &&
        scrollDistance - prePosition > 0;

      // update the pre position
      this.prePosition = scrollDistance;

      if (shouldTriggerLoading && !loading) {
        // set the `loading` to true
        this.loading = true;
        // trigger the `loadmore` event
        this.$emit('loadmore');
      }
    },
    // this method will be exposed to the end users,
    // users calls it to indicate that the loading is complete.
    reset() {
      if (this.disabled) return;
      this.loading = false;
    },
  },
};
</script>



