import {DEFAULT_FORMAT, DEFAULT_TIME} from './../const';
import {isOneOf} from 'utils/helper.js';
export default {
  props: {
    value: {
      required: true,
      type: [Array, String, Date],
      default: '',
    },
    type: {
      type: String,
      default: 'date',
      validator(value) {
        return isOneOf(value, ['date', 'week', 'year', 'month', 'datetime', 'time']);
      },
    },
    multiple: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: '',
    },
    size: {
      type: String,
      default: 'md',
    },
    clearable: {
      type: Boolean,
      default: false,
    },
    editable: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    format: {
      type: [String, Date],
      default: DEFAULT_FORMAT,
    },
    valueFormat: {
      type: [String, Date],
      default: DEFAULT_FORMAT,
    },
    shortcuts: {
      type: Array,
      default() {
        return [];
      },
    },
    // popper appendtoBody
    transfer: {
      type: Boolean,
      default: false,
    },
    // 默认panel显示的日期
    defaultDate: {
      type: [Date, String],
      default() {
        return new Date();
      },
    },
    disabledDate: {
      type: Function,
      default: function() {
        return false;
      },
    },
    disabledTime: {
      type: Object,
      default: function() {
        return {};
      },
    },
    // 设置用户选择日期时默认的时分秒
    defaultTime: {
      type: String,
      defaut: DEFAULT_TIME,
    },
    popperOptions: {
      type: Object,
      default() {
        return {
          modifiers: {
            flip: {
              enabled: true,
            },
            hide: {
              enabled: false,
            },
            preventOverflow: {
              enabled: true,
              boundariesElement: 'viewport',
            },
          },
        };
      },
    },
    dateRender: {
      type: Function,
      default: null,
    },
  },
};



