<template>
  <div
    v-clickoutside="handleClickoutside"
    :class="baseClass"
    @click="handleClick">
    <div
      ref="wrapper"
      :class="wrapperClass"
    >
      <slot>
        <byted-tag
          v-for="(item, index) in value"
          :key="index"
          :size="size"
          :disabled="disabled"
          :plain="disabled"
          closable
          @close="handleTagDelete(index)">{{ item }}</byted-tag>
      </slot>
      <input
        v-if="!readonly"
        ref="input"
        :placeholder="placeholder"
        :disabled="disabled"
        v-model="label"
        :style="labelInputStyle"
        type="text"
        class="bui-multiple-input-content"
        @keydown.enter="handleEnter"
        @keydown.delete="handleDelete"
        @keydown="handleKeydown"
        @keyup="handleKeyup"
        @focus="handleFocus"
        @compositionstart="handleCompositionStart"
        @compositionend="handleCompositionEnd"
      >
    </div>
    <byted-icon
      v-if="clearable"
      class="bui-multiple-input-clear"
      name="close-circle"
      @click="handleClear" />
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base';
import {isOneOf} from 'utils/helper.js';
import {getMeasureTextWidth} from 'utils/measure-text';
import clickoutside from 'directives/clickoutside';
import emitter from 'mixins/emitter';
export default {
  name: `${CSS_PREFIX}multiple-input`,
  directives: {clickoutside},
  mixins: [emitter],
  props: {
    placeholder: {
      type: String,
      default: '',
    },
    value: {
      type: Array,
      default: () => [],
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    clearable: {
      type: Boolean,
      default: false,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, ['md', 'sm', 'lg']);
      },
    },
    status: {
      type: String,
      default: '',
      validator(value) {
        return isOneOf(value, ['', 'error']);
      },
    },
    // 内部使用，暂时不对外,禁止 dispatch form-item.change
    validateEvent: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      isFocus: false,
      isComposition: false,
      label: '',
      wrapperWidth: 0,
    };
  },
  computed: {
    baseClass() {
      const base = `${CSS_PREFIX}multiple-input`;
      const arr = [base, `${base}-${this.size}`];
      if (this.clearable && (this.value.length || this.label)) {
        arr.push(`${base}-show-clear`);
      }
      if (this.isFocus) {
        arr.push(`${base}-focused`);
      }
      if (this.status) {
        arr.push(`${base}-${this.status}`);
      }
      if (this.disabled) {
        arr.push(`${base}-disabled`);
      }
      return arr;
    },
    wrapperClass() {
      const base = 'bui-multiple-input-wrapper';
      return {
        [base]: true,
        [`${base}-focused`]: this.isFocus,
      };
    },
    labelInputStyle() {
      const placeholderLen = getMeasureTextWidth(this.placeholder);
      const labelLen = getMeasureTextWidth(this.label);
      const min = 14;
      const minWidth = Math.min(this.wrapperWidth, Math.max(placeholderLen, labelLen, min));
      return {
        minWidth: `${minWidth}px`,
      };
    },
  },
  mounted() {
    this.$nextTick(()=>{
      this.initWrapperWidth();
    });
  },
  methods: {
    initWrapperWidth() {
      const paddingLeft = 12;
      const paddingRight = this.clearable ? 32 : 12;
      const clientWidth = (this.$refs.wrapper && this.$refs.wrapper.clientWidth);
      this.wrapperWidth = (clientWidth - paddingLeft - paddingRight) || 0;
    },
    handleClickoutside() {
      if (this.isFocus) {
        this.handleBlur();
      }
      if (this.label) {
        this.handleInputAdd(this.label);
      }
    },
    handleClick(evt) {
      this.$refs.input && this.$refs.input.focus();
      this.$emit('click', evt);
    },
    handleInput() {
      // this.$emit()
    },
    handleCompositionStart() {
      this.isComposition = true;
    },
    handleCompositionEnd() {
      this.isComposition = false;
    },
    handleBlur() {
      if (!this.isFocus) {
        return;
      }
      this.isFocus = false;
      this.$emit('blur');
      if (this.validateEvent) {
        this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.blur', [this.value]);
      }
    },
    handleFocus(evt) {
      if (this.isFocus) {
        return;
      }
      this.isFocus = true;
      this.$emit('focus', evt);
    },
    handleEnter(evt) {
      const target = evt.target;
      const value = target.value.trim();
      if (!value || this.isComposition) return;
      this.handleInputAdd(value);
    },
    handleInputAdd(value) {
      const current = this.value.slice();
      current.push(value);
      this.emitValueInput(current);
      this.resetInputValue();
    },
    handleDelete(evt) {
      const target = evt.target;
      if (target.value|| this.isComposition) return;
      const current = this.value.slice();
      current.pop();
      this.emitValueInput(current);
    },
    handleClear() {
      this.resetInputValue();
      if (!this.value.length) {
        return;
      }
      this.$emit('clear');
      this.emitValueInput([]);
    },
    handleKeydown(evt) {
      this.$emit('keydown', evt);
    },
    handleKeyup(evt) {
      this.$emit('keyup', evt);
    },
    handleTagDelete(index) {
      const current = this.value.slice();
      current.splice(index, 1);
      this.emitValueInput(current);
    },
    emitValueInput(value) {
      this.$emit('input', value);
      this.$emit('change', value);
      if (this.validateEvent) {
        this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [value]);
      }
    },
    resetInputValue() {
      this.label= '';
    },
  },
};
</script>



