<template>
  <div :class="baseClass">
    <slot/>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';

export default {
  name: `${CSS_PREFIX}radio-group`,

  mixins: [emitter],
  props: {
    value: {
      type: [String, Boolean, Number],
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isRadioButton: false,
    };
  },
  computed: {
    baseClass: function() {
      const classArr = [`${CSS_PREFIX}radio-group`];
      if (this.isRadioButton) {
        classArr.push('btn-group');
      }
      return classArr;
    },
  },
  mounted() {
    if (this.$children[0] && this.$children[0].$options.name === `${CSS_PREFIX}radio-button`) {
      this.isRadioButton = true;
    }
  },

  created() {
    this.$on('bui.radio.change', this.radioChange);
  },
  methods: {
    radioChange({val, checked}) {
      this.$emit('input', val);
      this.$emit('change', val);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [val]);
    },
  },
};
</script>



