<template>
  <div
    v-show="visible"
    :class="baseClass"
    @click="handleClick">
    <byted-checkbox
      v-if="select.multiple"
      :value="isSelected"
      :disabled="finallyDisabled"
      class="bui-option-checkbox"
      @click.native.stop
      @change="handleCheckChange"/>
    <div >
      <slot>{{ label }}</slot>
    </div>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';
import BytedCheckbox from 'components/checkbox';

export default {
  name: `${CSS_PREFIX}option`,
  components: {
    BytedCheckbox,
  },

  mixins: [emitter],

  inject: ['select', 'optgroup'],

  props: {
    value: {
      type: [String, Number, Array],
      required: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    label: {
      type: String,
      default: '',
    },
  },

  data() {
    return {
      visible: true,
      innerDisable: this.disabled,
    };
  },

  computed: {
    baseClass: function() {
      const baseCls= `${CSS_PREFIX}option`;
      const classArr = [baseCls];
      if (this.isSelected) {
        classArr.push(`${baseCls}-selected`);
      }
      if (this.finallyDisabled) {
        classArr.push(`${baseCls}-disabled`);
      }
      if (this.select.multiple) {
        classArr.push(`${baseCls}-multiple`);
      }
      if (this.isInGroup) {
        classArr.push(`${baseCls}-ingroup`);
      }
      classArr.push(`${baseCls}-${this.select.size}`);
      return classArr;
    },
    currentLabel() {
      const defaultSlot = this.$slots.default || [{}];
      return this.label || (defaultSlot && defaultSlot[0].text && (defaultSlot[0].text).trim()) || this.value;
    },
    isSelected() {
      return Array.isArray(this.select.value) ? this.select.value.includes(this.value) : this.select.value === this.value;
    },
    isInGroup() {
      return !!this.optgroup;
    },
    isGroupDisable() {
      if (!this.isInGroup) return false;
      return this.optgroup.disabled;
    },
    finallyDisabled() {
      return this.innerDisable || this.isGroupDisable;
    },
  },

  watch: {
    disabled(val) {
      this.innerDisable = val;
    },
  },

  created() {
    this.select.optionsArr.push(this);
    this.$on('queryChange', this.handleQueryChange);
  },

  beforeDestroy() {
    // 调用select上的销毁函数
    this.select.handleOptionDestory(this);
  },

  methods: {
    handleClick() {
      if (this.finallyDisabled) {
        return;
      }
      this.select.handleOptionClick(this); // 调用select上的函数
    },
    handleCheckChange() {
      this.handleClick();
    },
    handleQueryChange(query) {
      // query 里如果有正则中的特殊字符，需要先将这些字符转义
      const parsedQuery = String(query).replace(/(\^|\(|\)|\[|\]|\$|\*|\+|\.|\?|\\|\{|\}|\|)/g, '\\$1');
      this.visible = new RegExp(parsedQuery, 'i').test(this.currentLabel);
    },
  },
};
</script>




