
<template>
  <div>
    <template v-if="options.length > 0">
      <div
        v-for="(menu, menuIndex) in activeOptions"
        ref="cascaderMenu"
        :key="menuIndex"
        :class="menuClass"
        class="bui-cascader-menu"
      >
        <byted-infinite-scroll
          ref="scroll"
          :height="190"
          :disabled="!shouldInfiniteable(menuIndex)"
          @loadmore="handleLoadmore(menuIndex, menu)"
        >
          <ul class="bui-cascader-menu-list">
            <li
              v-if="multiple && menu.length"
              :class="{
                'bui-cascader-menu-item': true,
                'bui-cascader-menu-item-checkall': true,
              }"
              @click.self="handleMenuCheckAll(!menuAllChecked[menuIndex], menu, menuIndex)">
              <byted-checkbox
                :value="menuAllChecked[menuIndex]"
                :indeterminate="getMenuAllIndeterminate(menu, menuIndex)"
                class="bui-cascader-menu-item-checkbox"
                @change="(checked) => {handleMenuCheckAll(checked, menu, menuIndex)}"/>
              {{ t(`${LOCALE_PREFIX}.menu.selectAll`) }}
            </li>
            <li
              v-for="(item, itemIndex) in menu"
              :key="menuIndex + '' + itemIndex"
              :class="getMenuItemClass(item, itemIndex, menuIndex)"
              :ref="item.value === activeValue[menuIndex] ? 'activeItem' : null"
              @click.self="handleItemClick(item, itemIndex, menuIndex)">
              <byted-checkbox
                v-if="multiple && item.hasCheckbox"
                :value="item.checked"
                :disabled="item.disabled"
                :indeterminate="item.indeterminate"
                class="bui-cascader-menu-item-checkbox"
                @change="handleChecked(item, itemIndex, menuIndex)"/>
              <byted-radio
                v-else-if="isIndependentSingle"
                :value="item.checked"
                :disabled="item.disabled"
                class="bui-cascader-menu-item-radio"
                @click.native.stop.prevent="handleItemClick(item, itemIndex, menuIndex, true)" />
              <div
                class="bui-cascader-menu-item-content"
                @click.self="handleItemClick(item, itemIndex, menuIndex)">
                <template
                  v-if="item.renderLabel"
                >
                  <expand
                    :render="item.renderLabel"
                    :label="item.label"
                    :option="item"
                    @click="handleItemClick(item, itemIndex, menuIndex)"
                  />
                </template>
                <template
                  v-else
                >
                  {{ item.label }}
                </template>
                <template v-if="item.children && item.hasOwnProperty('loading') && item.loading">
                  <byted-loading-icon/>
                </template>
                <template v-else>
                  <byted-icon
                    v-if="item.children"
                    :class="`${item.value === activeValue[menuIndex] ? '' : 'bui-menu-item-arrow-disabled'} bui-menu-item-arrow`"
                    name="angle-right"
                    @click="handleItemClick(item, itemIndex, menuIndex)"
                  />
                </template>
              </div>
            </li>
          </ul>
          <template
            slot="loading">
            <slot
              v-bind="{ level: menuIndex }"
              name="infinite-loading" />
          </template>
        </byted-infinite-scroll>
      </div>
    </template>
    <template v-else>
      <div
        class="bui-cascader-menu-empty">
        <byted-icon name="empty"/>
        {{ _noDataText }}
      </div>
    </template>
  </div>
</template>

<script>
import {scrollIntoView} from 'utils/dom.js';
import {isValueChecked} from './util.js';
import {LOCALE_PREFIX} from '../../base.js';
import localeMixin from 'mixins/locale';
import expand from './expand.vue';
import BytedIcon from 'components/icon';
import BytedCheckbox from 'components/checkbox';
import BytedRadio from 'components/radio';
import BytedLoadingIcon from 'components/loading';
import BytedInfiniteScroll from 'components/infinite-scroll';

export default {
  name: `cascader-menus`,
  components: {
    expand,
    BytedIcon,
    BytedRadio,
    BytedCheckbox,
    BytedLoadingIcon,
    BytedInfiniteScroll,
  },
  mixins: [localeMixin],
  inject: ['cascader'],
  props: {
    options: {
      type: Array,
      default() {
        return [];
      },
    },
    originalOptions: {
      type: Array,
      default() {
        return [];
      },
    },
    value: {
      type: Array,
      default() {
        return [];
      },
    },
    visible: {
      type: Boolean,
      default: false,
    },
    // 非子节点选中是否向外派发
    changeOnSelect: {
      type: Boolean,
      default: false,
    },
    transfer: {
      type: Boolean,
      default: false,
    },
    multiple: {
      type: Boolean,
      default: false,
    },
    props: {
      type: Object,
      default: () => {
        return {};
      },
    },
  },

  data() {
    return {
      activeValue: this.value.length ? !this.multiple ? this.value.slice() : this.value[0].slice() : [],
      LOCALE_PREFIX,
    };
  },

  computed: {
    _noDataText() {
      return this.noDataText || this.t(`${LOCALE_PREFIX}.cascader.noDataText`);
    },
    // 按照层级生成的数组
    activeOptions: {
      get() {
        const loadActiveOptions = (options, originalOptions, activeOptions, parent) => {
          const level = activeOptions.length;
          activeOptions[level] = options;
          activeOptions[level].parent = parent;
          activeOptions[level].originalSiblings = originalOptions;

          const active = this.activeValue[level];
          if (active !== undefined && active !== null) {
            options.some((item, i) => {
              if (item.value === active) {
                options = item;
                originalOptions = originalOptions[i];
                parent = originalOptions;
                return true;
              }
              return false;
            });
            if (options && options.children) {
              loadActiveOptions(options.children, originalOptions.children, activeOptions, parent);
            }
          }
          return activeOptions;
        };
        return loadActiveOptions(this.options, this.originalOptions, [], null);
      },
    },
    menuAllChecked() {
      return this.activeOptions.map((menu) =>{
        const flag = menu.every((item) => {
          if (item.disabled) return true;
          return item.checked;
        });
        return flag;
      });
    },
    menuClass() {
      return {
        'bui-cascader-menu-multiple': this.multiple,
        'bui-cascader-menu-infinite': this.cascader.infinite,
        'bui-cascader-menu-independent-single': this.isIndependentSingle,
      };
    },
    isIndependentSingle() {
      return this.cascader.independent && !this.multiple;
    },
    isIndependentMultiple() {
      return this.cascader.independent && this.multiple;
    },
  },

  watch: {
    value(newVal) {
      if (!this.multiple) {
        this.activeValue = this.value.slice();
      } else if (this.multiple && !newVal.length) {
        // 选中项为空，激活项为空
        this.activeValue = [];
      }
    },
    visible(newVal) {
      if (newVal) {
        this.$forceUpdate();
        this.handleCascaderMenuEnter();
      }
    },
  },

  methods: {
    getMenuItemClass(item, itemIndex, menuIndex) {
      return {
        'bui-cascader-menu-item': true,
        'bui-cascader-menu-item-children': item.children,
        'bui-cascader-menu-item-leaf': !item.children,
        'active': item.value === this.activeValue[menuIndex],
        'disabled': item.disabled,
      };
    },
    shouldInfiniteable(level) {
      return typeof this.cascader.infinite === 'boolean' ? this.cascader.infinite : this.cascader.infinite.indexOf(level) > -1;
    },
    handleLoadmore(level, siblings) {
      this.cascader.$emit('loadmore', {
        level, parent: siblings.parent,
        siblings: siblings.originalSiblings,
        reset: this.$refs.scroll[level].reset,
      });
    },
    // getMenuChecked(menu, menuIndex) {
    //   const flag = Object.keys(menu).every((key) => {
    //     if (menu[key].disabled) return true;
    //     return menu[key].checked;
    //   });
    //   return flag;
    // },
    getMenuAllIndeterminate(menu, menuIndex) {
      return !this.menuAllChecked[menuIndex] && menu.some((item) => {
        // 选项checked为true or 有不确定性
        return item.indeterminate || item.checked;
      });
    },
    handleMenuCheckAll(checked, menu, menuIndex) {
      const value = this.value.slice();
      let result= [];
      menu.forEach((item) => {
        if (item.disabled) return;
        if (!checked) {
          result = this.handleLeafNodeUnChecked(item, value);
        } else {
          result = this.handleLeafNodeChecked(item, value);
        }
      });
      this.$emit('change', result, !this.multiple);
    },
    handleItemClick(item, itemIndex, menuIndex, makeSelect = false) {
      // 展开子选项
      if (item.disabled) return;
      if (item.children) {
        this.activeValue.splice(menuIndex, this.activeValue.length, item.value);

        // independent + non-multiple
        if (this.isIndependentSingle && makeSelect) {
          this.handleSelect(item, itemIndex, menuIndex, false /* close */);
        }
        this.handleActiveItem(item, itemIndex, menuIndex);
        return;
      }

      this.handleSelect(item, itemIndex, menuIndex, !this.isIndependentSingle);
      this.$nextTick(() => {
        const menuref = this.$refs.cascaderMenu[menuIndex];
        menuref && scrollIntoView(menuref, menuref.getElementsByClassName('active')[0]);
      });
    },
    handleActiveItem(item, itemIndex, menuIndex) {
      if (this.changeOnSelect) {
        this.$emit('change', this.activeValue.slice(), false);
      } else {
        this.$emit('active-item-change', this.activeValue.slice());
      }
    },
    handleSelect(item, itemIndex, menuIndex, close = !this.multiple) {
      this.activeValue.splice(menuIndex, this.activeValue.length, item.value);
      let value = this.value.slice();
      if (this.multiple) {
        this.handleChecked(item, itemIndex, menuIndex);
      } else {
        value = this.activeValue.slice();
        this.$emit('change', value, close);
      }
    },
    handleChecked(item, itemIndex, menuIndex) {
      if (!this.isIndependentMultiple) {
        this.activeValue.splice(menuIndex, this.activeValue.length, item.value);
      }
      const value = this.value.slice();
      let result= [];
      if (item.checked) {
        result = this.handleLeafNodeUnChecked(item, value);
      } else {
        result = this.handleLeafNodeChecked(item, value);
      }
      this.$emit('change', result, !this.multiple);
    },
    handleLeafNodeChecked(item, result) {
      if (this.isIndependentMultiple) {
        if (!isValueChecked(result, item.parent.concat(item.value), this.multiple)) {
          result.push(item.parent.concat(item.value));
        }
      } else {
        if (item.children && item.children.length) {
          item.children.forEach((cItem) => {
            this.handleLeafNodeChecked(cItem, result);
          });
        } else if (!isValueChecked(result, item.parent.concat(item.value), this.multiple)) {
          result.push(item.parent.concat(item.value));
        }
      }
      return result;
    },
    handleLeafNodeUnChecked(item, result) {
      const currentPath = item.parent.concat(item.value);
      if (item.checked || isValueChecked(result, currentPath, this.multiple)) {
        const index = result.map((item) => {
          return JSON.stringify(item);
        }).indexOf(JSON.stringify(currentPath));
        index > -1 && result.splice(index, 1);
        if (!this.isIndependentMultiple && item.children) {
          item.children.forEach((cItem) => {
            this.handleLeafNodeUnChecked(cItem, result);
          });
        }
      }
      return result;
    },
    handleParentNodeUnChecked(item) {

    },
    handleCascaderMenuEnter() {
      // 级联选择器打开时，处理选中项显示在可视区域
      const cascaderMenu = this.$refs.cascaderMenu || [];
      this.$nextTick(() =>
        cascaderMenu.forEach((menu) =>
          scrollIntoView(menu, menu.getElementsByClassName('active')[0])
        )
      );
    },
  },
};
</script>



