<template>
  <li :class="baseClass">
    <template v-if="ellipsis">
      <span
        v-clickoutside="clickoutsideHandle"
        class="bui-breadcrumb-content bui-breadcrumb-ellipsis"
      >
        <span @click="handleEllipsisClick">...</span>
        <bui-collapse-transition>
          <div
            v-show="isExpand"
            class="bui-breadcrumb-vertical-container"
          >
            <slot/>
          </div>
        </bui-collapse-transition>
      </span>
    </template>
    <template v-else>
      <a
        v-if="href"
        :href="href"
        :style="itemStyle"
        class="bui-breadcrumb-content"
        @click="handleClick"
      ><slot/></a>
      <span
        v-else
        :style="itemStyle"
        :class="['bui-breadcrumb-content', {'bui-breadcrumb-is-link': router && to}]"
        @click="handleClick"
      ><slot/></span>
    </template>
    <separator :separator="separator"/>
  </li>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import Separator from './separator.js';
import BuiCollapseTransition from 'utils/collapse-transition.vue';
import clickoutside from 'directives/clickoutside';

export default {
  name: `${CSS_PREFIX}breadcrumb-item`,
  inject: ['breadcrumb'],
  directives: {clickoutside},
  components: {
    BuiCollapseTransition,
    Separator,
  },
  props: {
    href: {
      type: String,
      default: '',
    },
    maxWidth: {
      type: String,
      default: '',
    },
    router: {
      type: Boolean,
      default: false,
    },
    to: {
      type: [String, Object],
      default() {
        return {};
      },
    },
    replace: {
      type: Boolean,
      default: false,
    },
    ellipsis: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      separator: '',
      isExpand: false,
    };
  },
  computed: {
    baseClass() {
      return [`${CSS_PREFIX}breadcrumb-item`];
    },
    itemStyle() {
      return this.maxWidth ? {
        'max-width': this.maxWidth,
        'overflow': 'hidden',
        'text-overflow': 'ellipsis',
        'white-space': 'nowrap',
      } : {};
    },
  },
  mounted() {
    this.separator = this.breadcrumb.separator;
  },
  methods: {
    handleClick(evt) {
      if (this.router) {
        const {to, $router} = this;
        if (!to || !$router) return;
        this.replace ? $router.replace(to) : $router.push(to);
      }
      this.$emit('click', evt);
    },
    handleEllipsisClick(evt) {
      this.isExpand = !this.isExpand;
    },
    clickoutsideHandle() {
      if (this.isExpand) {
        this.isExpand = false;
      }
    },
  },
};
</script>



