<template>
  <div :class="baseClass">
    <div
      :class="{
        'bui-switch-checked':value,
        'bui-switch-disabled':disabled,
        'bui-switch-loading': loading
      }"
      class="bui-switch"
      @click="handleClick"
    >
      <span
        v-if="loading"
        :class="{
          'bui-switch-loading-icon': loading,
        }"
      >
        <byted-loading-icon
          :class="{
            'bui-loading-group-default': !value,
            'bui-loading-group-active': value,
          }"
          :size="size"
        />
      </span>
      <div class="bui-switch-inner"/>
    </div>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import {isOneOf} from 'utils/helper.js';
import emitter from 'mixins/emitter';
import loadingIcon from 'components/loading';

export default {
  name: `${CSS_PREFIX}switch`,
  components: {[loadingIcon.iconName]: loadingIcon},
  mixins: [emitter],
  props: {
    value: {
      type: Boolean,
      default: false,
      require: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, ['md', 'sm', 'lg']);
      },
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {};
  },
  computed: {
    baseClass() {
      return [`${CSS_PREFIX}switch`, `${CSS_PREFIX}switch-${this.size}`];
    },
  },
  methods: {
    handleClick() {
      if (this.disabled || this.loading) {
        return;
      }
      this.$emit('input', !this.value);
      this.$emit('change', !this.value);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [!this.value]);
    },
  },
};
</script>



