import {
  addClass,
  removeClass,
  insertAfter,
} from 'utils/dom.js';
import {
  setScrollBar,
  resetScrollBar,
} from './setScrollEffect.js';

const backdrop = document.createElement('div');
addClass(backdrop, 'byted-modal-backdrop fade');
const className = 'byted-modal-open';

function getAllModal() {
  const modalList = new Array(...document.getElementsByClassName('byted-modal'));
  const openModalList = modalList.filter((item) => item.dataset['show'] === 'true');
  return {
    all: modalList,
    open: openModalList,
  };
}

export function addBackdrop() {
  const modalList = getAllModal();
  const isMulti = modalList.open.length > 0;
  setScrollBar();
  addClass(document.body, className);
  backdrop.remove();
  if (isMulti) {
    const refNode = modalList.open[modalList.open.length - 1];
    insertAfter(backdrop, refNode);
  } else {
    const refNode = modalList.all[0];
    document.body.insertBefore(backdrop, refNode);
  }
  setTimeout(() => {
    addClass(backdrop, 'in');
  }, 0);
};

export function removeBackdrop() {
  const modalList = getAllModal();
  const isMulti = modalList.open.length > 1; // 移除是延时的
  if (!isMulti) {
    removeClass(backdrop, 'in');
  }
  setTimeout(() => {
    const modalList = getAllModal();
    const isMulti = modalList.open.length > 0;
    if (!isMulti) {
      resetScrollBar();
      removeClass(document.body, className);
    }
    backdrop.remove();
    if (isMulti) {
      const refNode = modalList.open[modalList.open.length - 1];
      addClass(backdrop, 'in');
      document.body.insertBefore(backdrop, refNode);
    }
  }, 250); // 与 backdrop 的动画时间保持一致,防止 body 上的 overflow:hidden 带来的模态框抖动问题
};



