import SelectPanel from './src/select-panel.vue';
import SelectPanelModuler from './src/select-panel-moduler.vue';
import SelectPanelItem from './src/select-panel-item.vue';

SelectPanel.install = function(Vue) {
  Vue.component(SelectPanel.name, SelectPanel);
  Vue.component(SelectPanelModuler.name, SelectPanelModuler);
  Vue.component(SelectPanelItem.name, SelectPanelItem);
};

export default SelectPanel;



