<template>
  <div :class="baseClass">
    <textarea
      :rows="rows"
      :placeholder="placeholder"
      :disabled="disabled"
      :readonly="readonly"
      :value="value"
      :minlength="minlength"
      :maxlength="maxlength"
      :style="{ resize: resize }"
      class="bui-textarea"
      @input="handleInput"
      @blur="handleBlur"
      @focus="handleFocus"
      @keydown="handleKeydown"
      @keyup="handleKeyup"
      @change="handleChange"
      @click="handleClick"
    />
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import emitter from 'mixins/emitter';
import {isOneOf} from 'utils/helper.js';

export default {
  name: `${CSS_PREFIX}textarea`,
  mixins: [emitter],

  props: {
    value: {
      type: [String, Number],
      default: '',
    },
    placeholder: {
      type: String,
      default: '',
    },

    resize: {
      type: [String],
      default: 'none',
    },
    rows: {
      type: [String, Number],
      default: 3,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    // 输入的最小长度
    minlength: {
      type: Number,
      default: 0,
    },
    // 输入的最大长度
    maxlength: {
      type: Number,
      default: Infinity,
    },
    status: {
      type: String,
      default: '',
      validator(value) {
        return isOneOf(value, ['', 'error']);
      },
    },
  },
  computed: {
    baseClass() {
      const classArr = [`${CSS_PREFIX}textarea`];
      if (this.status) {
        classArr.push(`${CSS_PREFIX}textarea-error`);
      }
      return classArr;
    },
  },
  methods: {
    handleBlur(event) {
      this.$emit('blur', event);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.blur', [this.value]);
    },
    handleInput: function(event) {
      const value = event.target.value;
      this.$emit('input', value);
      this.$emit('change', value, event);
      this.dispatch(`${CSS_PREFIX}form-item`, 'bui.form.change', [this.value]);
    },
    handleFocus(event) {
      this.$emit('focus', event);
    },
    handleKeydown(event) {
      this.$emit('keydown', event);
    },
    handleKeyup(event) {
      this.$emit('keyup', event);
    },
    handleChange(event) {
      this.$emit('textarea-change', event);
    },
    handleClick(event) {
      this.$emit('click', event);
    },
  },
};
</script>




