<template>
  <div
    v-clickoutside="{handler: handleClickoutside, capture: useClickOutsideCapture}"
    :class="wrapperClass">
    <byted-popper
      v-show = "!disabled && popoverShow"
      ref = "popper"
      :class = "baseClass"
      :style = "popperStyle"
      :reference = "referenceEle"
      :placement = "placement"
      :popper-options = "popperMergeOptions"
      :append-to-body = "transfer"
      :fit-popper-resize = "fitPopperResize"
    >
      <!-- arrow -->
      <div
        v-if="arrow"
        x-arrow
        class="bui-popover-arrow"/>
      <!-- /arrow -->
      <div class="bui-popover-panel">
        <!-- header -->
        <div
          v-if = "$slots.header || title"
          class="bui-popover-header">
          <slot
            v-if = "$slots.header"
            name="header"/>
          <template v-else>{{ title }}</template>
        </div>
        <!-- /header -->
        <!-- body -->
        <div class="bui-popover-body">
          <slot
            v-if = "$slots.body"
            name="body"/>
        </div>
        <!-- /body -->
        <!-- footer -->
        <div
          v-if = "$slots.footer"
          class = "bui-popover-footer">
          <slot name = "footer"/>
        </div>
        <!-- /footer -->
        <slot/>
      </div>
    </byted-popper>
    <!-- 参照物对象 -->
    <slot name="reference"/>
  <!-- /参照物对象 -->
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import {on, off, contains, hasClass} from 'utils/dom.js';
import {isOneOf} from 'utils/helper.js';
import BytedPopper from 'components/popper';

export default {
  name: `${CSS_PREFIX}popover`,

  components: {
    BytedPopper,
  },

  directives: {
    clickoutside: {
      bind(el, binding, vnode) {
        const handler = typeof binding.value === 'object' ? binding.value.handler : binding.value;
        function documentHandler(e) {
          if (el && el.contains && el.contains(e.target)) {
            return false;
          }
          if (handler || binding.expression) {
            handler(e);
          }
        }
        const modifiers = binding.modifiers || {};
        const capture = modifiers.capture || binding.value.capture || false;

        el.__vueClickOutside__ = documentHandler;
        el.__vueCapture__ = capture;
        document.addEventListener('click', documentHandler, capture);
      },

      unbind(el, binding) {
        document.removeEventListener('click', el.__vueClickOutside__, el.__vueCapture__);
        delete el.__vueClickOutside__;
        delete el.__vueCapture__;
      },
    },
  },
  props: {
    placement: {
      type: String,
      default: 'top',
    },
    value: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: '',
    },
    trigger: {
      type: String,
      default: 'click',
      validator(value) {
        return isOneOf(value, ['click', 'hover', 'manual']);
      },
    },
    offset: {
      type: [String, Number],
      default: 0,
    },
    width: {
      type: [String, Number],
      default: '410px',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    // 统一命名为showDelay，之后版本会下掉该属性
    openDelay: {
      type: Number,
      default: 0,
      validator(value) {
        if (value) {
          console.warn('[BUI Warning][Popover] open-delay should change to show-delay ');
        }
        return true;
      },
    },
    showDelay: {
      type: Number,
      default: 0,
    },
    hideDelay: {
      type: Number,
      default: 200,
    },

    transfer: {
      type: [Boolean, HTMLElement],
      default: false,
    },
    status: {
      type: String,
      default: '',
      validator(value) {
        return isOneOf(value, ['', 'error']);
      },
    },
    // 鼠标是否可进入到 popper 中
    enterable: {
      type: Boolean,
      default: true,
    },
    popperOptions: {
      type: Object,
      default() {
        return {
          modifiers: {
            flip: {
              enabled: false,
            },
            hide: {
              enabled: false,
            },
            preventOverflow: {
              enabled: false,
            },
          },
        };
      },
    },
    // 是否监听popper内容变化自动更新弹框位置
    fitPopperResize: {
      type: Boolean,
      default: false,
    },
    popperClass: {
      type: [String, Array],
      default: '',
    },
    arrow: {
      type: Boolean,
      default: true,
    },
    useClickOutsideCapture: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      popoverShow: false,
      referenceEle: null, // 定位参照物对象
      timer: null, // 内部用定时器
      bindEvtInterval: null,
    };
  },
  computed: {
    wrapperClass: function(param) {
      const classArr = [`${CSS_PREFIX}popover-wrapper`];
      return classArr;
    },
    baseClass: function() {
      let classArr = [`${CSS_PREFIX}popover`];
      if (this.status) {
        classArr.push(`bui-popover-${this.status}`);
      }

      if (this.popperClass) {
        if (typeof this.popperClass === 'string') {
          classArr.push(this.popperClass);
        }
        if (Array.isArray(this.popperClass)) {
          classArr = classArr.concat(this.popperClass);
        }
      }
      return classArr;
    },
    popperStyle: function() {
      return {
        width: this.width,
      };
    },
    popper: function() {
      return this.$refs['popper'];
    },
    popperMergeOptions() {
      // 兼容offset
      const modifiers = Object.assign({}, this.popperOptions.modifiers, {
        offset: {
          offset: this.offset,
        },
      });
      // 兼容placement
      const placement = this.placement;
      return Object.assign({}, this.popperOptions, {placement, modifiers});
    },
  },
  watch: {
    value(newVal) {
      if (this.trigger === 'manual') {
        this.popoverShow = newVal;
      }
    },
    popoverShow(newVal) {
      if (this.disabled) {
        return;
      }
      if (this.trigger === 'manual') {
        this.$emit('input', newVal);
      }
      this.$emit('visible-change', newVal);
      if (newVal) {
        this.popper.update();
      } else {
        this.popper.destroy();
      }
    },
    disabled(newVal) {
      if (newVal) {
        this.popoverShow = false;
      }
    },
  },
  mounted() {
    // 处理 referenceEle
    if (this.$slots['reference']&&this.$slots['reference'][0]) {
      this.referenceEle = this.$slots['reference'][0].elm;
    }
    if (this.referenceEle) {
      this.bindRefenerceEvent();
    } else {
      // f**k vue
      // f**k vue again
      // 用 2s 延时来兜底
      let count = 0;
      this.bindEvtInterval = window.setInterval(() => {
        if (this.referenceEle) {
          window.clearInterval(this.bindEvtInterval);
          this.bindRefenerceEvent();
          this.bindEvtInterval = null;
        }
        // 防止出现定时器反复执行的情况
        count++;
        if (count > 10) {
          window.clearInterval(this.bindEvtInterval);
          this.bindEvtInterval = null;
        }
      }, 2000);
    }
    // 处理使用 manual 模式下,默认为 true 情况下
    // 需要保证在 refenerce 已经存在的情况下,进行赋值
    if (this.trigger === 'manual') {
      this.$nextTick(()=>{
        this.popoverShow = this.value;
      });
    }
  },
  destroyed() {
    this.unbindRefenerceEvent();
  },
  methods: {
    // 绑定参照物处理事件
    bindRefenerceEvent() {
      const referenceEle = this.referenceEle;
      if (!referenceEle || !this.popper) {
        // console.warning('定位参照物不存在，请使用 v-popover:xxx 指定定位参照物');
        return false;
      }
      if (this.trigger === 'click') {
        on(referenceEle, 'click', this.togglePop);
      } else if (this.trigger === 'hover') {
        on(referenceEle, 'mouseenter', this.handleMouseEnter);
        on(this.popper.$el, 'mouseenter', this.handleMouseEnter);
        on(referenceEle, 'mouseleave', this.handleMouseLeave);
        on(this.popper.$el, 'mouseleave', this.handleMouseLeave);
      }
    },
    handleMouseEnter(e) {
      if (e.target === this.popper.$el && !this.enterable) {
        return;
      }
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.showPop();
      }, this.showDelay || this.openDelay );
    },
    handleMouseLeave() {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.hidePop();
      }, this.hideDelay);
    },
    // 切换
    togglePop() {
      this.popoverShow ? this.hidePop() : this.showPop();
    },
    // @expose-api 隐藏
    hidePop() {
      if (this.disabled || !this.popoverShow) {
        return false;
      }
      this.popoverShow = false;
    },
    // 显示
    showPop() {
      if (this.disabled) {
        return false;
      }
      this.popoverShow = true;
    },
    unbindRefenerceEvent() {
      const reference = this.referenceEle;
      clearTimeout(this.timer);
      // 解绑事件
      off(reference, 'click', this.doToggle);
      off(reference, 'mouseleave', this.handleMouseLeave);
      off(reference, 'mouseenter', this.handleMouseEnter);
    },
    isInPopover(el) {
      do {
        if (hasClass(el, 'bui-popper')) {
          return el;
        }
      } while (el = el.parentNode);
      return false;
    },
    handleClickoutside(ev) {
      if (this.trigger !== 'click') return false;

      const popperEl = this.isInPopover(ev.target);
      if (
        // 1. The target element is contained in the popper
        contains(ev.target, this.$refs.popper && this.$refs.popper.$el) ||
        // 2. The `reference` element of the nested popper is contained in the parent popper
        (popperEl && popperEl.__vue__ && contains(popperEl.__vue__.reference, this.$refs.popper.$el))
      ) {
        return false;
      }

      this.hidePop();
    },
  },
};
</script>




