<template>
  <transition
    name="modal-fade"
    @after-enter="afterEnter"
    @after-leave="afterLeave">
    <div
      v-transfer-dom
      v-show="innerValue"
      :class="baseClass"
      :data-show="innerValue"
      data-transfer="true"
      tabindex="-1"
      @click.self="maskClick">
      <div
        v-if="vIfValue"
        :style="dialogComputedStyle"
        :class="sizeClass"
        class="bui-modal-dialog">
        <div class="bui-modal-content">
          <div
            v-if="hasHeader"
            :class="{
              'bui-modal-dividing-line': dividingLine,
            }"
            class="bui-modal-header">
            <slot
              v-if="$slots.header"
              name="header"/>
            <template v-else>
              <div class="bui-modal-title">{{ title }}</div>
              <byted-icon
                v-if="showClose === 'unset'"
                name="close"
                class="modal-close-icon"
                width="20px"
                height="20px"
                @click="closeModal"/>
            </template>
          </div>
          <div
            v-if="$slots.body || $slots.default"
            :class="{
              'bui-modal-dividing-line': dividingLine,
              'bui-no-slot-head': !hasHeader,
              'bui-no-slot-footer': !$slots.footer,
            }"
            class="bui-modal-body">
            <slot
              v-if="$slots.body"
              name="body"/>
            <slot v-else/>
          </div>
          <div
            v-if="$slots.footer"
            :class="{
              'bui-modal-dividing-line': dividingLine,
            }"
            class="bui-modal-footer">
            <slot name="footer"/>
          </div>
          <byted-icon
            v-if="showClose===true"
            name="close"
            class="bui-modal-close-icon"
            width="20px"
            height="20px"
            @click="closeModal"/>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';
import {addBackdrop, removeBackdrop} from './backdrop.js';
import TransferDom from 'directives/transfer-dom';
// import emitter from 'mixins/emitter';
import Popup from 'mixins/popup/index.js';
import BytedIcon from 'components/icon';
import {on, off} from 'utils/dom.js';
import {isOneOf} from 'utils/helper.js';

export default {
  name: `${CSS_PREFIX}modal`,
  directives: {TransferDom},
  components: {
    BytedIcon,
  },
  mixins: [Popup],
  props: {
    title: {
      type: [String, Function],
      default: '',
    },
    value: {
      type: Boolean,
      default: false,
    },
    top: {
      type: String,
      default: '',
    },
    size: {
      type: String,
      default: '',
    },
    dividingLine: {
      type: Boolean,
      default: false,
    },
    keyboard: {
      type: Boolean,
      default: false,
    },
    forceRender: {
      type: Boolean,
      default: false,
    },
    showClose: {
      type: [Boolean, String],
      default: 'unset',
      validator(value) {
        return isOneOf(value, ['unset', true, false]);
      },
    },
  },
  data() {
    return {
      innerValue: false,
    };
  },
  computed: {
    baseClass() {
      const classArr = [`${CSS_PREFIX}modal`];
      if (this.top) {
        classArr.push(`${CSS_PREFIX}modal-topfix`);
      }
      return classArr;
    },
    sizeClass() {
      const size = this.size;
      const classArr = [];
      if (size === 'small' || size === 'sm') {
        classArr.push(`bui-modal-sm`);
      } else if (size === 'large' || size === 'lg') {
        classArr.push(`bui-modal-lg`);
      } else if (size === 'normal'|| size === 'md') {

      }
      return classArr;
    },
    hasHeader() {
      return this.$slots.header || (this.title !== '' && this.title !== null);
    },
    modalWidth() {
      if (!['small', 'sm', 'large', 'lg', 'normal', 'md'].includes(this.size)) {
        return this.size;
      }
    },
    dialogComputedStyle() {
      const {modalWidth: width, top} = this;
      return {
        width,
        marginTop: top,
      };
    },
    vIfValue() {
      if (this.forceRender && !this.innerValue) {
        return false;
      } else {
        return true;
      }
    },
  },
  watch: {
    value(newValue) {
      this.innerValue = newValue;
      // this.toggleClass(newValue);
    },
    innerValue(newValue) {
      if (newValue) {
        this.$emit('show');
        if (this.keyboard) {
          on(document, 'keyup', this.handleKeyUp);
        }
      } else {
        this.$emit('hide');
        off(document, 'keyup', this.handleKeyUp);
      }
    },
  },
  beforeDestroy() {
    off(document, 'keyup', this.handleKeyUp);
  },
  destroyed() {
    // if (this.value) {
    //   removeBackdrop();
    // }
  },
  mounted() {
    if (this.value) {
      this.innerValue = this.value;
      this.open && this.open();
    }
  },
  methods: {
    handleKeyUp(e) {
      const keyCode = window.event ? e.keyCode : e.which;
      // 按ESC键，关闭modal弹窗
      if (keyCode === 27) {
        this.keyboard && this.closeModal();
      }
    },
    closeModal() {
      this.$emit('input', false);
    },
    toggleClass(show) {
      if (show) {
        addBackdrop();
        this.broadcast(`${CSS_PREFIX}table`, 'on-visible-change', true);
      } else {
        removeBackdrop();
      }
    },
    maskClick() {
      this.maskClosable && this.closeModal();
    },
    afterEnter() {
      this.$emit('shown');
    },
    afterLeave() {
      this.$emit('hidden');
    },
  },
};
</script>





