<script>
export default {
  name: 'Expand',
  props: {
    render: {
      type: Function,
      default: null,
    },
    label: {
      type: String,
      default: '',
    },
    option: {
      type: Object,
      default: () => { },
    },
  },
  methods: {
    handleClick(e) {
      e.stopPropagation();
      this.$emit('click');
    },
  },
  render() {
    if (!this.render) return null;
    return (
      <div class="bui-cascader-custom-render-item" onClick={this.handleClick}>
        {this.render(this.label, this.option)}
      </div>
    );
  },
};

</script>



