import {CSS_PREFIX} from '../../base.js';
import Loading from './loading.vue';

const elKey = 'buiLoading'; // 存储在原生node上的信息key字段名

/**
 * @param {Object} Vue Vue实例
 */
export default function(Vue) {
  Vue.directive(`${CSS_PREFIX}loading`, {
    name: `${CSS_PREFIX}loading`,
    bind(el, {value, arg}) {
      const subLoading = {...Loading, props: {...Loading.props}};
      subLoading.props.show = {
        type: Boolean,
        default: value,
      };
      const context = new Vue(subLoading).$mount();
      context.parent = el;
      context.inDirective = true;
      // context.parentStyle.position = context.parent.style.position;
      // context.parentStyle.overflow = context.parent.style.overflow;
      if (arg) {
        context.size = arg;
      }
      if (context.show) {
        context.showLoading();
      }
      el.appendChild(context.$el);
      el[elKey] = context; // 存储起来
    },
    update(el, {value, arg}) {
      const context = el[elKey];
      if (!context) {
        return;
      }
      context.show = value;
      if (arg) {
        context.size = arg;
      }
    },
    unbind(el) {
      const context = el[elKey];
      if (!context) {
        return;
      }
      delete el[elKey];
    },
  });
}



