<template>
  <div
    :class="baseClass"
    @click="handleClick">
    <byted-icon
      v-if="icon"
      :name="icon"
      color="#fff"
    />
    <img
      v-if="src"
      :src="src" >
    <span
      v-if="$slots.default"
      ref="textRef"
      :style="textStyle"
      :class="textClass">
      <slot/>
    </span>
    <div
      v-if="editable"
      :class="editableClass">
      <byted-icon
        name="camera"
        color="#fff"
      />
    </div>
  </div>
</template>

<script>
import {isOneOf} from 'utils/helper.js';
import BytedIcon from 'components/icon';
import {CSS_PREFIX} from '../../base.js';
// 尺寸对应像素大小
const sizeMap = {
  xl: 80,
  lg: 64,
  md: 48,
  sm: 30,
};

export default {
  name: `${CSS_PREFIX}avatar`,
  components: {
    BytedIcon,
  },
  props: {
    size: {
      type: String,
      default: 'md',
      validator(value) {
        return isOneOf(value, ['xl', 'lg', 'md', 'sm']);
      },
    },
    shape: {
      type: String,
      default: 'circle',
      validator(value) {
        return isOneOf(value, ['circle', 'square']);
      },
    },
    icon: {
      type: String,
      default: null,
    },
    src: {
      type: String,
      default: null,
    },
    editable: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {textStyle: {
      transform: 'scale(1) translateX(-50%)',
    }};
  },
  computed: {
    avatarClass() {
      return `${CSS_PREFIX}avatar`;
    },
    baseClass() {
      const classAttr = [this.avatarClass];
      if (this.size) {
        classAttr.push(`${this.avatarClass}-${this.size}`);
      }
      if (this.shape) {
        classAttr.push(`${this.avatarClass}-${this.shape}`);
      }
      if (this.icon) {
        classAttr.push(`${this.avatarClass}-icon`);
      }
      if (this.src) {
        classAttr.push(`${this.avatarClass}-img`);
      }
      return classAttr;
    },
    textClass() {
      return `${this.avatarClass}-text`;
    },
    editableClass() {
      return `${this.avatarClass}-editable`;
    },
  },
  mounted() {
    if (this.$refs.textRef) {
      const textWidth = this.$refs.textRef.getBoundingClientRect().width;
      const limit = sizeMap[this.size] - 8;
      if (textWidth > limit) {
        this.textStyle = {
          transform: `scale(${(1 / (textWidth / limit).toFixed(2))}) translateX(-50%)`,
        };
      }
    }
  },
  methods: {
    handleClick(e) {
      this.$emit('click', e);
    },
  },
};
</script>



