<template>
  <div
    :class="classes"
    :style="styles">
    <slot/>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';

export default {
  name: `${CSS_PREFIX}col`,

  props: {
    span: {
      type: [Number, String],
      default: 0,
    },
    offset: {
      type: [Number, String],
      default: 0,
    },
    push: {
      type: [Number, String],
      default: 0,
    },
    pull: {
      type: [Number, String],
      default: 0,
    },
    order: {
      type: [Number, String],
      default: '',
    },
  },
  computed: {
    classes() {
      const baseClass = `${CSS_PREFIX}col`;
      const classList = [
        baseClass,
        {
          [`${baseClass}-span-${this.span}`]: this.span,
          [`${baseClass}-order-${this.order}`]: this.order,
          [`${baseClass}-offset-${this.offset}`]: this.offset,
          [`${baseClass}-push-${this.push}`]: this.push,
          [`${baseClass}-pull-${this.pull}`]: this.pull,
        },
      ];
      return classList;
    },
    styles() {
      let style = {};
      style = {
        paddingLeft: this.gutter / 2 + 'px',
        paddingRight: this.gutter / 2 + 'px',
      };
      return style;
    },
    row() {
      let parent = this.$parent;
      while (parent && parent.$options && parent.$options.name !== `${CSS_PREFIX}row`) {
        parent = parent.$parent;
      }
      return parent;
    },
    gutter() {
      return (this.row && this.row.gutter) || 0;
    },
  },
};
</script>



