<template>
  <div :class="baseClass">
    <p class="bui-optgroup-label">{{ label }}</p>
    <slot/>
  </div>
</template>
<script>
import {CSS_PREFIX} from '../../base.js';

export default {
  name: `${CSS_PREFIX}optgroup`,

  inject: ['select'],

  provide() {
    return {
      optgroup: this,
    };
  },

  props: {
    label: {
      type: String,
      required: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      visible: true,
    };
  },

  computed: {
    baseClass: function() {
      const baseCls= `${CSS_PREFIX}optgroup`;
      const classArr = [baseCls];
      classArr.push(`${baseCls}-${this.select.size}`);
      return classArr;
    },
  },
};
</script>




