<template>
  <div :class="baseClass">
    <!-- 第 {{ currentPageTemp }}/ {{totalPage}} 页 -->
    <ul
      v-if="totalPage"
      class="bui-pagination-list">
      <li
        v-if="showTotal"
        class="bui-pagination-total-text">{{ totalText }}</li>

      <!-- prev -->
      <li
        :class="{ disabled: currentPageTemp <= 1 || disabled}"
        class="bui-pagination-prev"
        @click="currentPageTemp > 1 && goPage(currentPageTemp - 1)">
        <a>
          <span v-if="prevText">{{ prevText }}</span>
          <byted-icon
            v-else
            name="angle-left"
            class="bui-pagination-icon"/>
        </a>
      </li>
      <!-- /prev -->

      <template v-if="simple">
        <template v-if="simple.editable === false && plain">
          <li class="bui-pagination-simple-content">
            <span class="bui-pagination-simple-content-input">{{ simpleInput }}</span>
            /
            <span class="bui-pagination-simple-content-total">
              {{ totalPage }}
            </span>
          </li>
        </template>
        <template v-if="simple.editable === true && plain">
          <li class="bui-pagination-simple-content">
            <span class="bui-pagination-simple-content-input">
              <byted-input
                :value="simpleInput"
                :size="size"
                :disabled="disabled"
                type="number"
                @input="handleSimpleInput"
                @keyup.enter="handleSimpleInputEnter"
              />
            </span>
            /
            <span class="bui-pagination-simple-content-total">
              {{ totalPage }}
            </span>
          </li>
        </template>
        <template v-if="simple === true">
          <li class="bui-pagination-simple-content">
            <byted-dropdown
              :disabled="disabled"
              trigger="click"
              align="left">
              <byted-input
                :value="simpleInput"
                :size="size"
                :disabled="disabled"
                type="number"
                @input="handleSimpleInput"
                @keyup.enter="handleSimpleInputEnter" />
              <byted-dropdown-menu slot="list">
                <byted-dropdown-item
                  v-for="item in totalPage"
                  :key="item"
                  @click="handleItemSelect(item)">{{ item }}</byted-dropdown-item>
              </byted-dropdown-menu>
            </byted-dropdown>
          </li>
        </template>
      </template>

      <template v-else>
        <!-- 第一页 -->
        <li
          v-if="midList[0] > 1"
          :class="checkActive(1)"
          class="bui-pagination-num"
          @click="goPage(1)">
          <a>1</a>
        </li>
        <!-- /第一页 -->
        <!-- ... -->
        <li
          v-if="midList[0] > 2"
          class="bui-pagination-ellipsis"
        >
          <span>...</span>
        </li>
        <!-- /... -->
        <!-- 中间区域 -->
        <li
          v-for="page in midList"
          :key="page"
          :class="checkActive(page)"
          class="bui-pagination-num"
          @click="goPage(page)">
          <a>{{ page }}</a>
        </li>
        <!-- /中间区域 -->
        <!-- ... -->
        <li
          v-show="midList[midList.length - 1] < totalPage - 1"
          class="bui-pagination-ellipsis"
        >
          <span>...</span>
        </li>
        <!-- /... -->
        <!-- 最后一页 -->
        <li
          v-if="midList[midList.length - 1] < totalPage"
          :class="checkActive(totalPage)"
          class="bui-pagination-num"
          @click="goPage(totalPage)">
          <a>{{ totalPage }}</a>
        </li>
        <!-- /最后一页 -->
      </template>

      <!-- next -->
      <li
        :class="{ disabled: totalPage <= currentPageTemp || disabled}"
        class="bui-pagination-next"
        @click="totalPage > currentPageTemp && goPage(currentPageTemp + 1)">
        <a>
          <span v-if="nextText">{{ nextText }}</span>
          <byted-icon
            v-else
            name="angle-right"
            class="bui-pagination-icon"/>
        </a>
      </li>
      <!-- /next -->

      <li
        v-if="showSizeChanger"
        class="bui-pagination-size-changer">
        <byted-select
          v-model="pageSizeTemp"
          :size="size"
          :disabled="disabled"
          :placeholder="t(`${LOCALE_PREFIX}.pagination.placeholder`)"
          transfer
          @change="handlePageSizeChange">
          <byted-option
            v-for="item in pageSizes"
            :key="item"
            :value="item"
            :label="item + ' ' + pagesizeText">{{ item + ' ' + pagesizeText }}</byted-option>
        </byted-select>
      </li>

      <li
        v-if="showQuickJumper"
        class="bui-pagination-quick-jumper">
        <byted-input
          v-model="userInput"
          :size="size"
          :disabled="disabled"
          type="number"
          @keyup.enter="handleJump"/>
        <span
          class="bui-pagination-jumper-text"
          @click="handleJump">{{ _gotoText }}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import {CSS_PREFIX, LOCALE_PREFIX} from '../../base.js';
import {isOneOf} from 'utils/helper.js';
import {template} from 'utils/utils.js';
import localeMixin from 'mixins/locale';
import BytedDropdown from 'components/dropdown';
import BytedDropdownItem from 'components/dropdown/src/dropdown-item.vue';
import BytedDropdownMenu from 'components/dropdown/src/dropdown-menu.vue';
import BytedIcon from 'components/icon';
import BytedInput from 'components/input';
import BytedSelect from 'components/select';
import BytedOption from 'components/select/src/option.vue';

export default {
  name: `${CSS_PREFIX}pagination`,

  components: {
    BytedDropdown,
    BytedDropdownItem,
    BytedDropdownMenu,
    BytedIcon,
    BytedInput,
    BytedSelect,
    BytedOption,
  },
  mixins: [localeMixin],

  props: {
    size: {
      type: String,
      default: 'xs',
      validator(value) {
        return isOneOf(value, ['xs', 'sm']);
      },
    },
    plain: {
      type: Boolean,
      default: false,
    },
    simple: {
      type: [Boolean, Object],
      default: false,
      validator(value) {
        if (typeof value === 'boolean') return true;
        if (typeof value === 'object' && typeof value.editable === 'boolean') return true;
        return false;
      },
    },
    currentPage: {
      type: Number,
      default: 1,
    },
    pageSize: {
      type: Number,
      default: 10,
    },
    total: {
      type: Number,
      default: 0,
    },
    prevText: {
      type: String,
      default: '',
    },
    nextText: {
      type: String,
      default: '',
    },
    showTotal: {
      type: Boolean,
      default: false,
    },
    showQuickJumper: {
      type: Boolean,
      default: false,
    },
    showSizeChanger: {
      type: Boolean,
      default: false,
    },
    pageSizes: {
      type: Array,
      default() {
        return [10, 20, 30, 40];
      },
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      currentPageTemp: this.currentPage,
      pageSizeTemp: this.pageSize,
      userInput: '',
      simpleInput: this.currentPage,
      LOCALE_PREFIX,
    };
  },
  computed: {
    baseClass: function() {
      const baseClass = `${CSS_PREFIX}pagination`;
      const classArr= [baseClass, `${baseClass}-${this.size}`];
      if (this.plain && this.simple !== false) {
        classArr.push(`${baseClass}-plain`);
      }
      if (this.simple) {
        classArr.push(`${baseClass}-simple`);
      }
      if (this.disabled) {
        classArr.push(`${baseClass}-disabled`);
      }
      return classArr;
    },
    totalPage() {
      return Math.ceil(this.total / this.pageSizeTemp);
    },
    midList() {
      const totalPage = this.totalPage;
      const currentPageTemp = this.currentPageTemp;
      const minOffset = Math.max(4 - (totalPage - currentPageTemp - 1), 2);
      const maxOffset = Math.max(4 - (currentPageTemp - 1), 2);
      const min = Math.max(currentPageTemp - minOffset, 1);
      const max = Math.min(currentPageTemp + maxOffset, totalPage);
      const midList = [];
      for (let i = min; i <= max; i++) {
        midList.push(i);
      }
      return midList;
    },
    pagesizeText() {
      const globalPageSizeText = this.$BUI && this.$BUI.pagination ? this.$BUI.pagination.pagesizeText : '';
      return globalPageSizeText || this.t(`${LOCALE_PREFIX}.pagination.pagesize`);
    },
    totalText() {
      const opt = {total: this.total};
      const globalTotalText = this.$BUI && this.$BUI.pagination ? this.$BUI.pagination.totalText : '';
      const str = globalTotalText || this.t(`${LOCALE_PREFIX}.pagination.total`, opt);
      return template(str, opt);
    },
    _gotoText() {
      const globalGotoText = this.$BUI && this.$BUI.pagination ? this.$BUI.pagination.gotoText : '';
      return globalGotoText || this.t(`${LOCALE_PREFIX}.pagination.goto`);
    },
  },
  watch: {
    currentPage(val) {
      this.currentPageTemp = val;
    },
    pageSize(val) {
      this.pageSizeTemp = val;
    },
    currentPageTemp(val) {
      if (this.simple) {
        this.simpleInput = val;
      }
    },
  },

  methods: {
    checkActive(pageNum) {
      const classArr = [];
      if (pageNum === this.currentPageTemp) {
        classArr.push(`active`);
      }
      return classArr;
    },
    getValidPage(targetPage) {
      const currentPageTemp = this.currentPageTemp;
      const totalPage = this.totalPage;
      if (totalPage === 0 || currentPageTemp === targetPage) {
        return;
      }

      targetPage = parseInt(targetPage);
      targetPage = isNaN(targetPage) ? 1 : targetPage;

      if (targetPage < 1) {
        targetPage = 1;
      } else if (targetPage > totalPage) {
        targetPage = totalPage;
      }
      return targetPage;
    },
    goPage(targetPage) {
      if (this.disabled) return;
      targetPage = this.getValidPage(targetPage);
      if (!targetPage) return;
      this.currentPageTemp = targetPage;
      this.$emit('page-change', targetPage);
    },
    handleJump() {
      if (this.disabled) return;
      this.goPage(this.userInput);
      this.userInput = this.currentPageTemp;
    },
    handlePageSizeChange() {
      this.$emit('size-change', this.pageSizeTemp);
      if (this.currentPageTemp > this.totalPage) {
        this.currentPageTemp = this.totalPage;
        this.$emit('page-change', this.totalPage);
      }
    },
    handleSimpleInput(val) {
      const targetPage = this.getValidPage(val);
      if (targetPage) {
        this.simpleInput = targetPage;
      }
    },
    handleSimpleInputEnter() {
      const targetPage = this.getValidPage(this.simpleInput);
      if (!targetPage) return;
      this.currentPageTemp = targetPage;
      this.simpleInput = targetPage;
      this.$emit('page-change', targetPage);
    },
    handleItemSelect(targetPage) {
      targetPage = this.getValidPage(targetPage);
      if (!targetPage) return;
      this.currentPageTemp = targetPage;
      this.$emit('page-change', targetPage);
    },
  },
};
</script>



