<script>
import {getStyle, getComponentFromProp} from './util';
function noop() {}

export default {
  props: {
    duration: {
      type: [Number],
      default: 4500,
      validator(value) {
        if (value < 200) {
          console.warn('[BUI warning] Notification duration is too short, please increase it');
        }
        // value === null 不自动关闭
        return typeof value === 'number' || value === null;
      },
    },
    closable: {
      type: Boolean,
      default: false,
    },
    prefixCls: {
      type: String,
      default: '',
    },
    // 组件更新后，是否重置定时
    update: {
      type: Boolean,
      default: false,
    },
    // 类型是vnode或function
    // eslint-disable-next-line vue/require-default-prop
    closeIcon: {
      type: null,
    },
  },
  watch: {
    duration() {
      this.restartCloseTimer();
    },
  },

  mounted() {
    this.startCloseTimer();
  },
  updated() {
    if (this.update) {
      this.restartCloseTimer();
    }
  },

  beforeDestroy() {
    this.clearCloseTimer();
    // beforeDestroy调用后依然会触发onMouseleave事件
    // 此标志位阻止开启新的定时器
    this.willDestroy = true;
  },
  methods: {
    close() {
      this.clearCloseTimer();
      const onClose = this.$listeners['close'];
      onClose && onClose();
    },

    startCloseTimer() {
      this.clearCloseTimer();
      if (!this.willDestroy && this.duration) {
        this.closeTimer = setTimeout(() => {
          this.close();
        }, this.duration);
      }
    },

    clearCloseTimer() {
      if (this.closeTimer) {
        clearTimeout(this.closeTimer);
        this.closeTimer = null;
      }
    },
    restartCloseTimer() {
      this.clearCloseTimer();
      this.startCloseTimer();
    },
  },

  render() {
    const {
      prefixCls,
      closable,
      clearCloseTimer,
      startCloseTimer,
      $slots,
      close,
      $listeners,
    } = this;
    const componentClass = `${prefixCls}-notice`;
    const className = {
      [`${componentClass}`]: 1,
      [`${componentClass}-closable`]: closable,
    };
    const style = getStyle(this);
    const closeIcon = getComponentFromProp(this, 'closeIcon');
    return (
      <div
        class={className}
        style={style || {right: '50%'}}
        onMouseenter={clearCloseTimer}
        onMouseleave={startCloseTimer}
        onClick={$listeners.click || noop}
      >
        <div class={`${componentClass}-content`}>{$slots.default}</div>
        {closable ? (
          <a tabIndex="0" onClick={close} class={`${componentClass}-close`}>
            {closeIcon || <span class={`${componentClass}-close-x`} />}
          </a>
        ) : null}
      </div>
    );
  },
};
</script>




