// 整理数据，增加属性，使其能被vue监控到
function addData(param, expandAll) {
  let resultObj = {};
  resultObj = param.map(function(item) {
    item['selected'] = false;
    item['isOpen'] = expandAll;
    if (!!item['children'] && item['children'].length > 0) {
      item['children'].map(function(child) {
        child['selected'] = false;
        return child;
      });
    }
    return item;
  });
  return resultObj;
}
export default addData;



